<template>
  <div>
    <el-button type="warning" @click="isShowTask=true">新增任务</el-button>
    <el-table
      :data="taskTableData"
      tooltip-effect="dark"
      v-permi="'/customer/addTask'"
      style="width: 100%;margin-top: 20px;"
      border
      :fit="true"
    >
      <el-table-column prop="content" label="任务内容" align="center"></el-table-column>
      <el-table-column prop="startTime" label="开始时间" align="center"></el-table-column>
      <el-table-column prop="type" label="任务类型" width align="center">
        <template slot-scope="scope">{{ scope.row.type|type }}</template>
      </el-table-column>
      <el-table-column prop="remindTime" label="提醒时间" align="center"></el-table-column>
      <el-table-column prop="joinName" label="参与人" align="center"></el-table-column>
      <el-table-column prop="createName" label="创建人" align="center"></el-table-column>
      <el-table-column prop="createTime" label="创建时间" align="center"></el-table-column>
      <el-table-column label="操作" width="150" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" plain @click="editTask(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" @click="deleteTask(scope.$index, scope.row)" type="danger">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      style="text-align:right;margin-top: 20px"
      layout="prev, pager, next"
      @current-change="currentChange"
      :page-size="pageObj.pageSize"
      :total="pageObj.total"
    ></el-pagination>
    <new-task
      :isShowTask="isShowTask"
      :linkData="{id:customerId,isChange:1}"
      @sureDialogFather="getTaskTable();"
      @closeDialogFather="getSonCancel"
    ></new-task>
    <edit-task
      :isEditTask="isEditTask"
      :taskDetail="taskDetail"
      @sureDialogFather="getTaskTable();"
      @closeDialogFather="getSonCancel"
    ></edit-task>
  </div>
</template>

<script>
import API from "@/api/customer";
import EditTask from "@/components/element/EditTask"; // 编辑任务
import NewTask from "@/components/element/NewTask"; // 任务
export default {
  props: ["customerId"],
  components: { EditTask, NewTask },
  data() {
    return {
      isEditTask: false,
      isShowTask: false,
      taskDetail: {},
      taskTableData: [],
      pageObj: {
        pageSize: 10,
        pageNum: 1,
        total: null
      }
    };
  },
  methods: {
    currentChange(e) {
      this.pageObj.pageNum = e;
      this.getTaskTable();
    },
    getSonCancel() {
      this.isEditTask = false;
      this.isShowTask = false;
    },
    // 编辑任务
    editTask(index, row) {
      this.taskDetail = row;
      this.isEditTask = true;
    },
    // 删除任务
    deleteTask(index, row) {
      this.$confirm("是否删除此任务?", "确认删除", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          API.getdeleteTask(row.id).then(res => {
            this.$message({
              message: "删除成功",
              type: "success"
            });
            this.getTaskTable();
          });
        })
        .catch(() => {
          this.$message({
            message: "已取消",
            type: "info"
          });
        });
    },
    // 获取任务列表
    getTaskTable() {
      let params = {
        linkId: this.customerId,
        linkType: 1,
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize
      };
      API.findTask(params).then(res => {
        let data = res.data;
        this.pageObj.total = data.total;

        this.taskTableData = data.list;
      });
    }
  },
  filters: {
    type(value) {
      if (value === null || value === undefined) return;
      switch (value) {
        case 0:
          return "从不重复";
          break;
        case 1:
          return "每天重复";
          break;
        case 2:
          return "每周重复";
          break;
        case 3:
          return "每月重复";
          break;
        case 4:
          return "每年重复";
          break;
        default:
          return "未知类型";
          break;
      }
    }
  }
};
</script>