<template>
  <div>
    <el-dialog
      title="编辑部门"
      :visible.sync="dialogFormVisible"
      class="dialog"
      @close="$emit('closeDialog')"
    >
      <el-form :model="form" label-width="120px" :rules="rules" ref="form">
        <el-form-item label="部门名称" prop="name">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="上级部门" prop="pid">
          <el-select v-model="form.pid" placeholder="请选择上级部门">
            <el-option :value="0" label="无上级部门"></el-option>
            <el-option
              v-for="(item, index) in departmentList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-cascader v-model="value" :options="treeData" @change="handleChange"></el-cascader> -->
        <!-- <el-form-item label="所属部门">
          <el-select v-model="form.region" placeholder="请选择活动区域">
            <el-option label="区域一" value="shanghai"></el-option>
            <el-option label="区域二" value="beijing"></el-option>
          </el-select>
        </el-form-item>-->
        <el-form-item label="部门级别" prop="level">
          <el-select v-model="form.level" placeholder="1级权限最大，10级最小">
            <el-option v-for="(item,index) in 10" :key="index" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <!-- <el-input type="textarea" v-model="form.remarks"></el-input> -->
          <el-button type="success" @click="sureDialog">保 存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/admin";

export default {
  name: "EditDepartment",
  props: [
    "isEditDepartment", // 是否展示
    "departmentList", // 部门列表
    "departmentDetail" // 部门详情
  ],
  data() {
    return {
      dialogFormVisible: this.isEditDepartment,
      form: {},
      treeData: [],
      rules: {
        name: [{ required: true, message: "请输入部门名称", trigger: "blur" }],
        pid: [{ required: true, message: "请选择上级部门", trigger: "change" }],
        level: [
          { required: true, message: "请选择部门级别", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    handleChange() {},
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let params = this.form;
        delete params.createTime;
        console.log(params);
        API.updateDepart(params)
          .then(res => {
            console.log(res);
            this.$message({
              message: "新增成功",
              type: "success"
            });
            this.$emit("sureDialogFather");
            this.$emit("closeDialog");
          })
          .catch(err => {
            console.error(err);
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    },
    // 获取树状数据
    getTree(data) {
      data.forEach(function(item) {
        delete item.children;
        item.label = item.name;
        item.value = item.id;
      });
      // 将数据存储为 以 id 为 KEY 的 map 索引数据列
      var map = {};
      data.forEach(function(item) {
        map[item.id] = item;
      });
      var val = [];
      data.forEach(function(item) {
        // 以当前遍历项，的pid,去map对象中找到索引的id
        var parent = map[item.pid];
        // 好绕啊，如果找到索引，那么说明此项不在顶级当中,那么需要把此项添加到，他对应的父级中
        if (parent) {
          (parent.children || (parent.children = [])).push(item);
        } else {
          //如果没有在map中找到对应的索引ID,那么直接把 当前的item添加到 val结果集中，作为顶级
          val.push(item);
        }
      });
      return val;
    }
  },
  watch: {
    isEditDepartment(value) {
      this.dialogFormVisible = this.isEditDepartment;
      if (!value) return;
      // this.form.pid = this.editChildDepart || 0;
      this.form = Object.assign({}, this.departmentDetail);
      console.log(this.form);
    }
  }
};
</script>

<style lang="scss" scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>
