<template>
  <div>
    <el-dialog title="新增套餐" :visible.sync="outerVisible" @close="closeDialog">
      <el-form :model="form" :label-width="formLabelWidth" :rules="rules" ref="form">
        <el-form-item label="套餐名称" prop="name">
          <el-input placeholder="请输入" v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="充值金额" prop="money">
          <el-input-number v-model="form.money" :precision="2" :min="0" :step="100"></el-input-number>
        </el-form-item>
        <el-form-item label="赠送金额" prop="moneyGift">
          <el-input-number v-model="form.moneyGift" :precision="2" :min="0" :step="100"></el-input-number>
        </el-form-item>
        <el-form-item label="代账月数" prop="months">
          <el-input-number v-model="form.months" :step="1" :min="0"></el-input-number>
        </el-form-item>
        <el-form-item label="套餐说明" prop="description">
          <el-input type="textarea" :rows="4" placeholder="请输入内容" v-model="form.description"></el-input>
        </el-form-item>
        <el-form-item label="税务类型" prop="type">
          <el-select v-model="form.type" placeholder="请选择" clearable class="item-css">
            <el-option label="一班纳税人" :value="1"></el-option>
            <el-option label="小规模纳税人" :value="2"></el-option>
            <el-option label="外贸企业" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/combo";

// 新增套餐
export default {
  name: "EditChannel",
  props: ["isEditCombo", "detailData"],
  data() {
    return {
      outerVisible: this.isEditCombo,
      formLabelWidth: "100px",
      form: {
        name: "", // 套餐名
        months: "", // 月数
        moneyGift: "", // 赠送金额
        money: "", // 套餐金额
        type: "", // 套餐类型
        description: "" // 套餐说明
      },
      rules: {
        name: [{ required: true, message: "请输入套餐名称", trigger: "blur" }],
        money: [{ required: true, message: "请输入充值金额", trigger: "blur" }],
        moneyGift: [
          { required: true, message: "请输入赠送金额", trigger: "blur" }
        ],
        months: [
          { required: true, message: "请输入代账月数", trigger: "blur" }
        ],
        description: [
          { required: true, message: "请输入套餐说明", trigger: "blur" }
        ],
        type: [{ required: true, message: "请选择税务类型", trigger: "change" }]
      }
    };
  },
  methods: {
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather", this.outerVisible);
    },
    // 保存
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let params = this.form;
        console.log(params);

        API.updatePackage(params)
          .then(res => {
            this.$message({
              message: "保存成功",
              type: "success"
            });
            this.form = {
              name: "", // 套餐名
              months: "", // 月数
              moneyGift: "", // 赠送金额
              money: "", // 套餐金额
              type: "", // 套餐类型
              description: "" // 套餐说明
            };
            this.closeDialog();
            this.$emit("sureDialogFather");
          })
          .catch(err => {
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    }
  },
  mounted() {},
  watch: {
    isEditCombo(value) {
      this.outerVisible = this.isEditCombo;
      if (!value) return;
      this.form = Object.assign({}, this.detailData);
      this.$nextTick(() => {
        this.$refs.form.clearValidate();
      });
    }
  }
};
</script>
<style lang='scss' scoped>
.item-css {
  width: 100%;
}
</style>