<template>
  <div>
    <el-dialog
      title="新增机会"
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      @close="closeDialog"
    >
      <el-form :model="form" :rules="rules" label-width="120px" ref="form">
        <el-form-item label="机会名称" prop="name">
          <el-input v-model="form.name" placeholder="请输入"></el-input>
        </el-form-item>

        <el-form-item label="所属客户" prop="customerId">
          <el-select
            v-model="form.customerId"
            @change="customerChange"
            :disabled="Boolean(customerId)"
            placeholder="请选择"
            class="item-css"
            filterable
          >
            <el-option
              v-for="(item, index) in restaurants"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="关联联系人" prop="linkmanId">
          <el-select
            v-model="form.linkmanId"
            :no-data-text="form.customerId?'无数据':'请选择客户'"
            placeholder="请选择"
          >
            <el-option
              v-for="(item, i) in linkmenList"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务类型" prop="serviceId">
          <el-select v-model="form.serviceId" placeholder="请选择业务类型" clearable>
            <el-option
              v-for="(item, i) in BusinessTypes"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="预计销售金额" prop="expectPrice">
          <el-input v-model="form.expectPrice" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="预计签单日期 " prop="expectDate">
          <el-date-picker
            v-model="form.expectDate"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="销售阶段" prop="stage">
          <el-select v-model="form.stage" placeholder="请选择销售阶段" clearable>
            <el-option v-for="(item, i) in SaleStage" :key="i" :label="item.val" :value="item.code"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="机会类型" prop="type">
          <el-select v-model="form.type" placeholder="请选择机会类型" clearable>
            <el-option
              v-for="(item, i) in ChanceType"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="机会获取日期" prop="getTime">
          <el-date-picker
            v-model="form.getTime"
            :picker-options="pickerOptions"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="机会来源" prop="source">
          <el-select v-model="form.source" placeholder="请选择" clearable>
            <el-option
              v-for="(item, i) in clientSourceList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="下次跟进时间" prop="nextTime">
          <el-date-picker
            v-model="form.nextTime"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="所属部门" prop="followDepart">
          <el-select
            v-model="form.followDepart"
            placeholder="请选择"
            clearable
            @change="departChange($event)"
          >
            <el-option
              v-for="(item, index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="负责人" prop="followUser">
          <el-select v-model="form.followUser" @change="userChange" filterable placeholder="请选择">
            <el-option
              v-for="(item,index) in headList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="签单概率">
          <el-input v-model="form.probability" placeholder="请输入"></el-input>
        </el-form-item>-->
        <el-form-item label="备注" prop="remarks">
          <el-input
            v-model="form.remarks"
            style="width:60%;"
            type="textarea"
            show-word-limit
            maxlength="100"
            :autosize="{ minRows: 4, maxRows: 10}"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="sureDialog" type="success">保存</el-button>
        <el-button @click="closeDialog">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 新增机会
import API from "@/api/chance";
export default {
  name: "NewChance",
  props: {
    isShowChance: {
      type: Boolean,
      default: false
    },
    customerId: {
      type: Number | String,
      default: ""
    },
  },
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now(); //如果没有后面的-8.64e7就是不可以选择今天的
        }
      },
      rules: {
        name: [{ required: true, message: "请输入机会名称", trigger: "blur" }],
        customerId: [
          { required: true, message: "请选择所属客户", trigger: "change" }
        ],
        linkmanId: [
          { required: true, message: "请选择联系人", trigger: "change" }
        ],
        serviceId: [
          { required: true, message: "请选择业务类型", trigger: "change" }
        ],
        stage: [
          { required: true, message: "请选择销售阶段", trigger: "change" }
        ],
        type: [
          { required: true, message: "请选择机会类型", trigger: "change" }
        ],
        expectPrice: [
          { required: true, message: "请输入预计销售金额", trigger: "change" }
        ],
        source: [
          { required: true, message: "请选择机会来源", trigger: "change" }
        ],
        followDepart: [
          { required: true, message: "请选择所属部门", trigger: "change" }
        ],
        followUser: [
          { required: true, message: "请选择负责人", trigger: "change" }
        ]
      },
      dialogVisible: this.isShowChance,
      restaurants: [], // 所属客户
      linkmenList: [], // 关联联系人
      BusinessTypes: [], // 业务类型
      SaleStage: [], // 销售阶段
      ChanceType: [], // 机会类型
      clientSourceList: [], // 机会来源
      departmentsList: [], // 所属部门
      headList: [], // 所属负责人
      form: {
        name: "", // 机会名称
        customerId: "", // 客户id
        linkmanId: "", // 关联联系人id
        serviceId: "", // 业务类型id
        expectPrice: "", // 预计销售金额
        expectDate: "", // 预计签单日期
        stage: "", // 销售阶段id
        type: "", // 机会类型id
        getTime: "", // 机会获取日期
        source: "", // 机会来源id
        nextTime: "", // 下次跟进时间
        followDepart: "", // 所属部门
        followUser: "", // 负责人
        // probability: "", // 签单概率
        remarks: "" // 备注
      }
    };
  },
  methods: {
    userChange(val) {
      let departId = this.headList.find(item => {
        return item.id === val;
      }).departId;
      this.form.followDepart = departId;
      this.departChange("no_change");
    },
    // 关闭
    closeDialog() {
      this.dialogVisible = false;

      this.$emit("closeDialogFather", this.dialogVisible);
    },
    // 清除负责人
    clearFollowUser() {
      if (this.form.followDepart) {
        this.departChange(this.form.followDepart);
      }
    },
    // 保存
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let form = this.form;
        console.log(form);
        let params = {
          name: form.name,
          customerId: form.customerId,
          linkmanId: form.linkmanId,
          serviceId: form.serviceId,
          expectPrice: form.expectPrice,
          expectDate: form.expectDate,
          stage: form.stage,
          type: form.type,
          getTime: form.getTime,
          source: form.source,
          nextTime: form.nextTime,
          followDepart: form.followDepart,
          followUser: form.followUser,
          // probability: form.probability,
          remarks: form.remarks
        };
        API.postaddChance(params).then(res => {
          // console.log(res)
          this.$message({
            message: "保存成功",
            type: "success"
          });
          this.$refs.form.resetFields();
          this.$emit("sureDialogFather");
        });
      });
    },

    // 所属客户选择
    customerChange(val) {
      this.form.linkmanId = null;
      API.getCustomerLinkman(val).then(res => {
        this.linkmenList = res.data;
      });
    },
    // 所属部门
    departChange(flag) {
      if (flag !== "no_change") this.form.followUser = null;
      let val = this.form.followDepart;
      if (val) {
        API.getHead(val).then(res => {
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          this.headList = res.data;
        });
      }
    }
  },
  watch: {
    isShowChance(value) {
      this.dialogVisible = this.isShowChance;
      if (!value) return;
      let userInfo = this.$store.state.userInfo;
      this.form.followUser = userInfo.id;
      this.form.followDepart = userInfo.departId;
      // 所属客户
      API.getCustomerByCompanyName().then(res => {
        this.restaurants = res.data;
      });
      if (this.customerId) {
        this.form.customerId = this.customerId;
        API.getCustomerLinkman(this.customerId).then(res => {
          this.linkmenList = res.data;
        });
      }
      // 业务类型
      API.getType().then(res => {
        this.BusinessTypes = res.data;
      });
      // 销售阶段
      API.getSaleStage().then(res => {
        this.SaleStage = res.data;
      });
      // 机会类型
      API.getChanceType().then(res => {
        this.ChanceType = res.data;
      });

      //  机会来源实际就是客户来源的数据
      API.customerFrom().then(res => {
        this.clientSourceList = res.data;
      });
      // 机会来源
      // API.getchannel().then(res => {
      //   this.clientSourceList = res.data;
      // });
      // 所属部门
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
      // 所有的负责人
      API.getAllUser().then(res => {
        this.headList = res.data;
      });
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  .el-input__inner {
    width: auto;
  }
  // .el-input:not(.el-input--suffix) {
  //   width: 50%;
  // }
}
</style>