<template>
  <div>
    <el-button type="warning" @click="isAddLinkman=true"  v-permi="'/customer/addLinkman'">新增联系人</el-button>
    <el-table
      :data="linkmanTableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top:20px;"
      border
      :fit="true"
    >
      <el-table-column prop="name" label="姓名" align="center"></el-table-column>
      <el-table-column prop="depart" label="部门" align="center"></el-table-column>
      <el-table-column prop="job" label="职务" align="center"></el-table-column>
      <el-table-column prop="telephone" label="电话" align="center"></el-table-column>
      <el-table-column prop="mobile" label="手机" align="center"></el-table-column>
      <el-table-column prop="email" label="邮箱" align="center"></el-table-column>
      <el-table-column prop="remarks" label="备注" align="center"></el-table-column>
      <el-table-column label="操作" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" plain @click="editContact(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" @click="examineContact(scope.$index, scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <new-contact
      :isShowLinkman="isAddLinkman"
      :customerId="customerId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-contact>
    <edit-contacts
      :isShowLinkmanEdit="isEditLinkman"
      :linkmanId="linkmanId"
      :customerId="customerId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-contacts>
  </div>
</template>

<script>
import NewContact from "./NewContact";
import EditContacts from "./EditContacts";
import API from "@/api/customer";

export default {
  name: "TabContact",
  components: {
    NewContact,
    EditContacts
  },
  props: ["customerId"],
  data() {
    return {
      linkmanId: null,
      isAddLinkman: false,
      isEditLinkman: false,
      linkmanTableData: []
    };
  },
  methods: {
    editContact(index, row) {
      this.linkmanId = row.id;
      
      this.isEditLinkman = true;
    },
    getLinkmanTable() {
      API.getCustomerLinkman(this.customerId).then(res => {
        console.log(res);
        
        this.linkmanTableData = res.data;
      });
    },
    examineContact(index, row) {
      this.$router.push({
        path: "/sellManagement/linkmandetails",
        query: { id: row.id }
      });
    },
    getSonSure() {
      this.getSonCancel();
      this.getLinkmanTable();
    },
    getSonCancel() {
      this.isAddLinkman = false;
      this.isEditLinkman = false;
    }
  }
};
</script>