<template>
  <div class="administrators">
    <div>
      <el-form :model="screenData">
        <div style="display:flex;border-bottom:1px solid #e4e4e4;margin-bottom:20px;">
          <el-form-item label="角色" label-width="60px">
            <el-select
              v-model="screenData.roleName"
              clearable
              @change="roleChange"
              placeholder="请选择角色"
            >
              <el-option
                v-for="(item,index) in roleList"
                :key="index"
                :label="item.name"
                :value="item.name"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="搜索" label-width="120px" style="display:flex" class="fstyle">
            <el-input clearable v-model="screenData.name" autocomplete="off" placeholder="请输入管理员名"></el-input>
            <el-button type="primary" @click="getTableData()">搜索</el-button>
          </el-form-item>
        </div>
      </el-form>
    </div>
    <div>
      <div class="administrators-title">
        <el-button type="primary" v-permi="'/user/addUser'" @click="isAddAdministrator = true">
          <span class="el-icon-document-copy"></span> 新增成员
        </el-button>
        <el-button type="danger" @click="delUser" :disabled="!selectedItem.length">
          <span class="el-icon-delete"></span> 删除
        </el-button>
      </div>
      <div class="administrators-content">
        <el-table
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%"
          border
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55" align="center"></el-table-column>
          <el-table-column label="管理员名" prop="name" align="center"></el-table-column>
          <el-table-column prop="roleName" label="角色" align="center"></el-table-column>
          <el-table-column prop="departName" label="所属部门" align="center"></el-table-column>
          <el-table-column prop="postName" label="职位" align="center"></el-table-column>
          <el-table-column prop="telnum" label="手机号" align="center"></el-table-column>
          <el-table-column prop="createTime" label="添加时间" align="center"></el-table-column>
          <el-table-column prop="remarks" label="备注" align="center"></el-table-column>
          <el-table-column fixed="right" label="操作" width align="center">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!-- 分页器 -->
    <div>
      <el-pagination
        class="itemPage"
        @size-change="sizeChangePage"
        @current-change="currentChangePage"
        :current-page="pageObj.pageNum"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageObj.totalSize"
      ></el-pagination>
    </div>

    <add-administrator
      @closeDialogFather="isAddAdministrator=false"
      @sureDialogFather="getTableData()"
      :isAddAdministrator="isAddAdministrator"
      :departmentsList="departmentsList"
      :roleList="roleList"
      :postitionList="postitionList"
    ></add-administrator>
    <edit-administrator
      @closeDialogFather="isEditAdministrator=false"
      @sureDialogFather="getTableData()"
      :isEditAdministrator="isEditAdministrator"
      :departmentsList="departmentsList"
      :roleList="roleList"
      :postitionList="postitionList"
      :administratorDetail="administratorDetail"
    ></edit-administrator>
  </div>
</template>

<script>
import AddAdministrator from "../components/AddAdministrator";
import EditAdministrator from "../components/EditAdministrator";
import API from "@/api/admin";
import * as filters from "@/filters/index";

export default {
  name: "administrators",
  components: { AddAdministrator, EditAdministrator },
  data() {
    return {
      // 选中的选项
      selectedItem: [],
      // 新增职位
      isAddAdministrator: false,
      // 传给编辑组件的详情
      administratorDetail: {},
      // 编辑职位
      isEditAdministrator: false,
      // 角色列表
      roleList: [],
      // 职位列表
      postitionList: [],
      // 经过筛选的数据
      screenData: {
        roleName: "",
        name: ""
      },
      // 部门列表
      departmentsList: [],

      dialogFormVisible: false,
      tableData: [],
      pageObj: {
        totalSize: null,
        pageSize: 10,
        pageNum: 1
      }
    };
  },
  methods: {
    // 编辑人员
    handleEdit(index, row) {
      this.administratorDetail = row;
      this.isEditAdministrator = true;
    },
    // 删除管理员
    delUser() {
      this.$confirm("是否删除所选管理员？", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      }).then(() => {
        let arr = [];
        this.selectedItem.map(item => {
          arr.push(item.id);
        });
        let params = { ids: arr.join(",") };
        API.delUser(params)
          .then(res => {
            console.log(res);
            this.$message({
              type: "success",
              message: "删除成功"
            });
            this.getTableData();
          })
          .catch(err => {
            console.error(err);
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    },
    // 当前页修改
    currentChangePage(e) {
      this.pageObj.pageNum = e;
      this.getTableData();
    },
    // 页面大小修改
    sizeChangePage(e) {
      this.pageObj.pageSize = e;
      this.getTableData();
    },
    handleSelectionChange(val, k) {
      this.selectedItem = val;
    },
    // 	选择角色改变
    roleChange(e) {
      this.screenData.roleName = e;
      this.getTableData();
    },
    // 获取用户列表
    getTableData() {
      let params = {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize,
        name: this.screenData.name,
        roleName: this.screenData.roleName
      };
      API.findUsers(params)
        .then(res => {
          let data = res.data;
          this.pageObj.totalSize = data.total;
          for (const item of data.list) {
            item.createTime = filters.parseTime(new Date(item.createTime));
          }
          this.tableData = data.list;
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);
          console.error(err);
        });
    },
    // 获取部门列表
    getDepartmentsList() {
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
    },

    // 获取角色列表
    getRoleList() {
      API.getRoleList()
        .then(res => {
          if (res) {
            this.roleList = res.data;
          }
        })
        .catch(err => {
          console.error(err);
        });
    },

    // 获取职位列表
    getPostitionList() {
      API.findPositionsInfo()
        .then(res => {
          this.postitionList = res.data;
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    }
  },
  mounted() {
    this.getDepartmentsList();
    this.getPostitionList();
    this.getRoleList();
    this.getTableData();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.administrators {
  .administrators-title {
    margin-bottom: 20px;
  }
}
.fstyle {
  /deep/ div {
    display: flex;
  }
  /deep/.el-form-item__content {
    margin-left: 0px !important;
  }
}

.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
</style>
