<template>
  <div>
    <el-dialog
      :title="'操作成员：'+departmentDetail.name"
      :visible.sync="dialogFormVisible"
      @close="$emit('closeDialog')"
    >
      <div class="container">
        <el-form :model="form" class="role-serch" label-width="auto">
          <el-form-item label="角色">
            <el-select
              clearable
              v-model="form.roleName"
              placeholder="请选择角色"
              @change="findSysMemberAndDepMember()"
            >
              <el-option
                v-for="(item,index) in roleList"
                :key="index"
                :label="item.name"
                :value="item.name"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="职位">
            <el-select
              clearable
              v-model="form.positionName"
              placeholder="请选择职位"
              @change="findSysMemberAndDepMember()"
            >
              <el-option
                v-for="(item, index) in positionList"
                :key="index"
                :label="item.name"
                :value="item.name"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div class="formlist">
          <div>
            <el-transfer
              style="text-align: center;"
              v-model="departList"
              filterable
              @change="listChange"
              :titles="['系统成员', departmentDetail.name+'成员']"
              :button-texts="['移除', '加入']"
              :format="{
                            noChecked: '${total}',
                            hasChecked: '${checked}/${total}'
                        }"
              :data="allMemberList"
            >
              <span slot-scope="{ option }">{{ option.label }}</span>
            </el-transfer>
          </div>
        </div>
        <!-- <div style="text-align:right;margin-top:30px;">
        <el-button type="success" @click="sureDialog">保存</el-button>
        </div>-->
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/admin";

export default {
  name: "AddMember",
  props: ["isAddMember", "roleList", "departmentDetail"],
  data() {
    return {
      dialogFormVisible: false,
      form: {
        roleName: "",
        position: ""
      },
      value4: [],
      data: [],
      allMemberList: [], // 所有成员
      departList: [], // 当前部门成员
      positionList: [], // 职位列表
      removeMember: [], // 移除的人员
      addMember: [] // 增加的人员
    };
  },
  methods: {
    // 点击保存
    sureDialog() {
      let addArr = new Set(this.addMember);
      let removeArr = new Set(this.removeMember);
      console.log(...addArr);
      console.log(...removeArr);
    },
    // 点击左移或者右移的事件
    listChange(handleList, handle, ids) {
      /* 判断是否已存在其他部门 勿删 **
      let arr = [];
      // 添加到部门
      if (handle === "right") {
        let allMemberList = this.allMemberList;
        for (let i = 0; i < ids.length; i++) {
          for (let j = 0; j < allMemberList.length; j++) {
            if (
              ids[i] === allMemberList[j].key &&
              allMemberList[j].departName
            ) {
              let userName = allMemberList[j].label;
              let departName = allMemberList[j].departName;
              arr.push({ userName, departName });
            }
          }
        }
      }
      // 如果有成员已经有所属部门
      if (arr.length) {
        let strArr = [];
        for (const item of arr) {
          strArr.push(
            "成员: ‘" +
              item.userName +
              "’ 已经存在部门: ‘" +
              item.departName +
              "’ "
          );
        }
        let str = strArr.join("，<br/> ") + "<br/> 请先移除！";
        this.$alert(str, "提示", {
          confirmButtonText: "确定",
          type: "warning",
          dangerouslyUseHTMLString: true
        });
      } else {
      }
        */
      handle = handle === "left" ? "deleteMember" : "addMember";
      let params = {
        ids: ids.join(","),
        id: this.departmentDetail.id
      };
      API[handle](params)
        .then(res => {
          console.log(res);
          this.findSysMemberAndDepMember();
          this.$message({
            type: "success",
            message: "操作成功"
          });
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);

          console.error(err);
        });
    },
    // 查询需要显示的数据
    findSysMemberAndDepMember() {
      let params = {
        roleName: this.form.roleName, // 角色名称
        postName: this.form.positionName, // 职位名称
        id: this.departmentDetail.id // 部门id
      };
      API.findSysMemberAndDepMember(params)
        .then(res => {
          console.log(res);
          let allData = res.data[0];
          let departData = res.data[1];
          let allArr = [];
          let departArr = [];

          for (const item of allData) {
            allArr.push({
              key: item.id,
              label:
                item.name + (item.departName ? "--" + item.departName : ""),
              departName: item.departName
            });
          }
          for (const item of departData) {
            departArr.push(item.id);
          }
          this.allMemberList = allArr;
          this.departList = departArr;
        })
        .catch(err => {
          console.error(err);
        });
    }
  },
  watch: {
    isAddMember(value) {
      this.dialogFormVisible = value;
      if (!value) return;
      // 获取成员列表
      this.findSysMemberAndDepMember();
      // 如果获取过职位列表就不需要再获取
      if (this.positionList.length) return;
      API.findPositionsInfo()
        .then(res => {
          this.positionList = res.data;
        })
        .catch(err => {
          console.error(err);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
// /deep/.el-dialog {
