.role-serch {
  display: flex;
  // flex-direction:column;
  // align-items: center;
  justify-content: space-evenly;
  & > div {
    margin: 20px;
  }
}
.el-transfer {
  margin: 30px 0;
}
/deep/.el-transfer-panel {
  width: 300px;
  text-align: left;
}
</style>
