<template>
  <div>
    <el-dialog title="操作退回" :visible.sync="outerVisible" width="40%" @close="closeDialog">
      <el-form ref="form" :model="form" label-width="90px">
        <el-form-item
          label="驳回意见"
          prop="opinion"
          :rules="[{ required: true, message: '请输入驳回意见', trigger: 'blur' }]"
        >
          <el-input
            style="width:60%"
            v-model="form.opinion"
            type="textarea"
            placeholder="请输入内容"
            show-word-limit
            :maxlength="100"
            :autosize="{ minRows: 4, maxRows: 8}"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="success" @click="sureDialog">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/clue";

// 退回操作
export default {
  name: "RejectHandle",
  props: {
    isShowReject: Boolean,
    clueDetail: {
      type: Object,
      default: function() {
        return {};
      }
    }
  },
  data() {
    return {
      outerVisible: false,
      fileList: [], //上传的文件列表
      form: {
        opinion: ""
      }
    };
  },
  methods: {
    // 保存
    sureDialog() {
      let params = {
        clueId: this.clueDetail.id,
        opinion: this.form.opinion
      };
      API.rejectReturnClue(params).then(res => {
        console.log(res);
        this.$emit("sureDialogFather");
        this.closeDialog();
      });
    },
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$refs.form.resetFields();
      this.$emit("closeDialogFather");
    }
  },
  watch: {
    isShowReject(val) {

      this.outerVisible = this.isShowReject;
      if (!val) return;
      this.form.linkId = this.linkId;
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  padding: 0 20px;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>