<template>
  <div>
    <el-dialog
      title="编辑客户"
      :close-on-click-modal="false"
      :visible.sync="outerVisible"
      @close="closeDialog"
    >
      <el-form :model="form" label-width="120px" :rules="rules" ref="form">
        <el-form-item label="公司名称" prop="companyName">
          <el-input v-model="form.companyName" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="业务级别" prop="grade">
          <el-select v-model="form.grade" placeholder="请选择业务级别" clearable class="item-css">
            <el-option
              v-for="(item, index) in customerLevel"
              :key="index"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="公司电话" prop="telephone">
          <el-input v-model="form.telephone" maxlength="12" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="业务类型" prop="type">
          <el-select v-model="form.type" disabled placeholder="请选择业务类型" clearable class="item-css">
            <el-option
              v-for="(item, index) in customerTypeList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="渠道来源" prop="from">
          <el-select v-model="form.from" placeholder="请选择渠道来源" clearable class="item-css">
            <el-option
              v-for="(item, index) in channelSourceList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户来源" prop="customerFrom">
          <el-select v-model="form.customerFrom" placeholder="请选择客户来源" clearable class="item-css">
            <el-option
              v-for="(item, index) in clientSourceList"
              :key="index"
              :value="item.code"
              :label="item.val"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="注册类型" prop="registerName">
          <el-input v-model="form.registerName" placeholder="请输入"></el-input>
        </el-form-item>-->
        <el-form-item label="地区" prop="regionArr">
          <el-cascader
            v-model="form.regionArr"
            :options="region"
            placeholder="请选择"
            clearable
            class="item-css"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="所属部门" prop="followDepart">
          <el-select
            v-model="form.followDepart"
            placeholder="请选择"
            clearable
            class="item-css"
            @change="departChange($event)"
          >
            <el-option
              v-for="(item, index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="所属负责人" prop="followUser">
          <el-select v-model="form.followUser" @change="userChange" filterable placeholder="请选择">
            <el-option
              v-for="(item,index) in headList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="推广关键词">
          <el-input v-model="form.saleKeywords" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="客户搜索词">
          <el-input v-model="form.searchKeywords" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="客户状态" prop="status">
          <el-select v-model="form.status" placeholder="请选择" clearable class="item-css">
            <el-option
              v-for="(item, index) in customerType"
              :key="index"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="form.email" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="人员规模">
          <el-input v-model.number="form.staffSize" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="下次跟进时间">
          <el-date-picker
            v-model="form.nextFollwTime"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
            :picker-options="startDatePicker"
            class="item-css"
            default-time="00:00:00"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="备注">
          <el-input
            v-model="form.remarks"
            style="width:60%"
            type="textarea"
            :rows="4"
            maxlength="100"
            show-word-limit
            placeholder="请输入"
          ></el-input>
        </el-form-item>
        <el-form-item label="是否已加微信">
          <el-select v-model="form.isWechat" placeholder="请选择" clearable class="item-css">
            <el-option label="是" :value="1"></el-option>
            <el-option label="否" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否面谈">
          <el-select v-model="form.isFace" placeholder="请选择" clearable class="item-css">
            <el-option label="是" :value="1"></el-option>
            <el-option label="否" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success">保存</el-button>
          <el-button @click="closeDialog">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/customer";
export default {
  name: "EditCustomer",
  props: ["isShowEdit", "customerId"],
  data() {
    var validateTel = (rule, value, callback) => {
      var reg = /0\d{2}-\d{7,8}$/;
      var reg2 = /^\d{11}$/;
      if (!value || reg.test(value) || reg2.test(value)) {
        callback();
      } else {
        callback(new Error("请输入正确号码"));
      }
    };
    return {
      startDatePicker: {
        // 跟进事件只能选择今天之后的日期
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //如果没有后面的-8.64e6就是不可以选择今天的
        }
      },
      outerVisible: this.isShowEdit,
      customerLevel: [], // 业务级别
      customerTypeList: [], // 业务类型
      channelSourceList: [], // 客户来源
      region: [], // 地区
      departmentsList: [], // 所属部门
      headList: [], // 所属负责人
      customerType: [], // 客户状态
      clientSourceList: [], // 客户来源
      form: {}, // 表格的编辑弹框的数据
      province: "", // 省
      city: "", // 市
      followDepart: null, // 所属部门的id
      followUser: null, // 所属负责人的id
      rules: {
        companyName: [
          { required: true, message: "请输入客户名称", trigger: "blur" }
        ],
        grade: [
          { required: true, message: "请选择客户级别", trigger: "change" }
        ],
        telephone: [
          { required: false, validator: validateTel, trigger: "blur" }
        ],
        type: [
          { required: true, message: "请选择业务类型", trigger: "change" }
        ],
        from: [
          { required: true, message: "请选择渠道来源", trigger: "change" }
        ],
        customerFrom: [
          { required: true, message: "请选择客户来源", trigger: "change" }
        ],
        registerName: [
          { required: true, message: "请输入注册类型", trigger: "blur" }
        ],
        regionArr: [
          {
            type: "array",
            required: true,
            message: "请选择所属地区",
            trigger: "change"
          }
        ],
        followDepart: [
          { required: true, message: "请选择所属部门", trigger: "change" }
        ],
        followUser: [
          { required: true, message: "请选择负责人", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择客户状态", trigger: "change" }
        ],
        linkUserName: [
          { required: true, message: "请输入联系人姓名", trigger: "blur" }
        ],
        linkUserPhone: [
          { required: true, message: "请输入联系人电话", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    // 所属部门
    departChange(flag) {
      if (flag !== "no_change") this.form.followUser = null;
      let val = this.form.followDepart;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      }
    },
    // 所属负责人id
    userChange(val) {
      let departId = this.headList.find(item => {
        return item.id === val;
      }).departId;
      this.form.followDepart = departId;
      this.departChange("no_change");
    },

    // 编辑的保存
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let form = this.form;
        let data = {
          id: this.customerId,
          companyName: form.companyName,
          grade: form.grade,
          telephone: form.telephone,
          type: form.type,
          from: form.from,
          registerName: form.registerName,
          saleKeywords: form.saleKeywords,
          province: form.regionArr[0],
          city: form.regionArr[1],
          followDepart: form.followDepart,
          customerFrom: form.customerFrom,
          followUser: form.followUser,
          searchKeywords: form.searchKeywords,
          status: form.status,
          email: form.email,
          staffSize: form.staffSize,
          nextFollwTime: form.nextFollwTime,
          remarks: form.remarks,
          isWechat: form.isWechat,
          isFace: form.isFace
        };
        API.postUpdate(data).then(res => {
          console.log(res);
          if (res && res.status == 200) {
            this.$message({
              message: "保存成功",
              type: "success"
            });
            this.closeDialog();
            this.$emit("sureDialogFather");
          } else {
            this.$message.error("保存失败");
          }
        });
      });
    },
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$refs.form.resetFields();

      this.$emit("closeDialogFather", this.outerVisible);
    }
  },
  watch: {
    isShowEdit(val) {
      this.outerVisible = this.isShowEdit;
      if (!val) return;
      // 业务级别api
      API.getCustomerLevel().then(res => {
        this.customerLevel = res.data;
      });
      // 新增客户的业务类型api
      API.getType().then(res => {
        this.customerTypeList = res.data;
      });
      // 渠道来源api
      API.getCustomerSource().then(res => {
        this.channelSourceList = res.data;
      });
      // 客户来源
      API.customerFrom().then(res => {
        this.clientSourceList = res.data;
      });
      // 地区的api
      API.getRegion().then(res => {
        res.data.forEach(item => {
          item.value = item.id;
          if (item.children.length == "0") {
            item.children = "";
          } else {
            item.children.forEach(item1 => {
              item1.value = item1.id;
            });
          }
        });
        this.region = res.data;
      });
      // 所属部门的api
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });

      // 客户状态api
      API.getCustomerType().then(res => {
        this.customerType = res.data;
      });
      // 编辑的数据的回显
      API.getByid(this.customerId).then(res => {
        let data = res.data;
        console.log(data);

        return new Promise((resolve, reject) => {
          // 如果有已经选择了部门那么只查询当前部门下的员工
          if (data.followDepart) {
            API.getHead(data.followDepart).then(resHead => {
              resHead.data.forEach(item => {
                item.value = item.name;
              });
              this.headList = resHead.data;
              resolve();
            });
          } else {
            // 如果没有部门，那么查询所有负责人
            API.getAllUser().then(res => {
              res.data.forEach(item => {
                item.value = item.name;
              });
              this.headList = res.data;
              resolve();
            });
          }
        }).then(() => {
          data.regionArr = [data.province, data.city];
          this.form = data;
          this.$nextTick(() => {
            this.$refs.form.clearValidate();
          });
        });
      });
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>