<template>
  <div>
    <el-dialog
      title="新增客户"
      :close-on-click-modal="false"
      :visible.sync="outerVisible"
      @close="closeDialog"
    >
      <el-form :model="form" :rules="rules" ref="form" label-width="120px">
        <el-form-item label="公司名称" prop="companyName">
          <el-input v-model="form.companyName" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="业务级别" prop="grade">
          <el-select v-model="form.grade" placeholder="请选择业务级别" clearable class="item-css">
            <el-option
              v-for="(item, index) in customerLevel"
              :key="index"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="公司电话" prop="telephone">
          <el-input v-model="form.telephone" maxlength="12" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="业务类型" prop="type">
          <el-select v-model="form.type" placeholder="请选择业务类型" clearable class="item-css">
            <el-option
              v-for="(item, index) in customerTypeList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="渠道来源" prop="from">
          <el-select v-model="form.from" placeholder="请选择渠道来源" clearable class="item-css">
            <el-option
              v-for="(item, index) in channelSourceList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="客户来源" prop="customerFrom">
          <el-select v-model="form.customerFrom" placeholder="请选择客户来源" clearable class="item-css">
            <el-option
              v-for="(item, index) in clientSourceList"
              :key="index"
              :value="item.code"
              :label="item.val"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="注册类型" prop="registerName">
              <el-input v-model="form.registerName" placeholder="请输入"></el-input>
        </el-form-item>-->
        <el-form-item label="地区" prop="regionArr">
          <el-cascader
            :options="region"
            v-model="form.regionArr"
            placeholder="请选择"
            clearable
            class="item-css"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="所属部门" prop="followDepart">
          <el-select
            v-model="form.followDepart"
            placeholder="请选择"
            clearable
            class="item-css"
            @change="departChange()"
          >
            <el-option
              v-for="(item, index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属负责人" prop="followUser">
          <el-select
            v-model="form.followUser"
            @change="userChange"
            filterable
            placeholder="请选择"
          >
            <el-option
              v-for="(item,index) in headList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="推广关键词" prop="saleKeywords">
          <el-input v-model="form.saleKeywords" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="客户搜索词" prop="searchKeywords">
          <el-input v-model="form.searchKeywords" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="客户状态" prop="status">
          <el-select v-model="form.status" placeholder="请选择" clearable class="item-css">
            <el-option
              v-for="(item, index) in customerType"
              :key="index"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="form.email" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="人员规模" prop="staffSize">
          <el-input v-model="form.staffSize" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="下次跟进时间" prop="nextFollwTime">
          <el-date-picker
            v-model="form.nextFollwTime"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
            :picker-options="startDatePicker"
            class="item-css"
            default-time="00:00:00"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="备注" prop="remarks">
          <el-input
            v-model="form.remarks"
            style="width:60%"
            type="textarea"
            :rows="4"
            show-word-limit
            maxlength="100"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
        <el-form-item label="是否已加微信" prop="isWechat">
          <el-select v-model="form.isWechat" placeholder="请选择" clearable class="item-css">
            <el-option label="是" value="1"></el-option>
            <el-option label="否" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否面谈" prop="isFace">
          <el-select v-model="form.isFace" placeholder="请选择" clearable class="item-css">
            <el-option label="是" value="1"></el-option>
            <el-option label="否" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="微信号" prop="wechat">
          <el-input v-model="form.wechat" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="QQ号" prop="qq">
          <el-input v-model="form.qq" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="联系人姓名" prop="linkUserName">
          <el-input v-model="form.linkUserName" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="联系人电话" prop="linkUserPhone">
          <el-input v-model="form.linkUserPhone " maxlength="11" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success">保存</el-button>
          <el-button @click="closeDialog">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
// 新增客户
import API from "@/api/customer";
export default {
  name: "newcustomer",
  props: ["isShowCreate"],
  data() {
    var validateTel = (rule, value, callback) => {
      var reg = /0\d{2}-\d{7,8}$/;
      var reg2 = /^\d{11}$/;
      if (!value || reg.test(value) || reg2.test(value)) {
        callback();
      } else {
        callback(new Error("请输入正确号码"));
      }
    };
    var validateMobile = (rule, value, callback) => {
      if (/^\d{11}$/.test(value)) {
        callback();
      } else {
        callback(new Error("请输入正确号码"));
      }
    };
    return {
      startDatePicker: {
        // 跟进事件只能选择今天之后的日期
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //如果没有后面的-8.64e6就是不可以选择今天的
        }
      },
      rules: {
        companyName: [
          { required: true, message: "请输入客户名称", trigger: "blur" }
        ],
        grade: [
          { required: true, message: "请选择客户级别", trigger: "change" }
        ],
        telephone: [
          { required: false, validator: validateTel, trigger: "blur" }
        ],
        type: [
          { required: true, message: "请选择业务类型", trigger: "change" }
        ],
        from: [
          { required: true, message: "请选择渠道来源", trigger: "change" }
        ],
        customerFrom: [
          { required: true, message: "请选择客户来源", trigger: "change" }
        ],
        registerName: [
          { required: true, message: "请输入注册类型", trigger: "blur" }
        ],
        regionArr: [
          {
            type: "array",
            required: true,
            message: "请选择所属地区",
            trigger: "change"
          }
        ],
        followDepart: [
          { required: true, message: "请选择所属部门", trigger: "change" }
        ],
        followUser: [
          { required: true, message: "请选择负责人", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择客户状态", trigger: "change" }
        ],
        linkUserName: [
          { required: true, message: "请输入联系人姓名", trigger: "blur" }
        ],
        linkUserPhone: [
          { required: true, validator: validateMobile, trigger: "blur" }
        ]
      },
      outerVisible: this.isShowCreate,
      customerLevel: [], // 业务级别
      customerTypeList: [], // 业务类型
      channelSourceList: [], // 客户来源
      region: [], // 地区
      departmentsList: [], // 所属部门
      clientSourceList: [], // 客户来源
      headList: [], // 所属负责人
      customerType: [], // 客户状态
      form: {
        companyName: "", // 客户名称
        grade: null, // 业务级别
        telephone: "", // 电话
        type: null, // 业务类型
        from: null, // 客户来源
        customerFrom: null, // 客户来源
        registerName: "", // 注册类型
        regionArr: [],
        followDepart: "", // 所属部门
        followUser: "", // 所属负责人
        searchKeywords: "", // 搜索词
        status: "", // 客户状态
        email: "", // 邮箱
        staffSize: "", // 人员规模
        nextFollwTime: "", // 下次跟进时间
        remarks: "", // 备注
        isWechat: "", // 是否已加微信
        isFace: "", // 是否面谈
        wechat: "", // 微信号
        qq: "", // qq号
        linkUserName: "", // 联系人姓名
        linkUserPhone: "" // 联系人电话
      }
    };
  },
  methods: {
    // 清除选项
    clearFollowUser() {
      if (this.form.followDepart) {
        this.departChange();
      }
    },
    // 所属部门的事件获取对应的id
    departChange(flag) {
      if (flag !== "no_change") this.form.followUser = null;
      let val = this.form.followDepart;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      }
    },

    // 所属负责人id
    userChange(val) {
      let departId = this.headList.find(item => {
        return item.id === val;
      }).departId;
      this.form.followDepart = departId;
      this.departChange("no_change");
    },
    // 新增客户的保存
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let followUser = this.form.followUser;
        let userInfo = this.$store.state.userInfo;
        let params = {
          companyName: this.form.companyName,
          grade: this.form.grade,
          telephone: this.form.telephone,
          type: this.form.type,
          from: this.form.from,
          customerFrom: this.form.customerFrom,
          registerName: this.form.registerName,
          province: this.form.regionArr[0],
          city: this.form.regionArr[1],
          followDepart: this.form.followDepart,
          followUser,
          saleKeywords: this.form.saleKeywords,
          searchKeywords: this.form.searchKeywords,
          status: this.form.status,
          email: this.form.email,
          staffSize: this.form.staffSize,
          nextFollwTime: this.form.nextFollwTime,
          remarks: this.form.remarks,
          isWechat: this.form.isWechat,
          isFace: this.form.isFace,
          wechat: this.form.wechat,
          qq: this.form.qq,
          linkUserName: this.form.linkUserName,
          linkUserPhone: this.form.linkUserPhone,
          createId: userInfo.id
        };
        API.postAdd(params).then(res => {
          console.log(res);
          this.$message({
            message: "保存成功",
            type: "success"
          });
          this.$refs.form.resetFields();
          this.$emit("sureDialogFather");
        });
      });
    },
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather", this.outerVisible);
    }
  },
  watch: {
    isShowCreate(val) {
      this.outerVisible = this.isShowCreate;
      if (!val) return;
      // 客户来源
      API.customerFrom().then(res => {
        this.clientSourceList = res.data;
      });
      // 业务级别api
      API.getCustomerLevel().then(res => {
        this.customerLevel = res.data;
      });
      // 业务类型api
      API.getType().then(res => {
        this.customerTypeList = res.data;
      });
      // 渠道来源api
      API.getCustomerSource().then(res => {
        this.channelSourceList = res.data;
      });
      // 地区的api
      API.getRegion().then(res => {
        res.data.forEach(item => {
          item.value = item.id;
          if (item.children.length == "0") {
            item.children = "";
          } else {
            item.children.forEach(item1 => {
              item1.value = item1.id;
            });
          }
        });
        this.region = res.data;
      });
      // 所属部门的api
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
      // 所属负责人全部
      API.getAllUser().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.headList = res.data;
      });
      // 客户状态api
      API.getCustomerType().then(res => {
        this.customerType = res.data;
      });
    }
  }
};
</script>
<style scoped lang='scss'>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>