<template>
  <div>
    <el-form :model="form" label-width="120px">
      <el-row>
        <el-col :span="24" class="list">
          <el-form-item label="销售阶段">
            <ul>
              <li
                v-for="(item, index) in stageList"
                :key="index"
                @click="stageChange(index, item.code)"
                :class="stageActive == index ? 'addclass' : '' "
              >{{item.val}}</li>
            </ul>
          </el-form-item>
        </el-col>
        <el-col :span="24" class="list">
          <el-form-item label="预计签单日期">
            <ul>
              <li
                v-for="(item, index) in timeCodeList"
                :key="index"
                @click="dateChange(index, item.code)"
                :class="dateActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
              <!-- <li
                @click="showDefinedTime = true;dateActive = -1"
                class="list-li"
                v-if="!showDefinedTime"
              >自定义时间段</li>
              <li v-else>
                <el-date-picker
                  v-model="form.createTime"
                  @change="timeCustom"
                  type="daterange"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                ></el-date-picker>
              </li>-->
            </ul>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-col :span="7">
            <el-form-item label="所属部门">
              <el-select
                v-model="form.followDepart"
                placeholder="请选择"
                clearable
                class="item-css"
                @change="depart($event)"
              >
                <el-option
                  v-for="(item, index) in departmentsList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="负责人">
              <el-select
                v-model="form.followUser"
                placeholder="请选择"
                clearable
                :no-data-text="form.followDepart?'此部门暂无成员':'请选择部门'"
                class="item-css"
                @change="tableApi()"
              >
                <el-option
                  v-for="(item, index) in headList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col :span="8">
            <el-form-item label="搜索">
              <el-input v-model="channame" class="input-with-select">
                <el-select v-model="valid" slot="prepend" placeholder="请选择" style="width:110px;">
                  <el-option label="全部" value></el-option>
                  <el-option label="客户名称" value="1"></el-option>
                  <el-option label="机会名称" value="2"></el-option>
                </el-select>
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2">
            <el-button style="margin-left:20px;" type="primary" @click="onSubmit">查询</el-button>
          </el-col>
        </el-col>
        <el-col :span="24"></el-col>
      </el-row>
    </el-form>
    <!-- <el-row>
      <el-col :span="24" style="font-size:20px;text-align:center;">
        <i
          style="cursor: pointer;"
          :class="flag == true? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
          @click="show"
        ></i>
      </el-col>
    </el-row>-->

    <el-row style="margin-top:20px;">
      <!-- 筛选 -->
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>

      <el-button
        type="primary"
        icon="el-icon-folder-add"
        v-permi="'/customer/addChance'"
        @click="isShowChance=true"
      >新增机会</el-button>
      <el-button
        type="danger"
        icon="el-icon-delete"
        :disabled="!checkboxId.length"
        @click="deleteChance"
      >删除</el-button>
      <el-button type="success" icon="el-icon-message" @click="showSend">发送短信</el-button>
    </el-row>
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
      <el-table-column prop="name" v-if="showCol.indexOf('机会名称')>-1" label="机会名称" align="center"></el-table-column>
      <el-table-column
        prop="companyName"
        v-if="showCol.indexOf('对应客户')>-1"
        label="对应客户"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="expectPrice"
        v-if="showCol.indexOf('预计销售金额')>-1"
        label="预计销售金额"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="expectDate"
        v-if="showCol.indexOf('预计签单日期')>-1"
        label="预计签单日期"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="stageStr"
        v-if="showCol.indexOf('销售阶段')>-1"
        label="销售阶段"
        align="center"
      ></el-table-column>
      <!-- <el-table-column prop="probability" v-if="showCol.indexOf('签单概率')>-1" label="签单概率" align="center"></el-table-column> -->
      <el-table-column
        prop="followContents"
        show-overflow-tooltip
        v-if="showCol.indexOf('最新跟进记录')>-1"
        label="最新跟进记录"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="followCreateTime"
        v-if="showCol.indexOf('实际跟进时间')>-1"
        label="实际跟进时间"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="nextTime"
        v-if="showCol.indexOf('下次跟进时间')>-1"
        label="下次跟进时间"
        align="center"
      ></el-table-column>
      <el-table-column label="操作" width="150" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            plain
            v-permi="'/chance/updateChance'"
            @click="handleEdit(scope.$index, scope.row)"
          >编辑</el-button>
          <el-button size="mini" @click="handleView(scope.$index, scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      class="itemPage"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="page"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalSize"
    ></el-pagination>
    <new-chance
      :isShowChance="isShowChance"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-chance>
    <new-send
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-send>
    <edit-chance
      :isShowChanceEdit="isShowChanceEdit"
      :opportunityId="opportunityId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-chance>
  </div>
</template>

<script>
// 机会管理
import API from "@/api/chance";
import NewChance from "@/components/element/NewChance"; // 新增机会
import NewSend from "@/components/element/NewSend"; // 发送短信
import EditChance from "@/components/element/EditChance"; // 编辑
import { constants } from "crypto";
export default {
  name: "opportunity",
  components: {
    NewChance,
    NewSend,
    EditChance
  },
  data() {
    return {
      stageList: [], // 销售阶段
      stageActive: 0,
      dateActive: 0,
      timeCodeList: [], // 预计签单日期
      isShowChance: false,
      isShowSend: false,
      showDefinedTime: false, // 显示自定义时间段
      isShowChanceEdit: false,
      opportunityId: "", // 编辑当前的id
      departmentsList: [], // 所属部门
      headList: [], // 所属负责人
      channame: "",
      names: "",
      tableData: [],
      valid: "",
      form: {
        followDepart: "",
        followUser: "",
        createTime: []
      },
      page: 1, //当前第几页
      rows: 10, //每页默认显示几条
      totalSize: 0, //总条数
      stage: null, // 销售阶段code
      timeCode: null, // 预计签单日期code
      followUser: null, // 所属负责人
      companyName: null, // 客户名称
      name: null, // 机会名称
      showCol: [
        "机会名称",
        "对应客户",
        "预计销售金额",
        "预计签单日期",
        "销售阶段",
        "最新跟进记录"
      ],
      colOptions: [
        "机会名称",
        "对应客户",
        "预计销售金额",
        "预计签单日期",
        "销售阶段",
        "最新跟进记录"
      ],
      checkboxId: "" // 选中的
    };
  },
  methods: {
    colChange() {
      localStorage.setItem("opportunityTable", JSON.stringify(this.showCol));
    },
    // 封装的table的api
    tableApi() {
      let form = this.form;
      let data = {
        pageNum: this.page,
        pageSize: this.rows,
        stage: this.stage,
        timeCode: this.timeCode,
        startTime: form.createTime[0],
        endTime: form.createTime[1],
        followDepart: form.followDepart,
        followUser: form.followUser,
        companyName: form.companyName,
        name: form.name
      };
      API.postList(data).then(res => {
        // console.log(res.data)
        this.totalSize = res.data.total; //总条数
        this.tableData = res.data.list;
      });
    },
    //选择每页显示条数触发
    sizeChangePage(val) {
      this.rows = val;
      this.tableApi(this.rows);
    },
    //选择第几页触发
    currentChangePage(val) {
      this.page = val;
      this.tableApi(this.page);
    },
    // 销售阶段
    stageChange(index, code) {
      this.stageActive = index; //将点击的元素的索引赋值给变量
      this.stage = code;
      this.tableApi(this.stage);
    },
    // 预计签单日期
    dateChange(index, code) {
      this.dateActive = index;
      this.showDefinedTime = false;

      this.form.createTime = [];
      this.timeCode = code;
      this.tableApi(this.timeCode);
    },
    // 所属部门
    depart(val) {
      this.form.followDepart = val;
      this.form.followUser = "";
      this.tableApi();
      if (val) {
        API.getHead(val).then(res => {
          // res.data.forEach(item => {
          //   item.value = item.name;
          // });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          // res.data.forEach(item => {
          //   item.value = item.name;
          // });
          this.headList = res.data;
        });
      }
    },
    timeCustom() {
      this.dateActive = null;
      this.form.timeCode = null;
      this.tableApi();
    },
    querySearch(queryString, cb) {
      var restaurants = this.headList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    // // 所属负责人id
    // handleSelect(item) {
    //   this.followUser = item.id;
    //   this.tableApi(this.followUser);
    //   if (this.followUser != null) {
    //     API.getDepartByUserId(item.id).then(res => {
    //       this.departmentsList = res.data;
    //     });
    //   } else {
    //     API.getDepartments().then(res => {
    //       this.departmentsList = res.data;
    //     });
    //   }
    // },
    // handleBlurStr(val) {
    //   if (this.form.followUser == "") {
    //     API.getDepartments().then(res => {
    //       this.departmentsList = res.data;
    //     });
    //   }
    // },
    // 查询
    onSubmit() {
      this.form.companyName = this.form.name = "";
      if (this.valid == "1") {
        this.form.companyName = this.channame;
        this.tableApi();
      } else {
        this.form.name = this.channame;
        this.tableApi();
      }
    },

    // table发生变化触发
    handleSelectionChange(val) {
      let arr = [];
      val.map(ele => {
        arr.push(ele.id);
      });
      this.checkboxId = arr;
    },
    // 删除选中的机会
    deleteChance() {
      this.$confirm("是否删除所选机会？", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      }).then(() => {
        let Params = { ids: this.checkboxId.join(",") };
        API.postdeleteChance(Params).then(res => {
          this.$message({
            message: "删除成功",
            type: "success"
          });
        });
        this.tableApi();
      });
    },
    // 发送短信
    showSend() {
      this.isShowSend = true;
    },
    // 保存Dialog
    getSonSure() {
      this.isShowSend = false;
      this.isShowChance = false;
      this.isShowChanceEdit = false;
      this.tableApi();
    },
    // 关闭Dialog
    getSonCancel(val) {
      this.isShowSend = val;
      this.isShowChance = val;
      this.isShowChanceEdit = val;
    },
    // 编辑
    handleEdit(index, row) {
      this.opportunityId = row.id;
      this.isShowChanceEdit = true;
    },
    // 查看
    handleView(index, row) {
      this.$router.push({ path: "opportunitydetails", query: { id: row.id } });
      // console.log(index, row)
    }
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("opportunityTable"));
    if (showCol) this.showCol = showCol;
    // 初始加载
    this.tableApi();
    // 销售阶段
    API.getSaleStage().then(res => {
      let obj = { code: 0, val: "全部" };
      res.data.unshift(obj);
      this.stageList = res.data;
    });
    // 预计签单日期
    API.getCustomerTimeCode().then(res => {
      // let obj = { code: 0, name: "全部" };
      // res.data.unshift(obj);
      // res.data.splice(2, 1);
      // res.data.splice(3, 1);
      // res.data.splice(4, 1);
      this.timeCodeList = res.data;
    });
    // 所属部门的api
    API.getDepartments().then(res => {
      this.departmentsList = res.data;
    });
    // 所有负责人
    API.getAllUser().then(res => {
      // res.data.forEach(item => {
      //   item.value = item.name;
      // });
      // this.headList = res.data;
    });
  }
};
</script>
<style lang='scss' scoped>
ul {
  list-style-type: none;
  padding: 0;
}
.list {
  li {
    display: inline-block;
    padding: 0 20px;
    cursor: pointer;
  }
}
.item-css {
  width: 100%;
}
.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
</style>