<template>
  <div>
    <el-dialog title="新增投诉或建议" :visible.sync="outerVisible" @close="closeDialog">
      <el-form label-width="120px" ref="form" :rules="rules" :model="form">
        <el-form-item label="客户名称" prop="companyName">
          <el-input v-model="form.companyName" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="投诉人姓名" prop="linkName">
          <el-input v-model="form.linkName" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="联系方式" prop="linkTel">
          <el-input v-model="form.linkTel" maxlength="12" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="投诉类别" prop="type">
          <el-select v-model="form.type" placeholder="请选择" clearable @change="departChange">
            <el-option label="投诉" :value="1"></el-option>
            <el-option label="建议" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="内容" prop="text">
          <el-input
            v-model="form.text"
            style="width:60%"
            type="textarea"
            :rows="4"
            show-word-limit
            maxlength="200"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
        <el-form-item label="被投诉部门" prop="followDepart">
          <el-select v-model="form.followDepart" placeholder="请选择" clearable @change="departChange">
            <el-option
              v-for="(item, index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="被投诉人" prop="followUser">
          <el-select v-model="form.followUser" @change="userChange" filterable placeholder="请选择">
            <el-option
              v-for="(item,index) in headList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success">保存</el-button>
          <el-button @click="closeDialog">关闭</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import API from "@/api/after";
export default {
  name: "AddComplain",
  props: ["isAddComplain"],
  data() {
    var validateTel = (rule, value, callback) => {
      var reg = /0\d{2}-\d{7,8}$/;
      var reg2 = /^\d{11}$/;
      if (!value || reg.test(value) || reg2.test(value)) {
        callback();
      } else {
        callback(new Error("请输入正确号码"));
      }
    };
    return {
      outerVisible: false,
      rules: {
        companyName: [
          { required: true, message: "请输入客户名称", trigger: "blur" }
        ],
        linkName: [
          { required: true, message: "请输入投诉人姓名", trigger: "blur" }
        ],
        linkTel: [
          { required: true, validator: validateTel, trigger: "blur" }
        ],
        type: [{ required: true, message: "请选择类别", trigger: "change" }],
        text: [{ required: true, message: "请输入投诉内容", trigger: "blur" }]
      },
      form: {
        companyName: null,
        linkName: null,
        linkTel: null,
        type: null,
        text: null,
        followDepart: null,
        followUser: null
      },
      departmentsList: [],
      headList: []
    };
  },
  methods: {
    // 所属负责人id
    userChange(val) {
      let departId = this.headList.find(item => {
        return item.id === val;
      }).departId;
      this.form.followDepart = departId;
      this.departChange("no_change");
    },
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather");
    },
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let form = this.form;

        let params = {
          companyName: form.companyName,
          linkName: form.linkName,
          linkTel: form.linkTel,
          type: form.type,
          text: form.text,
          followDepart: form.followDepart,
          followUser: form.followUser
        };
        API.addAdvise(params)
          .then(res => {
            console.log(res);
            this.$message({
              message: "保存成功",
              type: "success"
            });
            this.$refs.form.resetFields();
            this.closeDialog();
            this.$emit("sureDialogFather");
          })
          .catch(err => {
            console.error(err);
          });
      });
    },
    // 所属部门的事件获取对应的id
    departChange(flag) {
      if (flag !== "no_change") this.form.followUser = null;
      let val = this.form.followDepart;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      }
    },
    querySearch(queryString, callback) {
      var restaurants = this.headList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      callback(results);
    }
  },
  watch: {
    isAddComplain(val) {
      this.outerVisible = val;
      if (!val) return;
      // 所属部门的api
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
      // 所属负责人全部
      API.getAllUser().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.headList = res.data;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>