// 机会管理

import fetch from '@/utils/fetch';
import Qs from 'qs'

export default {
    // get 带参请求
    // 销售阶段
    getSaleStage(data) {
        return fetch({
            url: `chance/findSaleStage`,
            method: 'get',
            params: data
        })
    },
    // 预计签单日期
    getCustomerTimeCode(data) {
        return fetch({
            url: `customer/findCustomerTimeCode`,
            method: 'get',
            params: data
        })
    },
    // 所属部门的api
    getDepartments(data) {
        return fetch({
            url: `customer/findAllDepart`,
            method: 'get',
            params: data
        })
    },
    // 根据id查询联系人
    getCustomerLinkman(data) {
        return fetch({
            url: `customer/findCustomerLinkman/${data}`,
            method: 'get'
        })
    },

    // 根据表格id查询附件
    getCustomerFileById(data) {
        return fetch({
            url: `customer/findCustomerFileById/${data}`,
            method: 'get'
        })
    },

    //查看跟进
    getFollowList(data) {
        return fetch({
            url: `/follow/getFollowList`,
            method: 'get',
            params: data
        })
    },
    // 所有负责人
    getAllUser(data) {
        return fetch({
            url: `customer/findAllUser`,
            method: 'get',
            params: data
        })
    },

    // 获取客户来源
    customerFrom(data) {
        return fetch({
            url: `clue/customerFrom`,
            method: 'get',
            params: data
        })
    },

    // 根据选择的所属部门的id来获取负责人
    getHead(data) {
        return fetch({
            url: `customer/findAllDepartUser/${data}`,
            method: 'get'
        })
    },
    // 根据选择的所属负责人的id来获取部门
    getDepartByUserId(data) {
        return fetch({
            url: `customer/findDepartByUserId/${data}`,
            method: 'get'
        })
    },
    // 根据id进行机会的详情页查询
    getChanceDetails(data) {
        return fetch({
            url: `chance/findChanceDetails/${data}`,
            method: 'get'
        })
    },
    // 跟进客户名称模糊查询 所属客户
    getCustomerByCompanyName(data) {
        return fetch({
            url: `customer/findCustomerByCompanyName`,
            method: 'get',
            params: data
        })
    },
    // 关联联系人
    getAllLinkmen(data) {
        return fetch({
            url: `chance/findAllLinkmen`,
            method: 'get',
            params: data
        })
    },
    // 业务类型
    getType(data) {
        return fetch({
            url: `customer/service/type`,
            method: 'get',
            params: data
        })
    },
    // 机会类型
    getChanceType(data) {
        return fetch({
            url: `customer/findChanceType`,
            method: 'get',
            params: data
        })
    },
    // 机会来源
    getchannel(data) {
        return fetch({
            url: `customer/channel`,
            method: 'get',
            params: data
        })
    },
    // 根据id进入编辑页面的数据回显
    findChanceById(data) {
        return fetch({
            url: `chance/findChanceById/${data}`,
            method: 'get'
        })
    },
    // post 带参请求
    // 表格数据api
    postList(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `chance/findChance?${data1}`,
            method: 'post'
        })
    },
    // 根据id进行机会的详情页的删除
    postdeleteChance(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `chance/deleteChance?${data1}`,
            method: 'post'
        })
    },
    // 新增机会
    postaddChance(data) {
        return fetch({
            url: `customer/addChance`,
            method: 'post',
            params: data
        })
    },
    // 修改机会
    postupdateChance(data) {
        return fetch({
            url: `chance/updateChance`,
            method: 'post',
            params: data
        })
    },
}