<template>
  <div class="user">
    <div class="user-header">
      <div>
        <span style="font-size:18px;">角色权限设置</span>
      </div>
      <div class="remind">
        <p>提示：1、角色代表操作权限和数据权限，操作权限是指具有该角色的用户是否能查看、新增、编辑、删除某些数据，数据权限是指具有该角色的用户能操作的数据的范围；</p>
        <p style="margin-top:10px;margin-left:36px">2、超级管理员的权限是系统默认的最大权限角色、不能修改。</p>
      </div>
      <div>
        <el-button type="primary" v-permi="'/roleRsfun/addRole'" @click="addRoleDialog = true">
          <span class="el-icon-folder-add"></span> 新增角色
        </el-button>
        <el-button
          type="success"
          :disabled="!selectedRole.id||selectedRole.name=='超级管理员'"
          @click="copyRole"
        >
          <span class="el-icon-document-copy"></span> 复制角色
        </el-button>
        <!-- <el-button
          type="warning"
          :disabled="!changeAuthority||!selectedRole"
          @click="changeAuthority=false"
        >
          <span class="el-icon-edit"></span> 修改权限
        </el-button>
        <el-button type="success" :disabled="changeAuthority" @click="changeAuthority=true">
          <span class="el-icon-document-checked"></span> 保存修改
        </el-button>-->
      </div>
    </div>
    <div class="user-content">
      <div class="user-left">
        <p>公司角色</p>
        <div>
          <el-menu default-active="2" class="el-menu-vertical-demo">
            <!-- <el-menu default-active="2" class="el-menu-vertical-demo" @select="useRole"> -->
            <!-- <el-menu-item index="1">
              <span slot="title">超级管理员</span>
            </el-menu-item>-->
            <el-menu-item
              @click="useRole(item)"
              :index="item.name"
              style="display:flex"
              v-for="(item,i) in roleList"
              :key="i"
            >
              <span>{{item.name}}</span>
              <div v-if="item.name!=='超级管理员'">
                <span class="el-icon-edit-outline" @click="editRole(item)"></span>
                <!-- 删除角色 -->
                <span @click="delteRole(item.id)" class="el-icon-delete"></span>
              </div>
            </el-menu-item>
          </el-menu>
        </div>
      </div>
      <div class="user-right">
        <el-tabs type="border-card">
          <el-tab-pane label="操作权限">
            <!-- {{checkList}} -->
            <div class="user-center">
              <div v-for="(allItem,index) in allFunction" :key="index">
                <p>{{allItem.category}}</p>
                <div class="content">
                  <el-checkbox-group
                    :disabled="!selectedRole.id||selectedRole.name=='超级管理员'"
                    v-model="checkList"
                  >
                    <el-checkbox
                      v-for="(item, index) in allItem.list"
                      @change="checkboxChange($event,item.id)"
                      :key="index"
                      :label="item.id"
                    >{{item.name}}</el-checkbox>
                  </el-checkbox-group>
                </div>
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane label="数据权限">
            <div class="list-form">
              <ul>
                <el-radio-group
                  :disabled="!selectedRole.id||selectedRole.name=='超级管理员'"
                  v-model="dataPermission"
                  @change="radioChange"
                >
                  <li>
                    <el-radio :label="0">仅自己</el-radio>
                    <p>只能操作分配给自己的数据</p>
                  </li>
                  <li>
                    <el-radio :label="1">下属部门的全部数据</el-radio>
                    <p>能操作自己、下属和自己所属部门及其子部门的数据</p>
                  </li>
                  <li>
                    <el-radio :label="2">全公司</el-radio>
                    <p>能操作全公司的数据</p>
                  </li>
                </el-radio-group>
              </ul>
            </div>
          </el-tab-pane>
        </el-tabs>
        <div></div>
      </div>
    </div>
    <div>
      <el-dialog
        title="新增角色"
        :visible.sync="addRoleDialog"
        @close="$refs.addName.resetFields()"
        :close-on-click-modal="false"
      >
        <el-form :model="addName" ref="addName" :label-width="formLabelWidth">
          <el-form-item
            prop="name"
            label="角色名称"
            :rules="[{ required: true, message: '名称不能为空',trigger:'blur'}]"
          >
            <el-input v-model="addName.name" autocomplete="off" placeholder="请填写角色名称"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addRoleSubmit">保存</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

      <el-dialog title="编辑角色" :visible.sync="editRoleDialog" :close-on-click-modal="false">
        <el-form :model="editName" ref="editName" :label-width="formLabelWidth">
          <el-form-item
            label="角色名称"
            prop="name"
            :rules="[{ required: true, message: '名称不能为空',trigger:'blur'}]"
          >
            <el-input v-model="editName.name" autocomplete="off" placeholder="请填写角色名称"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="editRoleSubmit">保存</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import API from "@/api/admin";

export default {
  name: "user",
  props: {
    msg: String
  },
  data() {
    return {
      dataPermission: "", // 数据权限
      // 是否禁用修改
      changeAuthority: true,
      // 新增角色姓名
      addName: {
        name: ""
      },
      // 编辑角色姓名
      editName: {
        name: ""
      },
      // 角色列表
      roleList: [],
      // 被选中的角色
      selectedRole: {},
      checked: true,
      checked2: false,
      addRoleDialog: false, // 新增角色
      editRoleDialog: false, // 编辑角色
      formLabelWidth: "80px",
      checkList: [], // 所有权限列表
      allFunction: null // 所有权限
    };
  },
  methods: {
    radioChange(e) {
      let params = {
        roleId: this.selectedRole.id,
        reviewsub: e
      };
      API.updateReviewSub(params)
        .then(res => {
          console.log(res);
          this.$message.closeAll();
          this.$message.success("修改成功");
          this.getRoleList();
        })
        .catch(err => {
          console.error(err);
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    },
    // 修改操作权限操作
    checkboxChange(handle, id) {
      console.log(handle, id);
      let params = {
        state: Number(handle),
        roleId: this.selectedRole.id,
        functionId: id
      };
      API.addFunction(params)
        .then(res => {
          console.log(res);
          this.$message.closeAll();
          this.$message({
            message: "修改成功",
            type: "success"
          });
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    },
    // 编辑角色
    editRole(e) {
      this.editRoleDialog = true;
      this.editName = Object.assign({}, e);
    },
    // 编辑提交按钮
    editRoleSubmit() {
      if (!this.editName.name) return;
      let params = {
        id: this.editName.id,
        name: this.editName.name
      };
      API.updateRole(params)
        .then(res => {
          this.$message({
            type: "success",
            message: "修改成功"
          });
          this.editRoleDialog = false;
        })
        .catch(err => {
          console.error(err);
          this.$message({
            type: "error",
            message: err
          });
        });
    },
    // 删除角色
    delteRole(e) {
      this.$confirm("是否删除角色", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      })
        .then(() => {
          API.delteRole({ id: e })
            .then(res => {
              console.log(res);
              this.$message({
                type: "success",
                message: "删除成功"
              });
              this.selectedRole = {};
              this.getRoleList();
            })
            .catch(err => {
              console.error(err);
              this.$message({
                type: "error",
                message: err
              });
            });
        })
        .catch(err => {
          console.error(err);
          this.$message({
            type: "error",
            message: "取消操作"
          });
        });
    },
    // 复制角色按钮
    copyRole() {
      console.log(this.selectedRole);

      if (!this.selectedRole.id) return;
      this.$confirm("是否复制角色", "确认复制", {
        distinguishCancelAndClose: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      })
        .then(() => {
          API.copyRole({ name: this.selectedRole.name })
            .then(res => {
              console.log(res);
              this.$message({
                message: "复制成功",
                type: "success"
              });
              this.getRoleList();
            })
            .catch(err => {
              console.error(err);
              this.$message({
                message: err,
                type: "error"
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "error",
            message: "已取消"
          });
        });
    },
    useRole(item) {
      this.dataPermission = item.reviewsub;
      this.selectedRole = item;
      API.find({
        roleId: item.id
      })
        .then(res => {
          console.log(res);
          let data = res.data;
          let arr = [];
          data.map(item => {
            arr.push(item.id);
          });
          this.checkList = arr;
          console.log(arr);
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 获取管理员列表
    getRoleList() {
      API.getRoleList()
        .then(res => {
          this.roleList = res.data;
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 获取全部权限列表
    getAllFunction() {
      API.getAllFunction()
        .then(res => {
          let data = res.data;
          let Obj = {};
          for (const item of data) {
            if (!Obj[item.sortNo]) {
              Obj[item.sortNo] = {
                category: item.category,
                list: []
              };
              Obj[item.sortNo].list.push(item);
            } else {
              Obj[item.sortNo].list.push(item);
            }
          }
          this.allFunction = Obj;
          console.log(Obj);
        })
        .catch(err => {
          console.error(err);
        });
    },
    // // 获取角色权限列表
    // getLimitsList() {
    //   API.find({})
    //     .then(res => {
    //       console.log(res);
    //     })
    //     .catch(err => {
    //       console.error(err);
    //     });
    // },
    // 提交表单
    addRoleSubmit() {
      if (!this.addName.name) return;
      console.log(this.addName.name);
      API.addRole({ name: this.addName.name })
        .then(res => {
          this.addName.name = "";
          this.addRoleDialog = false;
          this.getRoleList();
          this.$message({
            message: "添加成功",
            type: "success"
          });
        })
        .catch(err => {
          // if(err === "角色名已存在") {}
          this.$message({
            message: err,
            type: "error"
          });
        });
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  mounted() {
    this.getRoleList();
    this.getAllFunction();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  .el-input__inner {
    width: 50%;
  }
}

.list-form {
  padding: 10px 20px;
  .el-radio-group {
    width: 100%;
    font-size: inherit;
  }
  li {
    padding: 20px 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
      width: 280px;
    }
  }
}
.remind {
  margin: 20px 0px;
  padding: 20px 8px;
  background: rgba(223, 240, 216, 1);
  color: rgb(119, 166, 122);
}
.user-content {
  padding: 30px 10px 10px 30px;
  border: 1px solid rgb(228, 228, 228);
  height: 100%;
  margin-top: 20px;
  display: flex;
  .user-left {
    width: 30%;
    .el-menu {
      border: 1px solid rgb(228, 228, 228);
      // /deep/ :first-child.el-menu-item{
      //   background: #ddd;
      // }
      /deep/ .el-menu-item {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
      }
    }
    p {
      background: rgb(228, 228, 228);
      padding: 10px;
      font-size: 16px;
    }
  }
  .user-right {
    margin-left: 20px;
    width: 100%;
    .user-center {
      p {
        background: rgb(245, 245, 245);
        padding: 20px 20px;
        font-size: 14px;
        margin-top: 10px;
      }
      div.content {
        padding: 20px;
      }
      .el-tabs__content {
        padding: 0;
      }
      .el-checkbox {
        margin-bottom: 30px;
      }
    }
  }
}
</style>
