<template>
  <div class="login" :style="back">
    <div class="bc">
      <div class="login-title">
        <img src="../assets/login/logos.png" alt />
      </div>
      <div class="admin">
        <div class="admin-left">
          <div class="admin-left-title">
            <img src="../assets/login/logo.png" alt />
          </div>
          <div class="admin-left-bottom">
            <p>会计工厂管理平台</p>
            <ul>
              <li>
                <img src="../assets/login/logo1.png" alt />
                <span>线索管理</span>
              </li>
              <li>
                <img src="../assets/login/logo3.png" alt />
                <span>销售管理</span>
              </li>
              <li>
                <img src="../assets/login/logo2.png" alt />
                <span>售后管理</span>
              </li>
              <li>
                <img src="../assets/login/logo4.png" alt />
                <span>代账公司管理</span>
              </li>
            </ul>
          </div>
        </div>

        <div class="admin-right">
          <div class="admin-right-title" style>
            <span
              style="position:absolute;top:14px;left:0px;border:1px solid rgba(0, 88, 173, 0.2);width:120px"
            ></span>
            <span style="font-size:24px;color:#0058AD">用户登录</span>
            <span
              style="position:absolute;top:14px;right:0px;border:1px solid rgba(0, 88, 173, 0.2);width:120px"
            ></span>
          </div>
          <div class="admin-button">
            <div>
              <span>用户名：</span>
              <input type="text" @keydown.enter="login" v-model="ruleForm.userName" />
            </div>
            <div>
              <span>密&#160;&#160;&#160;码：</span>
              <input type="password" @keydown.enter="login" v-model="ruleForm.password" />
            </div>
            <button
              @click="login"
              :style="{'background':ruleForm.userName.length&&ruleForm.password.length?'':'#999'}"
            >
              <span>登录系统</span>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { setLocalStorage, getToken, setCookie, delCookie } from "@/utils/auth";
import API from "@/api/information";
import { encryptDes, decryptDes } from "@/utils/des";
export default {
  name: "login",
  data() {
    return {
      back: {
        backgroundImage: "url(" + require("../assets/login/logins.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "100% 100%",
        height: "100%"
      },
      ruleForm: {
        userName: "",
        password: ""
      }
    };
  },
  methods: {
    login() {
      let userName = this.ruleForm.userName;
      let password = this.ruleForm.password;
      if (!userName.length) {
        this.$message({
          type: "warning",
          message: "请输入用户名"
        });
        return;
      } else if (!password.length) {
        this.$message({
          type: "warning",
          message: "请输入密码"
        });
        return;
      }
      // this.$store
      //   .dispatch("Login", this.ruleForm)
      API.login(this.ruleForm)
        .then(res => {
          localStorage.removeItem("userInfo");
          setCookie("token", res.data);

          API.getUserInfo()
            .then(res => {
              let userJson = JSON.stringify(res.data.userPermission);
              let desUser = encryptDes(userJson);
              localStorage.setItem("userInfo", desUser);
              this.$store.commit("userInfo", res.data.userPermission);
              this.getPermissionList();

              this.$message.success("登录成功");
              this.$router.push("/clue");
            })
            .catch(err => {
              // alert("用户信息获取报错");
              if (err.indexOf("权限") === -1)
                this.$message.warning("获取用户信息失败");

              console.error(err);
            });
        })
        .catch(err => {
          this.$message.error(err + "！");
          this.ruleForm.password = "";
        });
    }
  }
};
</script>

