<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-col :span="8">
          <h1>{{message.name}}</h1>
        </el-col>
        <el-col :span="8">
          <h3>客户：{{customer.companyName}}</h3>
        </el-col>
      </el-col>
    </el-row>
    <el-row>
      <el-dropdown trigger="click" class="el-dro" @command="handleCommand">
        <el-button type="primary" class="el-dropdown-link">
          {{commandName}}
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item
            v-for="(item, i) in contractStatusList"
            :key="i"
            :command="item.val"
            :val="item.code"
          >{{item.val}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-button
        type="primary"
        @click="isShowContractEdit=true"
        permi="'/contract/updateContract'"
      >编辑</el-button>
      <el-button type="warning" @click="isShowPotential=true">转移负责人</el-button>
      <el-button type="danger" @click="deleteContract">删除</el-button>
      <el-button type="success" @click="showSend">发送短信</el-button>
    </el-row>

    <el-tabs type="card" @tab-click="tabClick">
      <el-tab-pane label="基本信息" class="quanjing">
        <el-card class="box-card">
          <div>
            <div class="text">基本信息</div>
            <div class="text">
              <p>合同名称：</p>
              <p>{{message.name}}</p>
            </div>
            <div class="text">
              <p>客户名称：</p>
              <p>{{customer.companyName}}</p>
            </div>
            <div class="text">
              <p>合同总金额：</p>
              <p>{{message.totalPrice}}</p>
            </div>
            <div class="text">
              <p>签约日期：</p>
              <p>{{message.signDate}}</p>
            </div>
            <div class="text">
              <p>提单日期：</p>
              <p>{{message.ladingBillTime}}</p>
            </div>
            <div class="text">
              <p>结束日期：</p>
              <p>{{message.endTime}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text">回款信息</div>
            <div class="text">
              <p>已开票金额：</p>
              <p>{{message.name}}</p>
            </div>
            <div class="text">
              <p>已回款金额：</p>
              <p>{{paymentTotal}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text">其他信息</div>
            <div class="text">
              <p>合同状态：</p>
              <p>{{message.statusStr}}</p>
            </div>
            <div class="text">
              <p>合同编号：</p>
              <p>{{message.signNo}}</p>
            </div>
            <div class="text">
              <p>活动类型：</p>
              <p>{{packageType.name}}</p>
            </div>
            <div class="text">
              <p>支付账户：</p>
              <p>{{message.payNoStr}}</p>
            </div>
            <!-- <div class="text  ">经办人(全名)
              <p>：</p>
              <p>{{user.name}}</d</p>
            iv>-->
            <!-- <div class="text">
              <p>合同附件：</p>
              <el-button size="minddle" type="primary">下载</el-button>
            </div>-->
            <div class="text">
              <p>下次跟进时间：</p>
              <p>{{message.nextFollowTime}}</p>
            </div>
            <div class="text">
              <p>进账备注：</p>
              <p>{{message.remarks}}</p>
            </div>
            <div class="text">
              <p>订单来源：</p>
              <p>{{message.fromName}}</p>
            </div>
            <div class="text">
              <p>电话：</p>
              <p>{{message.telephone}}</p>
            </div>
            <div class="text">
              <p>意向度：</p>
              <p>{{message.intentionStr}}</p>
            </div>
            <div class="text">
              <p>发票编号：</p>
              <p>{{message.receiptNo}}</p>
            </div>
            <div class="text">
              <p>发票开具：</p>
              <p>{{message.isInvoice}}</p>
            </div>
            <div class="text">
              <p>进账类型：</p>
              <p>{{message.incomeTypeStr}}</p>
            </div>
            <div class="text">
              <p>支付金额：</p>
              <p>{{message.payment}}</p>
            </div>
            <!-- <div class="text  ">
              <p>是否绑定微信公众号：</p>
              <p>{{message.nextFollowTime}}</d</p>
            iv>
             <div class="text">
              <p>公司名称：</p>
              <p>{{message.companyName}}</p>
            </div>-->
            <div class="text">
              <p>收据号：</p>
              <p>{{message.receiptNumber}}</p>
            </div>
            <!-- <div class="text">
              <p>搜索词：</p>
              <p>{{message.searchWords}}</p>
            </div>-->
            <el-divider></el-divider>
            <div class="text">系统信息</div>
            <div class="text">
              <p>负责人：</p>
              <p>{{user.name}}</p>
            </div>
            <div class="text">
              <p>创建人：</p>
              <p>{{message.createUserName}}</p>
            </div>
            <!-- <div class="text  ">
              <p>前负责人：</p>
              <p>{{message.oldFollowUser}}</d</p>
            iv>-->
            <div class="text">
              <p>所属部门：</p>
              <p>{{depart.name}}</p>
            </div>
            <!-- <div class="text  ">
              <p>前所属部门：</p>
              <p>{{message.oldFollowDepart}}</d</p>
            iv>-->
            <div class="text">
              <p>创建时间：</p>
              <p>{{message.createTime}}</p>
            </div>
            <div class="text">
              <p>更新于：</p>
              <p>{{message.updateTime}}</p>
            </div>
          </div>
        </el-card>
        <follow-up-history
          class="genjin"
          @writeFollow="isShowFollow=true"
          :customerId="customerId"
          :type="1"
          ref="followUpHistory"
        ></follow-up-history>
      </el-tab-pane>
      <el-tab-pane label="回款计划">
        <el-card>
          <div class="returned-title">
            <span>计划回款总额：{{paymentPriceTop.plan.planTotalPrice}}</span>
            <span>计划已回款金额：{{paymentPriceTop.plan.alreadyTotalPrice}}</span>
            <span>计划未回款金额：{{paymentPriceTop.plan.notTotalPrice}}</span>
            <span>逾期未回款金额：{{paymentPriceTop.plan.overTimeTotalPrice}}</span>
            <span v-show="!HavepaymentPrice.length">
              <el-button
                type="warning"
                v-permi="'/payment/addContractPaymentPlan'"
                @click="isNewReceivableplan=true"
              >新增回款计划</el-button>
            </span>
          </div>
        </el-card>
        <el-table
          :data="HavepaymentPrice"
          tooltip-effect="dark"
          style="width: 100%;"
          border
          :fit="true"
        >
          <el-table-column prop="opeDate" label="回款日期" align="center">
            <template slot-scope="scope">{{scope.row.opeDate.slice(0,10)}}</template>
          </el-table-column>
          <!-- <el-table-column prop="opeDate" label="实际回款时间" align="center"></el-table-column> -->
          <el-table-column prop="price" label="回款金额" align="center"></el-table-column>
          <el-table-column prop="userName" label="负责人" align="center"></el-table-column>
          <!-- <el-table-column prop="returnStatus" label="回款状态" align="center"></el-table-column> -->
          <el-table-column prop="realPay" label="实际回款金额" align="center"></el-table-column>
          <el-table-column prop="remarks" label="是否已完成" align="center">
            <template slot-scope="scope">{{scope.row.isOver?"是":"否"}}</template>
          </el-table-column>
          <el-table-column label="操作" width="100" align="center" fixed="right">
            <template slot-scope="scope">
              <!-- <el-button
                size="mini"
                :disabled="Boolean(scope.row.isOver)"
                @click="editPlan(scope.$index, scope.row)"
                type="primary"
                plain
              >编辑</el-button> -->
              <el-button
                size="mini"
                @click="handleDelete(scope.$index, scope.row, '计划')"
                type="danger"
              >删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="回款记录">
        <el-card>
          <div class="returned-title">
            <span>合同总金额：{{paymentPriceTop.record.totalPrice}}</span>
            <span>已回款总金额：{{paymentPriceTop.record.alreadyTotalPrice}}</span>
            <span>未回款总金额：{{paymentPriceTop.record.notTotalPrice}}</span>
            <!-- <span>逾期未回款金额：{{paymentPriceTop.record.overTimeTotalPrice}}</span> -->
            <el-button type="warning"
              v-permi="'/payment/addRealContractPayment'"
             @click="isNewReceivablerecord=true">新增回款记录</el-button>
          </div>
        </el-card>

        <el-table
          :data="NopaymentPrice"
          tooltip-effect="dark"
          style="width: 100%;"
          border
          :fit="true"
        >
          <el-table-column prop="opeDate" label="回款日期" align="center"></el-table-column>
          <el-table-column prop="price" label="回款金额" align="center"></el-table-column>
          <el-table-column prop="payNo" label="付款方式" align="center"></el-table-column>
          <el-table-column prop="payType" label="回款类型" align="center"></el-table-column>
          <el-table-column prop="paymentUser" label="收款人" align="center"></el-table-column>
          <el-table-column prop="remarks" label="备注" align="center"></el-table-column>
          <el-table-column label="操作" width="150" align="center" fixed="right">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                plain
                v-permi="'/payment/updateContractPaymentPlan'"
                @click="editRecord(scope.$index, scope.row)"
              >编辑</el-button>
              <!-- <el-button size="mini" @click="handleView(scope.$index, scope.row)">查看</el-button> -->
              <el-button
                size="mini"
                @click="handleDelete(scope.$index, scope.row,'记录')"
                type="danger"
              >删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="任务">
        <tab-task ref="tabTask" :customerId="customerId"></tab-task>
      </el-tab-pane>
      <el-tab-pane label="附件">
        <tab-annex ref="tabAnnex" :customerId="customerId"></tab-annex>
      </el-tab-pane>
      <!-- <el-tab-pane label="流转履历">
        <el-table
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%;margin-top: 20px;"
          border
          :fit="true"
        >
          <el-table-column prop="name" label="操作时间" align="center"></el-table-column>
          <el-table-column prop="name" label="操作人员" align="center"></el-table-column>
          <el-table-column prop="name" label="操作类型" align="center"></el-table-column>
          <el-table-column prop="name" label="备注" align="center"></el-table-column>
        </el-table>
      </el-tab-pane>-->
    </el-tabs>
    <new-receivablerecord
      :isNewReceivablerecord="isNewReceivablerecord"
      :detailData="addRecordData"
      @sureDialogFather="getSonSure;getContractRecordTable();getContractPlanTable()"
      @closeDialogFather="getSonCancel"
    ></new-receivablerecord>
    <NewContractEdit
      :isShowContractEdit="isShowContractEdit"
      :contractId="contractId"
      :contractStatusList="contractStatusList"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewContractEdit>
    <Potential
      :isShowPotential="isShowPotential"
      :contractId="contractId"
      @sureDialogFather="$router.go(-1)"
      @closeDialogFather="getSonCancel"
    ></Potential>
    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>

    <edit-receivableplan
      :isEditReceivableplan="isEditReceivableplan"
      :editPlanData="editPlanData"
      @sureDialogFather="getSonSure;getContractPlanTable()"
      @closeDialogFather="getSonCancel"
    ></edit-receivableplan>

    <new-receivableplan
      :contractDetail="contractDetail"
      :isNewReceivableplan="isNewReceivableplan"
      @sureDialogFather="getSonSure;getContractPlanTable()"
      @closeDialogFather="getSonCancel"
    ></new-receivableplan>
    <Followup
      :isShowFollow="isShowFollow"
      :customerId="customerId"
      @sureDialogFather="$refs.followUpHistory.getFollowData()"
      @closeDialogFather="getSonCancel"
    ></Followup>
    <edit-receivablerecord
      :isEditReceivablerecord="isEditReceivablerecord"
      :editRecordData="editRecordData"
      @sureDialogFather="getSonSure;getContractRecordTable();"
      @closeDialogFather="getSonCancel"
    ></edit-receivablerecord>
  </div>
</template>

<script>
import API from "@/api/contract";
import EditReceivablerecord from "../components/EditReceivablerecord"; // 回款记录的编辑
import NewReceivablerecord from "../components/NewReceivablerecord"; // 新增回款记录
import EditReceivableplan from "../components/EditReceivableplan"; // 编辑回款计划
import NewReceivableplan from "../components/NewReceivableplan"; // 配置回款计划
import Potential from "../components/Potential"; // 转移负责人

import TabTask from "@/components/element/TabTask";
import NewSend from "@/components/element/NewSend"; // 发送短信
import TabAnnex from "@/components/element/TabAnnex";
import Followup from "@/components/element/Followup"; // 写更进
import NewContractEdit from "@/components/element/EditContract"; // 编辑
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 跟进历史

export default {
  name: "contactdetails",
  components: {
    NewReceivablerecord,
    NewContractEdit,
    FollowUpHistory,
    TabAnnex,
    EditReceivableplan,
    Potential,
    NewSend,
    TabTask,
    NewReceivableplan,
    Followup,
    EditReceivablerecord
  },
  data() {
    return {
      contractStatusList: [], // 合同状态
      commandName: "签约",
      isNewReceivablerecord: false,
      isShowContractEdit: false,
      isShowPotential: false,
      isShowSend: false,
      isShowFollow: false,
      isNewReceivableplan: false,
      isEditReceivablerecord: false,
      editRecordData: "", // 编辑当条数据的id
      contractId: "", // 当前详情页的id
      customerId: null, // 客户id
      paymentTotal: "", // 当前详情页的已回款金额
      message: {}, // 基本信息
      customer: {},
      packageType: {},
      channel: {},
      user: {},
      addRecordData: {}, // 添加回款记录的数据
      depart: {},
      paymentPriceTop: {
        // 回款顶部数据
        plan: {}, // 回款计划
        record: {} // 回款记录
      },
      isEditReceivableplan: false, // 回款计划编辑
      editPlanData: {}, // 编辑计划的回显数据
      NopaymentPrice: [], // 无计划回款
      HavepaymentPrice: [], // 有计划回款
      tableData: [],
      contractDetail: {} // 回款传参
    };
  },
  methods: {
    // 点击下拉菜单项触发的事件回调
    handleCommand(command, val) {
      let data = {
        id: this.contractId,
        status: val.$attrs.val
      };
      API.postchangeContractStatus(data).then(res => {
        this.$message({
          type: "success",
          message: "修改成功!"
        });
        this.commandName = command;
      });
    },
    // 删除当前合同
    deleteContract() {
      this.$confirm("此操作将删除当前合同, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          API.postdeleteContract({ ids: this.contractId })
            .then(res => {
              this.$message({
                type: "success",
                message: "删除成功!"
              });
              this.$router.push({ path: "contract" });
            })
            .catch(err => {
              if (err.indexOf("权限") === -1) this.$message.warning(err);
            });
        })
        .catch(() => {
          this.$message({
            type: "error",
            message: "已取消删除"
          });
        });
    },
    // 发送短信
    showSend() {
      this.isShowSend = true;
    },
    // 写跟进
    showFollow() {
      this.isShowFollow = true;
    },

    editPlan(index, row) {
      this.editPlanData = {
        customerId: this.customerId,
        departId: row.departId,
        userName: row.userName,
        contractId: Number(this.contractId),
        opeDate: row.opeDate,
        price: row.price,
        id: row.id
      };
      this.isEditReceivableplan = true;
    },

    // 回款的编辑
    editRecord(index, row) {
      this.editRecordData = {
        opeDate: row.opeDate,
        customerId: this.customerId,
        payType: row.payTypeId,
        price: row.price,
        contractId: Number(this.contractId),
        remarks: row.remarks,
        userName: row.paymentUser,
        planpayId: row.planpayId,
        departId: row.departId,
        isOver: row.isOver,
        payNo: row.payNoId,
        id: row.id
      };

      this.isEditReceivablerecord = true;
    },
    // 无计划和有计划回款的删除
    handleDelete(index, row, name) {
      this.$confirm("是否删除选中的回款" + name, "删除" + name, {
        distinguishCancelAndClose: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      })
        .then(() => {
          API.postdeletePaymentPlans({
            ids: row.id
          }).then(res => {
            this.$message({
              message: "删除成功",
              type: "success"
            });
            if (name === "计划") {
              this.getContractPlanTable();
            } else {
              this.getContractRecordTable();
            }
          });
        })
        .catch(() => {});
    },
    // 有计划回款的查看
    handleView(index, row) {
      this.$router.push({
        path: "receivableRecorddetails",
        query: { id: row.id }
      });
    },
    // 保存Dialog
    getSonSure() {
      this.getSonCancel();
      this.getDetailData();
    },
    // 关闭Dialog 弹框点击关闭的值
    getSonCancel() {
      this.isNewReceivablerecord = false;
      this.isShowContractEdit = false;
      this.isShowPotential = false;
      this.isShowSend = false;
      this.isShowFollow = false;
      this.isNewReceivableplan = false;
      this.isEditReceivablerecord = false;
      this.isEditReceivableplan = false;
    },
    // tab 被选中时触发
    tabClick(tab) {
      if (tab.label == "基本信息") {
      } else if (tab.label == "回款计划") {
        this.getContractPlanTable();
      } else if (tab.label == "回款记录") {
        this.getContractRecordTable();
      } else if (tab.label == "任务") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "附件") {
        this.$refs.tabAnnex.getAnnexTable();
      } else if (tab.label == "流转履历") {
        // console.log('流转履历')
      }
    },
    getContractPlanTable() {
      API.getfindContractPaymentPlans(this.contractId).then(res => {
        let data = res.data;
        if (!data) {
          this.HavepaymentPrice = [];
          this.paymentPriceTop.plan = {
            planTotalPrice: null,
            notTotalPrice: null,
            alreadyTotalPrice: null,
            overTimeTotalPrice: null
          };
        } else {
          this.paymentPriceTop.plan = {
            planTotalPrice: data.planTotalPrice,
            notTotalPrice: data.notTotalPrice,
            alreadyTotalPrice: data.alreadyTotalPrice,
            overTimeTotalPrice: data.overTimeTotalPrice
          };
          this.HavepaymentPrice = data.payments;
        }
      });
    },
    getContractRecordTable() {
      API.getfindContractPayment(this.contractId).then(res => {
        let data = res.data;
        if (!data) {
          this.NopaymentPrice = [];
          this.paymentPriceTop.record = {
            alreadyTotalPrice: null,
            notTotalPrice: null,
            totalPrice: null,
            overTimeTotalPrice: null
          };
        } else {
          this.NopaymentPrice = data.contractPaymentVos;
          this.paymentPriceTop.record = {
            alreadyTotalPrice: data.alreadyTotalPrice,
            notTotalPrice: data.notTotalPrice,
            totalPrice: data.totalPrice,
            overTimeTotalPrice: data.overTimeTotalPrice
          };
        }
      });
    },
    getDetailData() {
      API.getContractDetails(this.contractId).then(res => {
        let data = res.data;
        this.message = data;
        this.channel = data.channel || {};
        this.customer = data.customer || {};
        this.packageType = data.packageType || {};
        this.user = data.user;
        this.depart = data.depart || {};
        this.customerId = data.customerId;
        this.contractDetail = {
          customerId: data.customer.companyName,
          customerIdNum: data.customerId,
          totalPrice: data.totalPrice,
          payment: data.payment,
          signDate: data.signDate,
          contractId: Number(this.contractId),
          id: this.customerId,
          residue: data.totalPrice - this.paymentTotal
        };
        this.addRecordData = {
          customerId: data.customerId,
          customerName: data.customer.companyName,
          contractId: Number(this.contractId)
        };
      });
    }
  },
  mounted() {
    this.contractId = this.$route.query.id;
    this.paymentTotal = this.$route.query.paymentTotal;
    // 基本信息
    this.getDetailData();
    // 合同状态
    API.getContractStatus().then(res => {
      // console.log(res);
      let statusList = res.data;
      API.getfindContractStatusById(this.contractId).then(res => {
        statusList.forEach(item => {
          if (res.data == item.code) {
            this.commandName = item.val;
          }
        });
        this.contractStatusList = statusList;
      });
    });
  }
};
</script>
<style lang='scss' scoped>
.el-dropdown-link {
  cursor: pointer;
}
.returned-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-right: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.text {
  font-size: 14px;
  line-height: 40px;
  display: flex;
  & > :first-child {
    width: 110px;
  }
  & > p:last-child {
    flex: 1;
    word-break: break-all;
    word-wrap: break-word;
  }
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
