<template>
  <div>
    <el-dialog
      :close-on-click-modal="false"
      title="新增成员"
      @close="closeDialog"
      :visible.sync="dialogFormVisible"
    >
      <el-form :model="form" :rules="rules" ref="form" :label-width="formLabelWidth">
        <el-form-item label="成员名称" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="成员手机号" prop="telnum">
          <el-input v-model="form.telnum"></el-input>
        </el-form-item>
        <el-form-item label="系统登录密码" prop="password">
          <el-input v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="所属部门" prop="departId">
          <el-select v-model="form.departId" placeholder="请选择所属部门">
            <el-option
              v-for="(item,index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="职务" prop="postId">
          <el-select v-model="form.postId" placeholder="请选择职务">
            <el-option
              v-for="(item, index) in postitionList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="角色" prop="roleId">
          <el-select v-model="form.roleId" placeholder="请选择角色">
            <el-option
              v-for="(item,index) in roleList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注">
          <el-input v-model="form.remarks" style="width: 60%" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="success" @click="sureDialog">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/admin";

export default {
  name: "AddAdministrator",
  //    ------是否开启dialog----------部门列表-------角色列表--------职位列表
  props: ["isAddAdministrator", "departmentsList", "roleList", "postitionList"],
  data() {
    return {
      formLabelWidth: "120px",
      dialogFormVisible: this.isAddAdministrator,
      form: {
        name: null,
        telnum: null,
        password: null,
        departId: null,
        postId: null,
        roleId: null,
        remarks: null
      },
      rules: {
        name: [{ required: true, message: "请输入成员名称", trigger: "blur" }],
        telnum: [
          { required: true, message: "请输入成员手机号", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入系统登陆密码", trigger: "blur" }
        ],
        departId: [
          { required: true, message: "请选择所属部门", trigger: "change" }
        ],
        postId: [{ required: true, message: "请选择职务", trigger: "change" }],
        roleId: [{ required: true, message: "请选择角色", trigger: "change" }]
      }
    };
  },
  methods: {
    // 提交
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let params = this.form;
        console.log(params);
        API.addUser(params)
          .then(res => {
            this.$message({
              type: "success",
              message: "添加成功"
            });
            this.closeDialog();
            this.$emit("sureDialogFather");
            console.log(res);
          })
          .catch(err => {
            console.error(err);
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    },
    // 关闭
    closeDialog() {
      this.$refs.form.resetFields();
      this.dialogFormVisible = false;
      this.$emit("closeDialogFather");
    }
  },
  watch: {
    isAddAdministrator(value) {
      this.dialogFormVisible = this.isAddAdministrator;
      if (!value) return;
    }
  }
};
</script>


<style lang="scss" scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>