<template>
  <div>
    <el-dialog
      title="处理详情"
      class="clear-float"
      :visible.sync="dialogFormVisible"
      :close-on-click-modal="false"
      @close="closeDialog"
    >
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="createUserName" label="处理人" width align="center"></el-table-column>
        <el-table-column prop="contents" label="处理结果" width align="center"></el-table-column>
        <el-table-column prop="statusName" label="处理状态" align="center"></el-table-column>
        <el-table-column prop="createTime" label="处理时间" align="center"></el-table-column>
      </el-table>
      <el-pagination
        class="itemPage"
        layout="total, prev, pager, next"
        @current-change="currentChangePage"
        :current-page="pageObj.pageNum"
        :total="pageObj.total"
        :page-size="pageObj.pageSize"
      ></el-pagination>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/after";

export default {
  name: "ExmaineResult",
  props: ["isExamineResult", "complainId"],
  data() {
    return {
      dialogFormVisible: false,
      tableData: [],
      pageObj: {
        pageSize: 10,
        pageNum: 1,
        total: null
      }
    };
  },
  methods: {
    currentChangePage() {},

    closeDialog() {
      this.dialogFormVisible = false;
      this.$emit("closeDialogFather");
    },
    getTableData() {
      let pageObj = this.pageObj;
      let params = {
        pageSize: pageObj.pageSize,
        pageNum: pageObj.pageNum,
        adviseId: this.complainId
      };
      API.getAdviseFollow(params).then(res => {
				let data =res.data
				this.pageObj.total = data.total;
				this.tableData = data.list
      });
    }
  },
  watch: {
    isExamineResult(val) {
      this.dialogFormVisible = val;
			if (!val) return;
			this.getTableData()
    }
  }
};
</script>

<style lang="scss" scoped>
.itemPage {
  margin-top: 20px;
  margin-right: 10px;
  float: right;
}
/deep/ .el-dialog__body:after {
  clear: both;
  display: block;
  content: "";
}
</style>