<template>
  <div>
    <el-button type="warning" v-permi="'/annex/addAnnex'" @click="isShowUpload=true">上传附件</el-button>
    <el-table
      :data="annexTableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      :fit="true"
    >
      <el-table-column prop="createTime" label="上传时间" align="center"></el-table-column>
      <el-table-column prop="name" label="附件名称" align="center"></el-table-column>
      <el-table-column prop="size" label="大小" align="center"></el-table-column>
      <el-table-column prop="remarks" label="备注" align="center"></el-table-column>
      <el-table-column label="操作" width="220" align="center" fixed="right">
        <template slot-scope="scope">
          <!-- <el-button size="mini" @click="editAnnex(scope.$index, scope.row)">编辑</el-button> -->
          <el-button size="mini" @click="handleUploadDownload(scope.$index, scope.row)">下载</el-button>
          <el-button size="mini" @click="deleteAnnex(scope.$index, scope.row)" type="danger">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      style="text-align:right;margin-top: 20px"
      layout="prev, pager, next"
      @current-change="currentChange"
      :page-size="pageObj.pageSize"
      :total="pageObj.total"
    ></el-pagination>

    <!-- 上传附件 -->
    <upload-annex
      :isShowUpload="isShowUpload"
      :linkId="customerId"
      @closeDialogFather="isShowUpload=false"
      @sureDialogFather="getAnnexTable()"
    ></upload-annex>

    <!-- 编辑附件 -->
    <el-dialog
      title="编辑"
      :close-on-click-modal="false"
      width="40%"
      class="editAnnexDialog"
      :visible.sync="editAnnexDialog"
      :modal-append-to-body="false"
    >
      <el-form :model="editAnnexForm" ref="editAnnexForm" label-width="110px">
        <el-form-item
          label="附件名称"
          style="width:50%"
          prop="name"
          :rules="{ required: true, message: '请输入附件名称', trigger: 'blur' }"
        >
          <el-input v-model="editAnnexForm.name"></el-input>
        </el-form-item>
        <el-form-item label="备注" style="width:60%">
          <el-input type="textarea" v-model="editAnnexForm.remarks"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitEditAnnex">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>


<script>
import API from "@/api/clue";
import UploadAnnex from "./UploadAnnex";
export default {
  props: ["customerId"],
  components: { UploadAnnex },
  data() {
    return {
      isShowUpload: false,
      editAnnexDialog: false,
      editAnnexForm: {
        name: "",
        remarks: ""
      },
      annexTableData: [],
      pageObj: {
        pageSize: 10,
        pageNum: 1,
        total: null
      }
    };
  },
  methods: {
    // 编辑附件
    editAnnex(index, row) {
      this.editAnnexDialog = true;
      this.editAnnexForm = row;
    },
    currentChange(e) {
      this.pageObj.pageNum = e;
      this.getAnnexTable();
    },
    // 保存修改
    submitEditAnnex() {
      this.$refs.editAnnexForm.validate(valid => {});
    },
    // 上传附件的下载
    handleUploadDownload(index, row) {
      window.open(
        this.$store.state.baseURL +
          "/annex/download?id=" +
          row.id +
          "&name=" +
          row.name
      );
    },
    // 删除附件
    deleteAnnex(index, row) {
      this.$confirm("是否删除？" + row.name, "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      }).then(() => {
        let params = {
          id: row.id
        };
        API.delAnnex(params).then(res => {
          this.$message({
            type: "success",
            message: "删除成功"
          });
          this.findAnnex();
        });
      });
    },
    //  获取数据
    getAnnexTable() {
      let params = {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize,
        linkId: this.customerId,
        linkType: 1
      };
      API.findAnnex(params).then(res => {
        let data = res.data;
        this.pageObj.total = data.total;
        this.annexTableData = data.list;
      });
    }
  }
};
</script>