<template>
  <div>
    <!-- 新增回款计划 -->
    <el-dialog title="新增回款计划" :visible.sync="outerVisible" @close="closeDialog" append-to-body>
      <el-form :model="form" label-width="120px" :rules="rules" ref="form">
        <el-form-item label="所属客户" prop="customerId">
          <el-select
            v-model="form.customerId"
            :disabled="form.showTable||Boolean(contractDetail)"
            @change="customerChange"
            placeholder="请选择"
            class="item-css"
            filterable
          >
            <el-option
              v-for="(item, index) in clientList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="合同标题" prop="contractId">
          <el-select
            v-model="form.contractId"
            :no-data-text="form.customerId?'此用户暂无合同':'请选择用户'"
            placeholder="请选择"
            @change="contractChange"
            :disabled="form.showTable||Boolean(contractDetail)"
          >
            <el-option
              v-for="(item, index) in contractList"
              :key="index"
              :disabled="Boolean(item.isPlan)||item.payment===item.totalPrice"
              :title="(item.isPlan||item.payment===item.totalPrice)&&'此合同已有计划或已完成'"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="合同总金额">
          <el-input v-model="form.totalPrice" disabled placeholder="请输入内容" style="width:50%"></el-input>
        </el-form-item>
        <el-form-item label="已付金额">
          <el-input v-model="form.payment" disabled placeholder="请输入内容" style="width:50%"></el-input>
        </el-form-item>
        <el-form-item label="剩余未付总额">
          <el-input v-model="form.residue" disabled placeholder="请输入内容" style="width:50%"></el-input>
        </el-form-item>
        <el-form-item label="签约日期">
          <el-date-picker
            disabled
            v-model="form.signDate"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="收款负责部门" prop="departId">
          <el-select
            v-model="form.departId"
            :disabled="form.showTable"
            placeholder="请选择"
            clearable
            @change="departChange()"
          >
            <el-option
              v-for="(item, index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="收款负责人" prop="userId">
          <el-select
            :disabled="form.showTable"
            v-model="form.userId"
            @change="userChange"
            filterable
            placeholder="请选择"
          >
            <el-option
              v-for="(item,index) in headList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="备注">
          <el-input
            style="width:60%"
            v-model="form.remarks"
            placeholder="请输入内容"
            type="textarea"
            show-word-limit
            maxlength="100"
            :autosize="{ minRows: 4, maxRows: 10}"
          ></el-input>
        </el-form-item>-->
        <!-- <el-form-item label="总回款期数">
          <el-input-number v-model="form.periodsNum" :min="1" @change="handleChange"></el-input-number>
        </el-form-item>-->
        <el-form-item v-if="!form.showTable&&form.residue>0">
          <el-button type="primary" @click="nextStep">下一步</el-button>
        </el-form-item>
        <el-divider v-show="form.showTable"></el-divider>
        <el-table
          v-if="form.showTable"
          :data="tableData"
          style="width: 100%"
          highlight-current-row
          border
          stripe
        >
          <el-table-column type="index" label="期次" width="65" align="center"></el-table-column>

          <el-table-column prop="date" label="计划回款日期" align="center">
            <template slot-scope="scope">
              <el-date-picker
                :disabled="!scope.row.edit"
                :picker-options="scope.row.startDatePicker"
                v-model="scope.row.opeDate"
                value-format="yyyy-MM-dd HH:mm:ss"
                type="date"
                placeholder="选择日期"
              ></el-date-picker>
            </template>
          </el-table-column>

          <!-- <el-table-column prop="name" label="计划回款占比" align="center">
            <template slot-scope="scope">
              <el-input v-model="scope.row.name" placeholder="请输入"></el-input>
            </template>
          </el-table-column>-->
          <el-table-column prop="price" width="130" label="计划回款金额" align="center">
            <template slot-scope="scope">
              <el-input
                class
                v-model="scope.row.price"
                :disabled="!scope.row.edit"
                placeholder="请输入"
              ></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="residue" width="140" label="剩余未回款金额" align="center">
            <template
              slot-scope="scope"
            >{{scope.$index == tableData.length - 1?balance(scope.$index, scope.row): scope.row.residue}}</template>
          </el-table-column>
          <el-table-column label="操作" width="100" align="center">
            <template slot-scope="scope">
              <div v-if="!scope.row.edit">
                <el-button
                  type="text"
                  icon="el-icon-delete"
                  @click="handleDelete(scope.$index, scope.row)"
                ></el-button>
              </div>
              <el-button v-else @click="finish(scope.$index,scope.row)" type="success">完成</el-button>
            </template>
          </el-table-column>
          <!-- <el-table-column prop="note" label="备注" width="150px" align="center">
            <template slot-scope="scope">
              <el-input v-model="scope.row.note" placeholder="请输入"></el-input>
            </template>
          </el-table-column>-->
        </el-table>
        <el-form-item v-if="tableData.length">
          <el-button
            :disabled="tableData[tableData.length-1].residue!==0"
            style="margin: 20px 20px 0 0"
            type="success"
            @click="sureDialog"
          >提交</el-button>
          <el-button type="info" @click="closeDialog">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/payment";
// 新增回款计划
export default {
  name: "NewReceivableplan",
  props: {
    isNewReceivableplan: {
      type: Boolean,
      default: false
    },
    contractDetail: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      rules: {
        customerId: [
          { required: true, message: "请选择客户", trigger: "change" }
        ],
        contractId: [
          { required: true, message: "请选择合同", trigger: "change" }
        ],
        departId: [
          { required: true, message: "请选择负责收款部门", trigger: "change" }
        ],
        userId: [{ required: true, message: "请选择负责人", trigger: "change" }]
      },
      outerVisible: this.isNewReceivableplan,
      form: {
        value: "",
        contractId: "",
        totalPrice: "",
        signDate: "",
        // periodsNum: 1,
        customerId: "",
        departId: "",
        showTable: false, //  是否展示可输入表格
        userId: ""
      },
      tableData: [],
      clientList: [], // 客户列表
      contractList: [], // 查询到的合同列表
      departmentsList: [], // 部门列表
      headList: [] // 负责人列表
    };
  },
  methods: {
    // 所属负责人
    userChange(val) {
      let departId = this.headList.find(item => {
        return item.id === val;
      }).departId;
      this.form.departId = departId;
      this.departChange("no_change");
    },
    departChange(flag) {
      if (flag !== "no_change") this.form.userId = null;
      let val = this.form.departId;
      if (val) {
        API.getHead(val).then(res => {
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          this.headList = res.data;
        });
      }
    },

    // 新增计划的完成按钮
    finish(index, row) {
      let { price, opeDate } = row;
      // 下一栏的剩余金额
      let residue = row.residue - row.price;
      // 判断是都输入正确
      if (!price || !opeDate || residue < 0) {
        this.$message.info("请正确填写计划回款日期和计划回款金额！");
        return;
      }
      // 当前栏显示的未还款余额
      this.tableData[index].residue = residue;
      // 如果有未还款那么增加新的一栏
      if (row.residue > 0) {
        this.addPeriods();
      }
      this.tableData[index].edit = false;
    },

    sureDialog() {
      let form = this.form;
      let customerId = this.contractDetail
        ? this.contractDetail.customerIdNum
        : form.customerId;
      let arr = [];
      this.tableData.map(ele => {
        arr.push({
          price: ele.price,
          opeDate: ele.opeDate
        });
      });
      let params = {
        contractId: form.contractId,
        departId: form.departId,
        customerId,
        userId: form.userId,
        planPayments: JSON.stringify(arr)
      };
      console.log(params);
      API.addContractPaymentPlan(params)
        .then(res => {
          console.log(res);
          this.$message({
            type: "success",
            message: "新增成功"
          });
          this.$emit("sureDialogFather");
          this.closeDialog();
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 添加期次
    addPeriods() {
      let residue, startDatePicker;
      let length = this.tableData.length;
      // 如果不是一期那么未还款金额为上一期剩下的
      if (length) {
        residue = this.tableData[length - 1].residue;
        let that = this;
        startDatePicker = {
          // 跟进事件只能选择今天之后的日期
          disabledDate(time) {
            if (that.tableData[length - 1]) {
              return (
                time.getTime() <
                new Date(that.tableData[length - 1].opeDate).getTime()
              ); //如果没有后面的-8.64e6就是不可以选择今天的
            }
          }
        };
      } else {
        // 否则是全部未还款金额
        residue = this.form.residue;
        startDatePicker = {
          // 跟进事件只能选择今天之后的日期
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7; //如果没有后面的-8.64e6就是不可以选择今天的
          }
        };
      }

      this.tableData.push({
        edit: true,
        price: residue, // 计划回款金额
        opeDate: "", // 计划回款日期
        residue, // 剩余未回款
        startDatePicker
      });
    },
    // 下一步
    nextStep() {
      this.$refs.form.validate(vali => {
        if (!vali) return;
        this.addPeriods();
        this.form.showTable = true;
      });
    },
    // 清空所属客户
    clearCustormer() {
      this.form.contractId = "";
      this.contractList = [];
    },
    // 选择标题
    contractChange(e) {
      let item = this.contractList.find(ele => ele.id === e);

      let form = this.form;
      form.totalPrice = item.totalPrice;
      form.signDate = item.signDate;
      form.payment = item.payment;
      form.residue = item.totalPrice - item.payment;
    },

    // 删除
    handleDelete(index, row) {
      this.$confirm("此期和之后的期数会全部删除", "删除计划", {
        distinguishCancelAndClose: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      })
        .then(() => {
          this.tableData.splice(index);
          this.addPeriods();
        })
        .catch(() => {});
    },
    // 清除负责人
    clearFollowUser() {
      if (this.form.departId) {
        this.departChange();
      }
    },
    customerChange(e) {
      this.form.contractId = null;

      let form = this.form;
      form.contractId = null;
      form.totalPrice = null;
      form.signDate = null;
      form.payment = null;
      form.residue = null;
      // 根据客户id查询所有合同
      API.getContractByCustomerId(e)
        .then(res => {
          this.$nextTick(() => {
            this.contractList = res.data;
          });
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    },

    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$refs.form.resetFields();
      this.form = {
        value: "",
        contractId: "",
        totalPrice: "",
        signDate: "",
        customerId: "",
        departId: "",
        showTable: false, //  是否展示可输入表格
        userId: ""
      };
      this.tableData = [];
      this.$emit("closeDialogFather", this.outerVisible);
    }
  },
  mounted() {},
  watch: {
    isNewReceivableplan(val) {
      this.outerVisible = this.isNewReceivableplan;
      if (!val) return;
      // this.tableData = [];
      if (this.contractDetail) {
        console.log(this.contractDetail);
        API.getContractByCustomerId(this.contractDetail.id)
          .then(res => {
            this.contractList = res.data;
            this.form = Object.assign(this.form, this.contractDetail);
          })
          .catch(err => {
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      } else {
        // 所属客户
        API.getCustomerByCompanyName().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.clientList = res.data;
        });
      }
      let userInfo = this.$store.state.userInfo;
      this.form.userId = userInfo.id;
      this.form.departId = userInfo.departId;
      API.getHead(userInfo.departId).then(res => {
        this.headList = res.data;
      });
      // 所有的负责人
      // API.getAllUser().then(res => {
      //   this.headList = res.data;
      // });
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
    }
  },
  computed: {
    balance() {
      // 显示未还余额
      return (index, row) => {
        let residue;
        let length = this.tableData.length;
        if (length > 1) {
          residue = this.tableData[length - 2].residue;
        } else {
          residue = this.form.residue;
        }

        let result = residue - this.tableData[length - 1].price;
        if (result < 0) {
          return "回款金额过大";
        } else {
          return result;
        }
      };
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  // .el-input:not(.el-input--suffix) {
  //   width: 50%;
  // }
}
</style>