<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-col :span="4">
          <h1>{{message.name}}</h1>
        </el-col>
        <el-col :span="8">
          <h3>客户：{{message.customerName}}</h3>
        </el-col>
      </el-col>
    </el-row>
    <el-row class="el-dro">
      <el-button
        type="success"
        @click="isShowLinkmanEdit=true"
        v-permi="'/linkman/updateLinkman'"
      >编辑</el-button>
      <el-button type="danger" @click="deleteLinkman">删除</el-button>
      <el-button type="warning" @click="showSend">发送短信</el-button>
    </el-row>

    <el-tabs type="card" @tab-click="handleClick">
      <el-tab-pane label="基本信息" class="quanjing">
        <el-card class="box-card">
          <div class="text item">
            <div>基本信息</div>
            <div>
              <p>姓名：</p>
              <p>{{message.name}}</p>
            </div>
            <div>
              <p>对应客户：</p>
              <p>{{message.customerName}}</p>
            </div>
            <div>
              <p>部门：</p>
              <p>{{message.linkmanDepart}}</p>
            </div>
            <div>
              <p>职务：</p>
              <p>{{message.job}}</p>
            </div>
            <el-divider></el-divider>
            <div>联系信息</div>
            <div>
              <p>电话：</p>
              <p>{{message.telephone}}</p>
            </div>
            <div>
              <p>手机：</p>
              <p>{{message.mobile}}</p>
            </div>
            <div>
              <p>微信号：</p>
              <p>{{message.weChat}}</p>
            </div>
            <div>
              <p>QQ：</p>
              <p>{{message.qq}}</p>
            </div>
            <div>
              <p>邮箱：</p>
              <p>{{message.email}}</p>
            </div>
            <div>
              <p>地区：</p>
              <p>{{message.province}}{{message.city}}</p>
            </div>
            <div>
              <p>地址：</p>
              <p>{{message.address}}</p>
            </div>
            <el-divider></el-divider>
            <div>其他信息</div>
            <div>
              <p>性别：</p>
              <p>{{message.sexStr}}</p>
            </div>
            <div>
              <p>生日：</p>
              <p>{{message.birthday}}</p>
            </div>
            <div>
              <p>备注：</p>
              <p>{{message.remarks}}</p>
            </div>
            <el-divider></el-divider>
            <div>系统信息</div>
            <div>
              <p>创建人：</p>
              <p>{{message.name}}</p>
            </div>
            <div>
              <p>创建时间：</p>
              <p>{{message.createTime}}</p>
            </div>
            <div>
              <p>更新于：</p>
              <p>{{message.updateTime}}</p>
            </div>
          </div>
        </el-card>
        <follow-up-history
          class="genjin box-card"
          @writeFollow="isShowFollow= true"
          :customerId="customerId"
          :type="1"
          ref="followUpHistory"
        ></follow-up-history>
      </el-tab-pane>

      <el-tab-pane label="任务">
        <tab-task ref="tabTask" :customerId="customerId"></tab-task>
      </el-tab-pane>

      <el-tab-pane label="附件">
        <tab-annex ref="tabAnnex" :customerId="customerId"></tab-annex>
      </el-tab-pane>
      <!-- <el-tab-pane label="流转履历">
        <el-table :data="tableData" tooltip-effect="dark" style="width: 100%;" border :fit="true">
          <el-table-column prop="name" label="操作时间" width align="center"></el-table-column>
          <el-table-column prop="name" label="操作人员" width align="center"></el-table-column>
          <el-table-column prop="name" label="操作类型" width align="center"></el-table-column>
          <el-table-column prop="name" label="备注" width align="center"></el-table-column>
        </el-table>
      </!-->
    </el-tabs>
    <edit-contacts
      :isShowLinkmanEdit="isShowLinkmanEdit"
      :linkmanId="linkmanId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-contacts>
    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>
    <Followup
      :isShowFollow="isShowFollow"
      :customerId="customerId"
      @sureDialogFather="$refs.followUpHistory.getFollowData();"
      @closeDialogFather="getSonCancel"
    ></Followup>
  </div>
</template>	

<script>
import API from "@/api/linkman";
import TabAnnex from "@/components/element/TabAnnex";
import TabTask from "@/components/element/TabTask";
import EditContacts from "@/components/element/EditContacts"; // 新增联系人
import NewSend from "@/components/element/NewSend"; // 发送短信
import Followup from "@/components/element/Followup"; // 写更进
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 跟进历史

export default {
  name: "linkmandetails",
  components: {
    EditContacts,
    TabTask,
    NewSend,
    TabAnnex,
    Followup,
    FollowUpHistory
  },
  data() {
    return {
      isShowLinkmanEdit: false,
      isShowSend: false,
      isShowFollow: false,
      linkmanId: "", // 当前详情的id
      customerId: null, // 客户id
      message: {}, // 基本信息
      tableData: []
    };
  },
  methods: {
    // 删除当前联系人
    deleteLinkman() {
      this.$confirm("是否删除此联系人?", "确认删除", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        API.postdeleteLinkman({ ids: this.linkmanId }).then(res => {
          this.$router.go(-1);
          this.$message({
            message: "删除成功",
            type: "success"
          });
        });
      });
    },
    handleUp() {},
    // 发送短信
    showSend() {
      this.isShowSend = true;
    },
    // 写跟进
    showFollow() {
      this.isShowFollow = true;
    },
    // 保存Dialog
    getSonSure() {
      this.getSonCancel();
      this.getDetailData();
    },
    // 关闭Dialog 弹框点击关闭的值
    getSonCancel() {
      this.isShowLinkmanEdit = false;
      this.isShowSend = false;
      this.isShowFollow = false;
    },
    // tab 被选中时触发
    handleClick(tab, event) {
      if (tab.label == "基本信息") {
        this.getDetailData();
      } else if (tab.label == "任务") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "附件") {
        this.$refs.tabAnnex.getAnnexTable();
      } else if (tab.label == "流转履历") {
        // console.log('流转履历')
      }
    },
    getDetailData() {
      API.getLinkmanDetails(this.linkmanId).then(res => {
        this.message = res.data;
        this.customerId = res.data.customerId;
      });
    }
  },
  mounted() {
    this.linkmanId = this.$route.query.id;
    this.getDetailData();
  }
};
</script>
<style lang='scss' scoped>
.el-dropdown-link {
  cursor: pointer;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-top: 20px;
  margin-bottom: 20px;
}

.text {
  font-size: 14px;
  div {
    margin-bottom: 18px;
    display: flex;
    & > :first-child {
      width: 100px;
      & > :last-child {
        flex: 1;
        word-break: break-all;
        word-wrap: break-word;
      }
    }
  }
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  width: 48%;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;
}
.quanjing {
  position: relative;
}
span {
  font-size: 18px;
}
</style>