<template>
  <div>
    <el-dialog title="转移至1客户" @close="closeDialog" :visible.sync="isShowDialog" width="40%">
      <el-form>
        <el-form-item label="所属部门" prop="departId">
          <el-select
            v-model="form.departId"
            placeholder="请选择"
            clearable
            @change="departChange($event)"
          >
            <el-option
              v-for="(item, index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="新负责人" prop="userId">
          <el-select v-model="form.userId" @change="userChange" filterable placeholder="请选择">
            <el-option
              v-for="(item,index) in headList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submit" type="success">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/highseas";

export default {
  props: ["isShowCustomer", "checkboxId"],
  data() {
    return {
      isShowDialog: false,
      id: "",
      headList: [],
      departmentsList: [],
      form: {
        departId: "",
        userId: ""
      }
    };
  },
  methods: {
    // 所属负责人
    userChange(val) {
      let departId = this.headList.find(item => {
        return item.id === val;
      }).departId;
      this.form.departId = departId;
      this.departChange("no_change");
    },
    // 所属部门
    departChange(flag) {
      if (flag !== "no_change") this.form.userId = null;
      let val = this.form.departId;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      }
    },
    submit() {
      let form = this.form;
      let params = {
        ids: this.checkboxId.join(","),
        userId:form.userId,
        departId: form.departId
      };
      API.posttoCustomer(params).then(res => {
        if (res.status == 200) {
          this.$message({
            message: "转移成功",
            type: "success"
          });
          this.closeDialog();
          this.$emit("sureDialogFather");
        }
      });
    },
    closeDialog() {
      this.isShowDialog = false;
      this.$emit("closeDialogFather");
    }
  },
  watch: {
    isShowCustomer(val) {
      this.isShowDialog = val;
      if (!val) return;
      API.getAllUser().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.headList = res.data;
      });

      // 所属部门
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>
