<template>
  <div>
    <el-dialog
      title="新增合同"
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      @close="closeDialog"
    >
      <el-form :model="form" label-width="120px" ref="form" :rules="rules">
        <el-form-item label="合同名称" prop="name">
          <el-input v-model="form.name" placeholder="请输入"></el-input>
        </el-form-item>
        <!-- {{form.customerId}} -->
        <el-form-item label="所属客户" prop="customerId">
          <el-select
            v-model="form.customerId"
            placeholder="请选择"
            :disabled="Boolean(customerDetail)"
            class="item-css"
            filterable
          >
            <el-option
              v-for="(item, index) in restaurants"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="合同总金额" prop="totalPrice">
          <el-input
            @blur="$message.warning('合同总金额一旦设定，不可更改')"
            v-model="form.totalPrice"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
        <el-form-item label="签约日期" prop="signDate">
          <el-date-picker
            v-model="form.signDate"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="合同编号" prop="signNo">
          <el-input v-model="form.signNo" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="业务类型" prop="serviceId">
          <el-select v-model="form.serviceId" placeholder="请选择" clearable>
            <el-option
              v-for="(item, i) in businessTypeList"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属套餐" prop="setMeal">
          <el-select v-model="form.setMeal" placeholder="请选择" clearable>
            <el-option
              v-for="(item, i) in PackageList"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="订单来源" prop="from">
          <el-select v-model="form.from" placeholder="请选择" clearable>
            <el-option
              v-for="(item, i) in clientSourceList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="提单日期" prop="ladingBillTime">
          <el-date-picker
            v-model="form.ladingBillTime"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="结束日期" prop="endTime">
          <el-date-picker
            v-model="form.endTime"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="合同状态" prop="status">
          <el-select v-model="form.status" placeholder="请选择" clearable>
            <el-option
              v-for="(item, i) in contractstatusList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="支付账号" prop="payNo">
          <el-select v-model="form.payNo" placeholder="请选择" clearable>
            <el-option
              v-for="(item, i) in payaccountList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="合同附件">
          <el-upload
            ref="upload"
            :action="$store.state.baseURL+'annex/addAnnex'"
            :file-list="fileList"
            :data="uploadData"
            :auto-upload="false"
            :on-success="uploadSuccess"
            :on-error="uploadError"
          >
            <el-button class="item" type="success" icon="el-icon-plus">选择文件</el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="下次跟进时间">
          <el-date-picker
            v-model="form.nextFollowTime"
            type="datetime"
            :picker-options="startDatePicker"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
          ></el-date-picker>
        </el-form-item>
        <!-- <el-form-item label="回款计划">
          <el-button type="primary" icon="el-icon-plus">添加回款计划</el-button>
        </el-form-item>-->
        <el-form-item label="进账备注">
          <el-input
            style="width:60%"
            type="textarea"
            show-word-limit
            maxlength="100"
            v-model="form.remarks"
            rows="4"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
        <el-form-item label="意向度">
          <el-select v-model="form.intention" placeholder="请选择" clearable>
            <el-option
              v-for="(item, i) in intentionList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="发票编号" prop="receiptNo">
          <el-input v-model="form.receiptNo" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="发票开具" prop="isInvoice">
          <el-select v-model="form.isInvoice" placeholder="请选择" clearable>
            <el-option label="是" value="1"></el-option>
            <el-option label="否" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="进账类型" prop="incomeType">
          <el-select v-model="form.incomeType" placeholder="请选择" clearable>
            <el-option
              v-for="(item, i) in sessionTypeList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="支付金额" prop="payment">
          <el-input v-model="form.payment" @blur="$message.warning('支付金额一旦设定，不可更改')"></el-input>
        </el-form-item>

        <el-form-item label="收据号" prop="receiptNumber">
          <el-input v-model="form.receiptNumber" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="所属部门" prop="departId">
          <el-select v-model="form.departId" placeholder="请选择" clearable @change="departChange()">
            <el-option
              v-for="(item, index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="负责人" prop="userId">
          <el-select v-model="form.userId" @change="userChange" filterable placeholder="请选择">
            <el-option
              v-for="(item,index) in headList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success">保存</el-button>
          <el-button @click="closeDialog">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
// 新增合同
import API from "@/api/contract";
export default {
  name: "NewContract",
  props: ["isShowContract", "customerDetail"],
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请填写支付金额"));
      } else if (Number(value) > Number(this.form.totalPrice)) {
        callback(new Error("支付金额不能大于总金额"));
      } else {
        // this.$message.warning("支付金额一旦设定，不可更改");
        callback();
      }
    };
    return {
      startDatePicker: {
        // 跟进事件只能选择今天之后的日期
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //如果没有后面的-8.64e6就是不可以选择今天的
        }
      },
      rules: {
        name: [{ required: true, message: "请输入合同名称", trigger: "blur" }],
        customerId: [
          { required: true, message: "请选择所属客户", trigger: "change" }
        ],
        totalPrice: [
          { required: true, message: "请输入合同总金额", trigger: "blur" }
        ],
        signDate: [
          { required: true, message: "请选择签约日期", trigger: "change" }
        ],
        signNo: [
          { required: true, message: "请填写合同编号", trigger: "blur" }
        ],
        serviceId: [
          { required: true, message: "请选择业务类型", trigger: "change" }
        ],
        setMeal: [
          { required: true, message: "请选择所属套餐", trigger: "change" }
        ],

        telephone: [
          { required: true, message: "请填写客户电话", trigger: "blur" }
        ],

        status: [
          { required: true, message: "请选择合同状态", trigger: "change" }
        ],
        // ladingBillTime: [
        //   { required: true, message: "请选择提单日期", trigger: "change" }
        // ],
        // endTime: [
        //   { required: true, message: "请选择结束日期", trigger: "change" }
        // ],
        payNo: [
          { required: true, message: "请选择支付账号", trigger: "change" }
        ],

        from: [
          { required: true, message: "请选择订单来源", trigger: "change" }
        ],
        isInvoice: [
          { required: true, message: "请选择是否开具发票", trigger: "change" }
        ],
        incomeType: [
          { required: true, message: "请选择进账类型", trigger: "change" }
        ],
        receiptNumber: [
          { required: true, message: "请填写收据号", trigger: "blur" }
        ],
        departId: [
          { required: true, message: "请选择所属部门", trigger: "change" }
        ],
        payment: [{ required: true, validator: validatePass, trigger: "blur" }],
        userId: [{ required: true, message: "请选择负责人", trigger: "change" }]
      },
      dialogVisible: this.isShowContract,
      restaurants: [], // 所属客户
      businessTypeList: [], // 业务类型
      PackageList: [], // 所属套餐
      clientSourceList: [], // 订单来源
      contractstatusList: [], // 合同状态
      payaccountList: [], // 支付账号
      intentionList: [], // 意向度
      sessionTypeList: [], // 进账类型
      departmentsList: [], // 所属部门
      headList: [], // 所属负责人
      fileList: [], //上传的文件列表
      uploadData: {},
      form: {
        name: "", // 合同名称
        customerId: "", // 所属客户
        totalPrice: "", // 合同总金额
        signDate: "", // 签约日期
        signNo: "", // 合同编号
        serviceId: "", // 业务类型
        setMeal: "", // 所属套餐
        from: "", // 客户来源
        ladingBillTime: "", // 提单日期
        endTime: "", // 结束日期
        status: "", // 合同状态
        payNo: "", // 支付账号
        nextFollowTime: "", // 下次跟进时间
        remarks: "", // 进账备注
        intention: "", // 意向度
        receiptNo: "", // 发票编号
        isInvoice: "", // 发票开具
        incomeType: "", // 进账类型
        payment: "", // 支付金额
        companyName: "", // 公司名称
        receiptNumber: "", // 收据号
        departId: "", // 所属部门
        userId: "" // 负责人
      }
    };
  },
  methods: {
    uploadSuccess(res) {
      if (res.status != 200) {
        this.$notify({
          title: "合同创建成功，附件上传失败",
          message: "失败原因： " + res.msg + "， 请在合同详情页重新上传。",
          type: "warning"
        });
      }
      this.fileList = [];
    },
    uploadError() {
      this.$message({
        type: "error",
        message: "上传失败"
      });
    },
    // 所属部门
    departChange(flag) {
      if (flag !== "no_change") this.form.userId = null;
      let val = this.form.departId;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      }
    },
    // 所属负责人id
    userChange(val) {
      let departId = this.headList.find(item => {
        return item.id === val;
      }).departId;
      this.form.departId = departId;
      this.departChange("no_change");
    },

    // 关闭
    closeDialog() {
      this.dialogVisible = false;

      this.$emit("closeDialogFather");
    },
    // 保存
    sureDialog() {
      this.$refs.form.validate(vali => {
        if (!vali) return;
        let form = this.form;
        this.uploadData = {
          linkId: form.customerId,
          linkType: 1
        };
        let params = {
          name: form.name,
          customerId:form.customerId,
          totalPrice: form.totalPrice,
          signDate: form.signDate,
          signNo: form.signNo,
          serviceId: form.serviceId,
          setMeal: form.setMeal,
          from: form.from,
          telephone: form.telephone,
          ladingBillTime: form.ladingBillTime,
          endTime: form.endTime,
          status: form.status,
          payNo: form.payNo,
          nextFollowTime: form.nextFollowTime,
          remarks: form.remarks,
          intention: form.intention,
          receiptNo: form.receiptNo,
          isInvoice: form.isInvoice,
          incomeType: form.incomeType,
          payment: form.payment,
          // companyName: form.companyName,
          receiptNumber: form.receiptNumber,
          departId: form.departId,
          userId: form.userId
        };
        API.postaddContract(params).then(res => {
          this.$message({
            message: "保存成功",
            type: "success"
          });
          this.$refs.upload.submit();
          this.$refs.form.resetFields();
          this.closeDialog();
          this.$emit("sureDialogFather");
        });
      });
    }
  },
  watch: {
    isShowContract(value) {
      this.dialogVisible = this.isShowContract;
      if (!value) return;
      // this.$nextTick(() => {
      //   this.$refs.form.clearValidate();
      // });

      console.log(this.customerDetail);
      if (this.customerDetail) {
        let customerDetail = this.customerDetail;
        this.form = Object.assign(this.form, {
          isShowContract: customerDetail.serviceId,
          companyName: customerDetail.companyName,
          customerId: this.$route.query.id
        });
      }
      let userInfo = this.$store.state.userInfo;

      this.form.departId = userInfo.departId;
      this.form.userId = userInfo.id;
      this.departChange("no_change");
      // 所属客户
      API.getCustomerByCompanyName().then(res => {
        this.restaurants = res.data;
      });
      // 业务类型
      API.getType().then(res => {
        this.businessTypeList = res.data;
      });
      // 所属套餐
      API.getPackageTypes().then(res => {
        this.PackageList = res.data;
      });
      //  订单来源实际就是客户来源的数据
      API.customerFrom().then(res => {
        this.clientSourceList = res.data;
      });
      // 合同状态
      API.getContractStatus().then(res => {
        this.contractstatusList = res.data;
      });
      // 支付账号
      API.getContractPayNo().then(res => {
        this.payaccountList = res.data;
      });
      // 意向度
      API.getContractIntention().then(res => {
        this.intentionList = res.data;
      });
      // 进账类型
      API.getContractIncomeType().then(res => {
        this.sessionTypeList = res.data;
      });
      // 所属部门
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  .inline-input {
    width: 100%;
  }
  .item-css {
    width: 50%;
  }
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>