<template>
  <div>
    <!-- <el-button type="primary" icon="el-icon-plus" @click="outerVisible = true">新增渠道</el-button> -->
    <el-dialog :title="contentObj.title" :visible.sync="outerVisible" @close="closeDialog">
      <el-form :model="form" :rules="rules" ref="form" label-width="100px">
        <el-form-item label="渠道名称" prop="name">
          <el-input placeholder="请输入" v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="渠道类型" prop="type">
          <el-select v-model="form.type" placeholder="请选择" clearable class="item-css">
            <el-option label="线上" value="1"></el-option>
            <el-option label="线下" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否收费" prop="isFree">
          <el-select v-model="form.isFree" placeholder="请选择" clearable class="item-css">
            <el-option label="免费" value="1"></el-option>
            <el-option label="收费" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/ditch";

// 新增渠道
export default {
  name: "NewChannel",
  props: ["isShowChannel", "channelDetail"],
  data() {
    return {
      outerVisible: this.isShowChannel,
      form: {
        name: "",
        type: "",
        isFree: "",
        pid: null
      },
      contentObj: {},
      rules: {
        name: [{ required: true, message: "请输入渠道名称", trigger: "blur" }],
        type: [
          { required: true, message: "请选择渠道类型", trigger: "change" }
        ],
        isFree: [
          { required: true, message: "请选择是否收费", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather", this.outerVisible);
    },
    // 保存
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let params = this.form;
        API[this.contentObj.api](params)
          .then(res => {
            console.log(res);
            this.$message({
              message: "保存成功",
              type: "success"
            });
            this.closeDialog();
            this.$emit("sureDialogFather");
          })
          .catch(err => {
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    }
  },
  mounted() {},
  watch: {
    isShowChannel(value) {
      this.outerVisible = this.isShowChannel;
      if (!value) return;
      if (this.channelDetail) {
        this.form.pid = this.channelDetail.id;

        this.contentObj = {
          title: "新增子渠道",
          api: "addChildrenChannel"
        };
      } else {
        this.contentObj = {
          title: "新增渠道",
          api: "addChannel"
        };
        this.form.pid = null;
      }
      this.$nextTick(() => {
        this.$refs.form.resetFields();
      });
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>