<template>
  <div>
    <el-form :model="form" label-width="100px">
      <el-row>
        <el-row :span="24">
          <el-form-item label="线索类型">
            <ul>
              <li
                v-for="(item, index) in getAllSupport"
                :key="index"
                class="list-li"
                @click="actives(index,item.code)"
                :class="{'pote':ins == index}"
              >{{ item.val }}</li>
            </ul>
          </el-form-item>
        </el-row>
        <el-row :span="24">
          <el-form-item label="线索状态">
            <ul>
              <li
                v-for="(item, index) in getAllstate"
                :key="index"
                class="list-li"
                @click="actives1(index,item.code)"
                :class="{'pote':ins1 == index}"
              >{{ item.val}}</li>
            </ul>
          </el-form-item>
        </el-row>
        <el-row :span="24">
          <el-form-item label="创建时间">
            <ul>
              <li
                v-for="(item, index) in getAlltime"
                :key="index"
                class="list-li"
                @click="actives2(index,item.code)"
                :class="{'pote':ins2 == index}"
              >{{ item.name }}</li>
              <li
                @click="showDefinedDate = true;ins2 = -1"
                class="list-li"
                v-if="!showDefinedDate"
              >自定义时间段</li>
              <li v-else>
                <el-date-picker
                  :unlink-panels="true"
                  v-model="definedDate"
                  type="daterange"
                  @change="actives2('defined')"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                ></el-date-picker>
              </li>
            </ul>
          </el-form-item>
        </el-row>
        <div class="slide"  :style="{'height':showMore?'170px':0}">
          <el-row :span="24">
            <el-col :span="8">
              <el-form-item label="所属地区">
                <el-cascader
                  :options="getRegion"
                  placeholder="请选择"
                  clearable
                  class="item-css"
                  @change="handleChange"
                ></el-cascader>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="所属部门">
                <el-select
                  v-model="formdata.department"
                  placeholder="请选择"
                  clearable
                  class="item-css"
                  @change="departs($event)"
                >
                  <el-option
                    v-for="(item, index) in getDepartments"
                    :key="index"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="负责人">
                <el-select
                  v-model="formdata.headuser"
                  placeholder="请选择"
                  clearable
                  :no-data-text="formdata.department?'此部门暂无成员':'请选择部门'"
                  class="item-css"
                  @change="tableApi()"
                >
                  <el-option
                    v-for="(item, index) in followUser"
                    :key="index"
                    :label="item.name"
                    :value="item.name"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :span="24">
            <el-col :span="8">
              <el-form-item label="所属级别">
                <el-select
                  v-model="formdata.CustomerLevel"
                  placeholder="请选择"
                  clearable
                  class="item-css"
                  @change="tableApi"
                >
                  <el-option
                    v-for="(item, index) in getCustomerLevel"
                    :key="index"
                    :label="item.val"
                    :value="item.code"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="线索来源">
                <el-select
                  v-model="formdata.CustomerSource"
                  placeholder="请选择"
                  clearable
                  class="item-css"
                  @change="tableApi"
                >
                  <el-option
                    v-for="(item, index) in getCustomerSource"
                    :key="index"
                    :label="item.name"
                    :value="item.name"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :span="24">
            <el-col :span="8">
              <el-form-item label="搜索">
                <el-select
                  v-model="formdata.searchType"
                  placeholder="请选择"
                  clearable
                  class="item-css"
                >
                  <el-option
                    v-for="(item, index) in getsearchType"
                    :key="index"
                    :label="item.name"
                    :value="item.code"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="10">
              <el-form-item label="关键字" class="input-search">
                <el-col :span="14">
                  <el-input
                    v-model="formdata.search"
                    @keydown.enter.native="tableApi"
                    placeholder="请输入"
                    clearable
                  ></el-input>
                </el-col>
                <el-col :span="6">
                  <el-button type="primary" @click="tableApi">查询</el-button>
                </el-col>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
        <p class="show-more-button">
          <el-button
            @click="showMore=!showMore"
            plain
            :icon="showMore?'el-icon-caret-top':'el-icon-caret-bottom'"
            type="primary"
            circle
            size="mini"
          ></el-button>
        </p>
      </el-row>
    </el-form>

    <el-row>
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="primary"
        icon="el-icon-folder-add"
        v-permi="'/clue/addClue'"
        @click="addClueDialog = true"
      >新增线索</el-button>
      <el-button
        type="primary"
        icon="el-icon-money"
        v-permi="'/clue/uploadFile'"
        @click="uploadDialog = true"
      >批量导入</el-button>
      <!-- <el-button type="success" @click="publicClue" icon="el-icon-document-copy">转至公海</el-button> -->
      <!-- <el-button
        type="primary"
        @click="transferType='transfer';isShowClueTransfer=multipleSelection.length>0"
        icon="el-icon-ship"
        :disabled="!multipleSelection.length"
      >转移其他人</el-button>-->
      <el-button
        type="primary"
        @click="transferType='copyClue';isShowClueTransfer=multipleSelection.length>0"
        icon="el-icon-circle-plus-outline"
        :disabled="multipleSelection.length!==1"
      >添加负责人</el-button>
      <el-button
        type="danger"
        @click="transferType='remove';isShowClueTransfer=multipleSelection.length===1"
        icon="el-icon-remove-outline"
        :disabled="multipleSelection.length!==1"
      >移除负责人</el-button>
      <!-- <el-button
        type="success"
        @click="isShowCustomer = true"
        :disabled="multipleSelection.length!==1"
        v-permi="'/clue/toCustomer'"
        icon="el-icon-s-fold"
      >转成客户</el-button>-->
      <el-dropdown trigger="click" @command="clueTransition">
        <el-button type="warning" :disabled="multipleSelection.length!==1" style="margin: 0 10px;">
          线索转换
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item v-permi="'/clue/toCustomer'" command="转成客户">转成客户</el-dropdown-item>
          <el-dropdown-item
            :disabled="multipleSelection[0]&&multipleSelection[0].isChange==1"
            command="转至公海"
          >转至公海</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-button
        type="danger"
        :disabled="!multipleSelection.length"
        @click="deleteClue"
        icon="el-icon-delete"
      >删除</el-button>
      <el-button
        type="warning"
        icon="el-icon-message"
        :disabled="!multipleSelection.length"
        @click="sendMessageDialog = true"
      >发送短信</el-button>
      <!-- <el-button type="success" icon="el-icon-message" @click="exportTable" class="item">导出表格</el-button> -->
      <el-dropdown trigger="click" @command="exportTable">
        <el-button type="warning" style="margin-left:10px;">
          导出表格
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item
            v-for="(item,index) in excNames"
            :key="index"
            :command="item"
          >{{item.name}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-row>
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      @selection-change="handleSelectionChange"
      :row-class-name="tableRowClassName"
      border
      :fit="true"
    >
      <el-table-column type="selection" width="50" align="center" fixed></el-table-column>
      <el-table-column
        v-if="showCol.indexOf('业务类型')>-1"
        label="业务类型"
        prop="registerName"
        align="center"
      ></el-table-column>
      <el-table-column v-if="showCol.indexOf('客户名称')>-1" align="center" prop="guest" label="客户名称"></el-table-column>
      <el-table-column
        v-if="showCol.indexOf('联系人')>-1"
        align="center"
        prop="linkName"
        label="联系人"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('电话')>-1"
        prop="telnum"
        label="电话"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column align="center" v-if="showCol.indexOf('所属地区')>-1" prop="region" label="所属地区"></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('线索状态')>-1"
        prop="status"
        sortable
        label="线索状态"
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('线索来源')>-1"
        prop="clueFrom"
        label="线索来源"
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('线索负责人')>-1"
        prop="userName"
        label="线索负责人"
      ></el-table-column>
      <!-- <el-table-column  align="center" prop="contents" label="最新跟进记录"></el-table-column> -->
      <el-table-column
        align="center"
        v-if="showCol.indexOf('下次跟进时间')>-1"
        prop="nextTime"
        width="120"
        label="下次跟进时间"
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('创建时间')>-1"
        prop="createTime"
        label="创建时间"
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('业务级别')>-1"
        prop="level"
        label="业务级别"
        width="180px"
      ></el-table-column>
      <el-table-column align="center" v-if="showCol.indexOf('搜索词')>-1" prop="keyWords" label="搜索词"></el-table-column>
      <el-table-column label="操作" width="150" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            plain
            @click="handleEdit(scope.$index, scope.row)"
            v-permi="'/clue/updateClue'"
          >编辑</el-button>
          <el-button size="mini" @click="handleView(scope.$index, scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>

    <div>
      <el-pagination
        class="itemPage"
        @size-change="sizeChangePage"
        @current-change="currentChangePage"
        :current-page="formdata.page"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalSize"
      ></el-pagination>
    </div>
    <!-- 转入公海 -->
    <to-high-seas
      @closeDialogFather="isShowHighSeas=false"
      :isShowHighSeas="isShowHighSeas"
      @sureDialogFather="tableApi()"
      :clueDetail="multipleSelection[0]"
    ></to-high-seas>

    <!-- 新增线索 -->
    <add-clue
      :dialogFormVisible="addClueDialog"
      @closeDialogFather="addClueDialog=false"
      @sureDialogFather="tableApi()"
      :getDepartments="getDepartments"
    ></add-clue>

    <!-- 线索编辑 -->
    <edit-clue
      :clueDetail="editData"
      :dialogFormVisible="showEditClue"
      @closeDialogFather="showEditClue=false"
      @sureDialogFather="tableApi()"
    ></edit-clue>

    <!-- 上传线索 -->
    <upload-clue
      :uploadDialog="uploadDialog"
      @sureDialogFather="tableApi()"
      @closeDialog="uploadDialog=false"
    ></upload-clue>

    <!-- 线索转移 -->
    <clue-transfer
      :isShowClueTransfer="isShowClueTransfer"
      :clueList="multipleSelection"
      :transferType="transferType"
      @closeDialogFather="isShowClueTransfer=false"
      @sureDialogFather="tableApi()"
    ></clue-transfer>

    <!-- 转成客户 -->
    <Customer
      :isShowCustomer="isShowCustomer"
      @closeDialogFather="isShowCustomer=false"
      :clueDetail="multipleSelection[0]"
      @sureDialogFather="tableApi()"
      :customerSource="getCustomerSource"
    ></Customer>

    <!-- 发送短信 -->
    <send-message
      :multipleSelection="multipleSelection"
      :isShowSend="sendMessageDialog"
      @closeDialogFather="sendMessageDialog=false"
    ></send-message>
  </div>
</template>

<script>
import API from "@/api/clue";
import * as filters from "@/filters/index";
import { filter } from "minimatch";
import ToHighSeas from "../components/ToHighSeas";
import ClueTransfer from "../components/ClueTransfer";
import Customer from "../components/Customer";
import EditClue from "../components/EditClue";
import SendMessage from "../components/SendMessage";
import AddClue from "../components/AddClue";
import UploadClue from "../components/UploadClue";
export default {
  name: "clue",
  components: {
    ClueTransfer,
    ToHighSeas,
    Customer,
    EditClue,
    SendMessage,
    UploadClue,
    AddClue
  },
  data() {
    return {
      isShowClueTransfer: false, // 转移给其他人
      isShowCustomer: false, // 转成客户
      showMore: false, // 收起和展开
      isShowHighSeas: false, // 转移至公海
      startDatePicker: {
        // 跟进事件只能选择今天之后的日期
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //如果没有后面的-8.64e6就是不可以选择今天的
        }
      },
      transferType: null, // 线索转移或者复制
      showDefinedDate: false, // 展示自定义时间
      definedDate: "", // 自定义时间段
      ins: 0,
      ins1: 0,
      ins2: 0,
      addClueDialog: false,
      uploadDialog: false,
      showEditClue: false, // 编辑
      sendMessageDialog: false,
      getAllSupport: [], //线索类型
      getAllstate: [], //线索状态
      getAlltime: [], //线索时间
      getDepartments: [],
      followUser: [],
      getCustomerLevel: [],
      getCustomerSource: [],
      getsearchType: [
        { name: "业务类型", code: 1 },
        { name: "客户名称", code: 2 },
        { name: "客户搜索词", code: 3 },
        { name: "联系方式", code: 7 }

        // { name: "电话", code: 4 },
        // { name: "微信", code: 5 },
        // { name: "QQ", code: 6 }
      ],
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      // 添加线索
      ruleForm: {
        registerName: null, // 注册类型
        guest: null, // 客户名称
        linkName: null, // 联系人姓名
        telnum: null, // 电话
        contents: null, // 跟进内容
        nextFollwTime: null, // 下次跟进事件
        clueFrom: null, // 线索来源
        status: null, // 线索状态
        remarks: null, // 备注
        level: null, // 业务等级
        keyWords: null, // 搜索词
        departName: null, // 所属部门
        followUserName: null, // 负责人
        wechat: null, // 微信
        qq: null, // qq
        province: null, //省
        city: null //市
      },
      editData: {},
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        region: [
          { required: true, message: "请选择活动区域", trigger: "change" }
        ]
      },
      totalSize: 0, //总条数
      formLabelWidth: "120px",
      showCol: [
        "业务类型",
        "客户名称",
        "联系人",
        "电话",
        "所属地区",
        "线索状态",
        "线索负责人",
        "下次跟进时间",
        "创建时间",
        "业务级别",
        "搜索词"
      ],
      colOptions: [
        "业务类型",
        "客户名称",
        "联系人",
        "电话",
        "所属地区",
        "线索状态",
        "线索负责人",
        "下次跟进时间",
        "创建时间",
        "业务级别",
        "搜索词"
      ],
      // 获取列表
      formdata: {
        page: 1, //当前第几页
        rows: 10, //每页默认显示几条
        timeCode: 0, // 转客户时间 默认0
        codeType: 3, //线索类型 默认3
        codeLevel: 4, //线索状态 默认4
        codeTime: 0, //线索时间
        searchType: null, //搜索类型
        startTime: null, // 开始时间
        endTime: null, // 自定义结束时间
        province: null, //省
        city: null, //市
        department: null, //所属部门
        CustomerSource: null, //线索来源
        headuser: null, //负责人
        CustomerLevel: null, //所属级别
        search: null
      },
      tableData: [],
      multipleSelection: [], // 已经选择的数据
      allCustomer: {
        arrlist: [],
        desc: "",
        arrlists: []
      },
      getRegion: [],
      excNames: [
        { api: "exportClueExc", name: "线索表" },
        { api: "exportDataExc", name: "网点表" },
        { api: "exportChannelExc", name: "渠道统计表" }
      ] // 表格名称
    };
  },

  methods: {
    clueTransition(name) {
      switch (name) {
        case "转至公海":
          this.isShowHighSeas = true;
          break;
        case "转成客户":
          this.isShowCustomer = true;
          break;

        default:
          break;
      }
    },
    colChange(col) {
      localStorage.setItem("clueTable", JSON.stringify(this.showCol));
    },
    tableRowClassName({ row, rowIndex }) {
      if (row.isChange == 1) {
        return "success-row";
      }
      return "";
    },
    // 删除操作
    deleteClue() {
      // 弹窗确认
      this.$confirm("是否确认删除所选线索", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      })
        .then(() => {
          let arr = [];
          for (const item of this.multipleSelection) {
            arr.push(item.id);
          }
          // 参数为,分隔字符
          API.delClue({ ids: arr.join(",") })
            .then(res => {
              this.$message({
                type: "success",
                message: "已删除"
              });
              this.tableApi();
            })
            .catch(err => {
              if (err.indexOf("权限") === -1) this.$message.warning(err);
            });
        })
        .catch(action => {
          this.$message({
            type: "error",
            message: "已取消"
          });
        });
    },
    // 页数大小改变
    sizeChangePage(e) {
      this.formdata.rows = e;
      this.tableApi();
    },
    // 当前页改变
    currentChangePage(e) {
      this.formdata.page = e;
      this.tableApi();
    },
    multipleSelections(val) {
      console.log(val);
      if (this.allCustomer.arrlists.indexOf(val.id) == -1) {
        this.allCustomer.arrlists.push(val);
      } else {
        this.allCustomer.arrlists.splice(
          this.allCustomer.arrlists.indexOf(val),
          1
        );
      }
    },

    exportTable(obj) {
      let formdata = this.formdata;
      let params = {
        pageNum: formdata.page,
        pageSize: formdata.rows,
        clueType: formdata.codeType,
        status: formdata.codeLevel,
        startTime: formdata.startTime,
        endTime: formdata.endTime,
        timeCode: formdata.timeCode,
        province: formdata.province,
        city: formdata.city,
        departName: formdata.department,
        userName: formdata.headuser,
        customerGrade: formdata.CustomerLevel,
        fromName: formdata.CustomerSource,
        searchType: formdata.searchType,
        searchName: formdata.search
      };
      API[obj.api](params)
        .then(res => {
          var blob = new Blob([res], {
            type: "application/vnd.ms-excel;charset=utf-8"
          }); //指定格式为vnd.ms-excel
          var downloadElement = document.createElement("a");
          var href = window.URL.createObjectURL(blob); //创建下载的链接
          downloadElement.href = href;
          document.body.appendChild(downloadElement);
          downloadElement.setAttribute("download", obj.name + ".xls");
          downloadElement.click(); //点击下载
          document.body.removeChild(downloadElement); //下载完成移除元素
          window.URL.revokeObjectURL(href); //释放掉blob对象
        })
        .catch(err => {
          console.error(err);
        });
    },
    //线索类型，状态时间筛选
    actives(index, code) {
      this.ins = index;
      console.log(code);

      this.formdata.codeType = code;
      console.log("线索类型", this.formdata);
      this.tableApi();
    },
    actives1(index, code) {
      this.ins1 = index;
      this.formdata.codeLevel = code;
      console.log("线索状态", this.formdata);
      this.tableApi();
    },
    actives2(index, code) {
      // 如果是用户自定义时间
      if (index === "defined" && this.definedDate) {
        let startTime = this.definedDate[0];
        startTime = filters.parseTime(startTime);
        let endTime = this.definedDate[1];
        endTime = filters.parseTime(endTime);

        this.formdata.startTime = startTime;
        this.formdata.endTime = endTime;
        this.timeCode = null; // 清空选择的时间
        this.tableApi();
      } else if (index !== "defined") {
        // 否则选择期限
        this.formdata.startTime = null; // 清空定义的时间
        this.formdata.endTime = null;
        this.showDefinedDate = false;
        this.ins2 = index;
        this.formdata.timeCode = code;
        console.log("code", this.formdata.timeCode);
        console.log("时间", this.formdata);
        this.tableApi();
      } else {
        this.ins2 = -1;
      }
    },
    // 转至公海
    publicClue() {
      if (!this.multipleSelection.length) return;
      // 弹窗确认
      this.$confirm("是否确认转至公海", "确认转出", {
        distinguishCancelAndClose: true,
        confirmButtonText: "转出",
        cancelButtonText: "取消"
      })
        .then(() => {
          let arr = [];
          for (const item of this.multipleSelection) {
            arr.push(item.id);
          }
          // 参数为,分隔字符
          API.toPublic({ ids: arr.join(",") })
            .then(res => {
              if (res.status !== 200) {
                throw new Error("转出失败");
              }
              this.$message({
                type: "success",
                message: "已转出"
              });
              this.tableApi();
            })
            .catch(err => {
              if (err.indexOf("权限") === -1) this.$message.warning(err);
            });
        })
        .catch(action => {
          console.log(action);

          this.$message({
            type: "error",
            message: "已取消"
          });
        });
    },
    //新增线索
    addClue() {
      let params = this.ruleForm;
      console.log(params);
      params.followTime = filters.parseTime(new Date(params.nextFollwTime));
      API.addClue(params)
        .then(res => {
          this.$message({
            message: "成功",
            type: "success"
          });
          this.addClueDialog = false;
          // 重新获取数据
          this.tableApi();
          // 初始化
          this.ruleForm = {
            registerName: null, // 注册类型
            guest: null, // 客户名称
            linkName: null, // 联系人姓名
            telnum: null, // 电话
            contents: null, // 跟进内容
            nextFollwTime: null, // 下次跟进事件
            clueFrom: null, // 线索来源
            status: null, // 线索状态
            remarks: null, // 备注
            level: null, // 业务等级
            keyWords: null, // 搜索词
            departName: null, // 所属部门
            followUserName: null, // 负责人
            wechat: null, // 微信
            qq: null, // qq
            province: null, //省
            city: null //市
          };
        })
        .catch(err => {
          console.log("add", err);
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    },
    //总查询
    tableApi() {
      let params = {
        pageNum: this.formdata.page,
        pageSize: this.formdata.rows,
        clueType: this.formdata.codeType,
        status: this.formdata.codeLevel,
        startTime: this.formdata.startTime,
        endTime: this.formdata.endTime,
        timeCode: this.formdata.timeCode,
        province: this.formdata.province,
        city: this.formdata.city,
        departName: this.formdata.department,
        userName: this.formdata.headuser,
        customerGrade: this.formdata.CustomerLevel,
        fromName: this.formdata.CustomerSource,
        searchType: this.formdata.searchType,
        searchName: this.formdata.search
      };
      console.log("发送表格请求参数", params);
      API.getAll(params).then(res => {
        this.totalSize = res.data.total;
        let list = res.data.list;
        list.forEach(item => {
          switch (item.status) {
            case "0":
              item.status = "未处理";
              break;
            case "1":
              item.status = "联系方式有效";
              break;
            case "2":
              item.status = "联系方式无效";
              break;
            case "3":
              item.status = "关闭";
              break;

            default:
              item.status = "暂无状态";
              break;
          }
        });
        this.tableData = list;
        console.log("获取列表数据", this.tableData);
      });
    },
    //所属地区
    handleChange(val) {
      this.formdata.province = val[0];
      this.formdata.city = val[1];
      this.tableApi();
    },
    // 新增页面的所属地区
    handldiqu(val) {
      this.ruleForm.province = val[0];
      this.ruleForm.city = val[1];
      console.log(this.ruleForm.province);
      console.log(this.ruleForm.city);
    },
    //负责人

    // 所属部门
    departs(val) {
      if (val != "") {
        API.getHead(val).then(res => {
          this.followUser = res.data;
        });
      }
      this.formdata.headuser = "";
      this.tableApi();
    },
    // 当选择项发生变化时会触发该事件
    handleSelectionChange(val) {
      this.multipleSelection = val;
      // console.log(this.multipleSelection);
    },

    // 编辑
    handleEdit(index, row) {
      this.editData = row;
      this.showEditClue = true;
    },
    // 删除
    // handleDelete(index, row) {
    //   console.log(index, row);
    // },
    // 查看
    handleView(index, row) {
      this.$router.push({
        path: "/clue/message",
        query: { id: row.id, userName: row.userName }
      });
    }
  },
  activated() {
    this.tableApi();
  },
  mounted() {
    API.getAllSupport().then(res => {
      this.getAllSupport = res.data;
      this.getAllSupport.unshift({
        val: "全部线索",
        code: 3
      });
    });
    // 把展示几列的数据放在local  如过没手动设置那么就使用默认
    let showCol = JSON.parse(localStorage.getItem("clueTable"));
    if (showCol) this.showCol = showCol;
    // 获取线索状态
    API.getAllstate().then(res => {
      this.getAllstate = res.data;
      this.getAllstate.unshift({
        val: "全部",
        code: 4
      });
    });
    // 获取转客户时间
    API.getAlltime().then(res => {
      this.getAlltime = res.data.slice(0, -3);
    });
    // 地区
    API.getRegion().then(res => {
      res.data.forEach(item => {
        item.value = item.label;
        if (item.children.length == "0") {
          item.children = "";
        } else {
          item.children.forEach(item1 => {
            item1.value = item1.label;
          });
        }
      });
      this.getRegion = res.data;
    });
    //所属部门
    API.getDepartments().then(res => {
      this.getDepartments = res.data;
    });
    //所属级别
    API.getCustomerLevel().then(res => {
      this.getCustomerLevel = res.data;
    });
    //线索来源
    API.getCustomerSource().then(res => {
      this.getCustomerSource = res.data;
    });
    // if (this.$store.state.userInfo) return;
    // console.log("未获取到用户信息");

    // this.getUserInfo();
  }
};
</script>

