<template>
  <div>
    <el-button type="warning" @click="isAddContract=true" v-permi="'/contract/add'">新增合同</el-button>

    <el-table
      :data="contractTableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      :fit="true"
    >
      <el-table-column prop="name" label="合同名称" align="center"></el-table-column>
      <el-table-column prop="totalPrice" label="合同总金额" align="center"></el-table-column>
      <el-table-column prop="signDate" label="合同时间" align="center"></el-table-column>
      <el-table-column prop="statusStr" label="合同状态" align="center"></el-table-column>
      <el-table-column prop="status" label="套餐" align="center"></el-table-column>
      <el-table-column prop="remarks" label="进账备注" align="center"></el-table-column>
      <el-table-column label="操作" width="150" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" plain @click="editContract(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" @click="examineContract(scope.$index, scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <new-contract
      :isShowContract="isAddContract"
      :customerId="customerId"
      :customerDetail="panoramic"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-contract>

    <!-- 编辑合同 -->
    <edit-contract
      :isShowContractEdit="isEditContract"
      :contractStatusList="contractStatusList"
      :contractId="contractId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-contract>
  </div>
</template>

<script>
import NewContract from "./NewContract";
import EditContract from "./EditContract"; // 编辑合同
import API from "@/api/customer";

export default {
  name: "TabContract",
  components: {
    NewContract,
    EditContract
  },
  props: ["panoramic", "customerId"],
  data() {
    return {
      contractTableData: [],
      isAddContract: false,
      isEditContract: false,
      contractStatusList: [],
      contractId: null
    };
  },
  methods: {
    editContract(index, row) {
      this.contractId = row.id;
      this.isEditContract = true;
    },
    examineContract(index, row) {
      this.$router.push({
        path: "/sellManagement/contractdetails",
        query: { id: row.id, paymentTotal: row.paymentTotal }
      });
    },
    getContractTable() {
      API.getContractByCustomerId(this.customerId).then(res => {
        let data = res.data;
        for (const dataItem of data) {
          for (const statuItem of this.contractStatusList) {
            if (dataItem.status == statuItem.code) {
              dataItem.statusStr = statuItem.val;
            }
          }
        }
        console.log(data);

        this.contractTableData = data;
      });
    },
    getSonSure() {
      this.getSonCancel();
      this.getContractTable();
    },
    getSonCancel() {
      this.isAddContract = false;
      this.isEditContract = false;
    }
  },
  mounted() {
    // 获取合同状态列表
    API.getContractStatus().then(res => {
      this.contractStatusList = res.data;
    });
  }
};
</script>

