<template>
  <div>
    <el-dialog title="分配售后负责人" :visible.sync="outerVisible" @close="closeDialog">
      <el-form label-width="100px" ref="form" :rules="rules" :model="form">
        <el-form-item label="所属部门" prop="departId">
          <el-select v-model="form.departId" placeholder="请选择" clearable @change="departChange">
            <el-option
              v-for="(item, index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="新负责人" prop="followUser">
          <el-select v-model="form.followUser" @change="userChange" filterable placeholder="请选择">
            <el-option
              v-for="(item,index) in headList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success">保存</el-button>
          <el-button @click="closeDialog">关闭</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
// 潜在客户转移
import API from "@/api/after";
export default {
  name: "potential",
  props: ["isShowPotential", "PotentialId"],
  data() {
    return {
      outerVisible: this.isShowPotential,
      form: {
        followUser: "",
        departId: ""
      },
      headList: [], // 新负责人
      departmentsList: [],
      rules: {
        departId: [
          { required: true, message: "请选择部门", trigger: "change" }
        ],
        followUser: [
          { required: true, message: "请选择负责人", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    // 所属负责人id
    userChange(val) {
      let departId = this.headList.find(item => {
        return item.id === val;
      }).departId;
      this.form.departId = departId;
      this.departChange("no_change");
    },
    departChange(flag) {
      if (flag !== "no_change") this.form.followUser = null;
      let val = this.form.departId;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      }
    },
    // 保存
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let params = {
          ids: this.PotentialId.join(","),
          dealUserId: this.form.followUser,
          dealDepartId: this.form.departId
        };
        console.log(params);

        API.toOther(params)
          .then(res => {
            this.$message({
              message: "已转交",
              type: "success"
            });
            this.$emit("sureDialogFather");
            this.$refs.form.resetFields();
          })
          .catch(err => {
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    },
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather", this.outerVisible);
    }
  },
  watch: {
    isShowPotential(value) {
      this.outerVisible = this.isShowPotential;
      if (!value) return;
      // 所属负责人
      API.getAllUser().then(res => {
        for (const item of res.data) {
          item.value = item.name;
        }
        this.headList = res.data;
      });
      // 所有部门
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>