<template>
  <!-- 回款计划 -->
  <div>
    <el-tabs type="card" @tab-click="tabChange" v-model="tabName">
      <el-tab-pane label="回款计划" name="plan">
        <el-row>
          <el-form :model="chooseObj.plan" label-width="120px">
            <el-col :span="24" class="list">
              <el-form-item label="计划回款日期">
                <ul>
                  <li
                    v-for="(item, index) in timeList"
                    :key="index"
                    @click="dateChange(index, item.code)"
                    :class="chooseObj.plan.timeActive == index ? 'addclass' : '' "
                  >{{item.name}}</li>
                  <li
                    @click="chooseObj.plan.showDefinedTime=true;chooseObj.plan.timeActive = -1"
                    class="list-li"
                    v-if="!chooseObj.plan.showDefinedTime"
                  >自定义时间段</li>
                  <li v-else>
                    <el-date-picker
                      v-model="chooseObj.plan.createTime"
                      type="daterange"
                      value-format="yyyy-MM-dd"
                      range-separator="至"
                      start-placeholder="开始日期"
                      end-placeholder="结束日期"
                      @change="tableApi"
                    ></el-date-picker>
                  </li>
                </ul>
              </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-col :span="8">
                <el-form-item label="合同所属部门">
                  <el-select
                    v-model="chooseObj.plan.departId"
                    placeholder="请选择"
                    clearable
                    class="item-css"
                    @change="departChange"
                  >
                    <el-option
                      v-for="(item, i) in departList"
                      :key="i"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="合同负责人">
                  <el-select
                    v-model="chooseObj.plan.userId"
                    placeholder="请选择"
                    clearable
                    class="item-css"
                    @change="tableApi"
                    :no-data-text="chooseObj.plan.departId?'此部门暂无成员':'请选择部门'"
                  >
                    <el-option
                      v-for="(item, index) in followUserList"
                      :key="index"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-col>
          </el-form>
        </el-row>

        <el-row>
          <!-- 筛选 -->
          <el-popover placement="top-start" width="300" trigger="click">
            <el-checkbox-group v-model="showPlanCol" @change="planColChange">
              <el-checkbox
                style="width:120px;line-height:25px"
                v-for="(item,index) in planColOptions"
                :key="index"
                :label="item"
              ></el-checkbox>
            </el-checkbox-group>
            <el-button
              slot="reference"
              style="margin-right:10px;"
              type="primary"
              icon="el-icon-s-operation"
              plain
            ></el-button>
          </el-popover>
          <el-button
            type="primary"
            icon="el-icon-folder-add"
            @click="isNewReceivableplan=true"
            v-permi="'/payment/addContractPaymentPlan'"
          >新增计划</el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            :disabled="!statisticsObj.plan.checkboxId.length"
            @click="deletePlan"
          >删除</el-button>
        </el-row>
        <el-row style="height:60px;line-height:60px;font-size:14px">
          <div>计划回款金额共计{{statisticsObj.plan.planAmount}}元，已回款金额共计{{statisticsObj.plan.realPayAmount}}元</div>
        </el-row>
        <el-table
          :data="planTableData"
          tooltip-effect="dark"
          style="width: 100%;"
          border
          stripe
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" align="center" fixed></el-table-column>
          <el-table-column
            prop="contractName"
            v-if="showPlanCol.indexOf('合同标题')>-1"
            label="合同标题"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="customerName"
            v-if="showPlanCol.indexOf('客户')>-1"
            label="客户"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="price"
            v-if="showPlanCol.indexOf('计划回款金额')>-1"
            label="计划回款金额"
            align="center"
          ></el-table-column>

          <el-table-column
            prop="realPay"
            v-if="showPlanCol.indexOf('已回款金额')>-1"
            label="已回款金额"
            align="center"
          ></el-table-column>

          <el-table-column
            prop="opeDate"
            v-if="showPlanCol.indexOf('计划回款时间')>-1"
            label="计划回款时间"
            align="center"
          >
            <template slot-scope="scope">{{scope.row.opeDate&&scope.row.opeDate.slice(0,11)}}</template>
          </el-table-column>
          <el-table-column
            prop="realPayDate"
            v-if="showPlanCol.indexOf('实际回款时间')>-1"
            label="实际回款时间"
            align="center"
          >
            <template
              slot-scope="scope"
            >{{scope.row.realPayDate&&scope.row.realPayDate.slice(0,11)}}</template>
          </el-table-column>
          <el-table-column
            prop="overDays"
            v-if="showPlanCol.indexOf('逾期天数')>-1"
            label="逾期天数"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="userName"
            v-if="showPlanCol.indexOf('合同负责人')>-1"
            label="合同负责人"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="departName"
            v-if="showPlanCol.indexOf('合同所属部门')>-1"
            label="合同所属部门"
            align="center"
          ></el-table-column>
          <el-table-column label="操作" align="center" fixed="right">
            <template slot-scope="scope">
              <!-- <el-button
                size="mini"
                :disabled="Boolean(scope.row.isOver)"
                plain
                type="primary"
                @click="handleEditplan(scope.$index, scope.row)"
              >编辑</el-button>-->
              <el-button size="mini" @click="handleViewplan(scope.$index, scope.row)">查看</el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页 -->
        <el-pagination
          class="itemPage"
          @size-change="sizeChangePage"
          @current-change="currentChangePage"
          :current-page="pageObj.plan.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pageObj.plan.total"
        ></el-pagination>
      </el-tab-pane>
      <el-tab-pane label="回款记录" name="record">
        <el-row>
          <el-form :model="chooseObj.record" label-width="120px">
            <el-col :span="24" class="list">
              <el-form-item label="回款类型">
                <ul>
                  <li
                    v-for="(item, index) in paymentTypeList"
                    :key="index"
                    @click="paymentTypeChange(index, item.code)"
                    :class="chooseObj.record.paymentActive == index ? 'addclass' : '' "
                  >{{item.val}}</li>
                </ul>
              </el-form-item>
            </el-col>
            <el-col :span="24" class="list">
              <el-form-item label="实际回款日期">
                <ul>
                  <li
                    v-for="(item, index) in contimeList"
                    :key="index"
                    @click="dateChange(index, item.code)"
                    :class="chooseObj.record.timeActive == index ? 'addclass' : '' "
                  >{{item.name}}</li>
                  <li
                    @click="chooseObj.record.showDefinedTime=true;chooseObj.record.timeActive = -1"
                    class="list-li"
                    v-if="!chooseObj.record.showDefinedTime"
                  >自定义时间段</li>
                  <li v-else>
                    <el-date-picker
                      v-model="chooseObj.record.createTime"
                      type="daterange"
                      value-format="yyyy-MM-dd"
                      range-separator="至"
                      start-placeholder="开始日期"
                      end-placeholder="结束日期"
                      @change="tableApi"
                    ></el-date-picker>
                  </li>
                </ul>
              </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-col :span="8">
                <el-form-item label="合同所属部门">
                  <el-select
                    v-model="chooseObj.record.departId"
                    placeholder="请选择"
                    clearable
                    class="item-css"
                    @change="departChange"
                  >
                    <el-option
                      v-for="(item, i) in departList"
                      :key="i"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="合同负责人">
                  <el-select
                    v-model="chooseObj.record.userId"
                    placeholder="请选择"
                    clearable
                    class="item-css"
                    :no-data-text="chooseObj.record.departId?'此部门暂无成员':'请选择部门'"
                    @change="tableApi"
                  >
                    <el-option
                      v-for="(item, index) in followUserList"
                      :key="index"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-col>
          </el-form>
        </el-row>

        <el-row>
          <!-- 筛选 -->
          <el-popover placement="top-start" width="300" trigger="click">
            <el-checkbox-group v-model="showRecordCol" @change="planRecordChange">
              <el-checkbox
                style="width:120px;line-height:25px"
                v-for="(item,index) in recordColOptions"
                :key="index"
                :label="item"
              ></el-checkbox>
            </el-checkbox-group>
            <el-button
              slot="reference"
              style="margin-right:10px;"
              type="primary"
              icon="el-icon-s-operation"
              plain
            ></el-button>
          </el-popover>
          <el-button
            type="primary"
            icon="el-icon-folder-add"
            @click="isNewReceivablerecord=true"
            v-permi="'/payment/addRealContractPayment'"
          >新增记录</el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            :disabled="!statisticsObj.record.checkboxId.length"
            @click="deletePlan"
          >删除</el-button>
        </el-row>
        <el-row style="height:60px;line-height:60px;font-size:14px">
          <div>共计回款金额{{TotalPrice}}元</div>
        </el-row>
        <el-table
          :data="recordTableData"
          tooltip-effect="dark"
          style="width: 100%;"
          border
          :fit="true"
          stripe
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" align="center" fixed></el-table-column>
          <el-table-column
            prop="opeDate"
            v-if="showRecordCol.indexOf('实际回款日期')>-1"
            label="实际回款日期"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="price"
            v-if="showRecordCol.indexOf('回款金额')>-1"
            label="回款金额"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="companyName"
            v-if="showRecordCol.indexOf('对应客户')>-1"
            label="对应客户"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="contractName"
            v-if="showRecordCol.indexOf('合同名称')>-1"
            label="合同名称"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="payNo"
            v-if="showRecordCol.indexOf('付款方式')>-1"
            label="付款方式"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="payType"
            v-if="showRecordCol.indexOf('回款类型')>-1"
            label="回款类型"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="userName"
            v-if="showRecordCol.indexOf('收款人')>-1"
            label="收款人"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="returnStatus"
            v-if="showRecordCol.indexOf('逾期状态')>-1"
            label="逾期状态"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="days"
            v-if="showRecordCol.indexOf('逾期天数')>-1"
            label="逾期天数"
            align="center"
          ></el-table-column>
          <el-table-column label="操作" align="center" fixed="right">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                plain
                v-permi="'/payment/updateContractPaymentPlan'"
                @click="handleEditrecord(scope.$index, scope.row)"
              >编辑</el-button>
              <el-button size="mini" @click="handleViewrecord(scope.$index, scope.row)">查看</el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页 -->
        <el-pagination
          class="itemPage"
          @size-change="sizeChangePage"
          @current-change="currentChangePage"
          :current-page="pageObj.record.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pageObj.record.total"
        ></el-pagination>
      </el-tab-pane>
    </el-tabs>

    <NewReceivableplan
      :isNewReceivableplan="isNewReceivableplan"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewReceivableplan>

    <edit-receivableplan
      :isEditReceivableplan="isEditReceivableplan"
      :editPlanData="editPlanData"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-receivableplan>

    <NewReceivablerecord
      :isNewReceivablerecord="isNewReceivablerecord"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewReceivablerecord>

    <edit-receivablerecord
      :isEditReceivablerecord="isEditReceivablerecord"
      :editRecordData="editRecordData"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-receivablerecord>
  </div>
</template>

<script>
// 回款管理
import API from "@/api/payment";
import EditReceivableplan from "../components/EditReceivableplan"; // 编辑回款计划
import NewReceivableplan from "../components/NewReceivableplan"; // 新增回款计划
import NewReceivablerecord from "../components/NewReceivablerecord"; // 新增回款记录
import EditReceivablerecord from "../components/EditReceivablerecord"; // 编辑回款记录
export default {
  name: "receivable",
  components: {
    NewReceivableplan,
    EditReceivableplan,
    NewReceivablerecord,
    EditReceivablerecord
  },
  data() {
    return {
      // 筛选器
      chooseObj: {
        // 回款计划过滤器
        plan: {
          userId:"",
          showDefinedTime: false, // 回款计划展示自定义时间
          timeCode: null, // 回款计划日期
          timeActive: 0, // 选择的下标
          createTime: []
        },
        // 回款记录过滤器
        record: {
          showDefinedTime: false, // 展示自定义时间
          timeActive: 0, // 选择的下标
          createTime: [],
          paymentActive: 0,
          payType: null // 回款类型
        }
      },
      // 计划已显示
      showPlanCol: [
        "合同标题",
        "客户",
        "计划回款金额",
        "已回款金额",
        "计划回款时间",
        "实际回款时间",
        "逾期天数",
        "合同负责人",
        "合同所属部门"
      ],
      // 计划的显示选项
      planColOptions: [
        "合同标题",
        "客户",
        "计划回款金额",
        "已回款金额",
        "计划回款时间",
        "实际回款时间",
        "逾期天数",
        "合同负责人",
        "合同所属部门"
      ],
      // 记录已显示
      showRecordCol: [
        "实际回款日期",
        "回款金额",
        "对应客户",
        "合同名称",
        "付款方式",
        "回款类型",
        "收款人",
        "逾期状态",
        "逾期天数"
      ],
      // 记录的显示选项
      recordColOptions: [
        "实际回款日期",
        "回款金额",
        "对应客户",
        "合同名称",
        "付款方式",
        "回款类型",
        "收款人",
        "逾期状态",
        "逾期天数"
      ],
      // 分页器
      pageObj: {
        plan: {
          pageNum: 1, //当前第几页
          pageSize: 10, //每页默认显示几条
          total: 0 //总条数
        },
        record: {
          pageNum: 1, //当前第几页
          pageSize: 10, //每页默认显示几条
          total: 0 //总条数
        }
      },
      planTableData: [], // 回款计划的数据
      recordTableData: [], // 回款记录数据
      showDefinedTime: false,
      tabName: "plan", // 默认显示回款计划
      timeList: [], // 计划回款日期
      paymentTypeList: [], // 回款类型
      contimeList: [], // 实际回款日期
      departList: [], // 合同所属部门
      followUserList: [], // 合同负责人
      statisticsObj: {
        plan: {
          planAmount: "", // 回款计划下的总金额
          realPayAmount: "", // 已收到回款
          checkboxId: [] // 选中的
        },
        record: {
          planAmount: "", // 回款计划下的总金额
          realPayAmount: "", // 已收到回款
          checkboxId: [] // 选中的
        }
      },
      TotalPrice: "", // 回款记录的总金额
      isNewReceivableplan: false,
      isEditReceivableplan: false,
      isNewReceivablerecord: false,
      isEditReceivablerecord: false,
      editPlanData: {}, // 编辑计划的参数
      editRecordData: {} // 编辑回款的参数
    };
  },
  methods: {
    planColChange() {
      localStorage.setItem("planTable", JSON.stringify(this.showPlanCol));
    },
    planRecordChange() {
      localStorage.setItem("recordTable", JSON.stringify(this.showRecordCol));
    },
    // 封装的table的api
    tableApi() {
      // 当前页的查询数据
      let obj = this.chooseObj[this.tabName];
      // 当前页的分页器数据
      let page = this.pageObj[this.tabName];
      let { pageNum, pageSize } = page;
      let startTime = obj.createTime && obj.createTime[0];
      let endTime = obj.createTime && obj.createTime[1];
      if (this.tabName === "plan") {
        // 回款计划
        let params = {
          pageNum,
          pageSize,
          startTime,
          endTime,
          timeCode: obj.timeCode,
          departId: obj.departId,
          userId: obj.userId
        };
        API.findPlanPaymentList(params).then(res => {
          let data = res.data;
          this.planTableData = data.paymentList.list;
          page.total = data.paymentList.total;
          this.statisticsObj.plan.planAmount = data.planAmount;
          this.statisticsObj.plan.realPayAmount = data.realPayAmount;
        });
      } else {
        // 回款记录
        let params = {
          pageNum,
          pageSize,
          startTime,
          endTime,
          payType: obj.payType,
          timeCode: obj.timeCode,
          departId: obj.departId,
          userId: obj.userId
        };
        this.getTotalPrice();
        API.postListcon(params).then(res => {
          let data = res.data;
          this.recordTableData = data.list;
          page.total = data.total;
        });
      }
    },
    //选择每页显示条数触发
    sizeChangePage(val) {
      this.pageObj[this.tabName].pageSize = val;
      this.tableApi(this.rows);
    },
    //选择第几页触发
    currentChangePage(val) {
      this.pageObj[this.tabName].pageNum = val;
      this.tableApi(this.page);
    },
    // 计划回款日期
    dateChange(index, code) {
      let obj = this.chooseObj[this.tabName];
      obj.showDefinedTime = false;
      obj.timeActive = index;
      obj.timeCode = code;
      obj.createTime = [];
      this.tableApi();
    },
    // 合同所属部门
    departChange(val) {
      this.chooseObj[this.tabName].userId = "";
      let obj = this.chooseObj[this.tabName];
      // this.tableApi(this.departId);
      if (val) {
        API.getHead(obj.departId).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.followUserList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.followUserList = res.data;
        });
      }
      this.tableApi();
    },
    // querySearch(queryString, cb) {
    //   var restaurants = this.followUserList;
    //   var results = queryString
    //     ? restaurants.filter(this.createFilter(queryString))
    //     : restaurants;
    //   cb(results);
    // },
    // createFilter(queryString) {
    //   return restaurant => {
    //     return (
    //       restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
    //       0
    //     );
    //   };
    // },
    // 合同负责人
    handleSelect(item) {
      this.userId = item.id;
      this.tableApi(this.userId);
      if (this.userId !== null) {
        API.getDepartByUserId(item.id).then(res => {
          this.departList = res.data;
        });
      }
      this.userId = null;
    },

    // table发生变化触发
    handleSelectionChange(val) {
      let arr = [];
      val.map(ele => {
        arr.push(ele.id);
      });
      this.statisticsObj[this.tabName].checkboxId = arr;
    },
    // 删除选中的计划
    deletePlan() {
      let name, delApi;
      name = this.tabName === "plan" ? "计划" : "记录";
      this.$confirm("是否删除选中的回款" + name, "删除" + name, {
        distinguishCancelAndClose: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      })
        .then(() => {
          let Params = {
            ids: this.statisticsObj[this.tabName].checkboxId.join(",")
          };
          API.postdeletePaymentPlans(Params).then(res => {
            this.$message({
              message: "删除成功",
              type: "success"
            });
            this.tableApi();
          });
        })
        .catch(() => {
          console.error(err);
        });
    },
    // 新增计划编辑
    handleEditplan(index, row) {
      this.editPlanData = row;
      this.isEditReceivableplan = true;
    },
    // 新增计划查看
    handleViewplan(index, row) {
      this.$router.push({
        path: "receivablePlandetails",
        query: { id: row.id }
      });
    },
    // tab发生变化
    tabChange(val) {
      if (val.label == "回款计划") {
        this.tabName = "plan";
      } else if (val.label == "回款记录") {
        this.tabName = "record";
      }
      this.tableApi();
    },

    // 回款类型
    paymentTypeChange(index, code) {
      let obj = this.chooseObj.record;
      obj.paymentActive = index;
      obj.payType = code;
      this.tableApi();
    },

    handleBlurcon() {
      if (this.form.userIdcon == "") {
        API.getDepartments().then(res => {
          this.departList = res.data;
        });
      }
    },

    // 记录编辑
    handleEditrecord(index, row) {
      console.log(row);
      this.editRecordData = row;
      this.isEditReceivablerecord = true;
    },
    // 新增记录查看
    handleViewrecord(index, row) {
      this.$router.push({
        path: "receivableRecorddetails",
        query: { id: row.id }
      });
    },
    // 获取回款总额
    getTotalPrice() {
      API.getfindPaymentTotalPrice().then(res => {
        this.TotalPrice = res.data;
      });
    },
    // 保存Dialog
    getSonSure() {
      this.getSonCancel();
      this.tableApi();
    },
    // 关闭Dialog 弹框点击关闭的值
    getSonCancel() {
      this.isNewReceivableplan = false;
      this.isNewReceivablerecord = false;
      this.isEditReceivablerecord = false;
      this.isEditReceivableplan = false;
    }
  },
  mounted() {
    let showPlanCol = JSON.parse(localStorage.getItem("planTable"));
    if (showPlanCol) this.showPlanCol = showPlanCol;
    let showRecordCol = JSON.parse(localStorage.getItem("recordTable"));
    if (showRecordCol) this.showRecordCol = showRecordCol;
    // 计划回款日期
    API.getCreationTime().then(res => {
      // let obj = { code: 0, name: "全部" };
      // res.data.unshift(obj);
      this.timeList = res.data;

      this.contimeList = res.data.slice(0, -3);
    });
    // 合同所属部门
    API.getDepartments().then(res => {
      this.departList = res.data;
    });
    // 合同负责人
    // API.getAllUser().then(res => {
    //   res.data.forEach(item => {
    //     item.value = item.name;
    //   });
    //   this.followUserList = res.data;
    // });
    // 回款类型
    API.getPaymentType().then(res => {
      // console.log(res.data)
      let obj = { code: 0, val: "全部" };
      res.data.unshift(obj);
      this.paymentTypeList = res.data;
    });
  },
  activated() {
    // 初始加载
    this.tableApi();
  }
};
</script>
