<template>
  <div>
    <!-- 查询区域 -->
    <el-form :model="form" label-width="100px">
      <el-row :span="24" class="list">
        <el-form-item label="客户状态">
          <ul>
            <li
              v-for="(item, index) in customerType"
              :key="index"
              @click="changeClientType(index, item.code)"
              :class="typeActive === index ? 'addclass' : '' "
            >{{item.val}}</li>
          </ul>
        </el-form-item>
      </el-row>
      <el-row :span="24" class="list">
        <el-form-item label="客户级别">
          <ul>
            <li
              v-for="(item, index) in customerLevel"
              :key="index"
              @click="changeClientGrade(index, item.code)"
              :class="gradeActive == index ? 'addclass' : '' "
            >{{item.val}}</li>
          </ul>
        </el-form-item>
      </el-row>
      <el-row :span="24" class="list">
        <el-form-item label="业务类型">
          <ul>
            <li
              v-for="(item, index) in customerTypeList"
              :key="index"
              @click="changeService(index, item.id)"
              :class="serviceActive == index ? 'addclass' : '' "
            >{{item.name}}</li>
          </ul>
        </el-form-item>
      </el-row>
      <div class="slide"  :style="{'height':showMore?'225px':0}">
        <el-row :span="24" class="list">
          <el-form-item label="创建时间">
            <ul>
              <li
                v-for="(item, index) in creationTime"
                :key="index"
                @click="changeCreateTime(index, item.code)"
                :class="timeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
              <li
                @click="showDefinedDate = true;timeActive = -1"
                class="list-li"
                v-if="!showDefinedDate"
              >自定义时间段</li>
              <li v-else>
                <el-date-picker
                  @change="timeCustom"
                  v-model="definedDate"
                  type="daterange"
                  value-format="yyyy-MM-dd"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                ></el-date-picker>
              </li>
            </ul>
          </el-form-item>
        </el-row>
        <el-row :span="24">
          <el-col :span="8">
            <el-form-item label="所属地区">
              <el-cascader
                :options="region"
                @change="regionChange"
                placeholder="请选择"
                clearable
                class="item-css"
              ></el-cascader>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="客户来源">
              <el-select
                v-model="form.source"
                placeholder="请选择"
                class="item-css"
                clearable
                @change="tableApi()"
              >
                <el-option
                  v-for="(item, index) in customerSourceList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :span="24">
          <el-col :span="8">
            <el-form-item label="所属部门">
              <el-select
                v-model="form.department"
                placeholder="请选择"
                clearable
                class="item-css"
                @change="departChange($event)"
              >
                <el-option
                  v-for="(item, index) in departmentsList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="负责人">
              <el-select
                v-model="form.followUser"
                placeholder="请选择"
                clearable
                class="item-css"
                :no-data-text="form.department?'此部门暂无成员':'请选择部门'"
                @change="tableApi()"
              >
                <el-option
                  v-for="(item, index) in headList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :span="24">
          <el-col :span="8">
            <el-form-item label="搜索">
              <el-select v-model="form.searchType" placeholder="请选择" clearable class="item-css">
                <el-option
                  v-for="(item, index) in getsearchType"
                  :key="index"
                  :label="item.name"
                  :value="item.code"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label="关键字" class="input-search">
              <el-col :span="14">
                <el-input
                  v-model="form.searchName"
                  @keydown.enter.native="tableApi"
                  placeholder="请输入"
                  class="input-with-select"
                  clearable
                ></el-input>
              </el-col>
              <el-col :span="6">
                <el-button type="primary" @click="tableApi()">查询</el-button>
              </el-col>
            </el-form-item>
          </el-col>
        </el-row>
      </div>
      <p class="show-more-button">
        <el-button
          @click="showMore=!showMore"
          plain
          :icon="showMore?'el-icon-caret-top':'el-icon-caret-bottom'"
          type="primary"
          circle
          size="mini"
        ></el-button>
      </p>
    </el-form>
    <!-- 收起和展开 -->
    <!-- <el-row>
      <el-col :span="24" style="font-size:20px;text-align:center;">
        <i
          style="cursor: pointer;"
          :class="flag == true? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
          @click="show"
        ></i>
      </el-col>
    </el-row>-->
    <!-- 按钮 -->
    <el-row style="margin-top:20px">
      <!-- 筛选 -->
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="primary"
        icon="el-icon-folder-add"
        @click="isShowCreate = true"
        v-permi="'/customer/add'"
      >新增客户</el-button>
      <el-button
        type="primary"
        icon="el-icon-money"
        v-permi="'/customer/batchUploadCustomer'"
        @click="isShowClues = true"
        class="item"
      >批量导入</el-button>
      <el-button
        type="warning"
        icon="el-icon-s-fold"
        v-permi="'/customer/returnCustomer'"
        @click="returnSalesBtn"
        :disabled="multipleSelection.length!==1"
        class="item"
      >退回售前</el-button>
      <el-button
        type="warning"
        icon="el-icon-document-copy"
        @click="isShowPotential=true"
        class="item"
        :disabled="!multipleSelection.length"
      >转移负责人</el-button>
      <!-- <el-button
        type="info"
        icon="el-icon-ship"
        class="item"
        :disabled="!multipleSelection.length"
        @click="showHigh"
      >转移至公海</el-button>-->
      <el-button
        type="success"
        icon="el-icon-message"
        @click="isShowSend=true"
        :disabled="!multipleSelection.length"
        class="item"
      >发送短信</el-button>
      <!-- <el-button type="success" icon="el-icon-message" @click="exportTable" class="item">导出表格</el-button> -->
      <el-dropdown trigger="click" @command="exportTable">
        <el-button type="warning" style="margin-left:10px;">
          导出表格
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item
            v-for="(item,index) in excNames"
            :key="index"
            :command="item"
          >{{item.name}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-row>
    <!-- 表格 -->
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      :fit="true"
      @selection-change="handleSelectionChange"
      highlight-current-row
    >
      <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
      <el-table-column
        prop="companyName"
        v-if="showCol.indexOf('公司名称')>-1"
        label="公司名称"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="address"
        v-if="showCol.indexOf('所属地区')>-1"
        label="所属地区"
        align="center"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="service.name"
        v-if="showCol.indexOf('业务类型')>-1"
        label="业务类型"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="customerGrade.name"
        v-if="showCol.indexOf('客户级别')>-1"
        label="客户级别"
        align="center"
      ></el-table-column>
      <el-table-column prop="telephone" v-if="showCol.indexOf('电话')>-1" label="电话" align="center"></el-table-column>
      <el-table-column
        prop="customerStatus.name"
        v-if="showCol.indexOf('客户状态')>-1"
        label="客户状态"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="createTime"
        width="170"
        v-if="showCol.indexOf('创建时间')>-1"
        label="创建时间"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="follow[0].contents"
        v-if="showCol.indexOf('最新跟进记录')>-1"
        label="最新跟进记录"
        show-overflow-tooltip
        align="center"
      >
        <!-- <template slot-scope="scope">
          <p>
            {{scope.row.follow[0]&&scope.row.follow[0].contents.slice(0,20)+'...'}}
          </p>
        </template>-->
      </el-table-column>
      <el-table-column
        prop="depart.name"
        v-if="showCol.indexOf('所属部门')>-1"
        label="所属部门"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="channel.name"
        v-if="showCol.indexOf('渠道来源')>-1"
        label="渠道来源"
        align="center"
      ></el-table-column>
      <el-table-column label="操作" width="160" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)"
            type="primary"
            v-permi="'/customer/updateCustomer'"
            plain
          >编辑</el-button>
          <el-button size="mini" @click="handleView(scope.$index, scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      class="itemPage"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="pageObj.pageNum"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageObj.total"
    ></el-pagination>

    <!-- 新建客户 -->
    <new-customer
      :isShowCreate="isShowCreate"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-customer>
    <!-- 批量上传 -->
    <upload-client
      :isShowClues="isShowClues"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></upload-client>

    <!-- 退回至售前 -->
    <return-sales
      :isShowSales="isShowSales"
      :seletedCustomer="multipleSelection[0]"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></return-sales>

    <!-- 转移负责人 -->
    <Potential
      :isShowPotential="isShowPotential"
      :PotentialId="selectIds"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></Potential>

    <!-- 发送短信 -->
    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>

    <!-- 编辑客户 -->
    <edit-customer
      :isShowEdit="isShowEdit"
      :customerId="customerId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-customer>
  </div>
</template>

<script>
// 客户管理
import API from "@/api/customer";
// import { watch } from 'fs'
import NewCustomer from "../components/NewCustomer"; // 新增客户
import UploadClient from "../components/UploadClient"; // 批量导入
import ReturnSales from "../components/ReturnSales"; // 退回至售前
import Potential from "../components/Potential"; // 转移负责人
import NewSend from "@/components/element/NewSend"; // 发送短信
import EditCustomer from "@/components/element/EditCustomer"; // 编辑客户
import { constants } from "crypto";
export default {
  name: "custormer",
  components: {
    NewCustomer,
    UploadClient,
    ReturnSales,
    Potential,
    NewSend,
    EditCustomer
  },
  data() {
    return {
      showMore: false, // 收起和展开
      customerType: [], // 客户类型
      customerLevel: [], // 客户级别
      creationTime: [], // 创建时间
      getsearchType: [
        { name: "业务类型", code: 1 },
        { name: "公司名称", code: 2 },
        { name: "客户搜索词", code: 3 },
        { name: "联系方式", code: 7 }
      ],
      showCol: [
        "公司名称",
        "所属地区",
        "业务类型",
        "客户级别",
        "客户状态",
        "创建时间",
        "最新跟进记录",
        "所属部门",
        "渠道来源"
      ],
      colOptions: [
        "公司名称",
        "所属地区",
        "业务类型",
        "客户级别",
        "客户状态",
        "创建时间",
        "最新跟进记录",
        "所属部门",
        "渠道来源"
      ],
      excNames: [
        { api: "exportCustomerExc", name: "客户表" },
        { api: "exportSignCustomerExc", name: "签单统计表" }
      ], // 表格名称
      typeActive: 0,
      gradeActive: 0,
      timeActive: 0,
      serviceActive: 0,
      region: [], // 地区
      departmentsList: [], // 所属部门
      headList: [], // 负责人
      customerSourceList: [], // 客户来源
      customerTypeList: [], // 业务类型
      definedDate: null,
      isShowCreate: false, // 新增弹框
      isShowClues: false, // 批量导入
      isShowSales: false, // 退回至售前
      isShowPotential: false, // 转移负责人
      isShowSend: false, // 发送短信
      isShowEdit: false, // 编辑
      customerId: "", // 编辑当前数据的id
      form: {
        department: "", // 所属部门
        followUser: "", // 负责人
        source: "", // 客户来源
        searchName: "", // 搜索
        searchType: "" //  搜索类型
      },
      tableData: [], // 表格的数据
      showDefinedDate: false, // 是否展示自定义时间段
      pageObj: {
        pageNum: 1, //当前第几页
        pageSize: 10, //每页默认显示几条
        total: 0 //总条数
      },
      codeType: null, // 点击客户类型传的code值
      codeLevel: null, // 点击客户级别传的code值
      codeTime: null, // 点击创建时间传的code值
      codeService: null, // 业务类型code
      startTime: null, // 点击是定义时间开始的值
      endTime: null, // 点击是定义时间结束的值
      province: null, // 省
      city: null, // 市
      multipleSelection: [] // 选中的
    };
  },
  methods: {
    colChange() {
      localStorage.setItem("customerTable", JSON.stringify(this.showCol));
    },
    // 封装table表格api
    tableApi() {
      let params = {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize,
        customerStatus: this.codeType,
        customerGrade: this.codeLevel,
        timeCode: this.codeTime,
        startTime: this.startTime,
        endTime: this.endTime,
        province: this.province,
        city: this.city,
        service: this.codeService,
        department: this.form.department,
        searchType: this.form.searchType,
        followUser: this.form.followUser,
        source: this.form.source,
        searchName: this.form.searchName
      };
      API.postList(params).then(res => {
        this.pageObj.total = res.data.total; //总条数
        res.data.list.forEach(item => {
          if (item.cityStr == null) {
            item.address = item.provinceStr;
          } else {
            item.address = item.provinceStr + item.cityStr;
          }
        });
        this.tableData = res.data.list;
      });
    },
    // 选择每页显示条数触发
    sizeChangePage(val) {
      this.pageObj.pageSize = val;
      this.tableApi(this.rows);
    },
    // 选择第几页触发
    currentChangePage(val) {
      this.pageObj.pageNum = val;
      this.tableApi();
    },
    // 客户类型
    changeClientType(index, code) {
      this.typeActive = index; //将点击的元素的索引赋值给变量
      this.codeType = code;
      this.tableApi(this.codeType);
    },
    // 客户级别
    changeClientGrade(index, code) {
      this.gradeActive = index; //将点击的元素的索引赋值给变量
      this.codeLevel = code;
      this.tableApi(this.codeLevel);
    },
    // 创建时间
    changeCreateTime(index, code) {
      this.startTime = this.endTime = "";
      this.definedDate = null;
      this.showDefinedDate = false;
      this.timeActive = index; //将点击的元素的索引赋值给变量
      this.codeTime = code;
      this.tableApi();
    },
    // 自定义时间
    timeCustom(val) {
      if (val != null) {
        this.startTime = val[0];
        this.endTime = val[1];
        this.timeCode = null;
        this.tableApi();
      }
    },
    // 地区组件里面的值改变返回的数据
    regionChange(value) {
      this.province = value[0];
      this.city = value[1];
      this.tableApi(this.province, this.city);
    },
    changeService(index, code) {
      this.serviceActive = index;
      this.codeService = code;
      this.tableApi();
    },
    // 改变所属部门
    departChange(val) {
      this.form.followUser = null;
      this.tableApi();
      if (!val) {
        this.headList = [];
        return;
      }
      // 根据选择的部门的id来获取负责人s
      API.getHead(val).then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.headList = res.data;
      });
    },
    querySearch(queryString, cb) {
      var restaurants = this.headList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    exportTable(obj) {
      let params = {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize,
        customerStatus: this.codeType,
        customerGrade: this.codeLevel,
        timeCode: this.codeTime,
        startTime: this.startTime,
        endTime: this.endTime,
        province: this.province,
        city: this.city,
        service: this.codeService,
        department: this.form.department,
        searchType: this.form.searchType,
        followUser: this.form.followUser,
        source: this.form.source,
        searchName: this.form.searchName
      };
      API[obj.api](params)
        .then(res => {
          console.log(res);

          var blob = new Blob([res], {
            type: "application/vnd.ms-excel;charset=utf-8"
          }); //指定格式为vnd.ms-excel
          var downloadElement = document.createElement("a");
          var href = window.URL.createObjectURL(blob); //创建下载的链接
          downloadElement.href = href;
          document.body.appendChild(downloadElement);
          downloadElement.setAttribute("download", obj.name + ".xls");
          downloadElement.click(); //点击下载
          document.body.removeChild(downloadElement); //下载完成移除元素
          window.URL.revokeObjectURL(href); //释放掉blob对象
        })
        .catch(err => {
          console.error(err);
        });
    },
    // table发生变化触发
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 转移至公海
    // showHigh() {
    //   this.$confirm(
    //     "移至公海后将无法在客户表中查看该客户信息，是否确认移至公海？",
    //     "提示",
    //     {
    //       confirmButtonText: "确定",
    //       cancelButtonText: "取消",
    //       type: "warning"
    //     }
    //   )
    //     .then(() => {
    //       let data = {
    //         ids: this.selectIds
    //       };
    //       API.posttoPublic(data)
    //         .then(res => {
    //           // console.log(res)
    //           this.$message({
    //             type: "success",
    //             message: "转移成功!"
    //           });
    //           this.tableApi();
    //         })
    //         .catch(err => {
    //           if (err.indexOf("权限") === -1) this.$message.warning(err);
    //         });
    //     })
    //     .catch(() => {
    //       this.$message({
    //         type: "info",
    //         message: "已取消转移"
    //       });
    //     });
    // },
    // 退回至售前判断
    returnSalesBtn() {
      let customer = this.multipleSelection[0];
      if (!customer.clueId) {
        this.$message({
          type: "error",
          message: "此用户无法退回售前"
        });
      } else {
        this.isShowSales = true;
      }
    },
    // 表格的编辑
    handleEdit(index, row) {
      // console.log(index, row)
      this.customerId = row.id;
      this.isShowEdit = true;
    },
    // 保存Dialog
    getSonSure() {
      this.getSonCancel();
      this.tableApi();
    },
    // 关闭Dialog
    getSonCancel() {
      this.isShowCreate = false;
      this.isShowClues = false;
      this.isShowSales = false;
      this.isShowPotential = false;
      this.isShowSend = false;
      this.isShowEdit = false;
    },
    // 表格的查看
    handleView(index, row) {
      this.$router.push({ path: "customerdetails", query: { id: row.id } });
    }
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("customerTable"));
    if (showCol) this.showCol = showCol;
    // 业务类型api
    API.getType().then(res => {
      let obj = { id: 0, name: "全部" };
      res.data.unshift(obj);
      this.customerTypeList = res.data;
    });
    // 客户类型api
    API.getCustomerType().then(res => {
      let data = res.data;
      data.unshift({
        code: 0,
        in: null,
        val: "全部类型"
      });
      this.customerType = res.data;
    });
    // 客户级别api
    API.getCustomerLevel().then(res => {
      let data = res.data;
      data.unshift({
        val: "全部级别",
        code: 0
      });
      this.customerLevel = data;
    });
    // 创建时间api
    API.getCreationTime().then(res => {
      let data = res.data;
      data.splice(7);
      this.creationTime = res.data;
    });
    // 地区的api
    API.getRegion().then(res => {
      res.data.forEach(item => {
        item.value = item.id;
        if (item.children.length == "0") {
          item.children = "";
        } else {
          item.children.forEach(item1 => {
            item1.value = item1.id;
          });
        }
      });
      this.region = res.data;
    });
    // 所属部门的api
    API.getDepartments().then(res => {
      this.departmentsList = res.data;
    });
    // // 所属负责人
    // API.getAllUser().then(res => {
    //   res.data.forEach(item => {
    //     item.value = item.name;
    //   });
    //   this.headList = res.data;
    // });
    // 渠道来源api
    API.getCustomerSource().then(res => {
      this.customerSourceList = res.data;
    });
    // 查询table接口，打开或刷新页面展示的数据
  },
  activated() {
    this.tableApi();
  },
  computed: {
    selectIds() {
      let arr = [];
      let multipleSelection = this.multipleSelection;
      for (const item of multipleSelection) {
        arr.push(item.id);
      }
      return arr.join(",");
    }
  }
};
</script>

