<template>
  <div>
    <el-dialog
      :model="form"
      :close-on-click-modal="false"
      title="编辑联系人"
      :visible.sync="dialogVisible"
      @close="closeDialog"
    >
      <el-form ref="form" :model="form" :rules="rules" label-width="120px">
        <el-form-item label="姓名" prop="name">
          <el-input v-model="form.name" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="所属客户" prop="customerId">
          <el-select
            v-model="form.customerId"
            disabled
            placeholder="请选择"
            class="item-css"
            filterable
          >
            <el-option
              v-for="(item, index) in restaurants"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile">
          <el-input v-model="form.mobile" disabled placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="固话" prop="telephone">
          <el-input v-model="form.telephone" maxlength="12" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select v-model="form.sex" placeholder="请选择" class="item-css">
            <el-option label="男" :value="1"></el-option>
            <el-option label="女" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="生日" prop="birthday">
          <el-date-picker
            v-model="form.birthday"
            value-format="yyyy-MM-dd"
            type="date"
            placeholder="选择生日"
            default-value="1980-1-1"
            class="item-css"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="地区" prop="regionArr">
          <el-cascader
            :options="region"
            placeholder="请选择"
            class="item-css"
            v-model="form.regionArr"
          ></el-cascader>
        </el-form-item>

        <el-form-item label="地址" prop="address">
          <el-input v-model="form.address" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="联系人部门" prop="depart">
          <el-input v-model="form.depart" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="联系人职务" prop="job">
          <el-input v-model="form.job" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="微信号" prop="wechat">
          <el-input v-model="form.wechat" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="QQ号" prop="qq">
          <el-input v-model="form.qq" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="form.email" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="remarks">
          <el-input
            v-model="form.remarks"
            type="textarea"
            show-word-limit
            maxlength="100"
            style="width:60%"
            :autosize="{ minRows: 4, maxRows: 10}"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success">保存</el-button>
          <el-button @click="closeDialog">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
// 新增联系人
import API from "@/api/linkman";

export default {
  name: "EditContact",
  props: ["isShowLinkmanEdit", "linkmanId","customerId"],
  data() {
    var validateTel = (rule, value, callback) => {
      var reg = /0\d{2}-\d{7,8}$/;
      if (!value || reg.test(value)) {
        callback();
      } else {
        callback(new Error("请输入正确号码"));
      }
    };
    var validateMobile = (rule, value, callback) => {
      if (/^\d{11}$/.test(value)) {
        callback();
      } else {
        callback(new Error("请输入正确号码"));
      }
    };
    return {
      rules: {
        name: [
          { required: true, message: "请输入联系人姓名", trigger: "blur" }
        ],
        mobile: [
          { required: true, validator: validateMobile, trigger: "blur" }
        ],
        telephone: [{ validator: validateTel, trigger: "blur" }],
        customerId: [
          { required: true, message: "请选择所属客户", trigger: "change" }
        ],
        address: [{ required: true, message: "请输入地址", trigger: "blur" }],
        sex: [{ required: true, message: "请选择性别", trigger: "change" }],
        birthday: [{ required: true, message: "请选择生日", trigger: "blur" }],
        regionArr: [
          {
            type: "array",
            required: true,
            message: "请选择所属地区",
            trigger: "change"
          }
        ]
      },
      dialogVisible: this.isShowLinkmanEdit,
      restaurants: [], // 所属客户
      region: [], // 地区
      form: {}
    };
  },
  methods: {
    // 关闭
    closeDialog() {
      this.$refs.form.resetFields();
      this.dialogVisible = false;
      this.$emit("closeDialogFather", this.dialogVisible);
    },
    // 保存
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let form = this.form;
        // let customerId =
        //   this.customerId ||
        //   this.restaurants.find(ele => ele.name === form.customerId).id;
        let params = {
          name: form.name,
          customerId: form.customerId,
          telephone: form.telephone,
          sex: form.sex,
          birthday: form.birthday,
          province: form.regionArr[0],
          city: form.regionArr[1],
          address: form.address,
          depart: form.depart,
          job: form.job,
          mobile: form.mobile,
          wechat: form.wechat,
          qq: form.qq,
          email: form.email,
          remarks: form.remarks,
          id: this.linkmanId
        };
        API.postupdateLinkman(params).then(res => {
          console.log(res);
          if (res.status == 200) {
            this.$message({
              message: "保存成功",
              type: "success"
            });
            this.$emit("sureDialogFather");
          }
        });
      });
    },
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    }
  },
  watch: {
    isShowLinkmanEdit(value) {
      this.dialogVisible = this.isShowLinkmanEdit;
      if (!value) return;

      Promise.all([
        API.getCustomerByCompanyName(),
        API.getLinkmanById(this.linkmanId)
      ])
        .then(res => {
          let restaurants = res[0].data;
          // 客户列表赋值
          restaurants.map(item => {
            item.value = item.name;
          });
          this.restaurants = restaurants;
          let formData = res[1].data;

          formData.regionArr = [formData.province, formData.city];
          
          // this.form.customerId = this.customerId || formData.customerId;
          this.form = formData;
          this.$nextTick(() => {
            this.$refs.form.clearValidate();
          });
        })
        .catch(err => {
          console.error(err);
        });
      // 地区的api
      API.getRegion().then(res => {
        res.data.forEach(item => {
          item.value = item.id;
          if (item.children.length == "0") {
            item.children = "";
          } else {
            item.children.forEach(item1 => {
              item1.value = item1.id;
            });
          }
        });
        this.region = res.data;
      });
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>