<template>
  <!-- 渠道管理 -->
  <div>
    <el-tabs type="card" @tab-click="tabChange" v-model="tabName">
      <el-tab-pane label="销售渠道" name="sell">
        <tab-sell ref="tabSell"></tab-sell>
      </el-tab-pane>

      <el-tab-pane label="支付渠道" name="payment">
        <tab-payment ref="tabPayment"></tab-payment>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import API from "@/api/ditch";
import TabSell from "./components/TabSell";
import TabPayment from "./components/TabPayment";
export default {
  name: "ditch",
  components: {
    TabSell,
    TabPayment
  },
  data() {
    return {
      tabName: "sell", // 默认显示销售渠道
      isShowChannel: false,
      paymentTableData: []
    };
  },
  methods: {
    tabChange() {
      if (this.tabName === "sell") {
        this.$refs.tabSell.getChannelList();
      } else {
        this.$refs.tabPayment.getPaymentList();
      }
    }
  },

};
</script>

<style lang="scss" scoped>



/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>