<template>
  <div>
    <!-- 编辑回款计划 -->
    <el-dialog title="编辑回款计划" :visible.sync="outerVisible" @close="closeDialog" append-to-body>
      <el-form :model="form" label-width="120px" :rules="rules" ref="form">
        <el-form-item label="所属客户" prop="customerId">
          <el-select v-model="form.customerId" placeholder="请选择" disabled>
            <el-option
              v-for="(item, index) in clientList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="合同标题" prop="contractId">
          <el-select v-model="form.contractId" placeholder="请选择" disabled>
            <el-option
              v-for="(item, index) in contractList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="计划回款金额" prop="price">
          <el-input v-model="form.price" placeholder="请输入内容" style="width:50%"></el-input>
        </el-form-item> 
        <!-- <el-form-item label="剩余未付总额">
          <el-input v-model="form.residue"  placeholder="请输入内容" style="width:50%"></el-input>
        </el-form-item>-->
        <el-form-item label="计划回款日期" prop="opeDate">
          <el-date-picker v-model="form.opeDate" type="date" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>
    

        <el-form-item>
          <el-button type="success" @click="sureDialog">完成</el-button>

          <el-button type="info" @click="closeDialog">取消</el-button>
        </el-form-item>

        <!-- <el-divider v-show="form.showTable"></el-divider> -->
        <!-- v-if="form.showTable" -->
        <el-table
          v-if="false"
          :data="tableData"
          style="width: 100%"
          highlight-current-row
          border
          stripe
        >
          <el-table-column type="index" label="期数" width="65" align="center"></el-table-column>

          <el-table-column prop="date" label="计划回款日期" align="center">
            <template slot-scope="scope">
              <el-date-picker
                :disabled="!scope.row.edit"
                :picker-options="scope.row.startDatePicker"
                v-model="scope.row.opeDate"
                value-format="yyyy-MM-dd HH:mm:ss"
                type="date"
                placeholder="选择日期"
              ></el-date-picker>
            </template>
          </el-table-column>

    
          <el-table-column prop="price" width="130" label="计划回款金额" align="center">
            <template slot-scope="scope">
              <el-input
                class
                v-model="scope.row.price"
                :disabled="!scope.row.edit"
                placeholder="请输入"
              ></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="residue" width="140" label="剩余未回款金额" align="center">
            <template slot-scope="scope">
              <div
                v-if="form.editTable"
              >{{scope.$index == tableData.length - 1?editBalance(scope.$index, scope.row): scope.row.residue}}</div>
              <div v-else>{{echoBalance(scope.$index, scope.row)}}</div>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="100" align="center">
            <template slot-scope="scope">
              <div v-if="!scope.row.edit">
                <el-button
                  type="text"
                  icon="el-icon-edit"
                  :title="scope.row.isOver?'此期已完成':null"
                  :disabled="Boolean(scope.row.isOver)"
                  @click="handleEdit(scope.$index, scope.row)"
                >修改</el-button>
              </div>
              <el-button v-else @click="finish(scope.$index,scope.row)" type="success">完成</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-form-item v-if="tableData.length">
          <el-button
            :disabled="tableData[tableData.length-1].residue!==0"
            style="margin: 20px 20px 0 0"
            type="success"
            @click="sureDialog"
          >提交</el-button>
          <el-button type="info" @click="closeDialog">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/payment";
/**
 *
 *  此页面编辑表格的代码逻辑比较复杂，
 *  如果不是必须修改需求那么尽量别动，
 *  写逻辑代码花了四五个小时,
 *  如果你迫不得已需要修改，那么请从html文件的判断开始看起，且最好备份一下此文件
 *  与新增不同的是：在回显和编辑两个状态运用了两个不同的computed
 *  回显使用echoBalance， 编辑使用editBalance
 *  关于editBalance请查看./NewReceivableplan.vue的computed
 *  每个方法都有步骤说明供参考
 */
// 编辑回款计划
export default {
  name: "EditReceivableplan",
  props: {
    isEditReceivableplan: {
      type: Boolean,
      default: false
    },
    editPlanData: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {
      rules: {
        customerId: [
          { required: true, message: "请选择客户", trigger: "change" }
        ],
        contractId: [
          { required: true, message: "请选择合同", trigger: "change" }
        ],
        departId: [
          { required: true, message: "请选择负责收款部门", trigger: "change" }
        ],
        userId: [
          { required: true, message: "请选择负责人", trigger: "change" }
        ],
        price: [
          { required: true, message: "输入计划回款金额", trigger: "blur" }
        ],
        opeDate: [
          { required: true, message: "请选择计划回款日期", trigger: "change" }
        ]
      },
      outerVisible: this.isEditReceivableplan,
      form: {
        value: "",
        contractId: "",
        totalPrice: "",
        signDate: "",
        // periodsNum: 1,
        customerId: "",
        departId: "",
        showTable: false, //  是否展示可输入表格
        userId: "",
        editTable: false
      },
      tableData: [],
      clientList: [], // 客户列表
      contractList: [], // 查询到的合同列表
      departmentsList: [], // 部门列表
      userList: [] // 负责人列表
    };
  },
  methods: {
    // 编辑按钮点击
    handleEdit(index, row) {
      this.$confirm("此期和之后的期数会全部删除", "修改计划", {
        distinguishCancelAndClose: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消"
      })
        .then(() => {
          // 1 点击编辑后，echoBalance便不再使用
          // 因为editBalance的逻辑，需要把计算出的余额赋值给tableData的每一条
          let residue = this.form.residue;
          // 2 循环tableData，用于计算每条余额
          for (let i = 0; i < this.tableData.length; i++) {
            // 3 设置一个计数器，累加第n条以及之前的还款金额
            let num = 0;
            // 4 开始累加自身以及之前的金额
            for (let j = 0; j <= i; j++) {
              num += this.tableData[j].price;
            }
            // 5 计算出差值后赋值给当前行的余额
            // 其实这么写并不聪明，做了很多重复计算，
            // 应该取上一行计算后的余额，奈何大脑不够用，逻辑算法搞不懂
            this.tableData[i].residue = residue - num;
          }
          // 6 然后从需要编辑的地方重写
          this.tableData.splice(index);
          // 7 此值改变之后html里最后一行会使用editBalance，如果不是最后一行则使用刚刚赋的值
          this.form.editTable = true;
          // 8 添加新的空行，空行的余额由editBalance实时计算
          this.addPeriods();
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 添加期次
    addPeriods() {
      let residue, startDatePicker;
      let length = this.tableData.length;
      // 如果不是第一期那么未还款金额为上一期剩下的
      if (length) {
        residue = this.tableData[length - 1].residue;
        let that = this;
        startDatePicker = {
          // 跟进事件只能选择今天之后的日期
          disabledDate(time) {
            if (that.tableData[length - 1]) {
              return (
                time.getTime() <
                new Date(that.tableData[length - 1].opeDate).getTime()
              ); //如果没有后面的-8.64e6就是不可以选择今天的
            }
          }
        };
      } else {
        // 否则是表单中全部未还款金额
        residue = this.form.residue;
        startDatePicker = {
          // 跟进事件只能选择今天之后的日期
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7; //如果没有后面的-8.64e6就是不可以选择今天的
          }
        };
      }
      this.tableData.push({
        edit: true,
        price: residue, // 计划回款金额
        opeDate: "", // 计划回款日期
        residue, // 剩余未回款
        startDatePicker
      });
    },
    // 新增计划的完成按钮
    finish(index, row) {
      let { price, opeDate } = row;
      // 下一栏的剩余金额
      let residue = row.residue - row.price;
      // 判断是都输入正确
      if (!price || !opeDate || residue < 0) {
        this.$message.info("请正确填写计划回款日期和计划回款金额！");
        return;
      }
      // 当前栏显示的未还款余额
      this.tableData[index].residue = residue;
      // 如果有未还款那么增加新的一栏
      if (row.residue > 0) {
        this.addPeriods();
      }
      this.tableData[index].edit = false;
    },

    // 所属负责人
    userChange(item) {
      if (item) {
        this.form.departId = item.departId;
      }
    },
    departChange() {
      let val = this.form.departId;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.userList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.userList = res.data;
        });
      }
      this.form.userId = null;
    },

    userSearch(queryString, cb) {
      var restaurants = this.userList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    sureDialog() {
      let form = this.form;
      let userId = this.userList.find(ele => ele.value === form.userId).id;
      // let arr = [];
      // this.tableData.map(ele => {
      //   arr.push({
      //     price: ele.price,
      //     opeDate: ele.opeDate,
      //     id: ele.id
      //   });
      // });
      let params = {
        contractId: form.contractId,
        departId: form.departId,
        customerId: form.customerId,
        userId,
        opeDate: form.opeDate,
        price: form.price,
        id: form.id
        // planPayments: JSON.stringify(arr)
      };
      console.log(params);
      API.updateContractPaymentPlan(params)
        .then(res => {
          console.log(res);
          this.$message({
            type: "success",
            message: "编辑成功"
          });
          this.$emit("sureDialogFather");
          this.closeDialog();
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 清空所属客户
    clearCustormer() {
      this.form.contractId = "";
      this.contractList = [];
    },
    // 清除负责人
    clearFollowUser() {
      if (this.form.departId) {
        this.departChange();
      }
    },

    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$refs.form.resetFields();
      this.form = {
        value: "",
        contractId: "",
        totalPrice: "",
        signDate: "",
        customerId: "",
        departId: "",
        editTable: false,
        showTable: false, //  是否展示可输入表格
        userId: ""
      };
      this.tableData = [];
      this.$emit("closeDialogFather");
    }
  },
  watch: {
    isEditReceivableplan(val) {
      this.outerVisible = this.isEditReceivableplan;
      if (!val) return;
      let data = Object.assign({}, this.editPlanData);
      console.log(data);
      this.form = {
        customerId: data.customerId,
        contractId: data.contractId,
        departId: data.departId,
        userId: data.userName,
        opeDate: data.opeDate,
        price: data.price,
        id: data.id
      };
      // 查询表格数据回显
      // API.getPaymentListByContractId({ contractId: data.contractId }).then(
      //   res => {
      //     console.log(res.data);
      //     this.tableData = res.data;
      //   }
      // );

      // 查询客户数据
      API.getContractByCustomerId(data.customerId)
        .then(res => {
          this.contractList = res.data;          
          // let item = res.data.find(ele => ele.id === data.contractId);
          // this.form.totalPrice = item.totalPrice;
          // this.form.signDate = item.signDate;
          // this.form.payment = item.payment;
          // this.form.residue = item.totalPrice - item.payment;
          // this.form.showTable = true;
        })
        .catch(err => {
          console.error(err);
        });
      // 所属客户
      API.getCustomerByCompanyName().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.clientList = res.data;
      });
      // 所有的负责人
      API.getAllUser().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.userList = res.data;
      });
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
    }
  },
  computed: {
    //  点击编辑期次后使用这个
    editBalance() {
      // 显示未还余额
      return (index, row) => {
        let residue;
        let length = this.tableData.length;
        if (length > 1) {
          residue = this.tableData[length - 2].residue;
        } else {
          residue = this.form.residue;
        }

        let result = residue - this.tableData[length - 1].price;
        if (result < 0) {
          return "回款金额过大";
        } else {
          return result;
        }
      };
    },
    // 如果没有点击编辑期次，那么用这个回显
    echoBalance() {
      return (index, row) => {
        // 1 算出未还款总额
        let residue = this.form.residue;
        // 2 计数器来计算此前每次计划还款的累加和
        let num = 0;
        // 3 循环累加此次之前的总额
        for (let i = 0; i < index; i++) {
          num += this.tableData[i].price;
        }
        // 4 未还款总额减去之前已还款总额，然后把此期还款的额度也减去，实时计算剩余未还款
        return residue - num - row.price;
      };
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  // .el-input:not(.el-input--suffix) {
  //   width: 50%;
  // }
}
</style>