<template>
  <div>
    <el-dialog title="上传附件" :visible.sync="outerVisible" width="40%" @close="closeDialog">
      <el-form ref="form" :model="form" label-width="90px">
        <el-form-item label="附件名称">
          <el-upload
            class="upload-demo"
            :on-success="uploadSuccess"
            ref="upload"
            :action="$store.state.baseURL+'annex/addAnnex'"
            :file-list="fileList"
            :auto-upload="false"
            :data="form"
            :on-error="uploadError"
          >
            <el-button class="item" type="warning" icon="el-icon-plus">选择文件</el-button>
            <div slot="tip" class="el-upload__tip">单个文件最大支持50MB,上传请耐心等待</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="备注" prop="remarks">
          <el-input
            v-model="form.remarks"
            type="textarea"
            placeholder="请输入内容"
            show-word-limit
            :maxlength="100"
            :autosize="{ minRows: 4, maxRows: 8}"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="$refs.upload.submit()" type="success">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 上传附件
export default {
  name: "upload",
  // -------是否展示--------当前详情页的id
  props: ["isShowUpload", "linkId"],
  data() {
    return {
      outerVisible: this.isShowUpload,
      fileList: [], //上传的文件列表
      form: {
        remarks: "",
        linkType: 0
      }
    };
  },
  methods: {
    uploadError() {
      this.$message({
        type: "error",
        message: "上传失败"
      });
    },
    uploadSuccess(res) {
      if (res.status !== 200) {
        this.$message({
          type: "info",
          message: res.msg
        });
        return;
      }
      this.fileList = [];
      this.$refs.form.resetFields();
      this.$message({
        type: "success",
        message: "保存成功"
      });
      this.$emit("sureDialogFather");
      this.closeDialog();
    },
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather");
    }
  },
  watch: {
    isShowUpload(val) {
      this.outerVisible = this.isShowUpload;
      if (!val) return;
      this.form.linkId = this.linkId;
    }
  }
};
</script>
<style lang='scss' scoped>
</style>