<template>
  <div class="department">
    <div class="department-left">
      <div class="left-title">
        <p>部门架构</p>
        <el-button type="primary" size="mini" v-permi="'/depart/addDepart'" @click="isAddDepartment = true; addChildDepart=false">
          <span>新增一级部门</span>
        </el-button>
      </div>
      <div class="tree">
        <el-tree :data="treeData" default-expand-all :props="defaultProps" @node-click="departNodeClick" node-key="id"></el-tree>
      </div>
    </div>
    <div class="department-right">
      <div class="right-title">
        <p>{{addChildDepart.name}}</p>
        <el-button v-permi="'/depart/addDepart'"  @click="isAddDepartment = true" type="primary" size="mini">
          <span>新增子级部门</span>
        </el-button>
      </div>
      <div class="form-list">
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column prop="departType" label="所属类别" width align="center"></el-table-column>
          <el-table-column prop="departNum" label="部门人数" width align="center"></el-table-column>
          <el-table-column prop="departAllNum" label="部门总人数" align="center" min-width="100"></el-table-column>
          <el-table-column prop="createTime" label="创建时间" width="200" align="center" class="c1"></el-table-column>
          <el-table-column fixed="right" label="操作" align="center" width="350">
            <template slot-scope="scope">
              <el-button size="mini" type="primary" plain @click="editDepartment( scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" v-permi="'/depart/removeDepart'" @click="deleteDepart( scope.row)">删除</el-button>
              <el-button size="mini" @click="examineMember( scope.row)">查看成员</el-button>
              <el-button size="mini" @click="addMember( scope.row)">操作成员</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>

    <!-- 添加部门 -->
    <add-department
      :departmentList="departmentList"
      @sureDialogFather="getTreeData();getDepartList()"
      :isAddDepartment="isAddDepartment"
      :addChildDepart="addChildDepart.id"
      @closeDialog="isAddDepartment=false"
    ></add-department>
    <!-- 编辑部门 -->
    <edit-department
      :departmentList="departmentList"
      @sureDialogFather="getTreeData();getDepartList()"
      :isEditDepartment="isEditDepartment"
      :departmentDetail="departmentDetail"
      @closeDialog="isEditDepartment=false"
    ></edit-department>

    <!-- 查看成员 -->
    <examine-member
      @closeDialog="isExamineMember=false"
      :departmentDetail="departmentDetail"
      :isExamineMember="isExamineMember"
      :roleList="roleList"
      @addMember="isExamineMember=false;isAddMember=true"
    ></examine-member>

    <!-- 操作成员 -->
    <add-member
      @closeDialog="isAddMember=false"
      :roleList="roleList"
      :isAddMember="isAddMember"
      :departmentDetail="departmentDetail"
    ></add-member>
  </div>
</template>

<script>
import AddDepartment from "../components/AddDepartment";
import EditDepartment from "../components/EditDepartment";
import ExamineMember from "../components/ExamineMember";
import AddMember from "../components/AddMember";
import * as filters from "@/filters/index";
import API from "@/api/admin";
export default {
  name: "department",
  components: { AddDepartment, EditDepartment, ExamineMember, AddMember },
  data() {
    // const generateData = _ => {
    //   const dataform = [];
    //   for (let i = 1; i <= 15; i++) {
    //     dataform.push({
    //       key: i,
    //       label: `备选项 ${i}`
    //       // disabled: i % 4 === 0
    //     });
    //   }
    //   return dataform;
    // };
    
    return {
      // 是否新增子级部门
      addChildDepart: {},
      // data: generateData(),
      isEditDepartment: false, // 打开编辑按钮
      isAddDepartment: false, // 打开添加按钮
      isExamineMember: false, // 打开查看成员按钮
      isAddMember: false, // 是否展示添加人员
      departmentDetail: {}, // 部门编辑回显数据
      roleList: [], // 角色列表
      list: [],
      treeData: [],
      departmentList: [],
      dialogFormVisible3: false,
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      tableData: [],
      // 树节点关键字
      defaultProps: {
        children: "departTrees",
        label: "name"
      }
    };
  },
  methods: {
    handleChange(value, direction, movedKeys) {
      console.log(value, direction, movedKeys);
    },
    //左边树节点点击
    departNodeClick(data) {
      console.log(data);

      // 如果当前展示的id与点击的id一样那么不做操作
      if (this.addChildDepart.id === data.id) return;
      this.addChildDepart = data;
      API.findDepart({ id: data.id })
        .then(res => {
          let data = res.data;
          console.log(data);

          data.createTime = filters.parseTime(new Date(data.createTime));
          this.tableData = [res.data];
        })
        .catch(err => {
          console.error(err);
        });
    },

    //右边操作按钮
    editDepartment(row) {
      this.departmentDetail = row;
      this.isEditDepartment = true;
    },
    // 查看成员
    examineMember(row) {
      this.departmentDetail = row;
      this.isExamineMember = true;
    },
    // 操作成员
    addMember(row) {
      this.departmentDetail = row;
      this.isAddMember = true;
    },
    // 删除部门
    deleteDepart(row) {
      let id = row.id;
      this.$confirm("是否删除所选部门？", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      }).then(() => {
        API.removeDepart({ id })
          .then(res => {
            this.tableData = [];
            this.$message({
              type: "success",
              message: "删除成功"
            });
            this.getTreeData();
          })
          .catch(err => {
            console.error(err);
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });

      // function findNode(treeArr, targetId) {
      //   for (let i = 0; i < treeArr.length; i++) {
      //     if (treeArr[i].id == targetId) {
      //       return treeArr[i];
      //     } else if (Array.isArray(treeArr[i].children)) {
      //       findNode(treeArr[i].children, targetId);
      //     }
      //   }
      // }
      // let node = findNode(this.treeData, id);
      // console.log(node);
    },
    // 获取角色列表
    getRoleList() {
      API.getRoleList()
        .then(res => {
          this.roleList = res.data;
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 获取扁平部门列表
    getDepartList() {
      API.findDeparts()
        .then(res => {
          this.departmentList = res.data;
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 获取部门树结构
    getTreeData() {
      API.treeData({ pid: 0 })
        .then(res => {
          let data = res.data;
          this.treeData = data;
        })
        .catch(err => {
          console.error(err);
        });
    }
  },
  mounted() {
    this.getDepartList();
    this.getTreeData();
    this.getRoleList();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.fstyle {
  /deep/ div {
    display: flex;
  }
}
.formlist {
  margin-top: 40px;
}
.department {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  .el-dialog {
    width: 40%;
  }
  .department-left {
    width: 30%;

    .left-title {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      font-size: 14px;
      background: #e4e4e4;
      padding: 12px;
      button {
        height: 32px;
      }
      p {
        line-height: 32px;
      }
    }

    .tree {
      border: 1px solid #e4e4e4;
      height: 100%;
      padding: 20px;
      /deep/ .el-tree-node {
        margin-bottom: 4px;
      }
    }
  }
  .department-right {
    width: 66%;
    /deep/ .c1 {
      min-width: 370px;
    }
    .right-title {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      font-size: 14px;
      background: #e4e4e4;
      padding: 12px;
      border: 1px solid #e4e4e4;
      button {
        height: 32px;
      }
      p {
        line-height: 32px;
      }
    }
  }
}
</style>
