import fetch from '@/utils/fetch';

export default {
	// 地区api
	getRegion(data) {
		return fetch({
			url: 'area/all',
			method: 'get',
			params: data
		})
	},

	// 创建时间
	getCreationTime(data) {
		return fetch({
			url: `customer/findCustomerTimeCode`,
			method: 'get',
			params: data
		})
	},

	// 获取投诉列表
	getAdviseList(data) {
		return fetch({
			url: `aftersales/getAdviseList`,
			method: 'post',
			params: data
		})
	},

	// 创建时间
	toOther(data) {
		return fetch({
			url: `aftersales/toOther`,
			method: 'get',
			params: data
		})
	},

	// 修改投诉状态
	updateAdviseStatus(data) {
		return fetch({
			url: `aftersales/updateAdviseStatus`,
			method: 'post',
			params: data
		})
	},
	// 投诉处理
	addAdviseFollow(data) {
		return fetch({
			url: `aftersales/addAdviseFollow`,
			method: 'post',
			params: data
		})
	},
	// 查看处理列表
	getAdviseFollow(data) {
		return fetch({
			url: `aftersales/getAdviseFollow`,
			method: 'post',
			params: data
		})
	},



	// 获取售后列表
	findAllCustomer(data) {
		return fetch({
			url: 'aftersales/findAllCustomer',
			method: 'post',
			params: data
		})
	},
	// 所属负责人
	getAllUser(data) {
		return fetch({
			url: `customer/findAllUser`,
			method: 'get',
			params: data
		})
	},
	// 删除投诉与建议
	deleteAdvise(data) {
		return fetch({
			url: `aftersales/deleteAdvise`,
			method: 'get',
			params: data
		})
	},
	// 根据选择的所属部门的id来获取负责人
	getHead(data) {
		return fetch({
			url: `customer/findAllDepartUser/${data}`,
			method: 'get'
		})
	},
	// 所属部门的api
	getDepartments(data) {
		return fetch({
			url: `customer/findAllDepart`,
			method: 'get',
			params: data
		})
	},
	// 回访数据
	getVisitList(data) {
		return fetch({
			url: `aftersales/getVisitList`,
			method: 'post',
			params: data
		})
	},
	// 添加投诉或建议
	addAdvise(data) {
		return fetch({
			url: `aftersales/addAdvise`,
			method: 'post',
			params: data
		})
	},
	// 业务类型
	getType(data) {
		return fetch({
			url: `customer/service/type`,
			method: 'get',
			params: data
		})
	},

	// 转移至公海
	posttoPublic(data) {
		return fetch({
			url: `customer/toPublic`,
			method: 'post',
			params: data
		})
	},

	// 根据表格id查询客户全景的当条数据
	getCustomerDetails(data) {
		return fetch({
			url: `customer/findCustomerDetails/${data}`,
			method: 'get'
		})
	},

	// 根据表格id查询联系人
	getCustomerLinkman(data) {
		return fetch({
			url: `customer/findCustomerLinkman/${data}`,
			method: 'get'
		})
	},

	// 根据表格id查询机会
	getChanceByCustomerId(data) {
		return fetch({
			url: `customer/findChanceByCustomerId/${data}`,
			method: 'get'
		})
	},
	// 根据表格id查询合同
	getContractByCustomerId(data) {
		return fetch({
			url: `customer/findContractByCustomerId/${data}`,
			method: 'get'
		})
	},
}