<template>
  <div>
    <el-dialog
      :close-on-click-modal="false"
      title="编辑管理员"
      @close="closeDialog"
      :visible.sync="dialogFormVisible"
    >
      <el-form :model="form" :rules="rules" ref="form" :label-width="formLabelWidth">
        <el-form-item label="管理员名称" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="管理员手机号" prop="telnum">
          <el-input v-model="form.telnum"></el-input>
        </el-form-item>
        <!-- <el-form-item label="系统登录密码" prop="password">
          <el-input disabled v-model="form.password"></el-input>
        </el-form-item>-->
        <el-form-item label="所属部门" prop="departId">
          <el-select v-model="form.departId" placeholder="请选择所属部门">
            <el-option
              v-for="(item,index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="职务" prop="postId">
          <el-select v-model="form.postId" placeholder="请选择职务">
            <el-option
              v-for="(item, index) in postitionList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="角色" prop="roleId">
          <el-select v-model="form.roleId" placeholder="请选择角色">
            <el-option
              v-for="(item,index) in roleList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注">
          <el-input v-model="form.remarks" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="success" @click="sureDialog">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/admin";

export default {
  name: "EditAdministrator",
  //    ------是否开启dialog----------部门列表-------角色列表--------职位列表-------------管理员详情
  props: [
    "isEditAdministrator",
    "departmentsList",
    "roleList",
    "postitionList",
    "administratorDetail"
  ],
  data() {
    return {
      formLabelWidth: "120px",
      dialogFormVisible: this.isEditAdministrator,
      form: {},
      rules: {
        name: [
          { required: true, message: "请输入管理员名称", trigger: "blur" }
        ],
        telnum: [
          { required: true, message: "请输入管理员手机号", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入系统登陆密码", trigger: "blur" }
        ],
        departId: [
          { required: true, message: "请选择所属部门", trigger: "change" }
        ],
        postId: [{ required: true, message: "请选择职务", trigger: "change" }],
        roleId: [{ required: true, message: "请选择角色", trigger: "change" }]
      }
    };
  },
  methods: {
    // 提交
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let params = this.form;
        console.log(params);
        API.updateUser(params)
          .then(res => {
            this.$message({
              type: "success",
              message: "编辑成功"
            });
            this.closeDialog();
            this.$emit("sureDialogFather");
            console.log(res);
          })
          .catch(err => {
            console.error(err);
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    },
    // 关闭
    closeDialog() {
      this.dialogFormVisible = false;
      this.$emit("closeDialogFather");
    }
  },
  watch: {
    isEditAdministrator(value) {
      this.dialogFormVisible = this.isEditAdministrator;
      if (!value) return;
      this.form = Object.assign({}, this.administratorDetail);
      this.$nextTick(() => {
        this.$refs.form.clearValidate();
      });
    }
  }
};
</script>
