<template>
  <div>
    <div class="ditch-btn">
      <el-button type="primary" @click="showPaymentDialog=true" v-permi="'/channel/addChannel'">新增渠道</el-button>
      <el-button type="danger" :disabled="!selectedItem.length" @click="delChannel">删除</el-button>
    </div>
    <div>
      <el-table
        :data="paymentTableData"
        style="width: 100%;margin-bottom: 20px;"
        @selection-change="handleSelectionChange"
        row-key="id"
        border
      >
        <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
        <el-table-column label="渠道名称" prop="name" width align="center"></el-table-column>
        <el-table-column label="操作" width="200" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              plain
              v-permi="'/channel/updateChannel'"
              @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button>
            <el-button
              size="mini"
              type="primary"
              v-show="scope.row.children"
              v-permi="'/channel/addChildrenChannel'"
              @click="addChildChannel(scope.$index, scope.row)"
            >增加子渠道</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div>
        <el-pagination
          class="itemPage"
          @size-change="sizeChangePage"
          @current-change="currentChangePage"
          :current-page="pageObj.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pageObj.totalSize"
        ></el-pagination>
      </div>
    </div>
    <el-dialog title="新增支付渠道" :visible.sync="showPaymentDialog">
      <el-form :model="addPaymentForm" ref="paymentForm" label-width="100px">
        <el-form-item
          label="渠道名称"
          prop="name"
          :rules="[{ required: true, message: '请填写名称',trigger:'blur'}]"
        >
          <el-input placeholder="请输入" v-model="addPaymentForm.name"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="surePaymentDialog" type="success">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/ditch";
export default {
  name: "TabPayment",
  data() {
    return {
      selectedItem: [], // 被选中的条目
      paymentTableData: [],
      pageObj: {
        pageNum: 1,
        pageSize: 10,
        totalSize: null
      },
      addPaymentForm: {
        name: null
      },
      paymentDetail: null,
      showPaymentDialog: false,
      isShowEdit:false
    };
  },
  methods: {
    surePaymentDialog() {
      this.$refs.paymentForm.validate(valid => {
        if (!valid) return;
        let params = { name: this.addPaymentForm.name };
        API.addPayAccount(params)
          .then(res => {
            this.$message.success("添加成功");
            this.$refs.paymentForm.resetFields();
            this.showPaymentDialog = false;
            this.getPaymentList();
          })
          .catch(err => {
            console.error(err);

            if (err.indexOf("权限") == -1) this.$message.error(err);
          });
      });
    },
    // 当选择项发生变化时会触发该事件
    handleSelectionChange(val) {
      this.selectedItem = val;
    },
    sizeChangePage() {
      this.pageObj.pageSize = e;
      this.getPaymentList();
    },
    currentChangePage() {
      this.pageObj.pageNum = e;
      this.getPaymentList();
    },
    //编辑触发按钮
    handleEdit(index, row) {
      this.isShowEdit = true;
      this.paymentDetail = row;
    },
    getPaymentList() {
      let params = {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize
      };
      API.getPayAccountList(params).then(res => {
        let data = res.data;
        this.paymentTableData = data.list;
        this.pageObj.totalSize = data.total;
      });
    },
    // 删除渠道
    delChannel() {
      /* this.$confirm("是否确认删除所选渠道？", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      })
        .then(() => {
          let arr = [];
          let selectedItem = this.selectedItem;
          for (const item of selectedItem) {
            arr.push(item.id);
          }
          let params = { ids: arr.join(",") };
          API.delChannel(params)
            .then(res => {
              console.log(res);
              this.$message({
                type: "success",
                message: "删除成功"
              });
              this.getPaymentList();
            })
            .catch(err => {
              console.error(err);
              if (err.indexOf("权限") === -1) this.$message.warning(err);
            });
        })
        .catch(err => {});*/
    }
  },
  mounted(){
    this.getPaymentList();
  }
};
</script>


<style lang="scss" scoped>
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
.ditch-btn {
  margin: 20px 0;
}
</style>
