<template>
  <div>
    <el-form label-width="120px" v-if="flag">
      <el-row>
        <el-col :span="24" class="list">
          <el-form-item label="生日 : ">
            <ul>
              <li
                v-for="(item, index) in creationTime"
                :key="index"
                @click="showStyle(index, item.code)"
                :class="timeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
              <!-- <li
                @click="showDefinedTime = true;timeActive = -1"
                class="list-li"
                v-if="!showDefinedTime"
              >自定义时间段</li>
              <li v-else>
                <el-date-picker
                  v-model="form.definedTime"
                  @change="timeCustom"
                  type="daterange"
                  value-format="yyyy-MM-dd"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                ></el-date-picker>
              </li>-->
            </ul>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-col :span="8">
            <el-form-item label="所属部门">
              <el-select
                v-model="form.department"
                placeholder="请选择"
                clearable
                class="item-css"
                @change="depart($event)"
              >
                <el-option
                  v-for="(item, index) in departmentsList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col :span="8">
            <el-form-item label="所属负责人">
              <el-select
                v-model="form.followUser"
                placeholder="请选择"
                clearable
                :no-data-text="form.department?'此部门暂无成员':'请选择部门'"
                class="item-css"
                @change="tableApi()"
              >
                <el-option
                  v-for="(item, index) in headList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-col>
        <el-col :span="8">
          <el-form-item label="地区 : ">
            <el-cascader
              :options="options"
              placeholder="请选择"
              v-model="form.regionArr"
              class="item-css"
              clearable
              @change="tableApi()"
            ></el-cascader>
          </el-form-item>
        </el-col>

        <el-col :span="10">
          <el-form-item label="搜索" class="input-search">
            <el-col :span="14">
              <el-input
                v-model="form.companyName"
                @clear="tableApi()"
                placeholder="请输入用户"
                clearable
              ></el-input>
            </el-col>
            <el-col :span="6">
              <el-button type="primary" @click="tableApi()">查询</el-button>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <!-- <el-row>
      <el-col :span="24" style="font-size:20px;text-align:center;">
        <i
          style="cursor: pointer;"
          :class="flag == true? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
          @click="show"
        ></i>
      </el-col>
    </el-row>-->
    <el-row style="margin-top:20px;">
      <!-- 筛选 -->
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="primary"
        icon="el-icon-folder-add"
        @click="showLinkman"
        v-permi="'/customer/addLinkman'"
      >新增联系人</el-button>
      <el-button
        type="danger"
        icon="el-icon-delete"
        @click="deleteLinkman"
        :disabled="!checkboxId.length"
      >删除</el-button>
      <el-button
        type="success"
        icon="el-icon-message"
        :disabled="!checkboxId.length"
        @click="showSend"
      >发送短信</el-button>
    </el-row>
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      :fit="true"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
      <!-- <el-table-column type="index" width="55" align="center" fixed></el-table-column> -->
      <el-table-column prop="name" v-if="showCol.indexOf('姓名')>-1" label="姓名" align="center"></el-table-column>
      <el-table-column
        prop="customerName"
        v-if="showCol.indexOf('对应客户')>-1"
        label="对应客户"
        align="center"
      ></el-table-column>
      <el-table-column prop="job" v-if="showCol.indexOf('职务')>-1" label="职务" align="center"></el-table-column>
      <el-table-column prop="mobile" v-if="showCol.indexOf('手机')>-1" label="手机" align="center"></el-table-column>
      <el-table-column prop="telephone" v-if="showCol.indexOf('电话')>-1" label="电话" align="center"></el-table-column>
      <el-table-column label="操作" width="150" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary "
            plain
            @click="handleEdit(scope.$index, scope.row)"
            v-permi="'/linkman/updateLinkman'"
          >编辑</el-button>
          <el-button size="mini" @click="handleView(scope.$index, scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      class="itemPage"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="page"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalSize"
    ></el-pagination>
    <new-contact
      :isShowLinkman="isShowLinkman"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-contact>
    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>
    <edit-contacts
      :isShowLinkmanEdit="isShowLinkmanEdit"
      :linkmanId="linkmanId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-contacts>
  </div>
</template>

<script>
// 联系人管理
import API from "@/api/linkman";
import NewContact from "@/components/element/NewContact"; // 新增联系人
import NewSend from "@/components/element/NewSend"; // 发送短信
import EditContacts from "@/components/element/EditContacts"; // 编辑
export default {
  name: "linkman",
  components: {
    NewContact,
    NewSend,
    EditContacts
  },
  data() {
    return {
      flag: true,
      isShowLinkman: false,
      isShowSend: false,
      isShowLinkmanEdit: false,
      linkmanId: "", // 编辑当条数据的id
      options: [], // 地区
      creationTime: [], // 生日
      timeActive: 0,
      departmentsList: [], // 所属部门
      headList: [], // 所属负责人
      tableData: [], // 表格数据
      colOptions: ["姓名", "对应客户", "职务", "手机", "电话"],
      showCol: ["姓名", "对应客户", "职务", "手机", "电话"],
      form: {
        regionArr: [],
        definedTime: [],
        followUser: null,
        department: null
      },
      showDefinedTime: false, // 是否展示自定义时间段
      page: 1, //当前第几页
      rows: 10, //每页默认显示几条
      totalSize: 0, //总条数
      checkboxId: [] // 选中的
    };
  },
  methods: {
    colChange() {
      localStorage.setItem("linkmanTable", JSON.stringify(this.showCol));
    },
    timeCustom() {
      this.timeActive = null;
      this.form.timeCode = null;
      this.tableApi();
    },
    show() {
      this.flag = !this.flag;
    },
    // 封装table表格api
    tableApi() {
      let form = this.form;
      form.regionArr = form.regionArr || [null, null];
      let paprams = {
        pageNum: this.page,
        pageSize: this.rows,
        province: form.regionArr[0],
        city: form.regionArr[1],
        timeCode: form.timeCode,
        startTime: form.definedTime[0],
        endTime: form.definedTime[1],
        department: form.department,
        followUser: form.followUser,
        name: form.companyName
      };
      API.postList(paprams).then(res => {
        // console.log(res.data)
        this.totalSize = res.data.total; //总条数
        res.data.list.forEach(item => {
          item.address = item.province + item.city;
        });
        this.tableData = res.data.list;
      });
    },
    //选择每页显示条数触发
    sizeChangePage(val) {
      this.rows = val;
      this.tableApi(this.rows);
    },
    //选择第几页触发
    currentChangePage(val) {
      this.page = val;
      this.tableApi(this.page);
    },
    // // 所属负责人
    // headuser(e){
    //   this.;
    // },
    // 生日
    showStyle(index, code) {
      this.showDefinedTime = false;
      this.form.definedTime = [];
      this.timeActive = index;
      this.form.timeCode = code;
      this.tableApi();
    },
    // 所属部门
    depart(val) {
      if (val) {
        API.getHead(val).then(res => {
          // res.data.forEach(item => {
          //   item.value = item.name;
          // });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          // res.data.forEach(item => {
          //   item.value = item.name;
          // });
          this.headList = res.data;
        });
      }
      this.form.followUser = "";
      this.tableApi();
    },
    // querySearch(queryString, cb) {
    //   var restaurants = this.headList;
    //   var results = queryString
    //     ? restaurants.filter(this.createFilter(queryString))
    //     : restaurants;
    //   // 调用 callback 返回建议列表的数据
    //   cb(results);
    // },
    // createFilter(queryString) {
    //   return restaurant => {
    //     return (
    //       restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
    //       0
    //     );
    //   };
    // },
    // 所属负责人id
    handleSelect(item) {
      this.followUser = item.id;
      this.tableApi(this.followUser);
      if (this.followUser != null) {
        API.getDepartByUserId(item.id).then(res => {
          this.departmentsList = res.data;
        });
      } else {
        API.getDepartments().then(res => {
          this.departmentsList = res.data;
        });
      }
    },
    handleBlurStr(val) {
      if (this.form.followUser == "") {
        API.getDepartments().then(res => {
          this.departmentsList = res.data;
        });
      }
    },

    // 新增联系人
    showLinkman() {
      this.isShowLinkman = true;
    },
    // table发生变化触发
    handleSelectionChange(val) {
      let arr = [];
      val.map(ele => {
        arr.push(ele.id);
      });
      console.log(arr);
      this.checkboxId = arr;
    },
    // 删除选中联系人
    deleteLinkman() {
      this.$confirm("是否删除所选客户？", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      }).then(() => {
        API.postdeleteLinkman({ ids: this.checkboxId.join(",") }).then(res => {
          this.$message({
            message: "删除成功",
            type: "success"
          });
          this.tableApi();
        });
      });
    },
    // 发送短信
    showSend() {
      this.isShowSend = true;
    },
    // 保存Dialog
    getSonSure() {
      this.isShowLinkman = false;
      this.isShowSend = false;
      this.isShowLinkmanEdit = false;
      this.tableApi();
    },
    // 关闭Dialog 弹框点击关闭的值
    getSonCancel(val) {
      this.isShowLinkman = val;
      this.isShowSend = val;
      this.isShowLinkmanEdit = val;
    },
    // 编辑
    handleEdit(index, row) {
      this.linkmanId = row.id;
      this.isShowLinkmanEdit = true;
    },
    // 查看
    handleView(index, row) {
      this.$router.push({ path: "linkmandetails", query: { id: row.id } });
    }
  },
  activated() {
    // 封装table表格api
    this.tableApi();
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("linkmanTable"));
    if (showCol) this.showCol = showCol;
    // 地区的api
    API.getRegion().then(res => {
      res.data.forEach(item => {
        item.value = item.id;
        if (item.children.length == "0") {
          item.children = "";
        } else {
          item.children.forEach(item1 => {
            item1.value = item1.id;
          });
        }
      });
      this.options = res.data;
    });
    // 生日api
    API.getCreationTime().then(res => {
      // let obj = { code: 0, name: "全部" };
      // res.data.unshift(obj);
      res.data.splice(2, 1);
      res.data.splice(3, 1);
      res.data.splice(4, 1);
      this.creationTime = res.data;
    });
    // 所属部门的api
    API.getDepartments().then(res => {
      this.departmentsList = res.data;
    });
    // 所有负责人
    API.getAllUser().then(res => {
      // res.data.forEach(item => {
      //   item.value = item.name;
      // });
      this.headList = res.data;
    });
  },
  updated() {}
};
</script>

<style lang='scss' scoped>
ul {
  list-style-type: none;
  padding: 0;
}
.list {
  li {
    display: inline-block;
    padding: 0 20px;
    cursor: pointer;
  }
}
.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}
.item-css {
  width: 100%;
}

.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}

.input-search button {
  margin-left: 30px;
}
</style>
