<template>
  <div>
    <!-- <el-button type="primary" @click="outerVisible = true">批量导入</el-button> -->
    <el-dialog title="批量导入" :visible.sync="outerVisible" width="40%" @close="closeDialog">
      <div class="item">
        1、
        <el-link type="success" @click="downfile">点击下载导入数据模板</el-link>将要导入的数据填充到数据导入模板文件中。
      </div>
      <div class="item">注意事项：</div>
      <div class="item">1）模板中的表头不可更改，表头行不可删除；</div>
      <div class="item">2）除必填的列以外，不需要的列可以删除</div>
      <div class="item">3）单次导入的数据不超过10000条。</div>
      <div class="item">2、选择要导入的数据文件。</div>
      <el-upload
        class="upload-demo"
        ref="upload"
        :on-success="uploadSuccess"
        :on-error="uploadError"
        :action="action"
        :file-list="fileList"
        :auto-upload="false"
      >
        <el-button class="item" type="primary" icon="el-icon-plus">选择文件</el-button>
      </el-upload>

      <div slot="footer" class="dialog-footer">
        <el-button @click="sureDialog" type="primary">开始导入</el-button>
        <el-button @click="closeDialog">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 批量导入
import API from "@/api/customer";
export default {
  name: "UploadClient",
  props: ["isShowClues"],
  data() {
    return {
      outerVisible: this.isShowClues,
      action: this.$store.state.baseURL + "/customer/batchUploadCustomer", // 上传路径
      fileList: [] //上传的文件列表
    };
  },
  methods: {
    uploadSuccess(res) {
      if (res.status !== 200) {
        this.$message({
          type: "info",
          message: res.msg
        });
        return;
      }
      this.fileList = [];
      this.$message({
        type: "success",
        message: "保存成功"
      });
      this.$emit("sureDialogFather");
      this.closeDialog();
    },
    uploadError(res) {
      res = res.toString().slice(7);
      let msg = JSON.parse(res).message;
      this.$message({
        type: "error",
        message: "上传失败：" + msg
      });
    },
    // 下载
    downfile() {
      window.location.href = this.$store.state.baseURL + "customer/downFile";
    },
    // 保存
    sureDialog() {
      this.$refs.upload.submit();
    },
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather");
    }
  },
  watch: {
    isShowClues() {
      this.outerVisible = this.isShowClues;
    }
  }
};
</script>
<style lang='scss' scoped>
.item {
  margin-bottom: 18px;
  margin-left: 20px;
}
</style>