<template>
  <div>
    <el-form :model="form" label-width="120px" v-if="flag">
      <el-row>
        <!-- <el-col :span="24" class="list">
          <el-form-item label="客户级别">
            <ul>
              <li
                v-for="(item, index) in customerLevel"
                :key="index"
                @click="showStyle1(index, item.code)"
                :class="active1 == index ? 'addclass' : '' "
              >{{item.val}}</li>
            </ul>
          </el-form-item>
        </el-col>

        <el-col :span="24" class="list">
          <el-form-item label="业务类型">
            <ul>
              <li
                v-for="(item, index) in customerTypeList"
                :key="index"
                @click="showStyle2(index, item.id)"
                :class="serviceActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
            </ul>
          </el-form-item>
        </el-col>-->
        <el-col :span="24" class="list">
          <el-form-item label="线索创建时间">
            <ul>
              <li
                v-for="(item, index) in creationTime"
                :key="index"
                @click="timeChange(index, item.code)"
                :class="timeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
              <li
                @click="showDefinedTime = true;timeActive = -1"
                class="list-li"
                v-if="!showDefinedTime"
              >自定义时间段</li>
              <li v-else>
                <el-date-picker
                  v-model="form.createTime"
                  @change="timeCustom"
                  type="daterange"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                ></el-date-picker>
              </li>
            </ul>
          </el-form-item>
        </el-col>

        <el-col :span="24">
          <el-col :span="8">
            <el-form-item label="地区">
              <el-cascader
                :options="regionList"
                placeholder="请选择"
                clearable
                v-model="form.regionArr"
                class="item-css"
                @change="region"
              ></el-cascader>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="所属部门">
              <el-select
                v-model="form.department"
                placeholder="请选择"
                clearable
                class="item-css"
                @change="departChange"
              >
                <el-option
                  v-for="(item, index) in getDepartments"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="负责人">
              <el-select
                v-model="form.followName"
                @change="tableApi"
                clearable
                placeholder="请选择"
                class="item-css"
              >
                <el-option
                  v-for="(item, index) in headList"
                  :key="index"
                  :label="item.name"
                  :value="item.name"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-col>
        <el-col :span="24">
          <el-col :span="8">
            <el-form-item label="搜索">
              <el-select v-model="form.searchType" placeholder="请选择" clearable class="item-css">
                <el-option
                  v-for="(item, index) in getsearchType"
                  :key="index"
                  :label="item.name"
                  :value="item.code"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label="关键字" class="input-search">
              <el-col :span="14">
                <el-input
                  v-model="form.searchName"
                  @clear="tableApi"
                  placeholder="请输入"
                  @keydown.enter.native="tableApi"
                  clearable
                ></el-input>
              </el-col>
              <el-col :span="6">
                <el-button type="primary" @click="tableApi()">查询</el-button>
              </el-col>
            </el-form-item>
          </el-col>
        </el-col>
      </el-row>
    </el-form>
    <!-- 收起展开按钮 -->
    <!-- <el-row>
      <el-col :span="24" style="font-size:20px;text-align:center;">
        <i
          style="cursor: pointer;"
          :class="flag == true? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
          @click="show"
        ></i>
      </el-col>
    </el-row>-->
    <!-- 按钮 -->
    <el-row style="margin-top:20px;">
      <!-- 筛选 -->
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="primary"
        icon="el-icon-document-copy"
        :disabled="checkboxId.length!==1"
        @click="isShowToClue=true"
      >转移至线索</el-button>
      <el-button
        type="danger"
        icon="el-icon-delete"
        :disabled="!checkboxId.length"
        @click="handleDelete"
      >删除</el-button>
    </el-row>
    <!-- 表格 -->
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      :fit="true"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="50" align="center" fixed></el-table-column>
      <el-table-column
        v-if="showCol.indexOf('业务类型')>-1"
        label="业务类型"
        prop="registerName"
        align="center"
      ></el-table-column>
      <el-table-column v-if="showCol.indexOf('客户名称')>-1" align="center" prop="guest" label="客户名称"></el-table-column>
      <el-table-column
        v-if="showCol.indexOf('联系人')>-1"
        align="center"
        prop="linkName"
        label="联系人"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('电话')>-1"
        prop="telnum"
        label="电话"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column align="center" v-if="showCol.indexOf('所属地区')>-1" prop="region" label="所属地区"></el-table-column>
      <!-- <el-table-column
        align="center"
        v-if="showCol.indexOf('线索状态')>-1"
        prop="status"
        sortable
        label="线索状态"
      ></el-table-column>-->
      <el-table-column
        align="center"
        v-if="showCol.indexOf('线索来源')>-1"
        prop="clueFrom"
        label="线索来源"
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('转入意见')>-1"
        prop="changePublicOpinion"
        label="转入意见"
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('线索负责人')>-1"
        prop="userName"
        label="线索负责人"
      ></el-table-column>
      <!-- <el-table-column  align="center" prop="contents" label="最新跟进记录"></el-table-column> -->
      <el-table-column
        align="center"
        v-if="showCol.indexOf('进入公海时间')>-1"
        prop="toPublicTime"
        width="120"
        label="进入公海时间"
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('创建时间')>-1"
        prop="createTime"
        label="创建时间"
      ></el-table-column>
      <!-- <el-table-column
        align="center"
        v-if="showCol.indexOf('业务级别')>-1"
        prop="level"
        label="业务级别"
        width="180px"
      ></el-table-column>-->
      <!-- <el-table-column align="center" v-if="showCol.indexOf('搜索词')>-1" prop="keyWords" label="搜索词"></el-table-column> -->

      <el-table-column label="操作" width="100" align="center" fixed="right">
        <template slot-scope="scope">
          <!-- <el-button
            size="mini"
            type="primary"
            plain
            @click="handleEdit(scope.$index, scope.row)"
          >编辑</el-button>-->
          <el-button size="mini" @click="handleView(scope.$index, scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      class="itemPage"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="page"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalSize"
    ></el-pagination>

    <!-- 转成客户弹窗 -->
    <move-client
      :checkboxId="checkboxId"
      :isShowToClue="isShowToClue"
      @closeDialogFather="isShowToClue=false"
      @sureDialogFather="tableApi()"
    ></move-client>

    <!-- <edit-customer
      :isShowEdit="isShowEdit"
      :customerId="customerId"
      :customerLevel="customerLevel.slice(0,1)"
      :customerTypeList="customerTypeList"
      @sureDialogFather="tableApi()"
      @closeDialogFather="isShowEdit=false"
    ></edit-customer>-->
  </div>
</template>

<script>
// 公海管理
import API from "@/api/clue";
import { constants } from "crypto";
import MoveClient from "./components/MoveClient"; // 转客户
import EditClue from "../components/EditClue"; // 编辑线索

export default {
  name: "highSeas",
  components: {
    EditClue,
    MoveClient
  },
  data() {
    return {
      flag: true, // 显示隐藏
      customerLevel: [], // 客户级别
      active1: 0,
      timeActive: 0,
      serviceActive: 0,
      regionList: [], // 地区数据
      customerTypeList: [], // 业务类型
      creationTime: [], // 转客户时间
      headList: [], // 前客服负责人
      isShowToClue: false, // 转移至客户
      showDefinedTime: false,
      form: {
        createTime: "", //转客户时间
        searchName: "", //搜索
        regionArr: [] // 地址
      },
      showCol: [
        "业务类型",
        "客户名称",
        "联系人",
        "电话",
        "所属地区",
        "转入意见",
        "线索负责人",
        "进入公海时间",
        "创建时间"
        // "业务级别",
        // "搜索词"
      ],
      colOptions: [
        "业务类型",
        "客户名称",
        "联系人",
        "电话",
        "所属地区",
        "转入意见",
        "线索负责人",
        "进入公海时间",
        "创建时间"
        // "业务级别",
        // "搜索词"
      ],
      getsearchType: [
        { name: "业务类型", code: 1 },
        { name: "客户名称", code: 2 },
        { name: "客户搜索词", code: 3 },
        { name: "联系方式", code: 7 }

        // { name: "电话", code: 4 },
        // { name: "微信", code: 5 },
        // { name: "QQ", code: 6 }
      ],
      tableData: [], // tabel的数据
      getDepartments: [], // 部门列表
      page: 1, //当前第几页
      rows: 10, //每页默认显示几条
      totalSize: 0, //总条数
      grade: null, // 客户级别
      province: null, // 省
      city: null, // 市
      service: null, // 业务类型
      codeTime: null, // 转客户时间
      startDate: null, // 自定义开始时间
      endDate: null, // 自定义结束时间
      checkboxId: "", // 选中的

      isShowEdit: false,
      customerId: "" // 编辑数据当条的id
    };
  },
  methods: {
    colChange() {
      localStorage.setItem("highSeasTable", JSON.stringify(this.showCol));
    },
    departChange(val) {
      if (val != "") {
        API.getHead(val).then(res => {
          this.headList = res.data;
        });
      }
      this.form.followName = "";
      this.tableApi();
    },
    // 封装table表格api
    tableApi() {
      let form = this.form;
      form.regionArr = form.regionArr || [null, null];
      let params = {
        pageNum: this.page,
        searchType: form.searchType,
        pageSize: this.rows,
        grade: this.grade,
        province: form.regionArr[0],
        city: form.regionArr[1],
        service: this.service,
        timeCode: this.codeTime,
        startDate: this.startDate ? this.startDate : null,
        endDate: this.endDate ? this.endDate : null,
        createTime: this.createTime,
        userName: form.followName,

        searchName: form.searchName
      };

      console.log(params);
      API.findPublicList(params).then(res => {
        this.totalSize = res.data.total; //总条数
        res.data.list.forEach(item => {
          switch (item.status) {
            case "0":
              item.status = "未处理";
              break;
            case "1":
              item.status = "联系方式有效";
              break;
            case "2":
              item.status = "联系方式无效";
              break;
            case "3":
              item.status = "关闭";
              break;

            default:
              item.status = "暂无状态";
              break;
          }
          if (item.city == null) {
            item.address = item.province;
          } else {
            item.address = item.province + item.city;
          }
        });
        this.tableData = res.data.list;
      });
    },

    //选择每页显示条数触发
    sizeChangePage(val) {
      this.rows = val;
      this.tableApi();
    },
    //选择第几页触发
    currentChangePage(val) {
      this.page = val;
      this.tableApi();
    },
    // 客户级别
    showStyle1(index, code) {
      this.active1 = index;
      this.grade = code;
      this.tableApi();
    },
    // 地区组件里面的值改变返回的数据
    region(val) {
      this.tableApi();
    },
    // 业务类型
    showStyle2(index, code) {
      this.serviceActive = index;
      this.service = code;
      this.tableApi();
    },
    // 转客户时间
    timeChange(index, code) {
      this.showDefinedTime = false;
      this.timeActive = index; // 将点击的元素的索引赋值给变量
      this.startDate = this.endDate = null;
      this.codeTime = code;
      this.tableApi();
    },
    // 自定义时间
    timeCustom(val) {
      if (val != null) {
        this.startDate = val[0];
        this.endDate = val[1];
        this.codeTime = null;
        this.tableApi();
      }
    },
    // // 前客服负责人
    // head(val) {
    //   this.followUser = val;
    //   this.tableApi();
    // },

    // table发生变化触发
    handleSelectionChange(val) {
      let arr = [];
      val.map(ele => {
        arr.push(ele.id);
      });
      this.checkboxId = arr;
    },
    // 删除
    handleDelete() {
      let params = { ids: this.checkboxId.join(",") };
      this.$confirm("是否删除所选客户？", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      }).then(() => {
        API.delClue(params).then(res => {
          this.$message({
            message: "删除成功",
            type: "success"
          });
          this.tableApi();
        });
      });
    },
    // 编辑
    handleEdit(index, row) {
      this.customerId = row.id;
      this.isShowEdit = true;
    },
    // 保存Dialog
    getSonSure() {
      this.isShowEdit = false;
    },

    // 查看
    handleView(index, row) {
      this.$router.push({ path: "highSeasdetails", query: { id: row.id } });
    }
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("highSeasTable"));
    if (showCol) this.showCol = showCol;
    //所属部门
    API.getDepartments().then(res => {
      this.getDepartments = res.data;
    });
    // 初始加载tabel
    this.tableApi();
    // 客户级别api
    API.getCustomerLevel().then(res => {
      let obj = { code: 0, val: "全部" };
      res.data.unshift(obj);
      this.customerLevel = res.data;
    });
    // 地区的api
    API.getRegion().then(res => {
      res.data.forEach(item => {
        item.value = item.label;
        if (item.children.length == "0") {
          item.children = "";
        } else {
          item.children.forEach(item1 => {
            item1.value = item1.label;
          });
        }
      });
      this.regionList = res.data;
    });
    // 业务类型api
    API.getType().then(res => {
      let obj = { id: 0, name: "全部" };
      res.data.unshift(obj);
      this.customerTypeList = res.data;
    });
    // 转客户时间
    API.getAlltime().then(res => {
      // let obj = { code: 0, name: "全部" };
      // res.data.unshift(obj);
      res.data.splice(7);
      this.creationTime = res.data;
    });
    // 所有员工
    // API.getAllUser().then(res => {
    //   res.data.map(ele => {
    //     ele.value = ele.name;
    //   });
    //   this.headList = res.data;
    // });
  },
  watch: {}
};
</script>

<style lang='scss' scoped>
ul {
  list-style-type: none;
  padding: 0;
}
.list {
  li {
    display: inline-block;
    padding: 0 20px;
    cursor: pointer;
  }
}
.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}

.item {
  margin-bottom: 18px;
  margin-left: 20px;
}

.item-css {
  width: 100%;
}
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}

.input-search button {
  margin-left: 30px;
}
</style>
