<template>
  <!-- 计划回访管理 -->
  <div>
    <!-- <el-tabs v-model="activeName" type="card" @tab-click="tabChange">
      <el-tab-pane label="有计划回访" name="plan">有计划回访</el-tab-pane>
      <el-tab-pane label="随机回访" name="random">随机回访</el-tab-pane>
    </el-tabs>-->

    <el-row>
      <el-form label-width="120px">
        <el-col :span="24" class="list">
          <el-form-item label="业务类型">
            <ul>
              <li
                v-for="(item, index) in customerTypeList"
                :key="index"
                @click="changeService(index, item.id)"
                :class="serviceActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
            </ul>
          </el-form-item>
        </el-col>
        <el-col :span="24" class="list">
          <el-form-item label="回访时间">
            <ul>
              <li
                v-for="(item, index) in creationTime"
                :key="index"
                @click="changeTime(index, item.code)"
                :class="timeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
              <li
                @click="showDefinedDate=true;timeActive = -1"
                class="list-li"
                v-if="!showDefinedDate"
              >自定义时间段</li>
              <li v-else>
                <el-date-picker
                  v-model="chooseData.definedDate"
                  @change="codeTime=null;getTableData()"
                  type="daterange"
                  value-format="yyyy-MM-dd"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                ></el-date-picker>
              </li>
            </ul>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-col :span="8">
            <el-form-item label="所属地区">
              <el-cascader
                :options="region"
                @change="regionChange"
                placeholder="请选择"
                clearable
                class="item-css"
              ></el-cascader>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label="客户名称" class="input-search">
              <el-col :span="14">
                <el-input
                  v-model="chooseData.companyName"
                  @keydown.enter.native="getTableData"
                  placeholder="请输入"
                  class="input-with-select"
                  clearable
                ></el-input>
              </el-col>
              <el-col :span="6">
                <el-button type="primary" @click="getTableData()">查询</el-button>
              </el-col>
            </el-form-item>
          </el-col>
        </el-col>
      </el-form>
    </el-row>

    <!-- 筛选 -->
    <el-popover placement="top-start" width="300" trigger="click">
      <el-checkbox-group v-model="showCol" @change="colChange">
        <el-checkbox
          style="width:120px;line-height:25px"
          v-for="(item,index) in colOptions"
          :key="index"
          :label="item"
        ></el-checkbox>
      </el-checkbox-group>
      <el-button
        slot="reference"
        style="margin-right:10px;"
        type="primary"
        icon="el-icon-s-operation"
        plain
      ></el-button>
    </el-popover>

    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      :fit="true"
      stripe
    >
      <!-- <el-table-column type="selection" width="55" align="center" fixed></el-table-column> -->
      <el-table-column
        prop="customerName"
        v-if="showCol.indexOf('客户名称')>-1"
        label="客户名称"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="startTime"
        v-if="showCol.indexOf('回访时间')>-1"
        label="回访时间"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="customerTel"
        v-if="showCol.indexOf('电话')>-1"
        label="电话"
        width="150"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="area"
        v-if="showCol.indexOf('所属地区')>-1"
        label="所属地区"
        align="center"
        show-overflow-tooltip
      >
        <!-- <template slot-scope="scope">{{ scope.row.provinceStr + scope.row.cityStr }}</template> -->
      </el-table-column>
      <el-table-column
        prop="finishTime"
        v-if="showCol.indexOf('成单时间')>-1"
        label="成单时间"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="type"
        v-if="showCol.indexOf('业务类型')>-1"
        label="业务类型"
        show-overflow-tooltip
        align="center"
      ></el-table-column>
      <el-table-column
        prop="contents"
        v-if="showCol.indexOf('任务内容')>-1"
        label="任务内容"
        align="center"
      ></el-table-column>
      <el-table-column
        v-if="showCol.indexOf('计划创建时间')>-1"
        label="计划创建时间"
        width="120"
        align="center"
        header-align="center"
      >
        <template slot-scope="scope">{{ scope.row.createTime }}</template>
      </el-table-column>
      <el-table-column
        prop="taskType"
        v-if="showCol.indexOf('重复任务')>-1"
        label="重复任务"
        align="center"
      ></el-table-column>
      <el-table-column label="操作" width="110" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="$router.push('CustomerPanoramic?id='+ scope.row.customerId)"
            type="primary"
            plain
          >查看/编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      class="itemPage"
      style="text-align:right;margin-top: 20px;"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="pageObj.pageNum"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageObj.total"
    ></el-pagination>

    <Followup
      :isShowFollow="isShowFollow"
      :customerId="customerId"
      @sureDialogFather="getTableData();isShowFollow = false"
      @closeDialogFather="isShowFollow = false"
    ></Followup>
  </div>
</template>

<script>
import API from "@/api/after";
import Followup from "@/components/element/Followup"; // 写跟进

export default {
  name: "retrunVisit",
  components: { Followup },
  data() {
    return {
      // activeName: "plan",
      creationTime: [],
      region: [],
      isShowFollow: false, //  写跟进
      timeActive: 0,
      serviceActive: 0,
      showDefinedDate: false,
      codeTime: null,
      codeService: null,
      customerTypeList: [], // 业务类型
      customerId: null,
      chooseData: {
        definedDate: [],
        companyName: null
      },
      showCol: [
        "客户名称",
        "回访时间",
        "电话",
        "所属地区",
        "成单时间",
        "业务类型",
        "任务内容",
        "计划创建时间",
        "重复任务"
      ],
      colOptions: [
        "客户名称",
        "回访时间",
        "电话",
        "所属地区",
        "成单时间",
        "业务类型",
        "任务内容",
        "计划创建时间",
        "重复任务"
      ],
      pageObj: {
        pageNum: 1,
        pageSize: 10,
        total: null
      },
      tableData: []
    };
  },
  methods: {
    colChange() {
      localStorage.setItem("returnVisitTable", JSON.stringify(this.showCol));
    },
    writeFollow(index, row) {
      this.customerId = row.customerId;
      this.isShowFollow = true;
    },
    // 地区组件里面的值改变返回的数据
    regionChange(value) {
      this.chooseData.province = value[0];
      this.chooseData.city = value[1];
      this.getTableData();
    },
    sizeChangePage(e) {
      this.pageObj.pageSize = e;
      this.getTableData();
    },
    currentChangePage() {
      this.pageObj.pageNum = e;
      this.getTableData();
    },
    changeService(index, code) {
      this.serviceActive = index;
      this.codeService = code;
      this.getTableData();
    },
    tabChange() {},
    changeTime(index, code) {
      this.codeTime = code;
      this.timeActive = index;
      this.chooseData.definedDate = [];
      this.showDefinedDate = false;
      this.getTableData();
    },
    getTableData() {
      let chooseData = this.chooseData;
      let params = {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize,
        service: this.codeService,
        timeCode: this.codeTime,
        city: this.chooseData.city,
        province: this.chooseData.province,
        startTime: chooseData.definedDate[0],
        endTime: chooseData.definedDate[1],
        companyName: chooseData.companyName
      };
      console.log(params);

      API.getVisitList(params)
        .then(res => {
          console.log(res.data);
          let data = res.data;
          this.tableData = data.list;
          this.pageObj.total = data.total;
        })
        .catch(err => {
          console.error(err);
        });
    }
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("returnVisitTable"));
    if (showCol) this.showCol = showCol;
    // 创建时间api
    API.getCreationTime().then(res => {
      let data = res.data;
      this.creationTime = data;
    });
    // 地区的api
    API.getRegion().then(res => {
      res.data.forEach(item => {
        item.value = item.id;
        if (item.children.length == "0") {
          item.children = "";
        } else {
          item.children.forEach(item1 => {
            item1.value = item1.id;
          });
        }
      });
      this.region = res.data;
    });
    // 业务类型api
    API.getType().then(res => {
      let obj = { id: 0, name: "全部" };
      res.data.unshift(obj);
      this.customerTypeList = res.data;
    });
    this.getTableData();
  }
};
</script>

<style lang="scss" scoped>
.item-css {
  width: 100%;
}

.list {
  li {
    display: inline-block;
    padding: 0 20px;
    cursor: pointer;
  }
}

.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}
.input-search button {
  margin-left: 30px;
}
</style>