<template>
  <div>
    <el-button type="warning" v-permi="'/customer/addChance'"  @click="isAddChance=true">新增机会</el-button>
    <el-table
      :data="chanceTableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      :fit="true"
      width="200px"
    >
      <el-table-column label="机会名称" width align="center" header-align="center">
        <template slot-scope="scope">{{ scope.row.name }}</template>
      </el-table-column>
      <el-table-column prop="expectPrice" label="预计销售金额" width align="center"></el-table-column>
      <el-table-column prop="expectDate" label="预计签单日期" width align="center"></el-table-column>
      <el-table-column prop="stage" label="销售阶段" width align="center"></el-table-column>
      <!-- <el-table-column prop="probability" label="签单概率" width align="center"></el-table-column> -->
      <el-table-column prop="remarks" label="备注" width align="center"></el-table-column>
      <el-table-column label="操作" width="150" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" plain v-permi="'/chance/updateChance'"  @click="editChance(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" @click="examineChance(scope.$index, scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- <el-pagination
      style="text-align:right;margin-top: 20px"
      layout="prev, pager, next"
      @current-change="currentChange"
      :page-size="pageObj.pageSize"
      :total="pageObj.total"
    ></el-pagination> -->
    <edit-chance
      :isShowChanceEdit="isEditChance"
      :opportunityId="opportunityId"
      :customerId="customerId"
      @sureDialogFather="getSonSure()"
      @closeDialogFather="getSonCancel"
    ></edit-chance>
    <new-chance
      :customerId="customerId"
      :isShowChance="isAddChance"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-chance>
  </div>
</template>

<script>
import API from "@/api/customer";
import NewChance from "./NewChance";
import EditChance from "./EditChance"; // 编辑机会

export default {
  name: "TabChance",
  components: { NewChance, EditChance },
  props: ["customerId", "customerName"],
  data() {
    return {
      chanceTableData: [],
      isEditChance: false,
			isAddChance: false,
			opportunityId:null,
      pageObj: {
        pageSize: 10,
        pageNum: 1,
        total: null
      }
    };
  },  
  methods: {
    currentChange(e) {
      this.pageObj.pageNum = e;
      this.getTaskTable();
    },
    editChance(index, row) {
      this.opportunityId = row.id;
      this.isEditChance = true;
    },
    examineChance(index, row) {
      this.$router.push({
        path: "/sellManagement/opportunitydetails",
        query: { id: row.id }
      });
    },
    getChanceTable() {
      let params= {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize,
      }
      API.getChanceByCustomerId(this.customerId).then(res => {
        this.chanceTableData = res.data;
      });
    },
    getSonCancel() {
      this.isAddChance = false;
      this.isEditChance = false;
    },
    getSonSure() {
      this.getSonCancel();
      this.getChanceTable();
    }
  }
};
</script>

<style lang="scss" scoped>
</style>