<template>
  <div>
    <el-row>
      <el-form label-width="100px">
        <el-col :span="24">
          <el-form-item label="投诉类型">
            <ul>
              <li
                v-for="(item, index) in complaintType"
                :key="index"
                @click="typeChange(index, item.code)"
                :class="typeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
            </ul>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="处理状态 ">
            <ul>
              <li
                v-for="(item, index) in disposeStatus"
                :key="index"
                @click="statusChange(index, item.code)"
                :class="statusActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
            </ul>
          </el-form-item>
        </el-col>
        <el-col :span="24" class="list">
          <el-form-item label="创建时间">
            <ul>
              <li
                v-for="(item, index) in creationTime"
                :key="index"
                @click="changeCreateTime(index, item.code)"
                :class="timeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
              <li
                @click="showDefinedDate = true;timeActive = -1"
                class="list-li"
                v-if="!showDefinedDate"
              >自定义时间段</li>
              <li v-else>
                <el-date-picker
                  v-model="chooseData.createTime"
                  @change="timeCustom"
                  type="daterange"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                ></el-date-picker>
              </li>
            </ul>
          </el-form-item>
        </el-col>
      </el-form>
    </el-row>
    <el-row>
      <!-- 筛选 -->
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="primary"
        @click="isAddComplain=true"
        v-permi="'/aftersales/addAdvise'"
        icon="el-icon-document-add"
      >新增投诉或建议</el-button>
      <el-button
        type="warning"
        @click="handleStatus(0)"
        :disabled="!checkboxId.length"
        icon="el-icon-close"
      >未处理</el-button>
      <el-button
        type="success"
        @click="handleStatus(1)"
        :disabled="!checkboxId.length"
        icon="el-icon-check"
      >完成处理</el-button>
      <el-button
        type="danger"
        @click="delComplain"
        :disabled="!checkboxId.length"
        icon="el-icon-delete"
      >删除</el-button>
    </el-row>

    <el-table
      :data="tableData"
      tooltip-effect="dark"
      @selection-change="handleSelectionChange"
      style="width: 100%;margin-top: 20px;"
      border
      stripe
    >
      <el-table-column type="selection" align="center" fixed></el-table-column>
      <el-table-column prop="companyName" v-if="showCol.indexOf('公司')>-1" label="公司" align="center"></el-table-column>
      <!-- <el-table-column prop="name" v-if="showCol.indexOf('办理业务时间')>-1" label="办理业务时间" align="center"></el-table-column> -->
      <el-table-column prop="linkName" v-if="showCol.indexOf('联系人')>-1" label="联系人" align="center"></el-table-column>
      <el-table-column prop="linkTel" v-if="showCol.indexOf('电话')>-1" label="电话" align="center"></el-table-column>
      <el-table-column
        prop="text"
        v-if="showCol.indexOf('具体说明')>-1"
        label="具体说明"
        width="250"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="followDepartName"
        v-if="showCol.indexOf('投诉部门')>-1"
        label="投诉部门"
        align="center"
        header-align="center"
      ></el-table-column>
      <el-table-column
        prop="followUserName"
        v-if="showCol.indexOf('投诉人')>-1"
        label="投诉人"
        align="center"
        header-align="center"
      ></el-table-column>
      <el-table-column v-if="showCol.indexOf('状态')>-1" label="状态" align="center">
        <template slot-scope="scope">
          <span
            :style="{color:scope.row.statusName==='已处理'?'#67C23A':'#f78989'}"
          >{{scope.row.statusName}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="typeName" v-if="showCol.indexOf('类型')>-1" label="类型" align="center"></el-table-column>
      <el-table-column
        prop="createTime"
        v-if="showCol.indexOf('提交时间')>-1"
        label="提交时间"
        width="200"
        align="center"
      ></el-table-column>
      <el-table-column label="操作" width="200" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            :disabled="scope.row.statusName==='已处理'"
            :type="scope.row.statusName==='已处理'?'':'primary'"
            plain
            v-permi="'/aftersales/addAdviseFollow'"
            @click="isAddHandleReault=true;complainId=scope.row.id"
          >添加处理</el-button>
          <el-button size="mini" @click="isExamineResult=true;complainId=scope.row.id">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      class="itemPage"
      style="text-align:right;margin-top: 20px;"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="pageObj.pageNum"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageObj.total"
    ></el-pagination>
    <!-- 添加投诉 -->
    <add-complain
      :isAddComplain="isAddComplain"
      @closeDialogFather="isAddComplain=false"
      @sureDialogFather="getTableData();isAddComplain=false"
    ></add-complain>
    <!-- 添加处理结果 -->
    <add-handle-result
      :isAddHandleReault="isAddHandleReault"
      :complainId="complainId"
      @closeDialogFather="isAddHandleReault=false"
      @sureDialogFather="getTableData();isAddHandleReault=false"
    ></add-handle-result>
    <!-- 查看投诉详情 -->
    <examine-result
      :isExamineResult="isExamineResult"
      :complainId="complainId"
      @closeDialogFather="isExamineResult=false"
    ></examine-result>
  </div>
</template>

<script>
import API from "@/api/after";
import AddComplain from "../components/AddComplain";
import AddHandleResult from "../components/AddHandleResult";
import ExamineResult from "../components/ExamineResult";
// 投诉与建议
export default {
  name: "complaintAdvice",
  components: {
    AddComplain,
    AddHandleResult,
    ExamineResult
  },
  data() {
    return {
      showCol: [
        "公司",
        "办理业务时间",
        "联系人",
        "电话",
        "具体说明",
        "投诉部门",
        "投诉人",
        "状态",
        "类型",
        "提交时间"
      ],
      colOptions: [
        "公司",
        "办理业务时间",
        "联系人",
        "电话",
        "具体说明",
        "投诉部门",
        "投诉人",
        "状态",
        "类型",
        "提交时间"
      ],
      isAddHandleReault: false, // 展示完成处理弹窗
      isExamineResult: false, // 展示完成处理弹窗
      complainId: null, // 处理投诉的id
      complaintType: [
        { name: "全部", code: null },
        { name: "建议", code: 0 },
        { name: "投诉", code: 1 }
      ],
      disposeStatus: [
        { name: "全部", code: null },
        { name: "已处理", code: 1 },
        { name: "未处理", code: 0 }
      ],
      creationTime: [],
      timeActive: 0,
      statusActive: 0,
      typeActive: 0,
      chooseData: {
        typeCode: null,
        statusCode: null,
        timeCode: null,
        createTime: []
      },
      pageObj: {
        pageNum: 1,
        pageSize: 10,
        total: null
      },
      checkboxId: [],
      isAddComplain: false,
      showDefinedDate: false,
      tableData: []
    };
  },
  methods: {
    colChange() {
      localStorage.setItem("complaintTable", JSON.stringify(this.showCol));
    },
    delComplain() {
      this.$confirm("是否删除选中数据?", "确认删除", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          let params = {
            ids: this.checkboxId.join(",")
          };
          API.deleteAdvise(params).then(res => {
            this.$message.success("删除成功");
            this.getTableData();
          });
        })
        .catch(() => {
          this.$message({
            message: "已取消",
            type: "info"
          });
        });
    },
    handleStatus(status) {
      this.$confirm("是否修改选中数据状态?", "确认修改", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          let params = {
            ids: this.checkboxId.join(","),
            status
          };
          API.updateAdviseStatus(params).then(res => {
            this.$message.success("修改成功");
            this.getTableData();
          });
        })
        .catch(() => {
          this.$message({
            message: "已取消",
            type: "info"
          });
        });
    },
    // table发生变化触发
    handleSelectionChange(val) {
      let arr = [];
      val.map(ele => {
        arr.push(ele.id);
      });
      this.checkboxId = arr;
    },
    // 自定义时间
    timeCustom(val) {
      if (val != null) {
        this.chooseData.codeTime = null;
        this.getTableData();
      }
    },
    sizeChangePage(e) {
      this.pageObj.pageSize = e;
      this.getTableData();
    },
    currentChangePage(e) {
      this.pageObj.pageNum = e;
      this.getTableData();
    },
    // 最近一次联系时间 的选取变化
    statusChange(index, code) {
      this.chooseData.statusCode = code;
      this.statusActive = index;
      this.getTableData();
    },
    typeChange(index, code) {
      this.chooseData.typeCode = code;
      this.typeActive = index;
      this.getTableData();
    },
    // 创建时间
    changeCreateTime(index, code) {
      this.chooseData.createTime = [];
      this.showDefinedDate = false;
      this.timeActive = index; //将点击的元素的索引赋值给变量
      this.chooseData.codeTime = code;
      this.getTableData();
    },
    getTableData() {
      let pageObj = this.pageObj;
      let chooseData = this.chooseData;
      let params = {
        pageSize: pageObj.pageSize,
        pageNum: pageObj.pageNum,
        type: chooseData.typeCode,
        status: chooseData.statusCode,
        startTime: chooseData.createTime[0],
        endTime: chooseData.createTime[1],
        timeCode: chooseData.codeTime
      };
      API.getAdviseList(params).then(res => {
        let data = res.data;
        this.pageObj.total = data.total;
        this.tableData = data.list;
      });
    }
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("complaintTable"));
    if (showCol) this.showCol = showCol;
    // 创建时间api
    API.getCreationTime().then(res => {
      let data = res.data;
      data.splice(7);
      this.creationTime = res.data;
    });
    this.getTableData();
  }
};
</script>
<style lang='scss' scoped>
ul {
  list-style-type: none;
  padding: 0;
}
.el-form-item li {
  display: inline-block;
  padding: 0 20px;
  cursor: pointer;
}

.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}
</style>