<template>
  <div>
    <el-form :v-model="formdata" label-width="100px">
      <el-row>
        <el-col :span="24">
          <el-form-item label="退回时间">
            <ul>
              <li
                v-for="(item, index) in getAlltime"
                :key="index"
                class="list-li"
                @click="timeChange(index,item.code)"
                :class="{'pote':timeActive == index}"
              >{{ item.name }}</li>
              <li
                @click="showDefinedDate = true;timeActive = null"
                class="list-li"
                v-if="!showDefinedDate"
              >自定义时间段</li>
              <li v-else>
                <el-date-picker
                  :unlink-panels="true"
                  v-model="formdata.definedDate"
                  type="daterange"
                  @change="timeChange('defined')"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                ></el-date-picker>
              </li>
            </ul>
          </el-form-item>
        </el-col>

        <el-col :span="8">
          <el-form-item label="所属地区">
            <el-cascader
              :options="getRegion"
              placeholder="请选择"
              clearable
              class="item-css"
              v-model="formdata.region"
              @change="getTableData"
            ></el-cascader>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="所属部门">
            <el-select
              v-model="formdata.department"
              placeholder="请选择"
              clearable
              class="item-css"
              @change="departsChange($event)"
            >
              <el-option
                v-for="(item, index) in getDepartments"
                :key="index"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="负责人">
            <el-select
              v-model="formdata.headuser"
              placeholder="请选择"
              clearable
              :no-data-text="formdata.department?'此部门暂无成员':'请选择部门'"
              class="item-css"
              @change="getTableData()"
            >
              <el-option
                v-for="(item, index) in userList"
                :key="index"
                :label="item.name"
                :value="item.name"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-col :span="8">
            <el-form-item label="搜索">
              <el-select v-model="formdata.searchType" placeholder="请选择" clearable class="item-css">
                <el-option
                  v-for="(item, index) in getsearchType"
                  :key="index"
                  :label="item.name"
                  :value="item.code"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label="关键字">
              <el-col :span="14">
                <el-input
                  v-model="formdata.searchName"
                  @keydown.enter.native="getTableData"
                  placeholder="请输入"
                  clearable
                ></el-input>
              </el-col>
              <el-col :span="6">
                <el-button style="margin-left: 20px" type="primary" @click="getTableData">查询</el-button>
              </el-col>
            </el-form-item>
          </el-col>
        </el-col>
      </el-row>
    </el-form>
    <el-row>
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="primary"
        :disabled="!multipleSelection.length"
        @click="isShowReject=true"
        icon="el-icon-delete"
      >驳回至销售</el-button>
      <el-button
        type="warning"
        :disabled="!multipleSelection.length"
        @click="isShowHighSeas=true"
        icon="el-icon-delete"
      >转至公海</el-button>
    </el-row>
    <el-table
      :data="tableData"
      @selection-change="multipleSelection=$event;"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
    >
      <el-table-column type="selection" width="50" align="center" fixed></el-table-column>
      <el-table-column
        v-if="showCol.indexOf('业务类型')>-1"
        label="业务类型"
        prop="registerName"
        align="center"
      ></el-table-column>
      <el-table-column v-if="showCol.indexOf('客户名称')>-1" align="center" prop="guest" label="客户名称"></el-table-column>
      <el-table-column
        v-if="showCol.indexOf('联系人')>-1"
        align="center"
        prop="linkName"
        label="联系人"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('电话')>-1"
        prop="telnum"
        label="电话"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column align="center" v-if="showCol.indexOf('所属地区')>-1" prop="region" label="所属地区"></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('线索来源')>-1"
        prop="clueFrom"
        label="线索来源"
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('线索负责人')>-1"
        prop="userName"
        label="线索负责人"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="returnClueOpinion"
        v-if="showCol.indexOf('退回意见')>-1"
        label="退回意见"
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('退回时间')>-1"
        prop="returnTime"
        label="退回时间"
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('创建时间')>-1"
        prop="createTime"
        label="创建时间"
      ></el-table-column>
      <el-table-column align="center" v-if="showCol.indexOf('业务级别')>-1" prop="level" label="业务级别"></el-table-column>
      <!-- <el-table-column align="center" v-if="showCol.indexOf('搜索词')>-1" prop="keyWords" label="搜索词"></el-table-column> -->
      <el-table-column label="操作" width="100" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleView(scope.$index, scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div>
      <el-pagination
        class="itemPage"
        @size-change="getTableData"
        @current-change="currentChangePage"
        :current-page="pageObj.pageNum"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageObj.total"
      ></el-pagination>
    </div>

    <!-- 操作退回 -->
    <reject-handle
      @sureDialogFather="getTableData()"
      @closeDialogFather="isShowReject=false"
      :isShowReject="isShowReject"
      :clueDetail="multipleSelection[0]"
    ></reject-handle>

    <!-- 转入公海 -->
    <to-high-seas
      @closeDialogFather="isShowHighSeas=false"
      :isShowHighSeas="isShowHighSeas"
      @sureDialogFather="getTableData()"
      :clueDetail="multipleSelection[0]"
    ></to-high-seas>
  </div>
</template>


<script>
import API from "@/api/clue";
import ToHighSeas from "../components/ToHighSeas";
import RejectHandle from "../components/RejectHandle";

export default {
  name: "sendBack",
  components: {
    ToHighSeas,
    RejectHandle
  },
  data() {
    return {
      isShowHighSeas: false,
      isShowReject: false, // 是否驳回
      formdata: {
        department: null,
        headuser: null,
        definedDate: null, // 自定义的时间段
        searchType: null,
        timeCode: null,
        searchName: null
      },
      getAlltime: [], // 时间选择
      showDefinedDate: false, // 显示自定义时间段
      timeActive: 0,
      tableData: [],
      pageObj: {
        pageSize: 10,
        pageNum: 1,
        total: null
      },
      getsearchType: [
        { name: "业务类型", code: 1 },
        { name: "客户名称", code: 2 },
        { name: "客户搜索词", code: 3 },
        { name: "联系方式", code: 7 }
      ],
      userList: [], // 员工列表
      getDepartments: [],
      getRegion: [],
      multipleSelection: [],
      showCol: [
        "业务类型",
        "客户名称",
        "联系人",
        "电话",
        "所属地区",
        // "线索来源",
        "线索负责人",
        "退回意见",
        "退回时间",
        "创建时间",
        "业务级别"
        // "搜索词"
      ],
      colOptions: [
        "业务类型",
        "客户名称",
        "联系人",
        "电话",
        "所属地区",
        // "线索来源",
        "线索负责人",
        "退回意见",
        "退回时间",
        "创建时间",
        "业务级别"
        // "搜索词"
      ]
    };
  },
  methods: {
    // 查看
    handleView(index, row) {
      this.$router.push({ path: "returnDetail", query: { id: row.id } });
    },
    departsChange(val) {
      this.formdata.department = val;

      this.formdata.headuser = "";
      this.getTableData();
    },
    timeChange(index, code) {
      console.log(typeof index);
      if (typeof index === "number") {
        this.timeActive = index;
        this.formdata.definedDate = null;
        this.showDefinedDate = false;
        this.formdata.timeCode = code;
      }
      this.getTableData();
    },
    getTableData() {
      let form = this.formdata;
      let region = form.region;
      let province = null; //省
      let city = null; //市
      let startTime = null;
      let endTime = null;
      let definedDate = form.definedDate;
      if (definedDate) {
        startTime = definedDate[0];
        endTime = definedDate[1];
      }
      if (region) {
        province = region[0];
        city = region[1];
      }
      let params = {
        province,
        city,
        startTime,
        endTime,
        timeCode: form.timeCode,
        departName: form.department,
        userName: form.headuser,
        searchType: form.searchType,
        searchName: form.searchName,
        pageSize: this.pageObj.pageSize,
        pageNum: this.pageObj.pageNum
      };
      console.log(params);

      API.findReturnClues(params).then(res => {
        console.log(res);
        let data = res.data;
        this.tableData = data.list;
        this.pageObj.total = data.total;
      });
    },
    currentChangePage() {},
    colChange(col) {
      localStorage.setItem("sendBack", JSON.stringify(this.showCol));
    }
  },
  activated() {
    this.getTableData();
  },
  mounted() {
    // 把展示几列的数据放在local  如过没手动设置那么就使用默认
    let showCol = JSON.parse(localStorage.getItem("sendBack"));
    if (showCol) this.showCol = showCol;
    API.getAlltime().then(res => {
      this.getAlltime = res.data.slice(0, -3);
    });
    //所属部门
    API.getDepartments().then(res => {
      this.getDepartments = res.data;
    });
    API.getRegion().then(res => {
      res.data.forEach(item => {
        item.value = item.label;
        if (item.children.length == "0") {
          item.children = "";
        } else {
          item.children.forEach(item1 => {
            item1.value = item1.label;
          });
        }
      });
      this.getRegion = res.data;
    });
  }
};
</script>


<style lang="scss" scoped>
.pote {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}
.list-li {
  padding: 0px 10px;
  display: inline-block;

  margin: 0 10px;
}
.item-css {
  width: 100%;
}

.list-li:hover {
  cursor: pointer;
}
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
</style>
