<!-- 线索的跟进记录-- 目前未使用此版本，勿删 -->
<template>
  <el-card class="follow-card">
    <div slot="header" class="follow-header">
      <div>
        <p>
          <span>跟进记录</span>
          &nbsp;&nbsp;&nbsp;
        </p>
        <el-button type="warning" v-permi="'/follow/writeFollow'" @click="$emit('writeFollow')">写跟进</el-button>
      </div>
      <!-- <slot></slot> -->
    </div>
    <div class="text">
      <el-collapse v-model="activeNames">
        <!-- 循环出所有的跟进人 -->
        <el-collapse-item
          v-for="(aItem,AIndex) in followList"
          :title="aItem.createUserName"
          :key="AIndex"
          :name="AIndex"
        >
          <div v-if="aItem.list.length">
            <el-timeline>
              <!-- 根据日期判断是否在一个时间线内 -->
              <el-timeline-item
                v-for="(bItem, bIndex) in followObj(aItem.list)"
                :key="bIndex"
                :timestamp="bIndex"
                placement="top"
              >
                <!-- 循环当天跟进 -->
                <el-card
                  style="width: 100%;margin: 10px 0;"
                  :key="cIndex"
                  v-for="(cItem,cIndex) in bItem"
                >
                  <div class="timeline-contents">{{cItem.contents}}</div>
                  <div class="timeline-item">
                    <span>创建时间: {{cItem.createTime}}</span>
                  </div>
                </el-card>
              </el-timeline-item>
            </el-timeline>
          </div>
          <div v-else>暂无跟进记录</div>
        </el-collapse-item>
      </el-collapse>
      <div class="timeline-block">
        <div class="pager">
          <el-pagination
            small
            @current-change="currentChange"
            layout="prev, pager, next"
            :total="pageObj.total"
            :page-size="pageObj.pageSize"
          ></el-pagination>
        </div>
      </div>
    </div>
  </el-card>
</template>


<script>
import API from "@/api/contract";
export default {
  props: {
    customerId: { type: Number, default: null },
    type: { type: Number, default: null }
  },
  data() {
    return {
      followList: [],
      pageObj: {
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      activeNames: []
    };
  },
  methods: {
    currentChange(a) {
      this.pageObj.pageNum = a;
      this.getFollowData();
    },
    getFollowData() {
      let params = {
        linkId: this.customerId,
        type: this.type,
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize
      };

      API.getFollowListByClueId(params).then(res => {
        let data = res.data;
        let list = data.list;
        let loginName = this.$store.state.userInfo.name;
        for (let i = 0; i < list.length; i++) {
          if (list[i].createUserName === loginName) {
            this.activeNames.push(i);
          }
        }

        this.followList = list;
        this.pageObj.total = data.total;
      });
    }
  },

  watch: {
    customerId(val) {
      if (val) {
        this.$nextTick(() => {
          this.getFollowData();
        });
      }
    }
  },
  computed: {
    followObj(list) {
      return list => {
        if (!list.length) return {};
        // let followList = JSON.parse(JSON.stringify(this.followList));
        let followObj = {};
        for (const item of list) {
          let followData = item.createTime && item.createTime.slice(0, 10);
          if (followObj[followData]) {
            followObj[followData].push(item);
          } else {
            followObj[followData] = [item];
          }
        }
        return followObj;
      };
    }
  }
};
</script>

<style lang="scss" scoped>
.follow-card {
  .follow-header {
    font-size: 18px;
    height: 100%;
    padding: 0 20px;
    div {
      align-items: center;
      display: flex;
      justify-content: space-between;
    }
  }
  .timeline-item {
    display: flex;
    justify-content: space-between;
    margin: 20px 0 10px;
    font-size: 14px;
  }
  .timeline-contents {
    font-size: 15px;
  }
}
.pager {
  text-align: right;
}
</style>
