<template>
  <div>
    <el-dialog title="退回至售前" :visible.sync="outerVisible" width="40%" @close="closeDialog">
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item
          label="退回意见"
          :rules="[{ required: true, message: '请输入处理意见', trigger: 'blur' }]"
          :label-width="formLabelWidth"
          prop="opinion"
        >
          <el-input
            type="textarea"
            style="width:60%"
            placeholder="请输入内容"
            v-model="form.opinion"
            :autosize="{ minRows: 4, maxRows: 10}"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="sureDialog" type="success">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/customer";

// 退回至售前
export default {
  name: "ReturnSales",
  props: ["isShowSales", "seletedCustomer"],
  data() {
    return {
      outerVisible: this.isShowSales,
      formLabelWidth: "100px",
      form: {
        opinion: "",
        state: ""
      },
      allState: [
        { label: "未处理", value: 0 },
        { label: "联系方式有效", value: 1 },
        { label: "联系方式无效", value: 2 },
        { label: "关闭", value: 3 }
      ],
      rules: {
        opinion: [
          { required: true, message: "请填写退回意见", trigger: "blur" }
        ],
        state: [
          { required: true, message: "请填选择客户状态", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather", this.outerVisible);
    },
    // 保存
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;

        let params = {
          opinion: this.form.opinion,
          customerId: this.seletedCustomer.id,
          clueId: this.seletedCustomer.clueId
        };
        API.returnCustomer(params)
          .then(res => {
            this.$message({
              message: "保存成功",
              type: "success"
            });
            this.$emit("sureDialogFather");
          })
          .catch(err => {
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    }
  },
  mounted() {},
  watch: {
    isShowSales(value) {
      this.outerVisible = this.isShowSales;
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>