<template>
  <div>
    <el-dialog title="处理投诉与建议" @close="closeDialog" :visible.sync="dialogVisible">
      <el-form :model="form" ref="form" label-width="120px" :rules="rules">
        <el-form-item label="处理结果" prop="contents">
          <el-input
            v-model="form.contents"
            style="width:60%;"
            maxlength="200"
            show-word-limit
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 10}"
          ></el-input>
        </el-form-item>
        <el-form-item label="是否完成" prop="isFinish">
          <el-select v-model="form.isFinish" placeholder="处理结果">
            <el-option label="已解决" :value="1"></el-option>
            <el-option label="未解决" :value="0"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog">取 消</el-button>
        <el-button type="primary" @click="sureDialog">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/after";

export default {
  name: "AddHandleReault",
  props: ["isAddHandleReault", "complainId"],
  data() {
    return {
      rules: {
        contents: [
          { required: true, message: "请填写处理结果", trigger: "blur" }
        ],
        isFinish: [
          { required: true, message: "请选择是否完成", trigger: "change" }
        ]
      },
      form: {
        contents: null,
        isFinish: null
      },
      dialogVisible: false
    };
  },
  methods: {
    closeDialog() {
      this.dialogVisible = false;
      this.$emit("closeDialogFather");
    },
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let params = {
          contents: this.form.contents,
          status: this.form.isFinish,
          adviseId: this.complainId
        };
        API.addAdviseFollow(params).then(res => {
          this.$message.success("添加成功");
          this.$refs.form.resetFields();
          this.closeDialog()
          this.$emit("sureDialogFather");
        });
      });
    }
  },
  watch: {
    isAddHandleReault(val) {
      this.dialogVisible = val;
      if (!val) return;
    }
  }
};
</script>

<style lang="scss" scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>