<template>
  <div>
    <el-dialog
      title="编辑任务"
      :close-on-click-modal="false"
      :visible.sync="outerVisible"
      width="40%"
      @close="closeDialog"
    >
      <el-form :model="form">
        <el-form-item
          label="任务内容"
          :label-width="formLabelWidth"
          prop="content"
          :rules="[{ required: true, message: '请输入任务内容', trigger: 'blur' }]"
        >
          <el-input v-model="form.content" placeholder="请输入任务内容"></el-input>
        </el-form-item>
        <el-form-item
          label="任务开始时间"
          :label-width="formLabelWidth"
          prop="startTime"
          :rules="[{ required: true, message: '请选择任务开始时间', trigger: 'change' }]"
        >
          <el-date-picker
            v-model="form.startTime"
            :picker-options="startDatePicker"
            value-format="yyyy-MM-dd HH:mm:ss"
            type="datetime"
            placeholder="选择日期时间"
            class="item-css"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="重复任务" :label-width="formLabelWidth">
          <el-select v-model="form.type" placeholder="请选择">
            <el-option
              v-for="item in repetitionList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="提醒时间" :label-width="formLabelWidth">
          <el-date-picker
            v-model="form.remindTime"
            value-format="yyyy-MM-dd HH:mm:ss"
            :picker-options="startDatePicker"
            type="datetime"
            placeholder="选择提醒时间"
            class="item-css"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label="参与人"
          :label-width="formLabelWidth"
          :remote-method="remoteMethod"
          prop="joinName"
        >
          <el-select v-model="form.joinName" multiple filterable placeholder="请选择" class="item-css">
            <el-option
              v-for="item in participantList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updateTask" type="success">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/customer";
import * as filters from "@/filters/index";

// 编辑任务
export default {
  name: "NewTask",
  props: ["isEditTask", "taskDetail"],
  data() {
    return {
      outerVisible: this.isEditTask,
      startDatePicker: {
        // 跟进事件只能选择今天之后的日期
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //如果没有后面的-8.64e6就是不可以选择今天的
        }
      },

      formLabelWidth: "110px",
      form: {
        id: "", // 任务id
        content: "", // 任务内容
        startTime: "", // 开始时间
        type: "", // 重复任务
        remindTime: "", // 提醒时间
        joinName: "", // 参与人
        linkId: "" // 关联id
      },
      // 重复列表
      repetitionList: [
        { name: "从不重复", id: 0 },
        { name: "每天重复", id: 1 },
        { name: "每周重复", id: 2 },
        { name: "每月重复", id: 3 },
        { name: "每年重复", id: 4 }
      ],
      // 参与人列表
      participantList: []
    };
  },
  methods: {
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.options = this.list.filter(item => {
            return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
          });
        }, 200);
      } else {
        this.options = [];
      }
    },
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather");
    },
    //编辑任务
    updateTask() {
      let form = this.form;
      let params = {
        id: form.id,
        content: form.content,
        startTime: form.startTime,
        type: form.type,
        remindTime: form.remindTime,
        joinName: form.joinName.join(",")
      };
      console.log(params);

      API.postupdateTask(params)
        .then(res => {
          console.log(res);

          this.$message({
            type: "success",
            message: "修改成功!"
          });
          this.$emit("sureDialogFather");
          this.closeDialog();
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    }
  },
  watch: {
    isEditTask(value) {
      this.outerVisible = this.isEditTask;
      if (!value) return;

      let taskDetail = Object.assign({}, this.taskDetail);
      console.log(taskDetail);
      let form = taskDetail;
      // for (const key in taskDetail) {
      //   if (taskDetail.hasOwnProperty(key) && form.hasOwnProperty(key)) {
      //     form[key] = taskDetail[key];
      //   }
      // }
      form.joinName = taskDetail.joinName.split(",");
      this.form = form;

      API.getAllUser().then(res => {
        let arr = [];
        for (const item of res.data) {
          arr.push({
            label: item.name,
            value: item.name
          });
        }
        this.participantList = arr;
      });
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input {
    width: auto;
  }
  // .el-input:not(.el-input--suffix) {
  //   width: 1%;
  // }
}
</style>