// 联系人管理
import fetch from '@/utils/fetch';
import Qs from 'qs'
export default {
    // get 带参请求
    // 地区api
    getRegion(data) {
        return fetch({
            url: `area/all`,
            method: 'get',
            params: data
        })
    },
    // 省
    // getProvince(data){
    //     return fetch({
    //         url:`area/findAllProvince`,
    //         method:'get',
    //         params:data
    //     })
    // },
    // 市
    // getCity(data){
    //     return fetch({
    //         url:`area/findCity/${data}`,
    //         method:'get'
    //     })
    // },

    //查看跟进
    getFollowList(data) {
        return fetch({
            url: `/follow/getFollowList`,
            method: 'get',
            params: data
        })
    },

    // 根据表格id查询附件
    getCustomerFileById(data) {
        return fetch({
            url: `customer/findCustomerFileById/${data}`,
            method: 'get'
        })
    },
    // 生日
    getCreationTime(data) {
        return fetch({
            url: `customer/findCustomerTimeCode`,
            method: 'get',
            params: data
        })
    },
    // 所属部门的api
    getDepartments(data) {
        return fetch({
            url: `customer/findAllDepart`,
            method: 'get',
            params: data
        })
    },
    // 所有负责人
    getAllUser(data) {
        return fetch({
            url: `sea/findAllUser`,
            method: 'get',
            params: data
        })
    },
    // 根据选择的所属部门的id来获取负责人
    getHead(data) {
        return fetch({
            url: `customer/findAllDepartUser/${data}`,
            method: 'get'
        })
    },
    // 根据选择的所属负责人的id来获取部门
    getDepartByUserId(data) {
        return fetch({
            url: `customer/findDepartByUserId/${data}`,
            method: 'get'
        })
    },
    // 根据id查询联系人的具体信息
    getLinkmanDetails(data) {
        return fetch({
            url: `linkman/findLinkmanDetails/${data}`,
            method: 'get'
        })
    },
    // 跟进客户名称模糊查询 所属客户
    getCustomerByCompanyName(data) {
        return fetch({
            url: `customer/findCustomerByCompanyName`,
            method: 'get',
            params: data
        })
    },
    // 联系人的数据回显相关
    getLinkmanById(data) {
        return fetch({
            url: `linkman/echoLinkmanById/${data}`,
            method: 'get'
        })
    },
    // post 带参请求
    // 表格数据api
    postList(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `linkman/findAllLinkman?${data1}`,
            method: 'post'
        })
    },
    // 删除联系人api
    postdeleteLinkman(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `linkman/deleteLinkman?${data1}`,
            method: 'post'
        })
    },
    // 新增联系人
    postaddLinkman(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/addLinkman?${data1}`,
            method: 'post'
        })
    },
    // 修改联系人
    postupdateLinkman(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `linkman/updateLinkman?${data1}`,
            method: 'post'
        })
    },
}