<template>
  <el-card class="follow-card">
    <div slot="header" class="follow-header">
      <div>
        <p>
          <span>跟进记录</span>
          &nbsp;&nbsp;&nbsp;
        </p>
        <el-button
          type="warning"
          v-permi="'/customer/addCustomerFollow'"
          @click="$emit('writeFollow')"
        >写跟进</el-button>
      </div>
      <slot></slot>
    </div>
    <div class="text">
      <div class="timeline-block">
        <el-timeline>
          <el-timeline-item
            v-for="(objItem, index) in followObj"
            :key="index"
            :timestamp="index"
            placement="top"
          >
            <el-card
              style="width: 100%;margin: 10px 0;"
              v-for="(item, index) in objItem"
              :key="index"
            >
              <div class="content">
                <div class="follow-content">
                  <div class="timeline-contents">{{item.contents}}</div>
                  <div>
                    <el-button
                      size="mini"
                      type="primary"
                      v-permi="'/follow/replyFollow'"
                      @click="outerVisible=true;writeId=item.id"
                      plain
                      round
                    >写批复</el-button>
                  </div>
                </div>
                <div class="timeline-item">
                  <span>创建人: {{item.createUserName||'未知用户'}}</span>
                  <span>创建时间: {{item.createTime}}</span>
                </div>
              </div>
              <div class="reply" v-show="item.children.length">
                <div v-for="(replyItem, index ) in item.children" :key="index">
                  <p>{{replyItem.contents}}</p>
                  <div class="reply-name-time">
                    <span>批复人:{{replyItem.createUserName}}</span>
                    <span>时间: {{replyItem.createTime}}</span>
                  </div>
                </div>
              </div>
            </el-card>
          </el-timeline-item>
        </el-timeline>
        <div class="pager">
          <el-pagination
            small
            @current-change="currentChange"
            layout="prev, pager, next"
            :total="pageObj.total"
            :page-size="pageObj.pageSize"
          ></el-pagination>
        </div>
      </div>
    </div>

    <!-- 批复 -->
    <el-dialog title="跟进批复" :visible.sync="outerVisible" width="40%">
      <el-form ref="form" :model="form" label-width="90px">
        <el-form-item
          label="批复意见"
          prop="contents"
          :rules="[{ required: true, message: '请输入批复意见', trigger: 'blur' }]"
        >
          <el-input
            style="width:60%"
            v-model="form.contents"
            type="textarea"
            placeholder="请输入内容"
            show-word-limit
            :maxlength="100"
            :autosize="{ minRows: 4, maxRows: 8}"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="success" @click="sureDialog">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </el-card>
</template>


<script>
import API from "@/api/contract";
export default {
  name: "FollowUpHistory",
  props: {
    customerId: { type: Number, default: null },
    type: { type: Number, default: null }
  },
  data() {
    return {
      followList: [],
      pageObj: {
        pageNum: 1,
        pageSize: 5,
        total: 0
      },
      writeId: null,
      form: {
        contents: ""
      },
      outerVisible: false
    };
  },
  methods: {
    currentChange(a) {
      this.pageObj.pageNum = a;
      this.getFollowData();
    },
    sureDialog() {
      let params = {
        contents: this.form.contents,
        type: this.type,
        linkId: this.customerId,
        pid: this.writeId
      };
      API.replyFollow(params).then(res => {
        console.log(res);
        this.$message.success("保存成功");
        this.writeId = null;
        this.$refs.form.resetFields();
        this.outerVisible = false;
        this.getFollowData();
      });
    },
    getFollowData() {
      let params = {
        linkId: this.customerId,
        type: this.type,
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize
      };

      API.getFollowList(params).then(res => {
        let data = res.data;
        this.followList = data.list;
        this.pageObj.total = data.total;
      });
    }
  },

  watch: {
    customerId(val) {
      if (val) {
        this.$nextTick(() => {
          this.getFollowData();
        });
      }
    }
  },
  computed: {
    followObj() {
      if (!this.followList.length) return {};
      let followList = JSON.parse(JSON.stringify(this.followList));
      let followObj = {};
      for (const item of followList) {
        let followData = item.createTime && item.createTime.slice(0, 10);
        if (followObj[followData]) {
          followObj[followData].push(item);
        } else {
          followObj[followData] = [item];
        }
      }
      console.log(followObj);

      return followObj;
    }
  }
};
</script>

<style lang="scss" scoped>
.follow-card {
  .follow-header {
    font-size: 18px;
    height: 100%;
    padding: 0 20px;
    div {
      align-items: center;
      display: flex;
      justify-content: space-between;
    }
  }

  .timeline-item,
  .follow-content {
    display: flex;
    justify-content: space-between;
    margin: 20px 0 10px;
    font-size: 14px;
  }
  .reply {
    & > div {
      margin-top: 15px;
      padding-top: 10px;
      border-top: 1px solid #ddd;
    }
    line-height: 24px;
    p {
      font-size: 16px;
      color:#555;
    }
    div.reply-name-time {

      display: flex;
      justify-content: space-between;
    }
    span {
      color: #999;
    }
  }
  .timeline-contents {
    font-size: 18px;
    line-height: 28px;
  }
}
/deep/.el-dialog {
  width: 40%;
  padding: 0 20px;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
.pager {
  text-align: right;
}
</style>
