<template>
  <div>
    <el-dialog
      title="编辑机会"
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      @close="closeDialog"
    >
      <el-form :model="form" :rules="rules" label-width="120px" ref="form">
        <el-form-item label="机会名称" prop="name">
          <el-input v-model="form.name" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="所属客户" prop="customerId">
          <el-select
            v-model="form.customerId"
            @change="customerChange"
            placeholder="请选择"
            class="item-css"
            filterable
          >
            <el-option
              v-for="(item, index) in restaurants"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="关联联系人" prop="linkmanId">
          <el-select v-model="form.linkmanId" placeholder="请选择">
            <el-option
              v-for="(item, i) in LinkmenList"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务类型" prop="serviceId">
          <el-select v-model="form.serviceId" placeholder="请选择业务类型">
            <el-option
              v-for="(item, i) in BusinessTypes"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="预计销售金额" prop="expectPrice">
          <el-input v-model="form.expectPrice" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="预计签单日期 ">
          <el-date-picker
            v-model="form.expectDate"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
            class
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="销售阶段" prop="stage">
          <el-select v-model="form.stage" placeholder="请选择销售阶段" clearable class>
            <el-option v-for="(item, i) in SaleStage" :key="i" :label="item.val" :value="item.code"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="机会类型">
          <el-select v-model="form.type" placeholder="请选择机会类型" clearable class>
            <el-option
              v-for="(item, i) in ChanceType"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="机会获取日期">
          <el-date-picker
            v-model="form.createTime"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
            class
          ></el-date-picker>
        </el-form-item>-->
        <el-form-item label="机会来源" prop="source">
          <el-select v-model="form.source" placeholder="请选择" clearable class>
            <el-option
              v-for="(item, i) in clientSourceList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="下次跟进时间">
          <el-date-picker
            v-model="form.nextTime"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
            class
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="备注">
          <el-input
            v-model="form.remarks"
            style="width:60%;"
            maxlength="100"
            show-word-limit
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 10}"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="sureDialog" type="success">保存</el-button>
        <el-button @click="closeDialog">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 新增机会
import API from "@/api/chance";
export default {
  name: "EditChance",
  props: ["isShowChanceEdit", "opportunityId", "customerId"],
  data() {
    return {
      rules: {
        name: [{ required: true, message: "请输入机会名称", trigger: "blur" }],
        customerId: [
          { required: true, message: "请选择所属客户", trigger: "change" }
        ],
        linkmanId: [
          { required: true, message: "请选择联系人", trigger: "change" }
        ],
        serviceId: [
          { required: true, message: "请选择业务类型", trigger: "change" }
        ],
        stage: [
          { required: true, message: "请选择销售阶段", trigger: "change" }
        ],
        expectPrice: [
          { required: true, message: "请输入预计销售金额", trigger: "change" }
        ],
        type: [
          { required: true, message: "请选择机会类型", trigger: "change" }
        ],
        source: [
          { required: true, message: "请选择机会来源", trigger: "change" }
        ],
        followDepart: [
          { required: true, message: "请选择所属部门", trigger: "change" }
        ],
        followUser: [
          { required: true, message: "请选择负责人", trigger: "change" }
        ]
      },
      dialogVisible: this.isShowChanceEdit,
      restaurants: [], // 所属客户
      LinkmenList: [], // 关联联系人
      BusinessTypes: [], // 业务类型
      SaleStage: [], // 销售阶段
      ChanceType: [], // 机会类型
      clientSourceList: [], // 机会来源
      departmentsList: [], // 所属部门
      headList: [], // 所属负责人
      form: {},
      followDepart: null, // 所属部门id
      followUser: null // 负责人id
    };
  },
  methods: {
    // 关闭
    closeDialog() {
      this.dialogVisible = false;
      this.$emit("closeDialogFather");
    },
    // 保存
    sureDialog() {
      let form = this.form;
      console.log(form);
      let params = {
        id: this.opportunityId,
        name: form.name,
        customerId: form.customerId,
        linkmanId: form.linkmanId,
        serviceId: form.serviceId,
        expectPrice: form.expectPrice,
        expectDate: form.expectDate,
        stage: form.stage,
        type: form.type,
        getTime: form.getTime,
        source: form.source,
        nextTime: form.nextTime,
        followDepart: form.followDepart,
        followUser: form.followUser,
        // probability: form.probability,
        remarks: form.remarks
      };
      API.postupdateChance(params).then(res => {
        console.log(res);
        if (res.status == 200) {
          this.$message({
            message: "保存成功",
            type: "success"
          });
          this.$emit("sureDialogFather");
          this.closeDialog();
        }
      });
    },
    customerChange(val) {
      this.form.linkmanId = null
      API.getCustomerLinkman(val).then(res => {
        this.LinkmenList = res.data;
      });
    }
  },
  watch: {
    isShowChanceEdit(value) {
      this.dialogVisible = this.isShowChanceEdit;
      if (!value) return;

      Promise.all([
        API.getCustomerByCompanyName(),
        API.getAllUser(),
        API.findChanceById(this.opportunityId)
      ]).then(resArr => {
        console.log(resArr);
        this.restaurants = resArr[0].data;

        // 获取负责人列表
        this.headList = resArr[1].data;

        // 用户信息回显
        let formData = resArr[2].data;
        // 关联联系人
        this.customerChange(formData.customerId);
        formData.regionArr = [formData.province, formData.city];
        this.form = formData;
        this.$nextTick(() => {
          this.$refs.form.clearValidate();
        });
      });
      // 业务类型
      API.getType().then(res => {
        this.BusinessTypes = res.data;
      });
      // 销售阶段
      API.getSaleStage().then(res => {
        this.SaleStage = res.data;
      });
      // 机会类型
      API.getChanceType().then(res => {
        this.ChanceType = res.data;
      });
      //  机会来源实际就是客户来源的数据
      API.customerFrom().then(res => {
        this.clientSourceList = res.data;
      });
      // 机会来源
      // API.getchannel().then(res => {
      //   this.clientSourceList = res.data;
      // });
      // 所属部门
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>