<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-col :span="18">
          <h1>{{message.companyName}}</h1>
        </el-col>
      </el-col>
    </el-row>
    <!-- 按钮 -->
    <el-row class="el-dro">
      <el-button type="primary" @click="isShowCustomer=true">转成客户</el-button>
      <el-button type="primary" @click="isShowEdit=true">编辑</el-button>
      <el-button type="danger" @click="deletehigh">删除</el-button>
      <el-button type="warning" @click="showSend">发送短信</el-button>
    </el-row>

    <el-tabs type="card" @tab-click="handleClick">
      <el-tab-pane label="基本信息" class="quanjing">
        <el-card class="box-card">
          <div>
            <div class="text">基本信息</div>
            <div class="text">
              <p>业务类型：</p>
              <p>{{message.serviceName}}</p>
            </div>
            <div class="text">
              <p>客户名称：</p>
              <p>{{message.companyName}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text">联系信息</div>
            <div class="text">
              <p>电话：</p>
              <p>{{message.telephone}}</p>
            </div>
            <div class="text">
              <p>微信号：</p>
              <p>{{message.wechat}}</p>
            </div>
            <div class="text">
              <p>QQ：</p>
              <p>{{message.qq}}</p>
            </div>
            <div class="text">
              <p>邮箱：</p>
              <p>{{message.email}}</p>
            </div>
            <div class="text">
              <p>业务类型：</p>
              <p>{{message.serviceName}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text">其他信息</div>
            <div class="text">
              <p>状态：</p>
              <p>{{message.statusStr}}</p>
            </div>
            <div class="text">
              <p>来源：</p>
              <p>{{message.channelName}}</p>
            </div>
            <div class="text">
              <p>下次跟进时间：</p>
              <p>{{message.nextFollowTime}}</p>
            </div>
            <div class="text">
              <p>备注：</p>
              <p>{{message.remarks}}</p>
            </div>
            <div class="text">
              <p>业务级别：</p>
              <p>{{message.gradeStr}}</p>
            </div>
            <div class="text">
              <p>搜索词：</p>
              <p>{{message.searchKeywords}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text">系统信息</div>
            <div class="text">
              <p>转公海时间：</p>
              <p>{{message.toPublicTime}}</p>
            </div>
            <div class="text">
              <p>售前负责人：</p>
              <p>{{message.customerServiceName}}</p>
            </div>
            <div class="text">
              <p>创建人：</p>
              <p>{{message.createUser}}</p>
            </div>
            <div class="text">
              <p>前负责人：</p>
              <p>{{message.followName}}</p>
            </div>
            <div class="text">
              <p>所属部门：</p>
              <p>{{message.followDepart}}</p>
            </div>
            <div class="text">
              <p>前所属部门：</p>
              <p>{{message.followDepart}}</p>
            </div>
            <div class="text">
              <p>创建时间：</p>
              <p>{{message.createTime}}</p>
            </div>
            <div class="text">
              <p>更新于：</p>
              <p>{{message.updateTime}}</p>
            </div>
          </div>
        </el-card>
        <follow-up-history
          class="genjin box-card"
          @writeFollow="isShowFollow= true"
          :customerId="Number(highId)"
          :type="1"
          ref="followUpHistory"
        ></follow-up-history>
      </el-tab-pane>

      <el-tab-pane label="任务">
        <tab-task ref="tabTask" :customerId="highId"></tab-task>
      </el-tab-pane>
      <el-tab-pane label="附件">
        <tab-annex ref="tabAnnex" :customerId="highId"></tab-annex>
      </el-tab-pane>
      <el-tab-pane label="流转履历">
        <el-table :data="tableData" tooltip-effect="dark" style="width: 100%;" border :fit="true">
          <el-table-column prop="name" label="操作时间" width align="center"></el-table-column>
          <el-table-column prop="name" label="操作人员" width align="center"></el-table-column>
          <el-table-column prop="name" label="操作类型" width align="center"></el-table-column>
          <el-table-column prop="name" label="备注" width align="center"></el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
    <edit-customer
      :isShowEdit="isShowEdit"
      :customerId="highId"
      :customerLevel="customerLevel"
      :customerTypeList="customerTypeList"
      @sureDialogFather="getSonSure"
      @closeDialogFather="isShowEdit=false"
    ></edit-customer>

    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>

    <Followup
      :isShowFollow="isShowFollow"
      :customerId="highId"
      @sureDialogFather="$refs.followUpHistory.getFollowData()"
      @closeDialogFather="getSonCancel"
    ></Followup>
    <!-- 转成客户弹窗 -->
    <move-client
      :followUserList="followUserList"
      :checkboxId="[highId]"
      :isShowCustomer="isShowCustomer"
      @closeDialogFather="isShowCustomer=false"
      @sureDialogFather="$router.go(-1)"
    ></move-client>
  </div>
</template>

<script>
// 公海详情
import API from "@/api/highseas";
import TabTask from "@/components/element/TabTask";
import TabAnnex from "@/components/element/TabAnnex";
import EditCustomer from "@/components/element/EditCustomer"; // 编辑
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 跟进历史
import NewSend from "@/components/element/NewSend"; // 发送短信
import Followup from "@/components/element/Followup"; // 写跟进
import MoveClient from "./components/MoveClient"; // 转客户
export default {
  name: "highSeasdetails",
  components: {
    EditCustomer,
    NewSend,
    Followup,
    FollowUpHistory,
    MoveClient,
    TabAnnex,
    TabTask
  },
  data() {
    return {
      isShowCustomer: false,
      newfuzeren: "", // 转移给新负责人
      isShowEdit: false,
      customerLevel: [], // 客户级别
      customerTypeList: [], // 业务类型
      followUserList: [], // 新负责人
      isShowSend: false,
      isShowFollow: false,
      highId: null, // 当前详情的id
      message: {}, // 基本信息
      tableData: []
    };
  },
  methods: {
    // 转移至客户的保存
    save() {
      let obj = {
        ids: this.highId,
        userId: this.newfuzeren
      };
      API.posttoCustomer(obj).then(res => {
        if (res.status == 200) {
          this.$router.push({ path: "highSeas" });
          this.$message(
            {
              message: "转移成功",
              type: "success"
            },
            1000
          );
          this.isShowCustomer = false;
        }
      });
    },

    // 删除
    deletehigh() {
      this.$confirm("是否删除此线索?", "确认删除", {
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      })
        .then(() => {
          API.postdeleteSeaCustomer({ ids: this.highId }).then(res => {
            this.$router.push({ path: "highSeas" });
            this.$message({
              message: "删除成功",
              type: "success"
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 发送短信按钮 目前不能用
    showSend() {
      this.isShowSend = true;
    },
    // 写跟进
    showFollow() {
      this.isShowFollow = true;
    },
    // 保存Dialog
    getSonSure() {
      this.getSonCancel();
      this.getDetailData();
    },
    // 关闭Dialog
    getSonCancel() {
      this.isShowEdit = false;
      this.isShowSend = false;
      this.isShowFollow = false;
    },
    // tab 被选中时触发
    handleClick(tab, event) {
      if (tab.label == "基本信息") {
      } else if (tab.label == "附件") {
        this.$refs.tabAnnex.getAnnexTable();
      } else if (tab.label == "任务") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "流转履历") {
        // console.log('流转履历')
      }
    },
    getDetailData() {
      API.getfindSeaCustomerById(this.highId).then(res => {
        this.message = res.data;
      });
    }
  },

  mounted() {
    this.highId = this.$route.query.id;
    this.getDetailData();
    // 客户级别api
    API.getCustomerLevel().then(res => {
      let obj = { code: 0, val: "全部" };
      res.data.unshift(obj);
      this.customerLevel = res.data;
    });
    // 业务类型api
    API.getType().then(res => {
      let obj = { id: 0, name: "全部" };
      res.data.unshift(obj);
      this.customerTypeList = res.data;
    });
    // 所以负责人
    API.getAllUser().then(res => {
      this.followUserList = res.data;
    });
  }
};
</script>
<style lang='scss' scoped>
.el-dropdown-link {
  cursor: pointer;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-top: 20px;
  margin-bottom: 20px;
}

.text {
  font-size: 14px;
  font-size: 14px;
  margin-bottom: 18px;
  display: flex;
  & > :first-child {
    width: 100px;
    // text-align: right;
    // margin-right:10px;
  }
  & > :last-child {
    flex: 1;
    word-break: break-all;
    word-wrap: break-word;
  }
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  width: 48%;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;
}
.quanjing {
  position: relative;
}
span {
  font-size: 18px;
}
</style>