<template>
  <div>
    <el-dialog
      :close-on-click-modal="false"
      title="线索转换"
      :visible.sync="outerVisible"
      class="lodings"
      @close="closeDialog"
    >
      <el-scrollbar style="height:100%" ref="myScrollbar">
        <el-form label-width="110px" :model="ruleForm" :rules="rules" ref="ruleForm">
          <el-form-item label="客户名称" prop="guest">
            <el-input v-model="ruleForm.guest" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="公司名称" prop="companyName">
            <el-input v-model="ruleForm.companyName" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="业务类型" prop="serviceType">
            <el-select
              v-model="ruleForm.serviceType"
              placeholder="请选择业务类型"
              clearable
              class="item-css"
            >
              <el-option
                v-for="(item, i) in businessTypeList"
                :key="i"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="客户级别" prop="grade">
            <el-select v-model="ruleForm.grade" placeholder="请选择业务级别" clearable class="item-css">
              <el-option
                v-for="(item,i) in getCustomerLevel"
                :key="i"
                :label="item.val"
                :value="item.code"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="客户状态" prop="status">
            <el-select v-model="ruleForm.status" placeholder="请选择" clearable class="item-css">
              <el-option
                v-for="(item, index) in customerType"
                :key="index"
                :label="item.val"
                :value="item.code"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="公司电话" prop="telnum">
            <el-input v-model="ruleForm.telnum" placeholder="请输入"></el-input>
          </el-form-item>

          <!-- <el-form-item label="注册类型" prop="registerName">
                <el-input v-model="ruleForm.registerName" placeholder="请输入"></el-input>
          </el-form-item>-->

          <el-form-item label="下次跟进时间" prop="nextFollwTime">
            <el-date-picker
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="ruleForm.nextFollwTime"
              :picker-options="startDatePicker"
              type="datetime"
              placeholder="选择日期"
            ></el-date-picker>
          </el-form-item>

          <el-form-item label="是否已加微信" prop="isWechat">
            <el-select v-model="ruleForm.isWechat" placeholder="请选择" clearable class="item-css">
              <el-option label="是" value="1"></el-option>
              <el-option label="否" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="是否面谈" prop="isFace">
            <el-select v-model="ruleForm.isFace" placeholder="请选择" clearable class="item-css">
              <el-option label="是" value="1"></el-option>
              <el-option label="否" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="推广关键词" prop="saleKeywords">
            <el-input v-model="ruleForm.saleKeywords" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="客户搜索词" prop="keywords">
            <el-input v-model="ruleForm.keywords" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="ruleForm.email" placeholder="请输入"></el-input>
          </el-form-item>
          <!-- <el-form-item label="QQ" prop="qq">
                <el-input v-model="ruleForm.qq" placeholder="请输入"></el-input>
              </el-form-item>
              <el-form-item label="微信" prop="wechat">
                <el-input v-model="ruleForm.wechat" placeholder="请输入"></el-input>
          </el-form-item>-->
          <el-form-item label="人员规模" prop="staffSize">
            <el-input v-model="ruleForm.staffSize" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="部门" prop="followDepart">
            <el-select
              v-model="ruleForm.followDepart"
              placeholder="请选择"
              @change="departChange"
              class="item-css"
            >
              <el-option
                v-for="(item, index) in departmentsList"
                :key="index"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="负责人" prop="followUser">
            <el-select
              v-model="ruleForm.followUser"
              no-data-text="此部门无成员"
              placeholder="请选择"
              class="item-css"
            >
              <el-option
                v-for="(item, index) in headuserList"
                :key="index"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="remarks" label="备注">
            <el-input type="textarea" style="width:60%" v-model="ruleForm.remarks"></el-input>
          </el-form-item>
          <el-form-item prop="opinion" label="转客户意见">
            <el-input type="textarea" style="width:60%" :maxlength="100" v-model="ruleForm.opinion"></el-input>
          </el-form-item>

          <el-form-item label="联系人姓名" prop="linkName">
            <el-input v-model="ruleForm.linkName" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="联系人电话" prop="linkTel">
            <el-input disabled v-model="ruleForm.linkTel" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="sureDialog" type="success">保存</el-button>
            <el-button @click="closeDialog">取消</el-button>
          </el-form-item>
        </el-form>
      </el-scrollbar>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/clue";
import * as filters from "@/filters/index";

// 线索转换
export default {
  name: "customer",
  //----------是否展示 -------回显数据--
  props: ["isShowCustomer", "clueDetail"],
  data() {
    return {
      outerVisible: this.isShowCustomer,
      businessTypeList: [],
      ruleForm: {},
      rules: {
        guest: [{ required: true, message: "请输入客户姓名", trigger: "blur" }],
        companyName: [
          { required: true, message: "请输入公司名称", trigger: "blur" }
        ],
        registerName: [
          { required: true, message: "请输入注册类型", trigger: "blur" }
        ],
        linkName: [
          { required: true, message: "请输入联系人姓名", trigger: "blur" }
        ],
        status: [
          { required: true, message: "请选择客户状态", trigger: "change" }
        ],
        // staffSize: [
        //   { required: true, message: "请输入人员规模", trigger: "blur" }
        // ],
        linkTel: [
          { required: true, message: "请输入联系人号码", trigger: "blur" }
        ],
        // telnum: [
        //   { required: true, message: "请输入电话号码", trigger: "blur" }
        // ],
        serviceType: [
          { required: true, message: "请选择业务类型", trigger: "change" }
        ],
        // isFace: [
        //   { required: true, message: "请选择是否面谈", trigger: "change" }
        // ],
        grade: [
          { required: true, message: "请选择业务级别", trigger: "change" }
        ],
        // keywords: [
        //   { required: true, message: "请输入搜索词", trigger: "blur" }
        // ],
        followDepart: [
          { required: true, message: "请选择负责部门", trigger: "change" }
        ],
        followUser: [
          { required: true, message: "请选择负责人", trigger: "change" }
        ]
      },
      headuserList: [], // 负责人列表
      getCustomerLevel: [], // 级别菜单
      getRegion: [], //  地区二级联动数据列表
      customerSource: [], // 获取线索来源
      customerType: [], // 客户状态
      departmentsList: [], // 部门列表
      startDatePicker: {
        // 跟进事件只能选择今天之后的日期
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //如果没有后面的-8.64e6就是不可以选择今天的
        }
      }
    };
  },
  methods: {
    // 选择部门更改后获取负责人名单
    departChange(val) {
      API.getHead(val)
        .then(res => {
          this.$set(this.ruleForm, "followUser", "");
          this.headuserList = res.data;
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 新增页面的所属地区
    handldiqu(val) {
      this.ruleForm.province = val[0];
      this.ruleForm.city = val[1];
      console.log(this.ruleForm.province);
      console.log(this.ruleForm.city);
    },
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather");
    },
    // 保存
    sureDialog() {
      this.$refs.ruleForm.validate(valid => {
        if (!valid) return;
        let ruleForm = this.ruleForm;
        let params = {
          guest: ruleForm.guest,
          companyName: ruleForm.companyName,
          businessType: ruleForm.serviceType,
          grade: ruleForm.grade,
          opinion: ruleForm.opinion,
          status: ruleForm.status,
          telnum: ruleForm.telnum,
          // registerName: ruleForm.registerName,
          nextFollwTime: ruleForm.nextFollwTime,
          isWechat: ruleForm.isWechat,
          isFace: ruleForm.isFace,
          keyWords: ruleForm.keywords,
          saleKeywords: ruleForm.saleKeywords,
          staffSize: ruleForm.staffSize,
          email: ruleForm.email,
          followDepart: ruleForm.followDepart,
          followUser: ruleForm.followUser,
          remarks: ruleForm.remarks,
          linkName: ruleForm.linkName,
          linkTel: ruleForm.linkTel,
          clueId: ruleForm.id
        };
        // params.clueId = params.id;
        console.log(params);

        API.toCustomer(params)
          .then(res => {
            console.log(res);
            this.$message({
              message: "保存成功",
              type: "success"
            });
            this.closeDialog();
            this.$emit("sureDialogFather");
          })
          .catch(err => {
            if (err.indexOf("权限") === -1) this.$message.warning(err);

            console.error(err);
          });
      });
    }
  },
  watch: {
    isShowCustomer(value) {
      this.outerVisible = this.isShowCustomer;
      if (!value) return;
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
      // 客户状态api
      API.getCustomerType().then(res => {
        this.customerType = res.data;
      });
      // 所属级别
      API.getCustomerLevel().then(res => {
        this.getCustomerLevel = res.data;
      });
      API.getRegion().then(res => {
        res.data.forEach(item => {
          item.value = item.label;
          if (item.children.length == "0") {
            item.children = "";
          } else {
            item.children.forEach(item1 => {
              item1.value = item1.label;
            });
          }
        });
        this.getRegion = res.data;
      });
      let clueDetail = JSON.parse(JSON.stringify(this.clueDetail));
      API.getClueInfo({
        id: this.clueDetail.id
      })
        .then(res => {
          let data = res.data;
          data.status = 1;
          data.regionArr = [data.province, data.city];
          data.linkTel = data.telnum;
          data.telnum = "";
          let userInfo = this.$store.state.userInfo;
          data.followDepart = userInfo.departId;
          data.followUser = userInfo.id;
          this.ruleForm = data;
          console.log(this.ruleForm);
          // 业务类型
          API.getType().then(res => {
            this.businessTypeList = res.data;
          });
          API.getHead(data.followDepart)
            .then(res => {
              this.headuserList = res.data;
              this.$refs.ruleForm.clearValidate();
            })
            .catch(err => {
              console.error(err);
            });
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  padding: 0 20px;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>