<template>
  <div class="ditch">
    <div class="ditch-search">
      <span>渠道类型</span>
      <el-select v-model="selected.type" clearable @change="getChannelList" placeholder="请选择">
        <el-option label="线上" value="1"></el-option>
        <el-option label="线下" value="0"></el-option>
      </el-select>

      <span>是否收费</span>
      <el-select v-model="selected.isFree" @change="getChannelList" clearable placeholder="请选择">
        <el-option label="收费" value="0"></el-option>
        <el-option label="免费" value="1"></el-option>
      </el-select>
    </div>

    <div class="ditch-btn">
      <el-button
        type="primary"
        @click="channelDetail=null;isShowChannel=true"
        v-permi="'/channel/addChannel'"
      >新增渠道</el-button>
      <el-button type="danger" :disabled="!selectedItem.length" @click="delChannel">删除</el-button>
    </div>
    <div>
      <el-table
        :data="channelTableData"
        style="width: 100%;margin-bottom: 20px;"
        @selection-change="handleSelectionChange"
        row-key="id"
        border
        :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
      >
        <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
        <el-table-column label="渠道名称" prop="name" width align="center"></el-table-column>
        <el-table-column prop="name" label="渠道类型" width align="center">
          <template slot-scope="scope">{{scope.row.type?"线上":"线下"}}</template>
        </el-table-column>
        <el-table-column prop="isFree" label="是否收费" width align="center">
          <template slot-scope="scope">{{scope.row.isFree?"免费":"收费"}}</template>
        </el-table-column>
        <el-table-column label="操作" width="200" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              plain
              v-permi="'/channel/updateChannel'"
              @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button>
            <el-button
              size="mini"
              type="primary"
              v-show="scope.row.children"
              v-permi="'/channel/addChildrenChannel'"
              @click="addChildChannel(scope.$index, scope.row)"
            >增加子渠道</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页器 -->
    <div>
      <el-pagination
        class="itemPage"
        @size-change="sizeChangePage"
        @current-change="currentChangePage"
        :current-page="pageObj.pageNum"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageObj.totalSize"
      ></el-pagination>
    </div>

    <!-- 增加销售渠道 -->
    <new-channel
      :isShowChannel="isShowChannel"
      :channelDetail="channelDetail"
      @closeDialogFather="isShowChannel =false"
      @sureDialogFather="getChannelList()"
    ></new-channel>

    <edit-channel
      :isShowEdit="isShowEdit"
      :channelDetail="channelDetail"
      @closeDialogFather="isShowEdit=false"
      @sureDialogFather="getChannelList()"
    ></edit-channel>
  </div>
</template>

<script>
import API from "@/api/ditch";
import NewChannel from "./NewChannel";
import EditChannel from "./EditChannel";

export default {
  name: "TabSell",
  components: {
    NewChannel,
    EditChannel
  },
  data() {
    return {
      isShowChannel: false,
      channelTableData: [], // 渠道列表
      // 已选择的状态
      selected: {
        type: null,
        isFree: null
      },
      isShowEdit: false, // 展示编辑弹窗
      channelDetail: {}, // 传给编辑组件的详情
      // 分页器数据
      pageObj: {
        pageNum: 1,
        pageSize: 10,
        totalSize: null
      },
      selectedItem: [], // 被选中的条目
      showSellDialog: true
    };
  },
  methods: {
    addChildChannel(index, row) {
      this.isShowChannel = true;
      this.channelDetail = row;
    },
    // 当选择项发生变化时会触发该事件
    handleSelectionChange(val) {
      this.selectedItem = val;
    },
    // 删除渠道
    delChannel() {
      this.$confirm("是否确认删除所选渠道？", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      })
        .then(() => {
          let arr = [];
          let selectedItem = this.selectedItem;
          for (const item of selectedItem) {
            arr.push(item.id);
          }
          let params = { ids: arr.join(",") };
          API.delChannel(params)
            .then(res => {
              console.log(res);
              this.$message({
                type: "success",
                message: "删除成功"
              });
              this.getChannelList();
            })
            .catch(err => {
              console.error(err);
              if (err.indexOf("权限") === -1) this.$message.warning(err);
            });
        })
        .catch(err => {});
    },
    // 改变每页显示条数
    sizeChangePage(e) {
      this.pageObj.pageSize = e;
      this.getChannelList();
    },
    // 修改当前页
    currentChangePage(e) {
      this.pageObj.pageNum = e;
      this.getChannelList();
    },
    //编辑触发按钮
    handleEdit(index, row) {
      this.isShowEdit = true;
      this.channelDetail = row;
    },
    // 获取渠道列表
    getChannelList() {
      let pageObj = this.pageObj;
      let selected = this.selected;
      let params = {
        pageNum: pageObj.pageNum,
        pageSize: pageObj.pageSize,
        type: selected.type,
        isFree: selected.isFree
      };
      API.getChannelList(params)
        .then(res => {
          let data = res.data;
          this.pageObj.totalSize = data.total;
          this.channelTableData = data.list;
        })
        .catch(err => {
          console.error(err);
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    }
  },
  mounted() {
    this.getChannelList();
  }
};
</script>

<style lang="scss" scoped>
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}

/deep/.el-table__row--level-1 {
  background: oldlace;
}

div {
  .ditch-search {
    span {
      display: inline-block;
      font-size: 14px;
      width: 80px;
      height: 40px;
      line-height: 40px;
    }
  }
  .el-select {
    margin-right: 60px;
  }
  .ditch-btn {
    margin: 20px 0;
  }
}
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>

