<template>
  <div>
    <el-dialog
      title="编辑职位"
      :close-on-click-modal="false"
      :visible.sync="dialogFormVisible"
      @close="closeDialog"
    >
      <el-form :model="form" :rules="rules" ref="form" label-width="120px">
        <el-form-item label="职位名称" prop="name">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="职位级别" prop="level">
          <el-select v-model="form.level" placeholder="1级权限最大，10级最小">
            <el-option v-for="(item, index) in 10" :key="index" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注">
          <el-input v-model="form.remarks" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="success" @click="sureDialog">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/admin";

export default {
  name: "editPosition",
  props: ["isEditPosition", "positionDetail"],
  data() {
    return {
      form: {},
      dialogFormVisible: this.isEditPosition,
      rules: {
        name: [{ required: true, message: "请输入职位名称", trigger: "blur" }],
        level: [{ required: true, message: "职位级别", trigger: "change" }]
      }
    };
  },
  methods: {
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let params = this.form;
        delete params.createTime;
        console.log(params);
        API.updatePosition(params)
          .then(res => {
            console.log(res);
            this.$message({
              message: "修改成功",
              type: "success"
            });
            this.$emit("sureDialogFather");
            this.closeDialog();
          })
          .catch(err => {
            console.error(err);
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    },
    closeDialog() {
      this.dialogFormVisible = false;
      this.$emit("closeDialog");
    }
  },
  watch: {
    isEditPosition(value) {
      this.dialogFormVisible = this.isEditPosition;
      if (!value) return;
      this.form = Object.assign({}, this.positionDetail);
      this.$nextTick(() => this.$refs.form.clearValidate());
    }
  }
};
</script>

