<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-col :span="8">
          <h1>{{allList.guest}}</h1>
        </el-col>
        <!-- <el-col :span="8">
          <h3>负责人: {{allList.userName}}</h3>
        </el-col>-->
      </el-col>
    </el-row>
    <el-row style="margin:20px 0">
      <el-dropdown trigger="click" @command="changeCommand">
        <el-button type="primary" class="el-dropdown-link">
          {{allList.status|status}}
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item
            v-for="(item,index) in getAllstate"
            :key="index"
            :command="item.code"
          >{{item.val}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-button
        type="primary"
        style="margin-left:10px;"
        @click="isShowCustomer = true"
        v-show="allList.isChange==0"
        v-permi="'/clue/toCustomer'"
      >转成客户</el-button>
      <!-- <el-button type="primary" @click="transferType='transfer';isShowClueTransfer=true;">转移其他人</el-button> -->
      <el-button type="primary" @click="transferType='copyClue';isShowClueTransfer=true">添加负责人</el-button>
      <el-button type="danger" @click="transferType='remove';isShowClueTransfer=true">移除负责人</el-button>
      <el-button type="success" @click="showEditClue=true" v-permi="'/clue/updateClue'">编辑</el-button>
      <el-button type="danger" @click="delClue">删除</el-button>
      <el-button type="warning">发送短信</el-button>
    </el-row>

    <el-tabs type="card" @tab-click="tabClick">
      <el-tab-pane label="基本信息" class="quanjing">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>基本资料</span>
          </div>
          <div class="item">
            <p>业务类型：</p>
            <p>{{allList.registerName}}</p>
          </div>
          <div class="item">
            <p>客户名称：</p>
            <p>{{allList.guest}}</p>
          </div>
          <div class="item">
            <p>是否转成客户：</p>
            <p>{{allList.isChange==1?'是':'否'}}</p>
          </div>
        </el-card>
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>联系信息</span>
          </div>
          <div class="item">
            <p>联系人姓名：</p>
            <p>{{allList.linkName}}</p>
          </div>
          <div class="item">
            <p>电话：</p>
            <p>{{allList.telnum}}</p>
          </div>
          <div class="item">
            <p>微信号：</p>
            <p>{{allList.wechat}}</p>
          </div>
          <div class="item">
            <p>QQ号：</p>
            <p>{{allList.qq}}</p>
          </div>
          <div class="item">
            <p>邮箱：</p>
            <p>{{allList.email}}</p>
          </div>
        </el-card>
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>其他信息</span>
          </div>
          <div class="item">
            <p>线索状态：</p>
            <p>{{allList.status|status}}</p>
          </div>
          <div class="item">
            <p>线索来源：</p>
            <p>{{allList.clueFrom}}</p>
          </div>
          <div class="item">
            <p>下次跟进时间：</p>
            <p>{{allList.nextTime}}</p>
          </div>
          <div class="item">
            <p>备注：</p>
            <p>{{allList.remarks}}</p>
          </div>
          <div class="item">
            <p>业务级别：</p>
            <p>{{allList.level}}</p>
          </div>
          <div class="item">
            <p>客户搜索词：</p>
            <p>{{allList.keyWords}}</p>
          </div>
          <div class="item">
            <p>推广关键词：</p>
            <p>{{allList.saleKeywords}}</p>
          </div>
        </el-card>
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>系统信息</span>
          </div>
          <div class="item" v-show="allList.changeTime">
            <p>转客户时间：</p>
            <p>{{allList.changeTime}}</p>
          </div>
          <div class="item">
            <p>线索负责人：</p>
            <p>{{$route.query.userName}}</p>
          </div>
          <div class="item">
            <p>创建人：</p>
            <p>{{allList.createUser}}</p>
          </div>
          <!-- <div class=" item">
            <p>前负责人：</p>
            <p>{{allList.registerName}}</p>
          </div>-->
          <div class="item">
            <p>所属部门：</p>
            <p>{{allList.followDepartName}}</p>
          </div>
          <!-- <div class=" item">
            <p>前所属部门：</p>
            <p>{{allList.isChange}}</p>
          </div>-->
          <div class="item">
            <p>创建时间：</p>
            <p>{{allList.createTime}}</p>
          </div>
          <div class="item" v-if="allList.isChange==1">
            <p>转客户时间：</p>
            <p>{{allList.changeTime}}</p>
          </div>
        </el-card>
        <!-- 跟进历史 -->
        <follow-up-history
          class="genjin box-card"
          @writeFollow="isShowFollow= true"
          :customerId="Number(linkId)"
          :type="0"
          ref="followUpHistory"
        >
          <!-- <span style="font-size:16px;">负责人: {{allList.userName}}</span> -->
        </follow-up-history>
      </el-tab-pane>
      <el-tab-pane label="任务">
        <el-button type="warning" @click="addTaskDialog = true">新增任务</el-button>
        <el-table
          :data="taskData"
          tooltip-effect="dark"
          style="width: 100%;margin-top: 20px;"
          border
          :fit="true"
          width="200px"
        >
          <el-table-column label="任务内容" width align="center" header-align="center">
            <template slot-scope="scope">{{ scope.row.content }}</template>
          </el-table-column>
          <el-table-column prop="startTime" label="开始时间" width align="center"></el-table-column>
          <el-table-column prop="type" label="任务类型" width align="center">
            <template slot-scope="scope">{{ scope.row.type|type }}</template>
          </el-table-column>
          <el-table-column prop="remindTime" label="提醒时间" width align="center"></el-table-column>
          <el-table-column prop="joinName" label="参与人" width align="center"></el-table-column>
          <el-table-column prop="createName" label="创建人" width align="center"></el-table-column>
          <el-table-column prop="createTime" label="创建时间" width align="center"></el-table-column>
          <el-table-column label="操作" width="150" align="center" fixed="right">
            <template slot-scope="scope">
              <el-button size="mini" @click="taskEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" @click="taskDelete(scope.$index, scope.row)" type="danger">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          class="itemPage"
          @size-change="sizeChangePage($event,'Task')"
          @current-change="currentChangePage($event,'Task') "
          :current-page="TaskPageObj.pageNum"
          layout="total, sizes, prev, pager, next, jumper"
          :total="TaskPageObj.totalSize"
        ></el-pagination>
      </el-tab-pane>
      <el-tab-pane label="附件">
        <el-button type="warning" @click="isShowUpload = true">上传附件</el-button>
        <el-table
          :data="annexList"
          tooltip-effect="dark"
          style="width: 100%;margin-top: 20px;"
          border
          :fit="true"
          width="200px"
        >
          <el-table-column label="上传时间" width align="center" header-align="center">
            <template slot-scope="scope">{{ scope.row.createTime }}</template>
          </el-table-column>
          <el-table-column prop="name" label="附件名称" width align="center"></el-table-column>
          <el-table-column prop="size" label="大小" width align="center"></el-table-column>
          <el-table-column prop="remarks" label="备注" width align="center"></el-table-column>
          <el-table-column label="操作" width="260" align="center" fixed="right">
            <template slot-scope="scope">
              <!-- <el-button size="mini" @click="editAnnex(scope.$index, scope.row)">编辑</el-button> -->
              <el-button size="mini" @click="downloadAnnex(scope.$index, scope.row)">下载</el-button>
              <el-button size="mini" @click="deleteAnnex(scope.$index, scope.row)" type="danger">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          class="itemPage"
          @size-change="sizeChangePage($event,'Annex')"
          @current-change="currentChangePage($event,'Annex')"
          :current-page="AnnexPageObj.pageNum"
          layout="total, sizes, prev, pager, next, jumper"
          :total="AnnexPageObj.totalSize"
        ></el-pagination>
      </el-tab-pane>
      <el-tab-pane label="流转履历">
        <tab-log ref="tabLog" :clueId="linkId"></tab-log>
      </el-tab-pane>
    </el-tabs>

    <!-- 写跟进 -->
    <follow-up
      :isShowFollow="isShowFollow"
      :linkId="linkId"
      @closeDialogFather="isShowFollow=false"
      @sureDialogFather="$refs.followUpHistory.getFollowData();findClue()"
    ></follow-up>

    <!-- 添加任务 -->
    <new-task
      :isShowTask="addTaskDialog"
      :linkData="{id:linkId,isChange:0}"
      @closeDialogFather="addTaskDialog=false"
      @sureDialogFather="findTask()"
    ></new-task>
    <!-- 编辑任务 -->
    <edit-task
      :isEditTask="editTaskDialog"
      @closeDialogFather="editTaskDialog=false"
      :taskDetail="editTaskData"
      @sureDialogFather="findTask()"
    ></edit-task>

    <!-- 上传附件 -->
    <upload-annex
      :isShowUpload="isShowUpload"
      :linkId="linkId"
      @closeDialogFather="isShowUpload=false"
      @sureDialogFather="findAnnex()"
    ></upload-annex>

    <!-- 编辑附件 -->
    <el-dialog
      title="编辑"
      :close-on-click-modal="false"
      width="40%"
      class="editAnnexDialog"
      :visible.sync="editAnnexDialog"
      :modal-append-to-body="false"
    >
      <el-form :model="editAnnexForm" ref="editAnnexForm" label-width="110px">
        <el-form-item
          label="附件名称"
          style="width:50%"
          prop="name"
          :rules="{ required: true, message: '请输入附件名称', trigger: 'blur' }"
        >
          <el-input v-model="editAnnexForm.name"></el-input>
        </el-form-item>
        <el-form-item label="备注" style="width:60%">
          <el-input type="textarea" v-model="editAnnexForm.remarks"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitEditAnnex">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!-- <el-dialog title="选择移除人员" :visible.sync="showRemoveFollow">
      <el-form :model="removeFollowForm">
        <el-form-item label="活动区域" label-width="120px">
          <el-select v-model="removeFollowForm.region" multiple filterable reserve-keyword>
            <el-option label="区域一" value="shanghai"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showRemoveFollow = false">取 消</el-button>
        <el-button type="primary" @click="showRemoveFollow = false">确 定</el-button>
      </div>
    </el-dialog>-->

    <!-- 转成客户 -->
    <Customer
      :isShowCustomer="isShowCustomer"
      @closeDialogFather="isShowCustomer=false"
      :clueDetail="allList"
    ></Customer>

    <!-- 线索转移 -->
    <clue-transfer
      :isShowClueTransfer="isShowClueTransfer"
      :clueList="[{id:linkId}]"
      :transferType="transferType"
      @closeDialogFather="isShowClueTransfer=false"
      @sureDialogFather="sureTransfer()"
    ></clue-transfer>

    <!-- 编辑 -->
    <EditClue
      :clueDetail="allList"
      :dialogFormVisible="showEditClue"
      @closeDialogFather="showEditClue=false"
      @sureDialogFather="findClue()"
    ></EditClue>
  </div>
</template>

<script>
import API from "@/api/clue";
import Customer from "../components/Customer";
import ClueTransfer from "../components/ClueTransfer";
import EditClue from "../components/EditClue";
import NewTask from "@/components/element/NewTask";
import EditTask from "@/components/element/EditTask";
import UploadAnnex from "../components/UploadAnnex";
import FollowUp from "../components/FollowUp";
import TabLog from "@/components/element/TabLog";
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 跟进历史
import * as filters from "@/filters/index";

const map = new Map();
export default {
  name: "message",
  components: {
    Customer,
    NewTask,
    FollowUpHistory,
    EditTask,
    TabLog,
    ClueTransfer,
    EditClue,
    UploadAnnex,
    FollowUp
  },
  data() {
    return {
      // 任务分页器
      TaskPageObj: {
        pageNum: 1,
        pageSize: 10,
        totalSize: null
      },
      // 附件分页器
      AnnexPageObj: {
        pageNum: 1,
        pageSize: 10,
        totalSize: null
      },
      transferType: "", // 线索转移或者复制
      showEditClue: false, // 展示编辑表单
      isShowCustomer: false, // 线索转换表单
      editTaskDialog: false, // 任务编辑
      isShowClueTransfer: false, //  线索转移表单
      isShowFollow: false, // 添加跟进
      addTaskDialog: false, // 新增任务表单
      isShowUpload: false,
      editTaskData: {}, // 任务编辑中回显的数据
      editAnnexDialog: false,
      // removeFollowForm: {},
      allList: [],
      uploadUrl: `${API.addAnnex}`,
      fileList: [
        {
          name: "food.jpeg",
          url:
            "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
        },
        {
          name: "food2.jpeg",
          url:
            "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
        }
      ],
      taskData: [], // 任务表格数据
      list2: [],
      list3: [],
      linkId: null,
      writeFollow: {
        contents: "",
        followUser: null,
        writeFollowstatus: "",
        writeFollowoption: [
          { label: "未处理", value: 0 },
          { label: "联系方式有效", value: 1 },
          { label: "联系方式无效", value: 2 },
          { label: "关闭", value: 3 }
        ]
      },
      getAllstate: [],
      annexList: [],
      options: [
        { name: "从不重复", id: 0 },
        { name: "每天重复", id: 1 },
        { name: "每周重复", id: 2 },
        { name: "每月重复", id: 3 },
        { name: "每年重复", id: 4 }
      ],
      editAnnexForm: {}
    };
  },

  methods: {
    sureTransfer() {
      let type = this.transferType;
      console.log(type);

      if (type === "transfer") {
        this.$router.go(-1);
      } else {
        this.findClue();
        this.$refs.followUpHistory.getFollowData();
      }
    },
    currentChangePage(e, type) {
      this[type + "PageObj"].pageNum = e;
      this["find" + type]();
      console.log(e);
    },
    sizeChangePage(e, type) {
      this[type + "PageObj"].pageSize = e;
      this["find" + type]();
      console.log(e);
    },
    // 删除线索
    delClue() {
      this.$confirm("是否确认删除所选线索", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      })
        .then(() => {
          // 参数为,分隔字符
          API.delClue({ ids: this.linkId })
            .then(res => {
              this.$message({
                type: "success",
                message: "已删除"
              });
              this.$router.go(-1);
            })
            .catch(err => {
              console.error(err);
              if (err.indexOf("权限") === -1) this.$message.warning(err);
            });
        })
        .catch(action => {
          console.log(action);

          this.$message({
            type: "error",
            message: "已取消"
          });
        });
    },
    // 更改状态
    changeCommand(e) {
      API.updateStatus({
        id: this.allList.id,
        status: e
      })
        .then(res => {
          this.allList.status = e;
          console.log(this.allList.status);
          this.$message({
            type: "success",
            message: "修改状态成功"
          });
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);

          console.error(err);
        });
    },
    //线索详情
    findClue(id) {
      API.findClue({ id: this.linkId })
        .then(res => {
          let data = res.data;
          // data.createTime = filters.parseTime(new Date(data.createTime));
          this.allList = res.data;
        })
        .catch(err => {
          console.error(err);
        });
    },

    // 获取任务列表
    findTask() {
      this.editTaskDialog = this.addTaskDialog = false;
      let params = {
        pageNum: this.TaskPageObj.pageNum,
        pageSize: this.TaskPageObj.pageSize,
        linkType: 0,
        linkId: this.linkId
      };
      API.findTask(params).then(res => {
        this.taskData = res.data.list;
        this.TaskPageObj.totalSize = res.data.total;
        console.log(this.taskData);
      });
    },
    findAnnex() {
      let params = {
        pageSize: this.AnnexPageObj.pageSize,
        pageNum: this.AnnexPageObj.pageNum,
        linkId: this.linkId,
        linkType: 0
      };
      API.findAnnex(params).then(res => {
        let list = res.data.list;
        list.forEach(item => {
          item.createTime = filters.parseTime(new Date(item.createTime));
        });
        this.AnnexPageObj.totalSize = res.data.total;
        this.annexList = list;
      });
    },
    //切换tab
    tabClick(tab, event) {
      let _this = this;
      if (tab.label == "基本信息") {
      } else if (tab.label == "任务") {
        this.findTask();
      } else if (tab.label == "附件") {
        this.findAnnex();
      } else if (tab.label == "流转履历") {
        this.$refs.tabLog.getLogTable();
      }
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    // 编辑任务
    taskEdit(index, row) {
      this.editTaskDialog = true;
      console.log(row);
      this.editTaskData = row;
    },
    // 保存修改
    submitEditAnnex() {
      this.$refs.editAnnexForm.validate(valid => {});
    },
    //删除任务
    taskDelete(index, row) {
      this.$confirm("是否确认删除所选任务", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      }).then(() => {
        API.delTask({
          ids: row.id
        })
          .then(res => {
            console.log(res);
            this.$message({
              type: "success",
              message: "删除成功"
            });
            this.findTask();
          })
          .catch(err => {
            console.error(err);
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
      console.log(index, row);
    },

    // 编辑附件
    editAnnex(index, row) {
      this.editAnnexDialog = true;
      console.log(index, row);
      this.editAnnexForm = row;
    },

    // 下载附件
    downloadAnnex(index, row) {
      window.open(
        this.$store.state.baseURL +
          "/annex/download?id=" +
          row.id +
          "&name=" +
          row.name
      );
    },
    // 删除附件
    deleteAnnex(index, row) {
      this.$confirm("是否删除？" + row.name, "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      }).then(() => {
        let params = {
          id: row.id
        };
        API.delAnnex(params).then(res => {
          this.$message({
            type: "success",
            message: "删除成功"
          });
          this.findAnnex();
        });
      });
    }
  },
  mounted() {
    let id = this.$route.query.id;
    this.linkId = id;
    // 获取四个状态
    API.getAllstate().then(res => {
      this.getAllstate = res.data;
      this.getAllstate.unshift({
        val: "全部",
        code: 4
      });
    });
    // 获取带过来的信息
    //详情加载
    this.findClue();
  },
  filters: {
    status(value) {
      if (value !== undefined) {
        let returnVal;
        value = value.toString();
        switch (value) {
          case "0":
            returnVal = "未处理";
            break;
          case "1":
            returnVal = "联系方式有效";
            break;
          case "2":
            returnVal = "联系方式无效";
            break;
          case "3":
            returnVal = "关闭";
            break;

          default:
            returnVal = "未知状态";
            break;
        }
        return returnVal;
      }
    },
    type(value) {
      if (value === null || value === undefined) return;
      switch (value) {
        case 0:
          return "从不重复";
          break;
        case 1:
          return "每天重复";
          break;
        case 2:
          return "每周重复";
          break;
        case 3:
          return "每月重复";
          break;
        case 4:
          return "每年重复";
          break;
        default:
          return "未知类型";
          break;
      }
    }
  }

  // watch: {
  //   isShowFollow(val, oldVal) {
  //     //监听弹窗
  //     if (val == true) {
  //       this.writeFollows();
  //     }
  //   }
  // }
};
</script>
<style lang='scss' scoped>
.lodings {
  .el-form-item {
    padding: 0px 80px;
  }
  /deep/ .el-dialog {
    margin-top: 15vh;
    height: 80%;
    // overflow: auto;
