<template>
  <div>
    <el-dialog title="新增任务" :visible.sync="outerVisible" width="40%" @close="closeDialog">
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="任务内容" :label-width="formLabelWidth" prop="content">
          <el-input v-model="form.content" placeholder="请输入任务内容"></el-input>
        </el-form-item>
        <el-form-item label="任务开始时间" :label-width="formLabelWidth" prop="startTime">
          <el-date-picker
            v-model="form.startTime"
            :picker-options="startDatePicker"
            value-format="yyyy-MM-dd HH:mm:ss"
            type="datetime"
            placeholder="选择日期时间"
            class="item-css"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="重复任务" :label-width="formLabelWidth" prop="type">
          <el-select v-model="form.type" placeholder="请选择">
            <el-option
              v-for="item in repetitionList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="提醒时间" :label-width="formLabelWidth" prop="remindTime">
          <el-date-picker
            v-model="form.remindTime"
            :picker-options="startDatePicker"
            type="datetime"
            placeholder="选择提醒时间"
            value-format="yyyy-MM-dd HH:mm:ss"
            class="item-css"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label="参与人"
          :label-width="formLabelWidth"
          :remote-method="remoteMethod"
          prop="joinName"
        >
          <el-select v-model="form.joinName" multiple filterable placeholder="请选择" class="item-css">
            <el-option
              v-for="item in participantList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addtasks" type="success">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/clue";
import * as filters from "@/filters/index";

// 新增任务
export default {
  name: "NewTask",
  props: ["isShowTask", "linkData"],
  data() {
    return {
      // startDatePicker: {
      //   // 跟进事件只能选择今天之后的日期
      //   disabledDate(time) {
      //     return time.getTime() < Date.now() - 8.64e7; //如果没有后面的-8.64e6就是不可以选择今天的
      //   }
      // },
      rules: {
        content: [
          { required: true, message: "请选择输入任务内容", trigger: "blur" }
        ],
        startTime: [
          { required: true, message: "请选择任务开始时间", trigger: "change" }
        ],
        type: [
          { required: true, message: "请选择重复周期", trigger: "change" }
        ],
        remindTime: [
          { required: true, message: "请选择提醒时间", trigger: "change" }
        ],
        joinName: [
          {
            type: "array",
            required: true,
            message: "请选择提醒时间",
            trigger: "change"
          }
        ]
      },
      outerVisible: this.isShowTask,
      startDatePicker: {
        // 跟进事件只能选择今天之后的日期
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //如果没有后面的-8.64e6就是不可以选择今天的
        }
      },

      formLabelWidth: "110px",
      form: {
        content: "", // 任务内容
        startTime: "", // 开始时间
        type: "", // 重复任务
        remindTime: "", // 提醒时间
        joinName: "" // 参与人
      },
      // 重复列表
      repetitionList: [
        { name: "从不重复", id: 0 },
        { name: "每天重复", id: 1 },
        { name: "每周重复", id: 2 },
        { name: "每月重复", id: 3 },
        { name: "每年重复", id: 4 }
      ],
      // 参与人列表
      participantList: []
    };
  },
  methods: {
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.options = this.list.filter(item => {
            return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
          });
        }, 200);
      } else {
        this.options = [];
      }
    },
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather", this.outerVisible);
    },
    //新增任务
    addtasks() {
      let params = {
        content: this.form.content,
        startTime: this.form.startTime,
        remindTime: this.form.remindTime,
        linkId: this.linkData.id,
        type: this.form.type,
        createTime: filters.parseTime(new Date()),
        linkType: this.linkData.isChange,
        createName: this.$store.state.userInfo.name,
        joinName: this.form.joinName.join(",")
      };
      console.log(params);

      API.addTask(params)
        .then(res => {
          this.$message({
            type: "success",
            message: "新增成功!"
          });
          this.$emit("sureDialogFather");
          this.closeDialog();
          this.$refs.form.resetFields();
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    }
  },
  mounted() {},
  watch: {
    isShowTask(value) {
      this.outerVisible = this.isShowTask;
      if (!value) return;

      API.getUserList({}).then(res => {
        let arr = [];
        for (const item of res.data) {
          arr.push({
            label: item.name,
            value: item.name
          });
        }
        this.participantList = arr;
      });
      this.$nextTick(() => {
        this.$refs.form.resetFields();
      });
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  .inline-input {
    width: 100%;
  }
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>