<template>
  <div>
    <el-dialog
      title="新增公司"
      :close-on-click-modal="false"
      :visible.sync="outerVisible"
      @close="closeDialog"
    >
      <el-form :model="form" label-width="100px" :rules="rules" ref="form">
        <el-form-item label="公司名称" prop="companyName">
          <el-input v-model="form.companyName"></el-input>
        </el-form-item>
        <el-form-item label="用户名" prop="userName">
          <el-input v-model="form.userName"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPassword">
          <el-input type="password" v-model="form.checkPassword"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="primary">保存</el-button>
          <el-button @click="closeDialog">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/business";

export default {
  name: "AddCompany",
  props: ["showAddDialog"],
  data() {
    // var password = (rule, value, callback) => {
    //   if (value === "") {
    //     callback(new Error("请输入密码"));
    //   } else {
    //     if (this.ruleForm.checkPass !== "") {
    //       this.$refs.ruleForm.validateField("password");
    //     }
    //     callback();
    //   }
    // };
    var checkPassword = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.form.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      outerVisible: this.showAddDialog,
      // 表单数据
      form: {
        companyName: "",
        userName: "",
        password: "",
        checkPassword: ""
      },
      rules: {
        companyName: [
          { required: true, message: "请输入公司名称", trigger: "blur" }
        ],
        userName: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
        checkPassword: [
          { required: true, validator: checkPassword, trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    // 确认提交
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let params = this.form;
        console.log(params);

        API.addCompany(params)
          .then(res => {
            this.$refs.form.resetFields();
            this.$message({
              type: "success",
              message: "添加成功"
            });
            this.$emit("sureDialogFather");
            this.closeDialog()
          })
          .catch(err => {
            console.error(err);
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    },
    // 关闭弹窗
    closeDialog() {
      this.$emit("closeDialog");
    }
  },
  watch: {
    showAddDialog(newVal) {
      this.outerVisible = newVal;
      if (!newVal) return;
    }
  }
};
</script>

<style lang="scss" scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>

