      <el-table-column prop="name" label="机会名称" align="center"></el-table-column>
      <el-table-column prop="companyName" label="对应客户" align="center"></el-table-column>
      <el-table-column prop="expectPrice" label="预计销售金额" align="center"></el-table-column>
      <el-table-column prop="expectDate" label="预计签单日期" align="center"></el-table-column>
      <el-table-column prop="stageStr" label="销售阶段" align="center"></el-table-column>
