<template>
  <div>
    <el-dialog
      title="编辑合同"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      @close="closeDialog"
    >
      <el-form :model="form" label-width="120px" :rules="rules" ref="form">
        <el-form-item
          label="合同名称"
          prop="name"
          :rules="[{ required: true, message: '请输入合同名称', trigger: 'blur' }]"
        >
          <el-input v-model="form.name" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="所属客户" prop="customerId">
          <el-select
            v-model="form.customerId"
            placeholder="请选择"
            class="item-css"
            disabled
            filterable
          >
            <el-option
              v-for="(item, index) in restaurants"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="合同总金额" prop="totalPrice">
          <el-input v-model="form.totalPrice" disabled placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="签约日期" prop="signDate">
          <el-date-picker
            v-model="form.signDate"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
            class="item-css"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="合同编号" prop="signNo">
          <el-input v-model="form.signNo" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="业务类型" prop="serviceId">
          <el-select v-model="form.serviceId" placeholder="请选择" clearable class="item-css">
            <el-option
              v-for="(item, i) in businessTypeList"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属套餐" prop="setMeal">
          <el-select v-model="form.setMeal" placeholder="请选择" clearable class="item-css">
            <el-option
              v-for="(item, i) in PackageList"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="订单来源" prop="from">
          <el-select v-model="form.from" placeholder="请选择" clearable class="item-css">
            <el-option
              v-for="(item, i) in clientSourceList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="提单日期">
          <el-date-picker
            v-model="form.ladingBillTime"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
            class="item-css"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="结束日期">
          <el-date-picker
            v-model="form.endTime"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
            class="item-css"
          ></el-date-picker>
        </el-form-item>

        <el-form-item label="合同状态" prop="status">
          <el-select v-model="form.status" placeholder="请选择" clearable class="item-css">
            <el-option
              v-for="(item, i) in contractStatusList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="支付账号">
          <el-select v-model="form.payNo" placeholder="请选择" clearable class="item-css">
            <el-option
              v-for="(item, i) in payaccountList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="下次跟进时间">
          <el-date-picker
            v-model="form.nextFollowTime"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期时间"
            class="item-css"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="进账备注">
          <el-input
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 10}"
            style="width:60%"
            maxlength="100"
            v-model="form.remarks"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
        <el-form-item label="意向度">
          <el-select v-model="form.intention" placeholder="请选择" clearable class="item-css">
            <el-option
              v-for="(item, i) in intentionList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="发票编号">
          <el-input v-model="form.receiptNo" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="发票开具">
          <el-select v-model="form.isInvoice" placeholder="请选择" clearable class="item-css">
            <el-option label="是" value="1"></el-option>
            <el-option label="否" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="进账类型">
          <el-select v-model="form.incomeType" placeholder="请选择" clearable class="item-css">
            <el-option
              v-for="(item, i) in sessionTypeList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="支付金额">
          <el-input disabled v-model="form.payment" placeholder="请输入"></el-input>
        </el-form-item>

        <el-form-item label="收据号" prop="receiptNumber">
          <el-input v-model="form.receiptNumber" placeholder="请输入"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="sureDialog" type="success">保存</el-button>
        <el-button @click="closeDialog">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 编辑合同
import API from "@/api/contract";
export default {
  name: "NewContractEdit",
  //          是否展示————————————当前合同id——————————合同状态列表—————————客户ID
  props: [
    "isShowContractEdit",
    "contractId",
    "contractStatusList",
    "customerId"
  ],
  data() {
    return {
      rules: {
        name: [{ required: true, message: "请输入合同名称", trigger: "blur" }],
        customerId: [
          { required: true, message: "请选择所属客户", trigger: "change" }
        ],
        totalPrice: [
          { required: true, message: "请输入合同总金额", trigger: "blur" }
        ],
        signDate: [
          { required: true, message: "请选择签约日期", trigger: "change" }
        ],
        signNo: [
          { required: true, message: "请填写合同编号", trigger: "blur" }
        ],
        serviceId: [
          { required: true, message: "请选择业务类型", trigger: "change" }
        ],
        setMeal: [
          { required: true, message: "请选择所属套餐", trigger: "change" }
        ],
        receiptNumber: [
          { required: true, message: "请填写收据号", trigger: "blur" }
        ],
        from: [
          { required: true, message: "请选择客户来源", trigger: "change" }
        ],
        telephone: [
          { required: true, message: "请填写客户电话", trigger: "blur" }
        ],
        payNo: [
          { required: true, message: "请选择支付账号", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择合同状态", trigger: "change" }
        ]
      },
      dialogVisible: this.isShowContractEdit,
      formLabelWidth: "100px",
      restaurants: [], // 所属客户
      businessTypeList: [], // 业务类型
      PackageList: [], // 所属套餐
      clientSourceList: [], // 订单来源
      payaccountList: [], // 支付账号
      intentionList: [], // 意向度
      sessionTypeList: [], // 进账类型
      departmentsList: [], // 所属部门
      headList: [], // 所属负责人
      fileList: [
        //上传的文件列表
      ],
      form: {}
    };
  },
  methods: {

    // 所属部门
    depart(val) {
      this.departId = val;
      if (val != null) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      }
      this.userId = null;
    },
    // 关闭
    closeDialog() {
      this.dialogVisible = false;
      this.$emit("closeDialogFather", this.dialogVisible);
    },
    // 保存
    sureDialog() {
      this.$refs.form.validate(vali => {
        if (!vali) return;
        let form = this.form;

        let params = {
          id: this.contractId,
          name: form.name,
          customerId: form.customerId,
          totalPrice: form.totalPrice,
          signDate: form.signDate,
          signNo: form.signNo,
          serviceId: form.serviceId,
          setMeal: form.setMeal,
          from: form.from,
          telephone: form.telephone,
          searchWords: form.searchWords,
          ladingBillTime: form.ladingBillTime,
          endTime: form.endTime,
          status: form.status,
          payNo: form.payNo,
          nextFollowTime: form.nextFollowTime,
          remarks: form.remarks,
          intention: form.intention,
          receiptNo: form.receiptNo,
          isInvoice: form.isInvoice,
          incomeType: form.incomeType,
          payment: form.payment,
          companyName: form.companyName,
          receiptNumber: form.receiptNumber
          // departId: this.departId,
          // userId: this.userId
        };
        console.log(params);

        API.postupdateContract(params).then(res => {
          console.log(res);
          if (res.status == 200) {
            this.$message(
              {
                message: "保存成功",
                type: "success"
              },
              
            );
            this.$emit("sureDialogFather");
          }
        });
      });
    }
  },
  watch: {
    isShowContractEdit(value) {
      this.dialogVisible = this.isShowContractEdit;
      if (!value) return;
      Promise.all([
        API.getCustomerByCompanyName(),
        API.getAllUser(),
        API.getfindContractById(this.contractId)
      ]).then(resArr => {
        // 客户数据
        let restaurants = resArr[0].data;
        restaurants.map(item => {
          item.value = item.name;
        });
        this.restaurants = restaurants;

        // 获取负责人列表
        let headList = resArr[1].data;
        this.headList = headList;

        // 用户信息回显
        let formData = resArr[2].data;
        formData.regionArr = [formData.province, formData.city];

        this.form = formData;
        this.$nextTick(() => {
          this.$refs.form.clearValidate();
        });
      });
      // 所属部门
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
      // 业务类型
      API.getType().then(res => {
        this.businessTypeList = res.data;
      });
      // 所属套餐
      API.getPackageTypes().then(res => {
        this.PackageList = res.data;
      });
      //  订单来源实际就是客户来源的数据
      API.customerFrom().then(res => {
        this.clientSourceList = res.data;
      });

      // 支付账号
      API.getContractPayNo().then(res => {
        this.payaccountList = res.data;
      });
      // 意向度
      API.getContractIntention().then(res => {
        this.intentionList = res.data;
      });
      // 进账类型
      API.getContractIncomeType().then(res => {
        this.sessionTypeList = res.data;
      });
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  .inline-input {
    width: 100%;
  }
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>