<template>
  <div>
    <el-row>
      <el-form label-width="140px">
        <el-row :span="24" class="list">
          <el-form-item label="业务类型">
            <ul>
              <li
                v-for="(item, index) in customerTypeList"
                :key="index"
                @click="changeService(index, item.id)"
                :class="serviceActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
            </ul>
          </el-form-item>
        </el-row>
        <el-row :span="24" class="list">
          <el-form-item label="成交日期">
            <ul>
              <li
                v-for="(item, index) in creationTime"
                :key="index"
                @click="changeTime(index, item.code)"
                :class="timeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
              <li
                @click="showDefinedDate=true;timeActive = -1"
                class="list-li"
                v-if="!showDefinedDate"
              >自定义时间段</li>
              <li v-else>
                <el-date-picker
                  v-model="chooseData.contactTime"
                  @change="timeCustom"
                  type="daterange"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                ></el-date-picker>
              </li>
            </ul>
          </el-form-item>
        </el-row>

        <el-row>
          <el-col :span="7">
            <el-form-item label="所属地区">
              <el-cascader
                :options="getRegion"
                v-model="chooseData.regionArr"
                clearable
                placeholder="请选择"
                class="item-css"
                @change="getTableData"
              ></el-cascader>
            </el-form-item>
          </el-col>

          <el-col :span="7">
            <el-form-item label="是否分配负责人">
              <el-select
                v-model="chooseData.isAllot"
                @change="isAllotChange"
                clearable
                placeholder="请选择"
                class="item-css"
              >
                <el-option label="是" :value="1"></el-option>
                <el-option label="否" :value="0"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="搜索">
              <el-input
                placeholder="请输入内容"
                @keydown.enter.native="getTableData"
                v-model="chooseData.search"
                clearable
                @clear="getTableData"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2">
            <el-button style="margin-left:20px;" @click="getTableData" type="primary">查询</el-button>
          </el-col>
        </el-row>

        <el-row :span="24" v-show="chooseData.isAllot==1">
          <el-col :span="7">
            <el-form-item label="所属部门">
              <el-select
                v-model="chooseData.department"
                placeholder="请选择"
                clearable
                class="item-css"
                @change="departChange($event)"
              >
                <el-option
                  v-for="(item, index) in departmentsList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="负责人">
              <el-select
                v-model="chooseData.followUser"
                placeholder="请选择"
                clearable
                class="item-css"
                :no-data-text="chooseData.department?'此部门暂无成员':'请选择部门'"
                @change="getTableData()"
              >
                <el-option
                  v-for="(item, index) in headList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-row>

    <!-- 筛选 -->
    <el-row>
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="primary"
        :disabled="!selectedItem.length"
        @click="isShowPotential=true"
        v-permi="'/aftersales/toOther'"
      >分配售后负责人</el-button>
      <el-button
        type="warning"
        :disabled="!selectedItem.length"
        icon="el-icon-message"
        @click="showSend"
      >发送短信</el-button>
    </el-row>

    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      :fit="true"
      @selection-change="handleSelectionChange"
      stripe
    >
      <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
      <el-table-column
        prop="companyName"
        v-if="showCol.indexOf('客户名称')>-1"
        label="客户名称"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="service.name"
        v-if="showCol.indexOf('业务类型')>-1"
        label="业务类型"
        align="center"
      ></el-table-column>
      <el-table-column
        v-if="showCol.indexOf('成交时间')>-1"
        label="成交时间"
        width="120"
        align="center"
        header-align="center"
      >
        <template slot-scope="scope">{{ scope.row.finishTime }}</template>
      </el-table-column>

      <el-table-column
        v-if="showCol.indexOf('所属地区')>-1"
        label="所属地区"
        prop="area"
        align="center"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column prop="name" v-if="showCol.indexOf('代账状态')>-1" label="代账状态" align="center"></el-table-column>
      <el-table-column
        prop="telephone"
        v-if="showCol.indexOf('电话')>-1"
        label="电话"
        width="150"
        align="center"
      ></el-table-column>
      <el-table-column v-if="showCol.indexOf('售后负责人')>-1" label="售后负责人" align="center">
        <template slot-scope="scope">{{ scope.row.user&&scope.row.user.name}}</template>
      </el-table-column>
      <el-table-column label="操作" width="150" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEditone(scope.$index, scope.row)"
            v-permi="'/customer/addChance'"
            type="primary"
            plain
          >添加二次需求</el-button>
          <el-button
            size="mini"
            @click="handleEdittow(scope.$index, scope.row)"
            type="primary"
            plain
            v-permi="'/task/addTask'"
          >添加联系计划</el-button>
          <el-button size="mini" @click="handleView(scope.$index, scope.row)" plain>查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      class="itemPage"
      style="text-align:right;margin-top: 20px;"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="pageObj.pageNum"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageObj.total"
    ></el-pagination>
    <!-- 分配负责人 -->
    <Potential
      :isShowPotential="isShowPotential"
      :PotentialId="selectedItem"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></Potential>
    <addsecondary-needs
      :isShowChance="isShowNeeds"
      :customerId="customerId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></addsecondary-needs>
    <addcontact-plan
      :isShowTask="isShowPlan"
      :linkData="{id:customerId,isChange:1}"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></addcontact-plan>
    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>
  </div>
</template>

<script>
// 会计工厂客户管理
import AddsecondaryNeeds from "../components/AddsecondaryNeeds"; // 添加二次需求
import AddcontactPlan from "../components/AddcontactPlan"; // 添加联系计划
import NewSend from "@/components/element/NewSend"; //  发送短信
import Potential from "../components/Potential"; // 转移负责人

import API from "@/api/after";

export default {
  name: "accountant",
  components: {
    Potential,
    AddsecondaryNeeds,
    AddcontactPlan,
    NewSend
  },
  data() {
    return {
      creationTime: [],
      timeActive: 0,
      customerId: null, // 添加计划的客户id
      selectedItem: [],
      serviceActive: 0,
      isShowPotential: false, // 转移售后负责人
      codeTime: null,
      codeService: null, // 业务类型code
      showDefinedDate: false, // 是否展示自定义时间input
      chooseData: {
        isAllot: null, // 是否分配负责人
        followUser: "",
        department: "",
        regionArr: [], // 已选择地址
        contactTime: [],
        searchSelect: ""
      },
      colOptions: [
        "客户名称",
        "业务类型",
        "成交时间",
        "所属地区",
        "代账状态",
        "电话",
        "售后负责人"
      ],
      showCol: [
        "客户名称",
        "业务类型",
        "成交时间",
        "所属地区",
        "代账状态",
        "电话",
        "售后负责人"
      ],
      customerTypeList: [], // 业务类型
      departmentsList: [], // 部门列表
      headList: [], // 人员列表
      getRegion: [], // 地址
      search: "",
      tableData: [],
      pageObj: {
        pageNum: 1,
        pageSize: 10,
        total: null
      },
      isShowNeeds: false,
      isShowPlan: false,
      isShowSend: false
    };
  },
  methods: {
    colChange() {
      localStorage.setItem("accountantTable", JSON.stringify(this.showCol));
    },
    handleSelectionChange(e) {
      this.selectedItem = e.map(item => item.id);
    },
    sizeChangePage(e) {
      this.pageObj.pageSize = e;
      this.getTableData();
    },
    currentChangePage() {
      this.pageObj.pageNum = e;
      this.getTableData();
    },
    // 自定义时间
    timeCustom(val) {
      if (val != null) {
        this.codeTime = null;
        this.getTableData();
      }
    },
    // 改变所属部门
    departChange(val) {
      this.chooseData.followUser = null;
      this.getTableData();
      if (!val) {
        this.headList = [];
        return;
      }
      // 根据选择的部门的id来获取负责人s
      API.getHead(val).then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.headList = res.data;
      });
    },
    isAllotChange(e) {
      console.log(e);
      if (!e) {
        this.chooseData.department = null;
        this.chooseData.followUser = null;
        this.getTableData();
      }
    },
    getTableData() {
      let pageObj = this.pageObj;
      let chooseData = this.chooseData;
      let params = {
        pageNum: pageObj.pageNum,
        pageSize: pageObj.pageSize,
        service: this.codeService,
        timeCode: this.codeTime,
        isDeal: chooseData.isAllot,
        province: chooseData.regionArr[0],
        city: chooseData.regionArr[1],
        startTime: chooseData.contactTime[0],
        endTime: chooseData.contactTime[0],
        dealDepartId: chooseData.department,
        dealUserId: chooseData.followUser
      };

      API.findAllCustomer(params)
        .then(res => {
          let data = res.data;
          this.pageObj.total = data.total;
          this.tableData = data.list;
        })
        .catch(err => {
          console.error(err);
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    },
    // 成交日期 的选取变化
    changeTime(index, code) {
      this.codeTime = code;
      this.timeActive = index;
      this.chooseData.contactTime = [];
      this.showDefinedDate = false;
      this.getTableData();
    },
    changeService(index, code) {
      this.serviceActive = index;
      this.codeService = code;
      this.getTableData();
    },
    // 发送短信
    showSend() {
      this.isShowSend = true;
    },

    // 查看
    handleView(index, row) {
      // console.log(index, row)
      this.$router.push({ path: "CustomerPanoramic", query: { id: row.id } });
    },
    // 添加二次需求
    handleEditone(index, row) {
      // console.log(index, row)
      this.customerId = row.id;
      this.isShowNeeds = true;
    },
    // 添加联系计划
    handleEdittow(index, row) {
      // console.log(index, row)
      this.customerId = row.id;
      this.isShowPlan = true;
    },
    // 保存Dialog
    getSonSure() {
      this.getSonCancel();
      this.getTableData();
    },
    // 关闭Dialog
    getSonCancel() {
      this.isShowSend = false;
      this.isShowNeeds = false;
      this.isShowPotential = false;
      this.isShowPlan = false;
      // console.log(this.isShow,'子组件点击取消后，父组件的isShow的值')
    }
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("accountantTable"));
    if (showCol) this.showCol = showCol;
    // 获取部门列表
    API.getDepartments().then(res => {
      this.departmentsList = res.data;
    });
    // 业务类型api
    API.getType().then(res => {
      let obj = { id: 0, name: "全部" };
      res.data.unshift(obj);
      this.customerTypeList = res.data;
    });

    // 创建时间api
    API.getCreationTime().then(res => {
      let data = res.data;
      data.splice(7);
      this.creationTime = res.data;
    });

    //地址
    API.getRegion().then(res => {
      res.data.forEach(item => {
        item.value = item.id;
        if (item.children.length == "0") {
          item.children = "";
        } else {
          item.children.forEach(item1 => {
            item1.value = item1.id;
          });
        }
      });
      this.getRegion = res.data;
    });
    this.getTableData();
  }
};
</script>
<style lang='scss' scoped>
ul {
  list-style-type: none;
  padding: 0;
}
.list {
  li {
    display: inline-block;
    padding: 0 20px;
    cursor: pointer;
  }
}

.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}
.item-css {
  width: 100%;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
</style>