<template>
  <div>
    <el-dialog
      title="编辑线索"
      :visible.sync="showDialog"
      :modal-append-to-body="false"
      :close-on-click-modal="false"
      @close="closeDialog"
      class="lodings"
    >
      <el-scrollbar style="height:100%" ref="myScrollbar">
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="120px"
          class="demo-ruleForm"
        >
          <el-form-item label="注册类型" prop="serviceType">
            <!-- <el-input v-model="ruleForm.serviceType" placeholder="个体、代账、注册+代账"></el-input> -->
            <el-select v-model="ruleForm.serviceType" placeholder="请选择" clearable>
              <el-option
                v-for="(item, i) in businessTypeList"
                :key="i"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="公司名称" prop="guest">
            <el-input v-model="ruleForm.guest"></el-input>
          </el-form-item>
          <el-form-item label="联系人姓名" prop="linkName">
            <el-input v-model="ruleForm.linkName"></el-input>
          </el-form-item>
          <!-- <el-form-item label="电话号码" prop="telnum">
            <el-input v-model="ruleForm.telnum"></el-input>
          </el-form-item>-->
          <el-form-item label="所属地区" prop="regionArr">
            <el-cascader
              :options="getRegion"
              v-model="ruleForm.regionArr"
              placeholder="请选择"
              class="item-css"
              @change="handldiqu"
            ></el-cascader>
          </el-form-item>
          <!-- <el-form-item label="跟进" prop="contents">
            <el-input v-model="ruleForm.contents"></el-input>
          </el-form-item>-->
          <el-form-item label="下次跟进时间" prop="nextFollwTime">
            <el-date-picker
              v-model="ruleForm.nextFollwTime"
              :picker-options="startDatePicker"
              value-format="yyyy-MM-dd HH:mm:ss"
              type="datetime"
              placeholder="选择日期"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="渠道来源" prop="source">
            <el-select v-model="ruleForm.source" disabled placeholder="请选渠道来源">
              <el-option
                v-for="(item,i) in getCustomerSource"
                :key="i"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="线索状态" prop="status">
            <el-select v-model="ruleForm.status">
              <el-option
                v-for="(item,i) in getAllstate"
                :key="i"
                :label="item.val"
                :value="item.code"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="备注">
            <el-input
              type="textarea"
              style="width:60%"
              show-word-limit
              maxlength="100"
              v-model="ruleForm.remarks"
            ></el-input>
          </el-form-item>
          <el-form-item label="业务级别" prop="grade">
            <el-select v-model="ruleForm.grade">
              <el-option
                v-for="(item,i) in getCustomerLevel"
                :key="i"
                :label="item.val"
                :value="item.code"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="推广关键词" prop="saleKeywords">
            <el-input v-model="ruleForm.saleKeywords" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="客户搜索词" prop="keywords">
            <el-input v-model="ruleForm.keywords" placeholder="如：南京注册公司"></el-input>
          </el-form-item>
          <!-- <el-form-item label="所属部门" prop="followDepart">
            <el-select v-model="ruleForm.followDepart" @change="departChange">
              <el-option
                v-for="(item,i) in getDepartments"
                :key="i"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="负责人" prop="followUser" required>
            <el-select v-model="ruleForm.followUser" no-data-text="此部门无成员">
              <el-option
                v-for="(item,i) in headuserList"
                :key="i"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>-->
          <el-form-item label="QQ号">
            <el-input v-model="ruleForm.qq"></el-input>
          </el-form-item>
          <el-form-item label="微信号">
            <el-input v-model="ruleForm.wechat"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="updateClue()">保存</el-button>
            <el-button type="info" @click="closeDialog">取消</el-button>
          </el-form-item>
        </el-form>
      </el-scrollbar>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/clue";
import * as filters from "@/filters/index";

export default {
  name: "EditCule",
  props: ["dialogFormVisible", "clueDetail"],
  data() {
    return {
      headuser: "",
      getAllstate: [],
      showDialog: false,
      headuserList: [], // 部门人员列表
      ruleForm: {},
      businessTypeList: [], // 注册类型
      rules: {
        serviceType: [
          { required: true, message: "请输入业务类型", trigger: "blur" }
        ],
        // guest: [{ required: true, message: "请输入客户姓名", trigger: "blur" }],
        linkName: [
          { required: true, message: "请输入联系人姓名", trigger: "blur" }
        ],
        telnum: [
          { required: true, message: "请输入电话号码", trigger: "blur" }
        ],
        regionArr: [
          {
            type: "array",
            required: true,
            message: "请选择所属地区",
            trigger: "change"
          }
        ],
        source: [
          { required: true, message: "请选择线索来源", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择线索状态", trigger: "change" }
        ],
        grade: [
          { required: true, message: "请选择业务级别", trigger: "change" }
        ],
        // keywords: [
        //   { required: true, message: "请输入搜索词", trigger: "blur" }
        // ],
        followDepart: [
          { required: true, message: "请选择负责部门", trigger: "change" }
        ],
        followUser: [
          { required: true, message: "请选择负责人", trigger: "change" }
        ]
      }, // 验证
      getRegion: [], // 地址
      startDatePicker: {
        // 跟进事件只能选择今天之后的日期
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //如果没有后面的-8.64e6就是不可以选择今天的
        }
      },
      getCustomerLevel: [],
      getCustomerSource: [],
      getDepartments: [], // 部门列表
      getHead: []
    };
  },
  methods: {
    departChange(val) {
      API.getHead(val)
        .then(res => {
          this.headuserList = res.data;
          this.$set(this.ruleForm, "followUser", "");
        })
        .catch(err => {
          console.error(err);
        });
    },
    //  关闭弹窗页面
    closeDialog() {
      this.showDialog = false;
      this.$refs.ruleForm.resetFields();

      this.$emit("closeDialogFather");
    },
    // 更新，发送请求
    updateClue() {
      this.$refs.ruleForm.validate(valid => {
        if (!valid) return;
        let ruleForm = this.ruleForm;
        let params = {
          province: ruleForm.regionArr[0],
          city: ruleForm.regionArr[1],
          nextFollwTime: ruleForm.nextFollwTime,
          guest: ruleForm.guest,
          linkName: ruleForm.linkName,
          telnum: ruleForm.telnum,
          source: ruleForm.source,
          keywords: ruleForm.keywords,
          saleKeywords: ruleForm.saleKeywords,
          serviceType: ruleForm.serviceType,
          // contents: ruleForm.contents,
          status: ruleForm.status,
          remarks: ruleForm.remarks,
          grade: ruleForm.grade,
          followDepart: ruleForm.followDepart,
          followUser: ruleForm.followUser,
          qq: ruleForm.qq,
          wechat: ruleForm.wechat,
          id: this.clueDetail.id
        };

        API.updateClue(params)
          .then(res => {
            console.log(res);
            this.$message({
              type: "success",
              message: "更新成功"
            });
            this.$emit("sureDialogFather");
            this.closeDialog();
          })
          .catch(err => {
            console.error(err);
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    },

    // 修改地区的操作
    handldiqu(val) {
      this.ruleForm.province = val[0];
      this.ruleForm.city = val[1];
      console.log(this.ruleForm.province);
      console.log(this.ruleForm.city);
    }
  },
  mounted() {},
  watch: {
    dialogFormVisible(val) {
      this.showDialog = this.dialogFormVisible;
      if (!val) return;

      //时间
      API.getRegion().then(res => {
        res.data.forEach(item => {
          item.value = item.label;
          if (item.children.length == "0") {
            item.children = "";
          } else {
            item.children.forEach(item1 => {
              item1.value = item1.label;
            });
          }
        });
        this.getRegion = res.data;
      });
      // 获取所有状态
      API.getAllstate().then(res => {
        this.getAllstate = res.data;
      });
      // 所属级别
      API.getCustomerLevel().then(res => {
        this.getCustomerLevel = res.data;
      });
      //所属部门
      API.getDepartments().then(res => {
        this.getDepartments = res.data;
        // 获取部门后获取部门人员列表
        API.getHead(res.data[0].id)
          .then(res => {
            this.headuserList = res.data;
          })
          .catch(err => {
            console.error(err);
          });
      });
      //渠道来源
      API.getCustomerSource().then(res => {
        this.getCustomerSource = res.data;
      });
      let clueDetail = JSON.parse(JSON.stringify(this.clueDetail));
      API.getClueInfo({
        id: this.clueDetail.id
      })
        .then(res => {
          let data = res.data;
          console.log(data);

          this.ruleForm = Object.assign({}, data);
          this.ruleForm.regionArr = [data.province, data.city];
          API.getHead(data.followDepart)
            .then(res => {
              this.headuserList = res.data;
              this.$refs.ruleForm.clearValidate();
            })
            .catch(err => {
              console.error(err);
            });
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
      // 业务类型
      API.getType().then(res => {
        this.businessTypeList = res.data;
      });
      // 滚动条置顶
      this.$nextTick(() => {
        let myScrollbar = this.$refs.myScrollbar.wrap;
        myScrollbar.scrollTop = 0;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>

