<template>
  <div>
    <el-dialog
      title="写跟进"
      :visible.sync="outerVisible"
      :modal-append-to-body="false"
      @close="closeDialog"
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="110px"
        class="demo-ruleForm"
      >
        <el-form-item label="内容" prop="contents">
          <el-input
            type="textarea"
            style="width:60%"
            :maxlength="100"
            :autosize="{ minRows: 4, maxRows: 8}"
            v-model="ruleForm.contents"
          ></el-input>
        </el-form-item>
        <!-- <el-form-item label="线索状态" prop="status">
          <el-select v-model="ruleForm.status">
            <el-option
              v-for="item in writeFollowoption"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>-->
        <el-form-item>
          <el-button type="primary" @click="sureDialog">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import * as filters from "@/filters/index";
import API from "@/api/clue";

// 写跟进
export default {
  name: "Followup",
  props: {
    isShowFollow: {
      type: Boolean,
      default: false
    },
    linkId: {
      type: String | Number,
      default: null
    }
  },
  data() {
    return {
      outerVisible: this.isShowFollow,
      formLabelWidth: "80px",
      ruleForm: {
        contents: null
      },
      rules: {
        contents: [
          { required: true, message: "请输入跟进内容", trigger: "blur" }
        ],
        status: [
          { required: true, message: "请选择线索状态", trigger: "change" }
        ]
      },
      writeFollowoption: [
        { label: "未处理", value: 0 },
        { label: "联系方式有效", value: 1 },
        { label: "联系方式无效", value: 2 },
        { label: "关闭", value: 3 }
      ]
    };
  },
  methods: {
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather");
    },
    // 保存
    sureDialog() {
      let time = new Date();
      this.$refs.ruleForm.validate(valid => {
        if (!valid) return;
        let params = {
          contents: this.ruleForm.contents,
          followUser: this.$store.state.userInfo.id,
          linkId: this.linkId,
          status: this.ruleForm.status,
          type: 0 //0:线索 1：客户
          // followTime: filters.parseTime(time)
        };
        console.log(params);

        API.writeFollow(params)
          .then(res => {
            this.$message({
              type: "success",
              message: "保存成功!"
            });
            this.$emit("sureDialogFather");
            this.$refs.ruleForm.resetFields();
            this.closeDialog();
          })
          .catch(err => {
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    }
  },
  mounted() {},
  watch: {
    isShowFollow() {
      this.outerVisible = this.isShowFollow;
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  padding: 0 20px;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>