import fetch from '@/utils/fetch';
// import Qs from 'qs'

export default {
    addClue(data) {
        return fetch({
            url: 'clue/addClue',
            method: 'post',
            params: data,
            name: "添加线索"
        })
    },
    getAll(data) {
        return fetch({
            url: 'clue/findClues',
            method: 'post',
            params: data,
            name: "查看"
        })
    },
    getAllSupport(data) {
        return fetch({
            url: 'clue/clueType',
            method: 'get',
            params: data,
            name: "线索"
        })
    },
    // 更改用户状态
    updateStatus(data) {
        return fetch({
            url: 'clue/updateStatus',
            method: 'get',
            params: data,
            name: "修改"
        })
    },

    // 转移至公海
    clueToPublic(data) {
        return fetch({
            url: `clue/clueToPublic`,
            method: 'post',
            params: data
        })
    },
    // 公海转线索
    publicToClue(data) {
        return fetch({
            url: `clue/publicToClue`,
            method: 'post',
            params: data
        })
    },



    // 获取所有用户
    getAllUser(data) {
        return fetch({
            url: `customer/findAllUser`,
            method: 'get',
            params: data,
            name: "查看"
        })
    },
    getAllstate(data) {
        return fetch({
            url: 'clue/status',
            method: 'get',
            params: data,
            name: ""
        })
    },
    // 导出合同
    exportClueExc(data) {
        return fetch({
            url: 'excel/getClueDetail',
            method: 'post',
            params: data,
            responseType: 'arraybuffer'
        })
    },
    // 导出网点表格
    exportDataExc(data) {
        return fetch({
            url: 'excel/getNetworkData',
            method: 'post',
            params: data,
            responseType: 'arraybuffer'
        })
    },
    // 导出渠道表格
    exportChannelExc(data) {
        return fetch({
            url: 'excel/getChannelCount',
            method: 'post',
            params: data,
            responseType: 'arraybuffer'
        })
    },
    // 修改线索
    updateClue(data) {
        return fetch({
            url: 'clue/updateClue',
            method: 'post',
            params: data,
            name: "修改"
        })
    },
    // 转公海
    toPublic(data) {
        return fetch({
            url: 'clue/toPublic',
            method: 'post',
            params: data,
            name: "修改"
        })
    },
    // 转给其他人
    toOther(data) {
        return fetch({
            url: 'clue/toOther',
            method: 'post',
            params: data,
            name: "修改"
        })
    },
    // 查看退回
    findReturnClues(data) {
        return fetch({
            url: 'clue/findReturnClues',
            method: 'post',
            params: data,
            name: "查看"
        })
    },
    // 复制其他人
    copyClue(data) {
        return fetch({
            url: 'clue/copyClue',
            method: 'post',
            params: data,
            name: "转移"
        })
    },
    getAlltime(data) {  //时间
        return fetch({
            url: '/customer/findCustomerTimeCode',
            method: 'get',
            params: data,
            name: ""
        })
    },
    // 获取公海列表
    findPublicList(data) {
        return fetch({
            url: `clue/findPublicList`,
            method: 'post',
            params: data
        })
    },
    // 地区api
    getRegion(data) {
        return fetch({
            url: 'area/all',
            method: 'get',
            params: data,
            name: ""
        })
    },
    // 获取部门列表
    getDepartments(data) {
        return fetch({
            url: 'customer/findAllDepart',
            method: 'get',
            params: data,
            name: ""
        })
    },
    // 根据选择的所属部门的id来获取负责人 
    getHead(data) {
        return fetch({
            url: `customer/findAllDepartUser/${data}`,
            method: 'get',
            name: "查看"
        })
    },
    // 删除
    delClue(data) {
        return fetch({
            url: `clue/delClue`,
            method: 'post',
            params: data,
            name: "删除"
        })
    },
    // 业务级别
    getCustomerLevel(data) {
        return fetch({
            url: 'customer/grade',
            method: 'get',
            params: data,
            name: ""
        })
    },
    // 线索来源api
    getCustomerSource(data) {
        return fetch({
            url: `customer/channel`,
            method: 'get',
            params: data,
            name: ""
        })
    },

    //详情任务
    findTask(data) {
        return fetch({
            url: `task/findTask`,
            method: 'get',
            params: data,
            name: "查看"
        })
    },
    //新增任务
    addTask(data) {
        return fetch({
            url: `task/addTask`,
            method: 'post',
            params: data,
            name: "新增"
        })
    },
    // 删除任务
    delTask(data) {
        return fetch({
            url: `task/delTask`,
            method: 'get',
            params: data,
            name: ""
        })
    },
    //编辑任务
    updateTask(data) {
        return fetch({
            url: `task/updateTask`,
            method: 'post',
            params: data,
            name: ""
        })
    },
    // 获取人员列表
    getUserList(data) {
        return fetch({
            url: `task/getUserList`,
            method: 'get',
            params: data,
            name: ""
        })
    },
    //附件
    findAnnex(data) {
        return fetch({
            url: `annex/findAnnex`,
            method: 'get',
            params: data,
            name: ""
        })
    },
    //上传附件  
    addAnnex(data) {
        return fetch({
            url: `annex/addAnnex`,
            method: 'post',
            data
        })
    },
    //写跟进
    writeFollow(data) {
        return fetch({
            url: `follow/writeFollow`,
            method: 'post',
            params: data,
            name: ""
        })
    },
    //查看跟进
    getFollowList(data) {
        return fetch({
            url: `/follow/getFollowList`,
            method: 'get',
            params: data,
        })
    },
    // 转成客户
    toCustomer(data) {
        return fetch({
            url: `clue/toCustomer`,
            method: 'post',
            params: data,
            name: ""
        })
    },
    // 查询线索日志
    findClueLog(data) {
        return fetch({
            url: `clue/findClueLog`,
            method: 'get',
            params: data,
            name: ""
        })
    },
    // 查询客户日志
    findCustomerLog(data) {
        return fetch({
            url: `customer/findCustomerLog`,
            method: 'get',
            params: data,
            name: ""
        })
    },

    //线索详情 
    findClue(data) {
        return fetch({
            url: `clue/findClue`,
            method: 'get',
            params: data,
            name: ""
        })
    },
    //发送短信接口
    sendsms(data) {
        return fetch({
            url: `/clue/sendsms`,
            method: 'get',
            params: data,
            name: ""
        })
    },
    // // 查看详情
    // findClueById(data) {
    //     return fetch({
    //         url: `/clue/findClueById`,
    //         method: 'get',
    //         params: data,
    //         name: ""
    //     })
    // },

    // 获取用户信息
    getUserInfo(data) {
        return fetch({
            url: '/getUserInfo',
            method: "get",
            params: data,
            name: ""
        })
    },
    // 驳回客户退回
    rejectReturnClue(data) {
        return fetch({
            url: '/clue/rejectReturnClue',
            method: "post",
            params: data,
            name: "操作"
        })
    },

    // 获取线索信息
    getClueInfo(data) {
        return fetch({
            url: '/clue/getClueInfo',
            method: "get",
            params: data,
            name: ""
        })
    },

    // 删除附件
    delAnnex(data) {
        return fetch({
            url: '/annex/delAnnex',
            method: "get",
            params: data,
            name: ""
        })
    },
    // 获取客户状态
    getCustomerType(data) {
        return fetch({
            url: `customer/status`,
            method: 'get',
            params: data,
            name: ""
        })
    },
    // 业务类型
    getType(data) {
        return fetch({
            url: `customer/service/type`,
            method: 'get',
            params: data
        })
    },
    // 移除联系人
    deleteClueFollowUser(data) {
        return fetch({
            url: `clue/deleteClueFollowUser`,
            method: 'post',
            params: data,
            name: "移除"
        })
    },

}
