<template>
  <div class="container">
    <el-dialog
      :title="'部门人员：'+departmentDetail.name"
      :visible.sync="dialogFormVisible"
      :close-on-click-modal="false"
      @close="$emit('closeDialog');form={}"
    >
      <el-form :model="form">
        <div class="form-div">
          <el-form-item label="角色" label-width="60px">
            <el-select
              :clearable="true"
              style="margin-right: 20px;"
              @change="getTableData()"
              v-model="form.roleName"
              placeholder="请选角色"
            >
              <el-option
                v-for="(item,index) in roleList"
                :key="index"
                :label="item.name"
                :value="item.name"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-input
              v-model="form.userName"
              style="width:200px;"
              :clearable="true"
              placeholder="请输入管理员名称"
              autocomplete="off"
            ></el-input>
            <el-button type="primary" style="margin-left:20px" @click="getTableData">搜索</el-button>
          </el-form-item>
        </div>
      </el-form>

      <div class="handle-button">
        <el-button type="primary" @click="$emit('addMember')">
          <span class="el-icon-document-copy"></span>
          新增
        </el-button>
        <el-button :disabled="!selectedItem.length" type="danger" @click="delMember">
          <span class="el-icon-delete"></span>
          删除
        </el-button>
      </div>

      <el-table
        :data="tableData"
        border
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="60" align="center"></el-table-column>
        <el-table-column prop="name" label="管理员姓名" width align="center"></el-table-column>
        <el-table-column prop="roleName" label="角色" width align="center"></el-table-column>
        <el-table-column prop="postName" label="职务" align="center"></el-table-column>
        <el-table-column prop="telnum" label="手机号" align="center"></el-table-column>
        <el-table-column prop="departName" label="部门" align="center"></el-table-column>
        <el-table-column prop="remarks" label="备注" align="center"></el-table-column>
      </el-table>
      <el-pagination
        class="itemPage"
        @current-change="currentChangePage"
        :current-page="pageObj.pageNum"
        layout="total, prev, pager, next, jumper"
        :total="pageObj.totalSize"
        :page-size="pageObj.pageSize"
      ></el-pagination>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/admin";

export default {
  name: "ExamineMember",
  //         是否展示-------------当前部门详情------角色列表
  props: ["isExamineMember", "departmentDetail", "roleList"],
  data() {
    return {
      dialogFormVisible: false,
      form: {},
      tableData: [],
      pageObj: {
        pageSize: 10,
        pageNum: 1,
        totalSize: null
      },
      selectedItem: [] // 被选中成员
    };
  },
  methods: {
    // 选项改变
    handleSelectionChange(e) {
      this.selectedItem = e;
    },
    // 删除成员
    delMember() {
      this.$confirm("是否删除所选成员？", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      })
        .then(() => {
          let arr = [];
          this.selectedItem.map(item => {
            arr.push(item.id);
          });
          console.log(arr);
          let params = {
            ids: arr.join(",")
          };
          API.deleteMember(params)
            .then(res => {
              this.$message({
                type: "success",
                message: "删除成功"
              });
              this.getTableData();
            })
            .catch(err => {
              console.error(err);
              if (err.indexOf("权限") === -1) this.$message.warning(err);
            });
        })
        .catch(() => {
          this.$message({
            type: "error",
            message: "已取消"
          });
        });
    },
    // 切换分页
    currentChangePage(e) {
      this.pageObj.pageNum = e;
      this.getTableData();
    },
    // 获取表格数据
    getTableData() {
      let params = {
        id: this.departmentDetail.id,
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize,
        roleName: this.form.roleName||null,
        userName: this.form.userName||null
      };
      API.findMember(params).then(res => {
        console.log(res);
        let data = res.data;
        this.tableData = data.list;
        this.pageObj.totalSize = data.total;
      });
    }
  },
  watch: {
    isExamineMember(value) {
      this.dialogFormVisible = value;
      if (!value) return;
      this.getTableData();
    }
  }
};
</script>


<style lang="scss" scoped>
.form-div {
  display: flex;
  justify-content: flex-start;
}
.itemPage {
  margin-top: 20px;
  margin-right: 10px;
  float: right;
}

.handle-button {
  margin-bottom: 20px;
}

/deep/.el-dialog {
  padding-bottom: 30px;
}
</style>

