<script>
import API from "@/api/customer";
import NewContact from "@/components/element/NewContact";
import NewContract from "@/components/element/NewContract";
import NewChance from "@/components/element/NewChance";
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 跟进历史
import Followup from "@/components/element/Followup"; // 写跟进
import Potential from "../components/Potential"; // 转移负责人
import TabTask from "@/components/element/TabTask";
import TabAnnex from "@/components/element/TabAnnex";
import TabChance from "@/components/element/TabChance";
import TabContract from "@/components/element/TabContract";
import TabLinkman from "@/components/element/TabLinkman";

export default {
  name: "CustomerPanoramic",
  components: {
    NewContact,
    NewContract,
    NewChance,
    Potential,
    FollowUpHistory,
    Followup,
    TabLinkman,
    TabChance,
    TabTask,
    TabAnnex,
    TabContract
  },
  data() {
    return {
      isShowLinkman: false,
      isShowChance: false,
      isShowContract: false,
      isShowPotential: false,
      isShowFollow: false,
      panoramic: {}, // 客户全景
      head: {}, // 客户全景里负责人
      customerGrade: {}, // 客户全景里客户级别
      channel: {}, // 客户全景里客户来源
      customerData: {}, // 客户资料
      linkmanTableData: [], // 联系人
      tableData: [], // 流转履历
      customerId: null
    };
  },
  methods: {
    // 保存Dialog
    getSonSure() {
      this.getSonCancel();
    },
    // 关闭Dialog
    getSonCancel() {
      this.isShowLinkman = false;
      this.isShowChance = false;
      this.isShowContract = false;
      // console.log(this.isShow,'子组件点击取消后，父组件的isShow的值')
    },
    addButton(name) {
      switch (name) {
        case "联系人":
          this.isShowLinkman = true;
          break;
        case "机会":
          this.isShowChance = true;
          break;
        case "合同":
          this.isShowContract = true;
          break;
      }
    },
    handleCommand3(name) {
      switch (name) {
        case "转移负责人":
          this.isShowPotential = true;
          break;
        case "转移至公海":
          this.$confirm(
            "移至公海后将无法在客户表中查看该客户信息，是否确认移至公海？",
            "提示",
            {
              confirmButtonText: "确定",
              cancelButtonText: "取消",
              type: "warning"
            }
          )
            .then(() => {
              let data = {
                ids: this.selectIds
              };
              API.posttoPublic(data)
                .then(res => {
                  // console.log(res)
                  this.$message({
                    type: "success",
                    message: "转移成功!"
                  });
                  this.tableApi();
                })
                .catch(err => {
                  if (err.indexOf("权限") === -1) this.$message.warning(err);
                });
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "已取消转移"
              });
            });
          break;

        default:
          break;
      }
    },
    // 编辑联系人
    handleEdit1(index, row) {
      console.log(index, row);
    },
    // 查看联系人
    handleView1(index, row) {
      console.log(index, row);
    },

    getContractPlanTable() {},
    getDetailData() {
      // 客户全景api
      API.getCustomerDetails(this.customerId).then(res => {
        // console.log(res.data)
        let data = res.data;
        // 是否加微信
        if (data.isWechat == 1) {
          data.isWechat = "是";
        } else {
          data.isWechat = "否";
        }
        // 是否面谈
        if (data.isFace == 1) {
          data.isFace = "是";
        } else {
          data.isFace = "否";
        }
        this.panoramic = data;
        this.head = data.user;
        this.customerGrade = data.customerGrade;
        this.channel = data.channel;
      });
    },
    // tab 被选中时触发
    tabClick(tab, event) {
      let id = this.customerId;
      if (tab.label == "客户全景") {
        this.getDetailData();
      } else if (tab.label == "客户资料") {
        API.getCustomerInformationVoById(id).then(res => {
          this.customerData = res.data;
        });
      } else if (tab.label == "联系人") {
        this.$refs.tabLinkman.getLinkmanTable();
      } else if (tab.label == "机会") {
        this.$refs.tabChance.getChanceTable();
      } else if (tab.label == "合同") {
        this.$refs.tabContract.getContractTable();
      } else if (tab.label == "任务") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "附件") {
        this.$refs.tabAnnex.getAnnexTable();
      } else if (tab.label == "流转履历") {
        console.log("流转履历");
      }
    }
  },
  activated() {
    let id = this.$route.query.id;
    this.customerId = id;
    this.getDetailData();
  },
  beforeRouteLeave(to, from, next) {
    if (to.name == "售后客户管理" || to.name == "回访管理") {
      this.$destroy();
      next();
    } else {
      next();
    }
  }
};
</script>
