<template>
  <div class="business-type-container">
    <div>
      
    </div>
    <div>
      <div class="business-btn">
        <el-button
          type="primary"
          v-permi="'/businessType/addType'"
          @click="showAddDialog = true"
        >新增公司</el-button>
        <!-- <el-button type="danger" :disabled="!selectedItem.length" @click="delBusiness">删除公司</el-button> -->
      </div>
      <el-table
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%;margin-top: 20px;"
        @selection-change="handleSelectionChange"
        border
        :fit="true"
      >
        <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
        <el-table-column label="公司名称" prop="companyName" width align="center"></el-table-column>
        <el-table-column label="创建时间" prop="createTime" width align="center"></el-table-column>
      </el-table>
      <!-- {{tableData}} -->
      <!-- 分页器 -->
      <div>
        <el-pagination
          class="itemPage"
          @size-change="sizeChangePage"
          @current-change="currentChangePage"
          :current-page="pageObj.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pageObj.totalSize"
        ></el-pagination>

        <!-- 添加业务的弹窗 -->
        <add-company
          @closeDialog="showAddDialog=false"
          @sureDialogFather="getTableData()"
          :showAddDialog="showAddDialog"
        ></add-company>
      </div>
    </div>
  </div>
</template>

<script>
import API from "@/api/business";
import AddCompany from "./components/AddCompany";

export default {
  name: "company",
  components: { AddCompany },
  data() {
    return {
      // 是否展示添加弹窗
      showAddDialog: false,
      // 是否展示编辑弹窗
      showEditDialog: false,
      //表格数据
      tableData: [],
      // 选中的条目
      selectedItem: [],
      // 分页器数据
      pageObj: {
        pageNum: 1,
        pageSize: 10,
        totalSize: null
      },
      // 给编辑组件的数据
      businessDetail: {}
    };
  },
  methods: {
    // 编辑
    handleEdit(index, row) {
      this.businessDetail = row;
      this.showEditDialog = true;
    },
    // 页数大小修改
    sizeChangePage(e) {
      console.log(e);
      this.pageObj.pageSize = e;
      this.getTableData();
    },
    // 更改当前页
    currentChangePage(e) {
      console.log(e);
      this.pageObj.pageNum = e;
      this.getTableData();
    },
    // 删除
    delBusiness() {
      this.$confirm("是否确认删除所选业务？", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      })
        .then(() => {
          let arr = [];
          let selectedItem = this.selectedItem;
          for (const item of selectedItem) {
            arr.push(item.id);
          }
          let params = { ids: arr.join(",") };
          API.delType(params)
            .then(res => {
              console.log(res);
              this.$message({
                type: "success",
                message: "删除成功"
              });
              this.getTableData();
            })
            .catch(err => {
              console.error(err);
              if (err.indexOf("权限") === -1) this.$message.warning(err);
            });
        })
        .catch(err => {
          console.error(err);
          this.$message({
            type: "error",
            message: "已取消"
          });
        });
    },
    // 选中条目所触发
    handleSelectionChange(e) {
      this.selectedItem = e;
    },
    // 获取表格数据
    getTableData() {
      let params = {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize
      };
      API.getCompanyList(params)
        .then(res => {
          let data = res.data;
          this.pageObj.totalSize = data.total;
          this.tableData = data.list;
        })
        .catch(err => {
          console.error(err);
          this.$message({
            type: "error",
            message: err
          });
        });
    }
  },
  mounted() {
    this.getTableData();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
</style>
