// 客户管理的api
import fetch from '@/utils/fetch';
import Qs from 'qs'
export default {
    // get 带参请求
    // 客户类型/状态
    getCustomerType(data) {
        return fetch({
            url: `customer/status`,
            method: 'get',
            params: data
        })
    },
    // 客户级别
    getCustomerLevel(data) {
        return fetch({
            url: `customer/grade`,
            method: 'get',
            params: data
        })
    },
    // 创建时间
    getCreationTime(data) {
        return fetch({
            url: `customer/findCustomerTimeCode`,
            method: 'get',
            params: data
        })
    },
    // 地区api
    getRegion(data) {
        return fetch({
            url: `area/all`,
            method: 'get',
            params: data
        })
    },
    // 所属部门的api
    getDepartments(data) {
        return fetch({
            url: `customer/findAllDepart`,
            method: 'get',
            params: data
        })
    },
    // 根据选择的所属部门的id来获取负责人
    getHead(data) {
        return fetch({
            url: `customer/findAllDepartUser/${data}`,
            method: 'get'
        })
    },
    // 根据选择的所属负责人的id来获取部门
    getDepartByUserId(data) {
        return fetch({
            url: `customer/findDepartByUserId/${data}`,
            method: 'get'
        })
    },
    // 所属负责人
    getAllUser(data) {
        return fetch({
            url: `customer/findAllUser`,
            method: 'get',
            params: data
        })
    },
    // 获取客户来源
    customerFrom(data) {
        return fetch({
            url: `clue/customerFrom`,
            method: 'get',
            params: data
        })
    },
    // 客户来源api
    getCustomerSource(data) {
        return fetch({
            url: `customer/channel`,
            method: 'get',
            params: data
        })
    },
    // 业务类型
    getType(data) {
        return fetch({
            url: `customer/service/type`,
            method: 'get',
            params: data
        })
    },
    // 根据表格id查询编辑当条数据
    getByid(data) {
        return fetch({
            url: `customer/findById/${data}`,
            method: 'get'
        })
    },
    // 根据表格id查询客户全景的当条数据
    getCustomerDetails(data) {
        return fetch({
            url: `customer/findCustomerDetails/${data}`,
            method: 'get'
        })
    },
    // 根据表格id查询客户资料
    getCustomerInformationVoById(data) {
        return fetch({
            url: `customer/findCustomerInformationVoById/${data}`,
            method: 'get'
        })
    },
    //查看跟进
    getFollowList(data) {
        return fetch({
            url: `/follow/getFollowList`,
            method: 'get',
            params: data
        })
    },
    // 导出客户excel
    exportCustomerExc(data) {
        return fetch({
            url: `/excel/getCustomer`,
            method: 'post',
            params: data,
            responseType: 'arraybuffer'
        })
    },
    // 导出签单excel
    exportSignCustomerExc(data) {
        return fetch({
            url: `/excel/getSignCutomerCount`,
            method: 'post',
            params: data,
            responseType: 'arraybuffer'
        })
    },

    // 根据表格id查询联系人
    getCustomerLinkman(data) {
        return fetch({
            url: `customer/findCustomerLinkman/${data}`,
            method: 'get'
        })
    },
    // 根据表格id查询机会
    getChanceByCustomerId(data) {
        return fetch({
            url: `customer/findChanceByCustomerId/${data}`,
            method: 'get'
        })
    },
    // 根据表格id查询合同
    getContractByCustomerId(data) {
        return fetch({
            url: `customer/findContractByCustomerId/${data}`,
            method: 'get'
        })
    },
    // 根据表格id查询任务
    findTask(data) {
        return fetch({
            url: `/task/findTask`,
            method: 'get',
            params: data
        })
    },
    // 根据表格id查询附件
    getCustomerFileById(data) {
        return fetch({
            url: `customer/findCustomerFileById/${data}`,
            method: 'get'
        })
    },
    // 所属客户
    getCustomerByCompanyName(data) {
        return fetch({
            url: `customer/findCustomerByCompanyName`,
            method: 'get',
            params: data
        })
    },
    // 下载模板
    getdownFile(data) {
        return fetch({
            url: `customer/downFile`,
            method: 'get',
            params: data
        })
    },
    // 重复任务
    getfindTaskTime(data) {
        return fetch({
            url: `customer/findTaskTime`,
            method: 'get',
            params: data
        })
    },

    // 获取所有合同状态
    getContractStatus(data) {
        return fetch({
            url: `contract/findContractStatus`,
            method: 'get',
            params: data
        })
    },

    // 任务的回显
    getfindTaskById(data) {
        return fetch({
            url: `customer/findTaskById/${data}`,
            method: 'get'
        })
    },
    // 删除任务
    getdeleteTask(data) {
        return fetch({
            url: `customer/deleteTask/${data}`,
            method: 'get'
        })
    },
    // 根据客户的id查询客户的状态
    getfindCustomerStatusById(data) {
        return fetch({
            url: `customer/findCustomerStatusById/${data}`,
            method: 'get'
        })
    },
    // post 带参请求
    // 表格数据api
    postList(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/all?${data1}`,
            method: 'post'
        })
    },
    // 新增弹框api
    postAdd(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/add?${data1}`,
            method: 'post'
        })
    },
    // 编辑客户
    postUpdate(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/updateCustomer?${data1}`,
            method: 'post'
        })
    },
    // 客户全景里新增联系人api
    postAddLinkman(data) {
        return fetch({
            url: `/customer/addLinkman`,
            method: 'post',
            params: data
        })
    },
    // 转移至公海
    posttoPublic(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/toPublic?${data1}`,
            method: 'post',
        })
    },
    // 转移负责人
    posttoOther(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/toOther?${data1}`,
            method: 'post',
        })
    },



    // 写跟进
    postaddCustomerFollow(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/addCustomerFollow?${data1}`,
            method: 'post',
        })
    },
    // 新增任务
    postaddTask(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/addTask?${data1}`,
            method: 'post',
        })
    },
    // 编辑任务
    postupdateTask(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/updateTask?${data1}`,
            method: 'post',
        })
    },
    // 上传附件
    postcustomerFile(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/customerFile?${data1}`,
            method: 'post',
        })
    },
    // 全景里的客户状态
    postchangeCustomerStatus(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/changeCustomerStatus?${data1}`,
            method: 'post',
        })
    },

    // 退回至售前
    returnCustomer(data) {
        return fetch({
            url: `/customer/returnCustomer`,
            method: 'get',
            params: data
        })
    },
}