<template>
  <div>
    <el-dialog title="写跟进" :visible.sync="outerVisible" width="40%" @close="closeDialog">
      <el-form :model="form" ref="form">
        <el-form-item
          label="内容"
          label-width="100px"
          prop="contents"
          :rules="[{ required: true, message: '内容不能为空',}]"
        >
          <el-input
            ref="contentInput"
            v-model="form.contents"
            type="textarea"
            placeholder="请输入内容"
            :autosize="{ minRows: 4, maxRows: 12}"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="sureDialog" type="success">保存</el-button>
        <el-button @click="closeDialog">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 写跟进
import API from "@/api/customer";
export default {
  name: "Followup",
  props: ["isShowFollow", "customerId"],
  data() {
    return {
      outerVisible: this.isShowFollow,
      customerStsus: [], // 客户状态
      form: {
        contents: "",
        status: ""
      }
    };
  },
  methods: {
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather", this.outerVisible);
    },
    // 保存
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let data = {
          contents: this.form.contents,
          status: this.form.status,
          customerId: this.customerId
        };
        API.postaddCustomerFollow(data).then(res => {
          this.$message({
            message: "保存成功",
            type: "success"
          });
          this.$refs.form.resetFields();
          this.$emit("sureDialogFather");
          this.closeDialog();
        });
      });
    }
  },
  watch: {
    isShowFollow(val) {
      this.outerVisible = this.isShowFollow;
      // 客户状态
      if (!val) return;
      this.$nextTick(() => {
        this.$refs.contentInput.focus();
      });
      API.getCustomerType().then(res => {
        this.customerStsus = res.data;
      });
    }
  }
};
</script>
<style lang='scss' scoped>
.item-css {
  width: 100%;
}
</style>