<template>
  <div>
    <el-dialog title="转移至线索" @close="closeDialog" :visible.sync="isShowDialog" width="40%">
      <el-form ref="form" :model="form" label-width="120px">
        <el-form-item
          label="处理意见"
          :rules="[{ required: true, message: '请输入处理意见', trigger: 'blur' }]"
          prop="opinion"
        >
          <el-input
            type="textarea"
            style="width:60%"
            placeholder="请输入内容"
            :maxlength="100"
            v-model="form.opinion"
            :autosize="{ minRows: 4, maxRows: 10}"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="submit" type="success">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/clue";

export default {
  props: ["isShowToClue", "checkboxId"],
  data() {
    return {
      isShowDialog: false,
      id: "",
      headList: [],
      departmentsList: [],
      form: {
        opinion: ""
      }
    };
  },
  methods: {
    submit() {
      let form = this.form;
      let params = {
        clueId: this.checkboxId[0],
        opinion: this.form.opinion
      };
      API.publicToClue(params).then(res => {
        if (res.status == 200) {
          this.$message({
            message: "转移成功",
            type: "success"
          });
          this.closeDialog();
          this.$emit("sureDialogFather");
        }
      });
    },
    closeDialog() {
      this.$refs.form.resetFields();

      this.isShowDialog = false;
      this.$emit("closeDialogFather");
    }
  },
  watch: {
    isShowToClue(val) {
      this.isShowDialog = val;
    }
  }
};
</script>

<style lang="scss" scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>
