<template>
  <!-- 回款记录详情 -->
  <div>
    <el-row>
      <el-col :span="24">
        <h1>{{detailData.companyName}}</h1>
      </el-col>
    </el-row>
    <el-row class="el-dro">
      <el-button type="primary" v-permi="'/payment/updateContractPaymentPlan'" @click="isEditReceivablerecord = true">编辑</el-button>
      <el-button type="danger" @click="deleteRecord">删除</el-button>
    </el-row>

    <el-tabs type="card" @tab-click="handleClick">
      <el-tab-pane label="基本信息">
        <el-card class="box-card">
          <div class="text item">
            <div class="text item">基本信息</div>
            <div class="text item">实际回款日期：{{detailData.opeDate}}</div>
            <div class="text item">回款金额：{{detailData.price}}</div>
            <div class="text item">对应客户：{{detailData.companyName}}</div>
            <div class="text item">合同名称：{{detailData.contractName}}</div>
            <!-- <div class="text item">未还金额：{{detailData.notPrice}}</div> -->
            <div class="text item">回款状态：{{detailData.isOver==1?"已完成":"未完成"}}</div>
            <el-divider></el-divider>
            <div class="text item">其他信息</div>
            <div class="text item">付款方式：{{detailData.payNo}}</div>
            <div class="text item">回款类型：{{detailData.payType}}</div>
            <!-- <div class="text item">逾期状态：{{detailData.returnStatus}}</div> -->
            <div class="text item">逾期天数：{{detailData.days}}</div>
            <div class="text item">收款人：{{detailData.userName}}</div>
            <div class="text item">备注：{{detailData.remarks}}</div>
            <el-divider></el-divider>
            <div class="text item">系统信息</div>
            <div class="text item">创建时间：{{detailData.createTime}}</div>
            <div class="text item">更新于：{{detailData.updateTime}}</div>
            <div class="text item">创建人：{{detailData.createUserName}}</div>
          </div>
        </el-card>
        <follow-up-history
          class="genjin"
          ref="followUpHistory"
          @writeFollow="isShowFollow=true"
          :customerId="customerId"
          :type="1"
        ></follow-up-history>
      </el-tab-pane>
      <el-tab-pane label="任务">
        <tab-task ref="tabTask" :customerId="customerId"></tab-task>
      </el-tab-pane>
      <el-tab-pane label="附件">
        <tab-annex ref="tabAnnex" :customerId="customerId"></tab-annex>
      </el-tab-pane>
    </el-tabs>
    <edit-receivablerecord
      :isEditReceivablerecord="isEditReceivablerecord"
      :editRecordData="detailData"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-receivablerecord>

    <Followup
      :isShowFollow="isShowFollow"
      :customerId="customerId"
      @sureDialogFather="$refs.followUpHistory.getFollowData()"
      @closeDialogFather="getSonCancel"
    ></Followup>
  </div>
</template>

<script>
// 回款记录详情
import API from "@/api/payment";
import EditReceivablerecord from "../components/EditReceivablerecord"; // 编辑
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 跟进历史
import Followup from "@/components/element/Followup"; // 写更进
import TabAnnex from "@/components/element/TabAnnex";
import TabTask from "@/components/element/TabTask";

export default {
  name: "receivablePlandetails",
  components: {
    EditReceivablerecord,
    FollowUpHistory,
    Followup,
    TabTask,
    TabAnnex
  },
  data() {
    return {
      isEditReceivablerecord: false,
      detailData: {},
      isShowFollow: false, // 是否展示跟进窗口
      customerId: null,
      file: [], // 附件
      isShowUpload: false // 上传附件
    };
  },
  methods: {
    // 删除回款记录
    deleteRecord() {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          API.postdeletePaymentPlans({ ids: this.paymentId }).then(res => {
            if (res.status == 200) {
              this.$message({
                type: "success",
                message: "删除成功!"
              });
              this.$router.go(-1);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "error",
            message: "已取消删除"
          });
        });
    },
    // 保存Dialog
    getSonSure() {
      this.getSonCancel();
      this.getDetailData();
    },
    // 关闭Dialog 弹框点击关闭的值
    getSonCancel(val) {
      this.isEditReceivablerecord = false;
      this.UploadAnnex = false;
      this.isShowFollow = false;
    },
    // tab 被选中时触发
    handleClick(tab, event) {
      if (tab.label == "基本信息") {
        this.getDetailData();
      } else if (tab.label == "任务") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "附件") {
        this.$refs.tabAnnex.getAnnexTable();
      }
    },

    getDetailData() {
      API.getPaymentById(this.paymentId).then(res => {
        console.log(res.data);
        let data = res.data;
        this.contractId = data.contractId;
        this.customerId = data.customerId;
        switch (Number(data.returnStatus)) {
          case 1:
            data.returnStatus = "未完成";
            break;
          case 2:
            data.returnStatus = "完成";
            break;
          case 3:
            data.returnStatus = "逾期";
            break;
          default:
            data.returnStatus = "未知状态";
            break;
        }
        this.detailData = res.data;
      });
    }
  },
  mounted() {
    this.paymentId = this.$route.query.id;
    this.getDetailData();
  }
};
</script>
<style lang='scss' scoped>
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-top: 20px;
  margin-bottom: 20px;
}

.text {
  font-size: 14px;
}
.item {
  margin-bottom: 18px;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;

  width: 48%;
}
.box-card {
  width: 48%;
}
</style>