import fetch from '@/utils/fetch';


export default {
	// 获取数据
	findType(data) {
		return fetch({
			url: "businessType/findType",
			methods: "get",
			params: data
		})
	},
	// 增加数据
	addType(data) {
		return fetch({
			url: "businessType/addType",
			methods: "get",
			params: data
		})
	},
	// 删除数据
	delType(data) {
		return fetch({
			url: "businessType/delType",
			methods: "get",
			params: data
		})
	},
	// 添加企业
	addCompany(data) {
		return fetch({
			url: "user/addCompany",
			methods: "get",
			params: data
		})
	},
	// 查询企业
	getCompanyList(data) {
		return fetch({
			url: "user/getCompanyList",
			methods: "get",
			params: data
		})
	},
	// 编辑修改数据
	updateType(data) {
		return fetch({
			url: "businessType/updateType",
			methods: "get",
			params: data
		})
	},
}