<template>
  <div class="information">
    <div>
      <el-form ref="form" :model="form" label-width="120px">
        <el-form-item label="头像">
          <el-upload
            :disabled="changeAuthority"
            class="avatar-uploader"
            action="https://jsonplaceholder.typicode.com/posts/"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="form.imageUrl" :src="form.imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="姓名">
          <el-input v-model="form.name" :disabled="changeAuthority"></el-input>
        </el-form-item>
        <el-form-item label="手机号码">
          <el-input v-model="form.telnum" :disabled="changeAuthority"></el-input>
        </el-form-item>
        <el-form-item label="部门">
          <el-input v-model="form.departName" :disabled="changeAuthority"></el-input>
        </el-form-item>
        <el-form-item label="职位">
          <el-input v-model="form.postName" :disabled="changeAuthority"></el-input>
        </el-form-item>
        <el-form-item label="角色">
          <el-input v-model="form.roleName" :disabled="changeAuthority"></el-input>
        </el-form-item>
      </el-form>
      <!-- <div class="btn">
        <el-button type="warning" :disabled="!changeAuthority" @click="changeAuthority=false">编辑</el-button>
        <el-button type="success" :disabled="changeAuthority" @click="changeAuthority=true">确定</el-button>
      </div>-->
    </div>
  </div>
</template>

<script>
import API from "@/api/information";
export default {
  data() {
    return {
      // 是否禁用修改
      changeAuthority: true,
      form: this.$store.state.userInfo
    };
  },
  methods: {
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    }
  }
};
</script>

<style lang="scss" scoped>
.information {
  position: relative;
  & > div {
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    width: 400px;
  }
}

.cipher {
  width: 30%;
  margin: auto;
  margin-top: 20%;
}

.el-form {
  .el-form-item {
    /deep/ .el-form-item__label {
      padding-right: 40px;
    }
  }
}
.btn {
  text-align: right;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
/deep/ .el-upload {
  border: 1px solid #ddd;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>