<template>
  <div>
    <el-dialog
      title="批量导入"
      :visible.sync="outerVisible"
      :modal-append-to-body="false"
      class="lodings2"
      @close="closeDialog"
    >
      <div>
        1.
        <el-link type="success" @click="downfile">点击下载导入数据模板</el-link>，将要导入的数据填充到数据导入模板文件中
      </div>
      <div>注意事项：</div>
      <div>（1）模板中的表头不可更改，表头行不可删除；</div>
      <div>（2）除必填的列以外，不需要的列可以删除；</div>
      <div>（3）单次导入的数据不超过10000条。</div>
      <div>2、选择要导入的数据文件。</div>
      <el-upload
        class="upload-demo"
        ref="upload"
        :action="$store.state.baseURL+'clue/uploadFile'"
        :file-list="fileList"
        :auto-upload="false"
        :on-success="uploadSuccess"
        :on-error="uploadError"
      >
        <el-button slot="trigger" size="small" type="primary">+ 选择文件</el-button>
      </el-upload>
      <div style="text-align:left;margin-top:40px">
        <el-button type="success" @click="$refs.upload.submit();">开始导入</el-button>
        <el-button type="info" @click="closeDialog">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ["uploadDialog"],
  data() {
    return {
      outerVisible: this.uploadDialog,
      fileList: []
    };
  },
  methods: {
    // 上传成功的钩子
    uploadSuccess(res) {
      if (res.status !== 200) {
        this.$message({
          type: "info",
          message: res.msg
        });
        return;
      }
      this.fileList = [];
      // this.$refs.form.resetFields();
      this.$message({
        type: "success",
        message: "保存成功"
      });
      this.$emit("sureDialogFather");
      this.closeDialog();
    },
    // 上传失败的钩子
    uploadError(res) {
      res = res.toString().slice(7);
      let msg = JSON.parse(res).message;
      this.$message({
        type: "error",
        message: "上传失败：" + msg
      });
    },
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialog");
    },
    // 下载模板
    downfile() {
      window.location.href = this.$store.state.baseURL + "clue/downFile";
    }
  },
  watch: {
    uploadDialog(newVal) {
      this.outerVisible = newVal;
      if (!newVal) return;
    }
  }
};
</script>

<style lang="scss" scoped>
/deep/.el-dialog {
  width: 40%;
  div {
    padding-left: 20px;
    margin-bottom: 20px;
  }
}
</style>
