// 公海管理的api
import fetch from '@/utils/fetch';
import Qs from 'qs'
export default {
    // get 带参请求
    // 客户级别
    getCustomerLevel(data) {
        return fetch({
            url: `customer/grade`,
            method: 'get',
            params: data
        })
    },
    // 地区api
    getRegion(data) {
        return fetch({
            url: `area/all`,
            method: 'get',
            params: data
        })
    },
    // 业务类型
    getType(data) {
        return fetch({
            url: `customer/service/type`,
            method: 'get',
            params: data
        })
    },
    // 转客户时间
    getCreationTime(data) {
        return fetch({
            url: `customer/findCustomerTimeCode`,
            method: 'get',
            params: data
        })
    },
    // 所有负责人
    getAllUser(data) {
        return fetch({
            url: `customer/findAllUser`,
            method: 'get',
            params: data
        })
    },
    // 根据表格id查询编辑当条数据
    getByid(data) {
        return fetch({
            url: `customer/findById/${data}`,
            method: 'get'
        })
    },
    // 客户来源api
    getCustomerSource(data) {
        return fetch({
            url: `customer/channel`,
            method: 'get',
            params: data
        })
    },
    // 所属部门的api
    getDepartments(data) {
        return fetch({
            url: `customer/findAllDepart`,
            method: 'get',
            params: data
        })
    },
    // 根据选择的所属部门的id来获取负责人
    getHead(data) {
        return fetch({
            url: `customer/findAllDepartUser/${data}`,
            method: 'get'
        })
    },
    // 根据选择的所属负责人的id来获取部门
    getDepartByUserId(data) {
        return fetch({
            url: `customer/findDepartByUserId/${data}`,
            method: 'get'
        })
    },
    // 客户类型
    getCustomerType(data) {
        return fetch({
            url: `customer/status`,
            method: 'get',
            params: data
        })
    },
    // 客户类型
    getfindSeaCustomerById(data) {
        return fetch({
            url: `sea/findSeaCustomerById/${data}`,
            method: 'get'
        })
    },
    // 根据表格id查询附件
    getCustomerFileById(data) {
        return fetch({
            url: `customer/findCustomerFileById/${data}`,
            method: 'get'
        })
    },
    // 根据表格id附件下载
    getCustomerFileDownload(data) {
        return fetch({
            url: `customer/customerFileDownload/${data}`,
            method: 'get'
        })
    },
    // 根据表格id附件删除
    getdeleteCustomerFile(data) {
        return fetch({
            url: `customer/deleteCustomerFile/${data}`,
            method: 'get'
        })
    },
    // post 带参请求
    // 表格数据api
    postList(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `sea/findAllSeaCustomerVo?${data1}`,
            method: 'post'
        })
    },
    //查看跟进
    getFollowList(data) {
        return fetch({
            url: `/follow/getFollowList`,
            method: 'get',
            params: data
        })
    },
    // 转移至客户api
    posttoCustomer(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `sea/toCustomer?${data1}`,
            method: 'post'
        })
    },
    // 删除公海api
    postdeleteSeaCustomer(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `sea/deleteSeaCustomer?${data1}`,
            method: 'post'
        })
    },
    // 编辑api
    postUpdate(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/updateCustomer?${data1}`,
            method: 'post'
        })
    },
}