// 回款管理

import fetch from '@/utils/fetch';
import Qs from 'qs'

export default {
	// get 带参请求
	// 计划回款日期
	getCreationTime(data) {
		return fetch({
			url: `customer/findCustomerTimeCode`,
			method: 'get',
			params: data
		})
	},
	// 回款状态
	getPaymentStatus(data) {
		return fetch({
			url: `payment/findPaymentStatus`,
			method: 'get',
			params: data
		})
	},
	//查看跟进
	getFollowList(data) {
		return fetch({
			url: `/follow/getFollowList`,
			method: 'get',
			params: data
		})
	},
	// 根据表格id查询附件
	getCustomerFileById(data) {
		return fetch({
			url: `customer/findCustomerFileById/${data}`,
			method: 'get'
		})
	},
	// 所属部门的api
	getDepartments(data) {
		return fetch({
			url: `customer/findAllDepart`,
			method: 'get',
			params: data
		})
	},
	// 所属负责人
	getAllUser(data) {
		return fetch({
			url: `customer/findAllUser`,
			method: 'get',
			params: data
		})
	},
	// 根据选择的所属部门的id来获取负责人
	getHead(data) {
		return fetch({
			url: `customer/findAllDepartUser/${data}`,
			method: 'get'
		})
	},
	// 根据选择的所属负责人的id来获取部门
	getDepartByUserId(data) {
		return fetch({
			url: `customer/findDepartByUserId/${data}`,
			method: 'get'
		})
	},
	// 回款类型
	getPaymentType(data) {
		return fetch({
			url: `payment/findPaymentType`,
			method: 'get',
			params: data
		})
	},
	// 根据回款计划的id查询回款计划的详情页面
	getPaymentById(data) {
		return fetch({
			url: `payment/findPaymentById/${data}`,
			method: 'get',
		})
	},
	// 所属客户
	getCustomerByCompanyName(data) {
		return fetch({
			url: `customer/findCustomerByCompanyName`,
			method: 'get',
			params: data
		})
	},
	// 查询所有合同
	getfindAllContractIdAndName(data) {
		return fetch({
			url: `payment/findAllContractIdAndName`,
			method: 'get',
			params: data
		})
	},
	// 根据客户id查询合同
	getContractByCustomerId(data) {
		return fetch({
			url: `customer/findContractByCustomerId/${data}`,
			method: 'get'
		})
	},
	// 回款类型
	getfindPaymentType(data) {
		return fetch({
			url: `payment/findPaymentType`,
			method: 'get',
			params: data
		})
	},
	// 支付账号
	getContractPayNo(data) {
		return fetch({
			url: `contract/findContractPayNo`,
			method: 'get',
			params: data
		})
	},
	// 根据所属客户id查询合同名称
	getpaymentfindContractById(data) {
		return fetch({
			url: `payment/findContractById/${data}`,
			method: 'get'
		})
	},
	// 根据合同的名称模糊查询客户
	getfindCustomerByContractId(data) {
		return fetch({
			url: `payment/findCustomerByContractId/${data}`,
			method: 'get'
		})
	},
	// 根据合同的id查询合同有多少计划回款期次
	getfindContractPeriods(data) {
		return fetch({
			url: `payment/findContractPeriods/${data}`,
			method: 'get'
		})
	},

	// 查询回款记录的总金额
	getfindPaymentTotalPrice(data) {
		return fetch({
			url: `payment/findPaymentTotalPrice`,
			method: 'get',
			params: data
		})
	},
	// 回款记录的回显
	getfindPayHistoryById(data) {
		return fetch({
			url: `payment/findPayHistoryById/${data}`,
			method: 'get'
		})
	},
	// post 带参请求
	// 表格数据api
	findPlanPaymentList(data) {
		return fetch({
			url: `/payment/findPlanPaymentList`,
			method: 'post',
			params: data
		})
	},
	// 表格数据api
	postListcon(data) {
		let data1 = Qs.stringify(data)
		return fetch({
			url: `payment/findAllPayHistory?${data1}`,
			method: 'post'
		})
	},

	// 回款计划的批量删除
	postdeletePaymentPlans(data) {
		let data1 = Qs.stringify(data)
		return fetch({
			url: `payment/deletePaymentPlans?${data1}`,
			method: 'post',
		})
	},

	// 添加回款计划
	addContractPaymentPlan(data) {
		return fetch({
			url: `/payment/addContractPaymentPlan`,
			method: 'post',
			params: data,
			// header: "contentType : 'application/json'"
		})
	},

	// 编辑回款计划1
	updateContractPaymentPlan(data) {
		return fetch({
			url: `/payment/updateContractPaymentPlan`,
			method: 'post',
			params: data,
			// header: "contentType : 'application/json'"
		})
	},
	// // 获取回款计划详情
	getPaymentPlanById(data) {
		return fetch({
			url: `/payment/getPaymentPlanById`,
			method: 'get',
			params: data,
			// header: "contentType : 'application/json'"
		})
	},

	// 获取合同的计划
	getPaymentListByContractId(data) {
		return fetch({
			url: `/payment/getPaymentListByContractId`,
			method: 'get',
			params: data,
			// header: "contentType : 'application/json'"
		})
	},

	//  添加回款记录
	addRealContractPayment(data) {
		return fetch({
			url: `/payment/addRealContractPayment`,
			method: 'post',
			params: data,
			// header: "contentType : 'application/json'"
		})
	},
}