<template>
  <div class="combo-container">
    <div class="business-btn">
      <el-button
        type="primary"
        v-permi="'/packageManage/addPackage'"
        @click="isAddCombo = true"
      >新增套餐</el-button>
      <el-button type="warning" @click="changeState(0)" :disabled="!selectedItem.length">停用</el-button>
      <el-button type="success" @click="changeState(1)" :disabled="!selectedItem.length">恢复启用</el-button>
      <el-button type="danger" :disabled="!selectedItem.length" @click="delCombo">删除</el-button>
    </div>

    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      @selection-change="handleSelectionChange"
      border
      :fit="true"
    >
      <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
      <el-table-column label="套餐名称" prop="name" min-width="250" align="center"></el-table-column>
      <el-table-column label="税务类型" prop="type" align="center">
        <template slot-scope="scope">{{scope.row.type|taxType}}</template>
      </el-table-column>
      <el-table-column label="最低金额" prop="money" align="center"></el-table-column>
      <el-table-column label="赠送金额" prop="moneyGift" align="center"></el-table-column>
      <el-table-column label="代账" prop="months" align="center">
        <template slot-scope="scope">{{scope.row.months+'个月'}}</template>
      </el-table-column>
      <el-table-column label="状态" prop="cycle" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.state===1" style="color:#67C23A">启用</span>
          <span v-else style="color:#E6A23C">停用</span>
        </template>
      </el-table-column>
      <el-table-column label="套餐说明" prop="description" min-width="250" align="center"></el-table-column>
      <el-table-column label="操作" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            v-permi="'/packageManage/updatePackage'"
            type="primary"
            plain
            @click="handleEdit(scope.$index, scope.row)"
          >编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页器 -->
    <div>
      <el-pagination
        class="itemPage"
        @size-change="sizeChangePage"
        @current-change="currentChangePage"
        :current-page="pageObj.pageNum"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageObj.totalSize"
      ></el-pagination>
    </div>

    <!-- 添加套餐 -->
    <add-combo
      :isAddCombo="isAddCombo"
      @sureDialogFather="getTableData()"
      @closeDialogFather="isAddCombo=false"
    ></add-combo>

    <!-- 编辑套餐 -->
    <edit-combo
      @closeDialogFather="isEditCombo=false"
      :isEditCombo="isEditCombo"
      :detailData="detailData"
      @sureDialogFather="getTableData()"
    ></edit-combo>
  </div>
</template>

<script>
import API from "@/api/combo";
import AddCombo from "./components/AddCombo";
import EditCombo from "./components/EditCombo";
export default {
  name: "combo",
  components: { AddCombo, EditCombo },
  data() {
    return {
      // 添加套餐
      isAddCombo: false,
      // 编辑套餐
      isEditCombo: false,
      // 编辑回显的数据
      detailData: null,
      // 选中的条目
      selectedItem: [],
      // 表格展示的数据
      tableData: [],
      pageObj: {
        totalSize: null,
        pageSize: 10,
        pageNum: 1
      }
    };
  },
  methods: {
    // 修改停用和启用状态
    changeState(state) {
      let handle = state == 1 ? "启用" : "停用";
      this.$confirm("是否" + handle + "所选套餐？", "确认" + handle, {
        distinguishCancelAndClose: true,
        confirmButtonText: handle,
        cancelButtonText: "取消"
      })
        .then(() => {
          let arr = [];
          for (const item of this.selectedItem) {
            arr.push(item.id);
          }
          let params = {
            ids: arr.join(","),
            state
          };
          API.updateState(params)
            .then(res => {
              console.log(res);
              this.getTableData();
            })
            .catch(err => {
              console.error(err);
              this.$message({
                message: err,
                type: "error"
              });
            });
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);

          console.error(err);
        });
    },
    // 当前页修改
    currentChangePage(e) {
      this.pageObj.pageNum = e;
      this.getTableData();
    },
    // 页面大小修改
    sizeChangePage(e) {
      this.pageObj.pageSize = e;
      this.getTableData();
    },
    // 删除套餐
    delCombo() {
      this.$confirm("是否删除所选套餐？", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      })
        .then(() => {
          let arr = [];
          for (const item of this.selectedItem) {
            arr.push(item.id);
          }
          let params = {
            ids: arr.join(",")
          };
          API.delPackage(params)
            .then(res => {
              console.log(res);
              this.$message({
                type: "success",
                message: "删除成功"
              });
              this.getTableData();
            })
            .catch(err => {
              console.error(err);
              if (err.indexOf("权限") === -1) this.$message.warning(err);
            });
        })
        .catch(err => {
          console.error(err);
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    },
    // 编辑套餐
    handleEdit(e, row) {
      this.detailData = row;
      this.isEditCombo = true;
    },
    // 修改选中触发
    handleSelectionChange(e) {
      this.selectedItem = e;
    },
    // 获取表格数据
    getTableData() {
      let params = {
        pageSize: this.pageObj.pageSize,
        pageNum: this.pageObj.pageNum
      };
      API.findPackage(params)
        .then(res => {
          let data = res.data;
          this.pageObj.totalSize = data.total;
          this.tableData = data.list;
        })
        .catch(err => {
          console.error(err);
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    }
  },
  mounted() {
    this.getTableData();
  },
  filters: {
    taxType(value) {
      if (!value) return;
      switch (value) {
        case 1:
          return "一般纳税人";
          break;
        case 2:
          return "小规模纳税人";
          break;
        case 3:
          return "外贸企业";
          break;
      }
    }
  }
};
</script>
<style lang='scss' scoped>
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
</style>