<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-col :span="8">
          <h1>{{panoramic.companyName}}</h1>
        </el-col>
        <el-col :span="16">
          <h3>负责人: {{head.name}}</h3>
        </el-col>
      </el-col>
    </el-row>
    <el-row>
      <el-dropdown trigger="click" @command="handleCommand1">
        <el-button type="primary" plain class="el-dropdown-link">
          {{commandName}}
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item
            v-for="(item, i) in customerStatusList"
            :key="i"
            :command="item.val"
            :val="item.code"
          >{{item.val}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-dropdown trigger="click" class="el-dro" @command="handleCommand2">
        <el-button type="primary" plain class="el-dropdown-link">
          添加
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="联系人" v-permi="'/customer/addLinkman'">联系人</el-dropdown-item>
          <el-dropdown-item command="机会" v-permi="'/customer/findChanceByCustomerId'">机会</el-dropdown-item>
          <el-dropdown-item command="合同" v-permi="'/contract/add'">合同</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <!-- <el-dropdown trigger="click" class="el-dro" @command="handleCommand3">
        <el-button type="primary" plain class="el-dropdown-link">
          转移
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="转移负责人">转移负责人</el-dropdown-item>
          <el-dropdown-item command="转移至公海">转移至公海</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>-->

      <el-button
        type="primary"
        class="el-dro"
        v-permi="'/customer/toOther'"
        @click="removeUser"
      >转移负责人</el-button>
      <el-button
        type="primary"
        class="el-dro"
        v-permi="'/customer/updateCustomer'"
        @click="isShowEdit=true"
      >编辑客户</el-button>
      <el-button
        type="warning"
        class="el-dro"
        v-permi="'/customer/returnCustomer'"
        v-show="panoramic.clueId"
        @click="isShowSales=true"
      >退回至售前</el-button>
      <el-button type="warning" @click="isShowSend=true">发送短信</el-button>
    </el-row>

    <el-tabs type="card" @tab-click="tabClick">
      <el-tab-pane label="客户全景" class="quanjing">
        <el-card>
          <div slot="header" class="clearfix">
            <span>基本资料</span>
          </div>

          <div class="text">
            <p>客户级别：</p>
            <p>{{customerGrade&&customerGrade.name}}</p>
          </div>
          <div class="text">
            <p>备注：</p>
            <p>{{panoramic.remarks}}</p>
          </div>
          <div class="text">
            <p>电话：</p>
            <p>{{panoramic.telephone}}</p>
          </div>
          <div class="text">
            <p>渠道来源：</p>
            <p>{{channel&&channel.name}}</p>
          </div>
          <div class="text">
            <p>客户来源：</p>
            <p>{{panoramic.customerFromName}}</p>
          </div>
          <div class="text">
            <p>业务类型：</p>
            <p>{{panoramic.type}}</p>
          </div>
          <div class="text">
            <p>是否已加微信：</p>
            <p>{{panoramic.isWechat}}</p>
          </div>
          <div class="text">
            <p>是否面谈：</p>
            <p>{{panoramic.isFace}}</p>
          </div>
          <div class="text">
            <p>客户搜索词：</p>
            <p>{{panoramic.searchKeywords}}</p>
          </div>
          <div class="text">
            <p>推广关键词：</p>
            <p>{{panoramic.saleKeywords}}</p>
          </div>
        </el-card>
        <el-card>
          <div slot="header" class="clearfix">
            <span>联系人</span>
            <el-button
              style="float: right; padding: 0;font-size: 30px;"
              type="text"
              @click="isShowLinkman=true"
            >
              <i class="el-icon-plus"></i>
            </el-button>
          </div>
          <div v-for="(item, i) in panoramic.linkmens" :key="i">
            <div class="text">
              <p>姓名：</p>
              <p>{{item.name}}</p>
            </div>
            <div class="text">
              <p>电话：</p>
              <p>{{item.mobile}}</p>
            </div>
            <el-divider></el-divider>
          </div>
        </el-card>
        <el-card class>
          <div slot="header" class="clearfix">
            <span>机会</span>
            <el-button
              style="float: right; padding: 0;font-size: 30px;"
              type="text"
              @click="isShowChance=true"
            >
              <i class="el-icon-plus"></i>
            </el-button>
          </div>
          <div v-for="(item, i) in panoramic.chances" :key="i">
            <div class="text">
              <p>机会名称：</p>
              <p>{{item.name}}</p>
            </div>
            <div class="text">
              <p>预计销售金额：</p>
              <p>￥{{item.expectPrice}}</p>
            </div>
            <div class="text">
              <p>预计签单日期：</p>
              <p>{{item.expectDate}}</p>
            </div>
            <el-divider></el-divider>
          </div>
        </el-card>
        <el-card>
          <div slot="header" class="clearfix">
            <span>合同</span>
            <el-button
              style="float: right; padding: 0;font-size: 30px;"
              type="text"
              @click="isShowContract=true"
            >
              <i class="el-icon-plus"></i>
            </el-button>
          </div>
          <div v-for="(item, i) in panoramic.contracts" :key="i">
            <div class="text">
              <p>合同名称：</p>
              <p>{{item.name}}</p>
            </div>
            <div class="text">
              <p>合同总金额：</p>
              <p>{{item.totalPrice}}</p>
            </div>
            <div class="text">
              <p>合同时间：</p>
              <p>{{item.signDate}}</p>
            </div>
            <el-divider></el-divider>
          </div>
        </el-card>
        <el-card>
          <div slot="header" class="clearfix">
            <span>交易数据</span>
          </div>
          <div>
            <div class="text">
              <p>已成交：</p>
              <p>{{totalPrice}}</p>
            </div>
            <div class="text">
              <p>已回款：</p>
              <p>{{panoramic.totalContractsPrice}}</p>
            </div>
            <!-- <div class="text">潜在交易：{{}}</div> -->
          </div>
        </el-card>
        <follow-up-history
          class="genjin"
          @writeFollow="isShowFollow= true"
          :customerId="Number(customerId)"
          :type="1"
          ref="followUpHistory"
        ></follow-up-history>
      </el-tab-pane>
      <el-tab-pane label="客户资料">
        <el-card style="width: 100%;">
          <div slot="header" class="clearfix">
            <span>客户资料</span>
          </div>
          <div class="font14">
            <div class="title">基本信息</div>
            <el-row>
              <el-col :span="8">
                <div>客户名称：{{customerData.companyName}}</div>
              </el-col>
              <el-col :span="8">
                <div>电话：{{customerData.telephone}}</div>
              </el-col>
              <el-col :span="8">
                <div>地区：{{customerData.area}}</div>
              </el-col>
              <el-col :span="8">
                <div>业务级别：{{customerData.customerGrade}}</div>
              </el-col>
              <el-col :span="8">
                <div>邮箱：{{customerData.email}}</div>
              </el-col>
              <el-col :span="8">
                <div>渠道来源：{{customerData.from}}</div>
              </el-col>
              <el-col :span="8">
                <div>业务类型：{{customerData.type}}</div>
              </el-col>
            </el-row>
            <el-divider></el-divider>
            <div class="title">其他信息</div>
            <el-row>
              <el-col :span="8">
                <div>客户状态：{{customerData.status}}</div>
              </el-col>
              <el-col :span="8">
                <div>人员规模：{{ customerData.staffSize}}</div>
              </el-col>
              <el-col :span="8">
                <div>是否已加微信：{{customerData.isWechat}}</div>
              </el-col>
              <el-col :span="8">
                <div>下次跟进时间：{{customerData.nextFollowTime}}</div>
              </el-col>
              <el-col :span="8">
                <div>是否面谈：{{customerData.isFace}}</div>
              </el-col>
              <el-col :span="8">
                <div>客户来源：{{customerData.customerFrom}}</div>
              </el-col>
              <el-col :span="8">
                <div>搜索词：{{customerData.search_keywords}}</div>
              </el-col>
              <el-col :span="8">
                <div class="remark">
                  备注：
                  <span>{{customerData.remarks}}</span>
                </div>
              </el-col>
            </el-row>
            <el-divider></el-divider>
            <div class="title">系统信息</div>
            <el-row>
              <el-col :span="8">
                <div>负责人：{{customerData.followUser}}</div>
              </el-col>
              <el-col :span="8">
                <div>创建时间：{{customerData.createTime}}</div>
              </el-col>
              <el-col :span="8">
                <div>所属部门：{{customerData.followDepart}}</div>
              </el-col>
              <el-col :span="8">
                <div>更新于：{{customerData.updateTime}}</div>
              </el-col>
            </el-row>
          </div>
        </el-card>
      </el-tab-pane>
      <el-tab-pane label="联系人">
        <tab-linkman ref="tabLinkman" :customerId="customerId"></tab-linkman>
      </el-tab-pane>
      <el-tab-pane label="机会">
        <tab-chance ref="tabChance" :customerName="panoramic.companyName" :customerId="customerId"></tab-chance>
      </el-tab-pane>
      <el-tab-pane label="合同">
        <tab-contract ref="tabContract" :panoramic="panoramic" :customerId="customerId"></tab-contract>
      </el-tab-pane>
      <el-tab-pane label="任务">
        <tab-task ref="tabTask" :customerId="customerId"></tab-task>
      </el-tab-pane>
      <el-tab-pane label="附件">
        <tab-annex ref="tabAnnex" :customerId="customerId"></tab-annex>
      </el-tab-pane>
      <el-tab-pane label="流转履历">
        <tab-log ref="tabLog" :customerId="customerId"></tab-log>
      </el-tab-pane>
    </el-tabs>
    <edit-customer
      :isShowEdit="isShowEdit"
      :customerId="customerId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-customer>
    <Potential
      :isShowPotential="isShowPotential"
      :PotentialId="PotentialId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></Potential>
    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>
    <new-contact
      :isShowLinkman="isShowLinkman"
      :customerId="customerId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-contact>
    <new-chance
      :customerId="customerId"
      :customerName="panoramic.companyName"
      :isShowChance="isShowChance"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-chance>
    <new-contract
      :isShowContract="isShowContract"
      :customerId="customerId"
      :customerDetail="panoramic"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-contract>
    <Followup
      :isShowFollow="isShowFollow"
      :customerId="customerId"
      @sureDialogFather="$refs.followUpHistory.getFollowData()"
      @closeDialogFather="getSonCancel"
    ></Followup>
    <!-- 退回至售前 -->
    <return-sales
      :isShowSales="isShowSales"
      :seletedCustomer="panoramic"
      @sureDialogFather="$router.go(-1)"
      @closeDialogFather="getSonCancel"
    ></return-sales>
  </div>
</template>

<script>
import API from "@/api/customer";
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 跟进历史
import Potential from "../components/Potential"; // 转移负责人
import EditCustomer from "@/components/element/EditCustomer"; // 编辑客户
import NewSend from "@/components/element/NewSend"; // 发送短信
import NewContact from "@/components/element/NewContact"; // 新增联系人
import NewChance from "@/components/element/NewChance"; // 新增机会
import NewContract from "@/components/element/NewContract"; // 新增合同
import ReturnSales from "../components/ReturnSales"; // 退回至售前
import Followup from "@/components/element/Followup"; // 写跟进
import EditContacts from "@/components/element/EditContacts"; // 编辑联系人
import EditChance from "@/components/element/EditChance"; // 编辑机会
import EditContract from "@/components/element/EditContract"; // 编辑合同
import TabTask from "@/components/element/TabTask";
import TabAnnex from "@/components/element/TabAnnex";
import TabChance from "@/components/element/TabChance";
import TabLog from "@/components/element/TabLog";
import TabContract from "@/components/element/TabContract";
import TabLinkman from "@/components/element/TabLinkman";

export default {
  name: "customerdetails",
  components: {
    Potential,
    FollowUpHistory,
    EditCustomer,
    ReturnSales,
    NewSend,
    TabLinkman,
    TabLog,
    TabChance,
    TabTask,
    TabAnnex,
    TabContract,
    NewContact,
    NewChance,
    NewContract,
    Followup,
    EditContacts,
    EditChance,
    EditContract
  },
  data() {
    return {
      customerStatusList: [], // 客户状态
      isShowPotential: false, // 转移负责人
      PotentialId: "", // 转移负责人id
      isShowEdit: false, // 编辑客户
      isShowSales: false, // 退回至售前
      isShowSend: false, // 发送短信
      isShowLinkman: false, // 新增联系人
      linkmanId: "", // 编辑联系人当条数据的id
      isShowChance: false, // 新增机会
      opportunityId: "", // 编辑机会当前的id
      isShowContract: false, // 新增合同
      contractId: "", // 编辑合同当前的id
      isShowFollow: false, // 写跟进
      isShowUpload: false, // 上传
      taskDetail: "", // 编辑任务当前详情
      commandName: "", // 客户状态
      panoramic: {}, // 客户全景
      head: {}, // 客户全景里负责人
      customerGrade: {}, // 客户全景里客户级别
      channel: {}, // 客户全景里客户来源
      totalPrice: "", // 客户全景里客已成交
      customerData: {}, // 客户资料
      linkmanTableData: [], // 联系人
      chanceTableData: [], // 机会
      contractTableData: [], // 合同
      tabName: "客户全景", // 当前的tab
      customerId: null, // 客户管理页面传来的id
      contractStatusList: [] // 合同状态列表
    };
  },
  methods: {
    // 点击下拉菜单项触发的事件回调
    handleCommand1(command, val) {
      // console.log(command, val.$attrs.val)
      let data = {
        id: this.customerId,
        status: val.$attrs.val
      };
      API.postchangeCustomerStatus(data).then(res => {
        this.$message({
          type: "success",
          message: "修改成功!"
        });
        this.commandName = command;
      });
    },
    handleCommand2(command) {
      if (command == "联系人") {
        this.isShowLinkman = true;
      } else if (command == "机会") {
        this.isShowChance = true;
      } else if (command == "合同") {
        this.isShowContract = true;
      }
    },
    removeUser(command) {
      // let id = this.customerId;
      // if (command == "转移负责人") {
      this.PotentialId = this.customerId;
      this.isShowPotential = true;
      // } else if (command == "转移至公海") {
      //   this.$confirm("此操作将永久转移至公海, 是否继续?", "提示", {
      //     confirmButtonText: "确定",
      //     cancelButtonText: "取消",
      //     type: "warning"
      //   })
      //     .then(() => {
      //       API.posttoPublic({ ids: id }).then(res => {
      //         if (res.status == 200) {
      //           this.$message({
      //             type: "success",
      //             message: "转移成功!"
      //           });
      //         }
      //       });
      //     })
      //     .catch(() => {
      //       this.$message({
      //         type: "error",
      //         message: "已取消转移"
      //       });
      //     });
      // }
    },
    // 保存Dialog
    getSonSure() {
      this.getSonCancel();
      this.getCustomerData();
      this.tabClick(this.tabName);
    },
    // 关闭Dialog
    getSonCancel() {
      this.isShowPotential = false;
      this.isShowEdit = false;
      this.isShowSend = false;
      this.isShowLinkman = false;
      this.isShowSales = false;
      this.isShowChance = false;
      this.isShowContract = false;
      this.isShowFollow = false;
      this.isShowUpload = false;
    },

    // 查看联系人
    handleView1(index, row) {
      this.$router.push({ path: "linkmandetails", query: { id: row.id } });
    },

    // 查看机会
    handleView2(index, row) {
      this.$router.push({ path: "opportunitydetails", query: { id: row.id } });
    },

    // 查看合同
    handleView3(index, row) {
      this.$router.push({
        path: "contractdetails",
        query: { id: row.id, paymentTotal: row.paymentTotal }
      });
    },

    // 获取联系人表格数据
    getLinkmanTable() {
      API.getCustomerLinkman(this.customerId).then(res => {
        this.linkmanTableData = res.data;
      });
    },
    // 获取合同表格数据
    getContractTable() {
      API.getContractByCustomerId(this.customerId).then(res => {
        let data = res.data;
        data.map(tableEle => {
          this.contractStatusList.map(listEle => {
            if (tableEle.status == listEle.code) {
              tableEle.status = listEle.val;
            }
          });
        });
        this.contractTableData = data;
      });
    },
    getChanceTable() {
      API.getChanceByCustomerId(this.customerId).then(res => {
        this.chanceTableData = res.data;
      });
    },
    // tab 被选中时触发
    tabClick(tab) {
      let id = this.customerId;
      let label = typeof tab === "object" ? tab.label : tab;
      this.tabName = label;
      if (label == "客户全景") {
        this.getCustomerData();
      } else if (label == "客户资料") {
        API.getCustomerInformationVoById(id).then(res => {
          this.customerData = res.data;
        });
      } else if (tab.label == "联系人") {
        this.$refs.tabLinkman.getLinkmanTable();
      } else if (tab.label == "机会") {
        this.$refs.tabChance.getChanceTable();
      } else if (tab.label == "合同") {
        this.$refs.tabContract.getContractTable();
      } else if (label == "任务") {
        this.$refs.tabTask.getTaskTable();
      } else if (label == "附件") {
        this.$refs.tabAnnex.getAnnexTable();
      } else if (label == "流转履历") {
        this.$refs.tabLog.getLogTable();
      }
    },
    getCustomerData() {
      let id = this.$route.query.id;
      // 客户全景api
      API.getCustomerDetails(id)
        .then(res => {
          // console.log(res.data);
          // 是否加微信
          if (res.data.isWechat == 1) {
            res.data.isWechat = "是";
          } else {
            // } else if(res.data.isWechat == 0){
            res.data.isWechat = "否";
          }
          // 是否面谈
          if (res.data.isFace == 1) {
            res.data.isFace = "是";
          } else {
            // } else if (res.data.isFace == 0){
            res.data.isFace = "否";
          }
          let totalPrice = 0; // 已成交
          res.data.contracts.forEach(item => {
            totalPrice += item.totalPrice;
          });
          this.totalPrice = totalPrice;
          this.panoramic = res.data;
          this.head = res.data.user;
          this.customerGrade = res.data.customerGrade;
          this.channel = res.data.channel;
        })
        .catch(err => {
          console.error(err);
        });
    }
  },
  mounted() {
    let id = this.$route.query.id;
    this.customerId = id;
    this.getCustomerData();
    API.getCustomerType().then(res => {
      // 获取全部客户状态
      let customerStatusList = res.data;
      API.getfindCustomerStatusById(id).then(res => {
        customerStatusList.forEach(item => {
          if (res.data == item.code) {
            this.commandName = item.val;
          }
        });
      });
      this.customerStatusList = customerStatusList;
    });
    // 获取合同状态列表
    API.getContractStatus().then(res => {
      this.contractStatusList = res.data;
    });
  }
};
</script>
<style lang='scss' scoped>
.el-dropdown-link {
  cursor: pointer;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-left: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
/deep/ .el-card {
  width: 48%;
  margin-bottom: 20px;
}
/deep/ .el-tab-pane {
  position: relative;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;
}
span {
  font-size: 18px;
}
div.text {
  font-size: 14px;
  margin-bottom: 18px;
  display: flex;
  & > :first-child {
    width: 100px;
    // text-align: right;
    // margin-right:10px;
  }
  & > :last-child {
    flex: 1;
    word-break: break-all;
    word-wrap: break-word;
  }
}
.font14 {
  font-size: 14px;
  line-height: 24px;
  & > div.title {
    font-weight: bold;
    font-size: 16px;
  }
}
