<template>
  <div>
    <el-form :model="form" label-width="120px">
      <el-row>
        <el-row :span="24" class="list">
          <el-form-item label="合同状态">
            <ul>
              <li
                v-for="(item, index) in contractStatusList"
                :key="index"
                @click="showStyle(index, item.code)"
                :class="active == index ? 'addclass' : '' "
              >{{item.val}}</li>
            </ul>
          </el-form-item>
        </el-row>
        <el-row :span="24" class="list">
          <el-form-item label="回款状态">
            <ul>
              <li
                v-for="(item, index) in statusList"
                :key="index"
                @click="showStyle2(index, item.code)"
                :class="active2 == index ? 'addclass' : '' "
              >{{item.val}}</li>
            </ul>
          </el-form-item>
        </el-row>
        <el-row :span="24" class="list">
          <el-form-item label="预计签单日期">
            <ul>
              <li
                v-for="(item, index) in timeCodeList"
                :key="index"
                @click="timeChange(index, item.code)"
                :class="timeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>

              <li
                @click="showDefinedTime=true;timeActive = -1"
                class="list-li"
                v-if="!showDefinedTime"
              >自定义时间段</li>
              <li v-else>
                <el-date-picker
                  v-model="form.createTime"
                  @change="timeCustom"
                  type="daterange"
                  value-format="yyyy-MM-dd"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                ></el-date-picker>
              </li>
            </ul>
          </el-form-item>
        </el-row>

        <div class="slide" :style="{'height':showMore?'60px':0}">
          <el-row>
            <el-col :span="7">
              <el-form-item label="所属部门">
                <el-select
                  v-model="form.departId"
                  placeholder="请选择"
                  clearable
                  class="item-css"
                  @change="depart($event)"
                >
                  <el-option
                    v-for="(item, index) in departmentsList"
                    :key="index"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="7">
              <el-form-item label="所属负责人">
                <el-select
                  v-model="form.userId"
                  placeholder="请选择"
                  clearable
                  class="item-css"
                  @change="tableApi"
                >
                  <el-option
                    v-for="(item, index) in followUserList"
                    :key="index"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="7">
              <el-form-item label="搜索">
                <el-input
                  v-model="form.name"
                  @keydown.enter.native="tableApi"
                  placeholder="请输入合同名称"
                  clearable
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="2">
              <el-button
                style="margin-left:20px;margin-top:7px;"
                type="primary"
                @click="tableApi()"
              >查询</el-button>
            </el-col>
          </el-row>
        </div>
        <p class="show-more-button">
          <el-button
            @click="showMore=!showMore"
            plain
            :icon="showMore?'el-icon-caret-top':'el-icon-caret-bottom'"
            type="primary"
            circle
            size="mini"
          ></el-button>
        </p>
      </el-row>
    </el-form>

    <el-row style="margin-top:20px;">
      <!-- 筛选 -->
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="primary"
        icon="el-icon-folder-add"
        v-permi="'/contract/add'"
        @click="isShowContract=true"
      >新增合同</el-button>
      <el-button
        type="danger"
        icon="el-icon-delete"
        :disabled="!checkboxId.length"
        @click="deleteContract"
      >删除</el-button>
      <el-button type="success" icon="el-icon-message" @click="exportTable" class="item">导出表格</el-button>
    </el-row>
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      :fit="true"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
      <el-table-column
        prop="customer.companyName"
        v-if="showCol.indexOf('客户名称')>-1"
        label="客户名称"
        align="center"
      ></el-table-column>
      <el-table-column prop="name" v-if="showCol.indexOf('合同名称')>-1" label="合同名称" align="center"></el-table-column>
      <el-table-column
        prop="signDate"
        v-if="showCol.indexOf('签约日期')>-1"
        label="签约日期"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="payStatusStr"
        v-if="showCol.indexOf('回款状态')>-1"
        label="回款状态"
        align="center"
      ></el-table-column>
      <el-table-column prop="user.name" v-if="showCol.indexOf('经办人')>-1" label="经办人" align="center"></el-table-column>
      <el-table-column
        prop="totalPrice"
        v-if="showCol.indexOf('合同总金额')>-1"
        label="合同总金额"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="paymentTotal"
        v-if="showCol.indexOf('已回款金额')>-1"
        label="已回款金额"
        align="center"
      ></el-table-column>
      <el-table-column v-if="showCol.indexOf('线索进入日期')>-1" label="线索进入日期" align="center">
        <template
          slot-scope="scope"
        >{{scope.row.clueCreateTime&&scope.row.clueCreateTime.slice(0,11)}}</template>
      </el-table-column>
      <el-table-column
        prop="businessType.name"
        v-if="showCol.indexOf('业务类型')>-1"
        label="业务类型"
        align="center"
      ></el-table-column>
      <el-table-column label="操作" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            plain
            permi="'/contract/updateContract'"
            @click="handleEdit(scope.$index, scope.row)"
          >编辑</el-button>
          <el-button size="mini" @click="handleView(scope.$index, scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      class="itemPage"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="page"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalSize"
    ></el-pagination>
    <new-contract
      :isShowContract="isShowContract"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-contract>
    <edit-contract
      :isShowContractEdit="isShowContractEdit"
      :contractStatusList="contractStatusList"
      :contractId="contractId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-contract>
  </div>
</template>

<script>
// 合同管理
import API from "@/api/contract";
import NewContract from "@/components/element/NewContract"; // 新增合同
import EditContract from "@/components/element/EditContract"; // 编辑
export default {
  name: "contract",
  components: {
    NewContract,
    EditContract
  },
  data() {
    return {
      contractStatusList: [], // 合同状态
      active: 0,
      showMore: false, // 收起和展开
      statusList: [], // 回款状态
      active2: 0,
      timeCodeList: [], // 预计签单日期
      timeActive: 0,
      departmentsList: [], // 所属部门
      followUserList: [], // 所属负责人
      isShowContract: false,
      isShowContractEdit: false,
      contractId: "", // 编辑当条的id
      form: {
        createTime: [],
        departId: "",
        userId: "",
        name: ""
      },
      showCol: [
        "客户名称",
        "合同名称",
        "签约日期",
        "回款状态",
        "经办人",
        "合同总金额",
        "已回款金额",
        "线索进入日期",
        "业务类型"
      ],
      colOptions: [
        "客户名称",
        "合同名称",
        "签约日期",
        "回款状态",
        "经办人",
        "合同总金额",
        "已回款金额",
        "线索进入日期",
        "业务类型"
      ],
      tableData: [],
      showDefinedTime: false, // 是否展示自定义时间
      page: 1, //当前第几页
      rows: 10, //每页默认显示几条
      totalSize: 0, //总条数
      status: null, // 合同状态
      paymentStatus: null, // 回款状态
      timeCode: null, // 预计签单日
      checkboxId: [] // 选中的
    };
  },
  methods: {
    exportTable() {
      let form = this.form;
      let startSignDate, endSignDate;
      if (form.createTime) {
        startSignDate = form.createTime[0];
        endSignDate = form.createTime[1];
      }
      let params = {
        pageNum: this.page,
        pageSize: this.rows,
        status: this.status,
        paymentStatus: this.paymentStatus,
        timeCode: this.timeCode,
        startSignDate,
        endSignDate,
        departId: form.departId,
        userId: form.userId,
        name: form.name
      };
      API.exportContractExc(params)
        .then(res => {
          console.log(res);

          var blob = new Blob([res], {
            type: "application/vnd.ms-excel;charset=utf-8"
          }); //指定格式为vnd.ms-excel
          var downloadElement = document.createElement("a");
          var href = window.URL.createObjectURL(blob); //创建下载的链接
          downloadElement.href = href;
          document.body.appendChild(downloadElement);
          downloadElement.setAttribute("download", "合同表.xls");
          downloadElement.click(); //点击下载
          document.body.removeChild(downloadElement); //下载完成移除元素
          window.URL.revokeObjectURL(href); //释放掉blob对象
        })
        .catch(err => {
          console.error(err);
        });
    },
    colChange() {
      localStorage.setItem("contractTable", JSON.stringify(this.showCol));
    },
    // 封装的table的api
    tableApi() {
      let form = this.form;
      let startSignDate, endSignDate;
      if (form.createTime) {
        startSignDate = form.createTime[0];
        endSignDate = form.createTime[1];
      }
      let params = {
        pageNum: this.page,
        pageSize: this.rows,
        status: this.status,
        paymentStatus: this.paymentStatus,
        timeCode: this.timeCode,
        startSignDate,
        endSignDate,
        departId: form.departId,
        userId: form.userId,
        name: form.name
      };
      API.postList(params).then(res => {
        console.log(res.data);
        let data = res.data;
        this.totalSize = data.total; //总条数
        for (const item of data.list) {
          item.signDate = item.signDate.slice(0, 10);
        }
        this.tableData = data.list;
      });
    },
    //选择每页显示条数触发
    sizeChangePage(val) {
      this.rows = val;
      this.tableApi(this.rows);
    },
    //选择第几页触发
    currentChangePage(val) {
      this.page = val;
      this.tableApi(this.page);
    },
    // 合同状态
    showStyle(index, code) {
      this.active = index; //将点击的元素的索引赋值给变量
      this.status = code;
      this.tableApi(this.status);
    },
    // 回款状态
    showStyle2(index, code) {
      this.active2 = index;
      this.paymentStatus = code;
      this.tableApi(this.paymentStatus);
    },
    // 预计签单日期
    timeChange(index, code) {
      this.showDefinedTime = false;
      this.form.createTime = [];
      this.timeActive = index;
      this.timeCode = code;
      this.tableApi(this.timeCode);
    },
    // 自定义时间
    timeCustom(val) {
      this.timeCode = null;
      this.tableApi();
    },
    // 所属部门
    depart(val) {
      this.form.userId = "";
      this.tableApi();
      if (val) {
        API.getHead(val).then(res => {
          // res.data.forEach(item => {
          //   item.value = item.name;
          // });
          this.followUserList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          // res.data.forEach(item => {
          //   item.value = item.name;
          // });
          this.followUserList = res.data;
        });
      }
    },
    // querySearch(queryString, cb) {
    //   var restaurants = this.followUserList;
    //   var results = queryString
    //     ? restaurants.filter(this.createFilter(queryString))
    //     : restaurants;
    //   // 调用 callback 返回建议列表的数据
    //   cb(results);
    // },
    // createFilter(queryString) {
    //   return restaurant => {
    //     return (
    //       restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
    //       0
    //     );
    //   };
    // },
    // 所属负责人id
    handleSelect(item) {
      this.userId = item.id;
      this.tableApi(this.userId);
      this.userId = null;
    },
    // table发生变化触发
    handleSelectionChange(val) {
      let arr = [];
      val.map(ele => {
        arr.push(ele.id);
      });
      this.checkboxId = arr;
    },
    // 删除选中的合同
    deleteContract() {
      this.$confirm("是否删除所选合同？", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      }).then(() => {
        let params = {
          ids: this.checkboxId.join(",")
        };
        API.postdeleteContract(params)
          .then(res => {
            this.$message({
              message: "删除成功",
              type: "success"
            });
            this.tableApi();
          })
          .catch(err => {
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    },
    // 保存Dialog
    getSonSure() {
      this.isShowContract = false;
      this.isShowContractEdit = false;
      this.tableApi();
    },
    // 关闭Dialog
    getSonCancel(val) {
      this.isShowContract = val;
      this.isShowContractEdit = val;
    },
    // 编辑
    handleEdit(index, row) {
      this.contractId = row.id;
      this.isShowContractEdit = true;
      // console.log(index, row)
    },
    // 查看
    handleView(index, row) {
      this.$router.push({
        path: "contractdetails",
        query: { id: row.id, paymentTotal: row.paymentTotal }
      });
    }
  },
  activated() {
    // 初始加载
    this.tableApi();
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("contractTable"));
    if (showCol) this.showCol = showCol;
    // 合同状态
    API.getContractStatus().then(res => {
      let obj = { code: 0, val: "全部" };
      res.data.unshift(obj);
      this.contractStatusList = res.data;
    });
    // 回款状态
    API.getPaymentStatus().then(res => {
      let obj = { code: 0, val: "全部" };
      res.data.unshift(obj);
      this.statusList = res.data;
    });
    // 预计签单日期
    API.getCustomerTimeCode().then(res => {
      // let obj = { code: 0, name: "全部" };
      // res.data.unshift(obj);
      res.data.splice(2, 1);
      res.data.splice(3, 1);
      res.data.splice(4, 1);
      this.timeCodeList = res.data;
    });
    // 所属部门的api
    API.getDepartments().then(res => {
      this.departmentsList = res.data;
    });
    // 所属负责人
    API.getAllUser().then(res => {
      // res.data.forEach(item => {
      //   item.value = item.name;
      // });
      this.followUserList = res.data;
    });
  }
};
</script>
<style lang='scss' scoped>
ul {
  list-style-type: none;
  padding: 0;
}
.item-css {
  width: 100%;
}

.show-more-button {
  margin-bottom: 20px;
  text-align: center;
}

.el-form {
  .el-form-item {
    margin: 8px 0;
  }
}

.slide {
  transition: all 0.5s;
  overflow: hidden;
}

.list {
  li {
    display: inline-block;
    padding: 0 20px;
    cursor: pointer;
  }
}

.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
</style>