<template>
  <div>
    <el-table :data="tableData" tooltip-effect="dark" style="width: 100%;" border :fit="true">
      <el-table-column prop="createTime" label="操作时间" align="center"></el-table-column>
      <el-table-column prop="createUserName" label="操作人员" align="center"></el-table-column>
      <el-table-column prop="statusName" label="操作类型" align="center"></el-table-column>
      <el-table-column prop="opinion" label="处理意见" align="center"></el-table-column>
    </el-table>
    <el-pagination
      @size-change="sizeChangePage"
      style="text-align:right;margin-top: 20px"
      layout="sizes,prev, pager, next,total"
      @current-change="currentChange"
      :page-size="pageObj.pageSize"
      :total="pageObj.total"
    ></el-pagination>
  </div>
</template>

<script>
import API from "@/api/clue";

export default {
  name: "TabLog",
  props: ["clueId", "customerId"],
  data() {
    return {
      tableData: [],
      pageObj: {
        pageSize: 10,
        pageNum: 1,
        total: null
      }
    };
  },
  methods: {
    sizeChangePage(e) {
      this.pageObj.pageSize = e;
      this.getLogTable();
    },
    getLogTable() {
      let params = {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize,
        clueId: this.clueId,
        customerId: this.customerId
      };

      let apiName = this.clueId ? "findClueLog" : "findCustomerLog";
      API[apiName](params)
        .then(res => {
          let data = res.data;

          this.tableData = data.list;
          this.pageObj.total = data.total;
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    },
    currentChange(e) {
      this.pageObj.pageNum = e;
      this.getLogTable();
    }
  }
};
</script>


<style lang="scss" scoped>
</style>





