<template>
  <div class="position">
    <div class="position-title">
      <el-button type="primary" v-permi="'/position/addPosition'" @click="isAddPosition = true">
        <span class="el-icon-document-copy"></span> 新增职位
      </el-button>
      <el-button type="danger" @click="delPosition" :disabled="!selectedItem.length">
        <span class="el-icon-delete"></span> 删除
      </el-button>
    </div>
    <div class="position-content">
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        border
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="60" align="center"></el-table-column>
        <el-table-column prop="level" label="职位级别" width="160" align="center"></el-table-column>
        <el-table-column prop="name" label="职位名称" align="center"></el-table-column>
        <el-table-column prop="createTime" label="创建时间" width="230" align="center"></el-table-column>
        <el-table-column prop="remarks" label="备注" align="center"></el-table-column>
        <el-table-column fixed="right" label="操作" width="150" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- <div style="margin-top: 20px">
                <el-button @click="toggleSelection([tableData[1], tableData[2]])">切换第二、第三行的选中状态</el-button>
                <el-button @click="toggleSelection()">取消选择</el-button>
      </div>-->
    </div>

    <!-- 分页器 -->
    <div>
      <el-pagination
        class="itemPage"
        @size-change="sizeChangePage"
        @current-change="currentChangePage"
        :current-page="pageObj.pageNum"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageObj.totalSize"
      ></el-pagination>
    </div>

    <!-- 添加职位 -->
    <add-position
      @sureDialogFather="getTableData()"
      @closeDialog="isAddPosition=false"
      :isAddPosition="isAddPosition"
    ></add-position>

    <!-- 编辑职位 -->
    <Edit-position
      @sureDialogFather="getTableData()"
      @closeDialog="isEditPosition=false"
      :isEditPosition="isEditPosition"
      :positionDetail="positionDetail"
    ></Edit-position>
  </div>
</template>

<script>
import API from "@/api/admin";
import * as filters from "@/filters/index";
import EditPosition from "../components/EditPosition";
import AddPosition from "../components/AddPosition";

export default {
  name: "position",
  components: { AddPosition, EditPosition },
  data() {
    return {
      // 新增职位弹窗
      isAddPosition: false,
      // 编辑职位弹窗
      isEditPosition: false,
      // 表格数据
      tableData: [],
      dialogFormVisible: false,
      selectedItem: [],
      // 编辑参数
      positionDetail: {},
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      formLabelWidth: "120px",
      pageObj: {
        totalSize: null,
        pageSize: 10,
        pageNum: 1
      }
    };
  },
  methods: {
    // 当前页修改
    currentChangePage(e) {
      this.pageObj.pageNum = e;
      this.getTableData();
    },
    // 页面大小修改
    sizeChangePage(e) {
      this.pageObj.pageSize = e;
      this.getTableData();
    },
    // 获取表格数据
    getTableData() {
      let params = {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize
      };
      API.findPositions(params)
        .then(res => {
          console.log(res);
          let data = res.data;
          this.pageObj.totalSize = data.total;
          data.list.map(item => {
            item.createTime = filters.parseTime(new Date(item.createTime));
          });
          this.tableData = data.list;
        })
        .catch(err => {
          console.error(err);
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    },
    // 删除选项
    delPosition() {
      this.$confirm("是否删除所选职位？", "确认删除", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "取消"
      })
        .then(() => {
          let arr = [];
          this.selectedItem.map(item => {
            arr.push(item.id);
          });
          let params = {
            ids: arr.join(",")
          };
          API.delPosition(params)
            .then(res => {
              console.log(res);
              this.$message({
                type: "success",
                message: "删除成功"
              });
              this.getTableData();
            })
            .catch(err => {
              console.error(err);
              if (err.indexOf("权限") === -1) this.$message.warning(err);
            });
        })
        .catch(() => {
          this.$message({
            type: "error",
            message: "已取消"
          });
        });
    },
    // toggleSelection(rows) {
    //     if (rows) {
    //     rows.forEach(row => {
    //         this.$refs.multipleTable.toggleRowSelection(row);
    //     });
    //     } else {
    //     this.$refs.multipleTable.clearSelection();
    //     }
    // },

    // 勾选触发
    handleSelectionChange(val, k) {
      this.selectedItem = val;
      console.log(val, k);
    },
    // 编辑按钮
    handleEdit(index, row) {
      console.log(index, row);
      this.positionDetail = row;
      this.isEditPosition = true;
    }
  },
  mounted() {
    this.getTableData();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.position {
  .position-title {
    margin-bottom: 20px;
  }
}

.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
</style>
