<template>
  <div>
    <el-dialog
      :title="contentObj.title"
      :visible.sync="outerVisible"
      width="40%"
      @close="closeDialog"
    >
      <el-form :model="form" ref="form" label-width="120px">
        <el-form-item label="所属部门" prop="departId">
          <el-select v-model="form.departId" clearable @change="departChange" placeholder="请选择">
            <el-option
              v-for="(item,index) in getDepartments"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="contentObj.label" prop="userId">
          <el-select v-model="form.userId" @change="userChange" filterable placeholder="请选择">
            <el-option
              v-for="(item,index) in headList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" :disabled="!this.form.userId" type="success">保存</el-button>
        </el-form-item>
      </el-form>
      <!-- <div slot="footer" class="dialog-footer">
      </div>-->
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/clue";

// 潜在客户转移
export default {
  name: "potential",
  props: ["isShowClueTransfer", "clueList", "transferType"],
  data() {
    return {
      outerVisible: this.isShowClueTransfer,
      contentObj: {},
      headList: [],
      getDepartments: [],
      form: {
        departId: "",
        userId: ""
      }
    };
  },
  methods: {
    // 清除负责人
    clearFollowUser() {
      if (this.form.departId) {
        this.departChange();
      }
    },
    userChange(val) {
      let departId = this.headList.find(item => {
        return item.id === val;
      }).departId;
      this.form.departId = departId;
      this.departChange("no_change");
    },
    departChange(flag) {
      if (flag !== "no_change") this.form.userId = null;
      let val = this.form.departId;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      }
    },
    // 保存
    sureDialog() {
      let arr = [];
      for (const item of this.clueList) {
        arr.push(item.id);
      }
      let params = {
        ids: arr.join(","),
        departId: this.form.departId,
        userId: this.form.userId
      };
      console.log(params);
      let api = this.contentObj.api;
      API[api](params)
        .then(res => {
          this.$message({
            message: "操作成功",
            type: "success"
          });
          this.$refs.form.resetFields();
          this.closeDialog();
          this.$emit("sureDialogFather");
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    },
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather", this.outerVisible);
    }
  },
  watch: {
    isShowClueTransfer(value) {
      this.outerVisible = this.isShowClueTransfer;
      if (!value) return;
      switch (this.transferType) {
        case "transfer":
          this.contentObj = {
            title: "线索转移",
            label: "新负责人",
            api: "toOther"
          };
          break;
        case "copyClue":
          this.contentObj = {
            title: "添加负责人",
            label: "新负责人",
            api: "copyClue"
          };
          break;
        case "remove":
          this.contentObj = {
            title: "移除负责人",
            label: "删除负责人",
            api: "deleteClueFollowUser"
          };
          break;

        default:
          break;
      }
      API.getAllUser().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.headList = res.data;
      });
      //所属部门
      API.getDepartments().then(res => {
        this.getDepartments = res.data;
      });
    }
  }
};
</script>
<style lang='scss' scoped>
</style>