import fetch from '@/utils/fetch';

export default {
	// 获取全部套餐
	findPackage(data) {
		return fetch({
			url: "packageManage/findPackage",
			method: "get",
			params: data
		})
	},

	// 增加套餐
	addPackage(data) {
		return fetch({
			url: "packageManage/addPackage",
			method: "post",
			params: data
		})
	},

	// 删除套餐
	delPackage(data) {
		return fetch({
			url: "packageManage/delPackage",
			method: "get",
			params: data
		})
	},

	// 编辑套餐
	updatePackage(data) {
		return fetch({
			url: "packageManage/updatePackage",
			method: "post",
			params: data
		})
	},

	// 修改停用启用状态
	updateState(data) {
		return fetch({
			url: "packageManage/updateState",
			method: "get",
			params: data
		})
	},

}