<template>
  <div>
    <el-dialog title="转移至公海" width="40%" :visible.sync="outerVisible" @close="closeDialog">
      <el-form :model="toHighSeasForm" ref="form" label-width="120px">
        <el-form-item
          label="处理意见"
          prop="opinion"
          :rules="[{ required: true, message: '请输入处理意见', trigger: 'blur' }]"
        >
          <el-input
            style="width:60%"
            v-model="toHighSeasForm.opinion"
            type="textarea"
            placeholder="请输入内容"
            show-word-limit
            :maxlength="100"
            :autosize="{ minRows: 4, maxRows: 8}"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <!-- <el-button @click="isShowHighSeas = false">取 消</el-button> -->
          <el-button type="primary" @click="sureDialog">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/clue";

export default {
  name: "ToHighSeas",
  props: ["isShowHighSeas", "clueDetail"],
  data() {
    return {
      outerVisible: false,
      toHighSeasForm: {
        opinion: ""
      }
    };
  },
  methods: {
    sureDialog() {
      let params = {
        opinion: this.toHighSeasForm.opinion,
        clueId: this.clueDetail.id
      };
      API.clueToPublic(params)
        .then(res => {
          this.$message({
            type: "success",
            message: "操作成功"
          });
          this.$emit("sureDialogFather");
          this.closeDialog();
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);
          console.error(err);
        });
    },
    closeDialog() {
      this.$refs.form.resetFields();
      this.outerVisible = false;
      this.$emit("closeDialogFather");
    }
  },
  watch: {
    isShowHighSeas(val) {
      this.outerVisible = val;
      if (!val) return;
    }
  }
};
</script>


<style lang="scss" scoped>
/deep/.el-dialog {
  width: 40%;
  padding: 0 20px;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>
