<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-col :span="6">
          <h1>{{message.name}}</h1>
        </el-col>
        <el-col :span="8">
          <h3>客户：{{message.companyName}}</h3>
        </el-col>
      </el-col>
    </el-row>
    <el-row class="el-dro">
      <el-button type="primary" v-permi="'/chance/updateChance'" @click="isShowChanceEdit=true">编辑</el-button>
      <el-button type="danger" @click="deleteChance">删除</el-button>
      <el-button type="warning" @click="showSend">发送短信</el-button>
    </el-row>

    <el-tabs type="card" @tab-click="handleClick">
      <el-tab-pane label="基本信息" class="quanjing">
        <el-card class="box-card">
          <div>
            <div class="text">基本信息</div>
            <div class="text">
              <p>机会名称：</p>
              <p>{{message.name}}</p>
            </div>
            <div class="text">
              <p>对应客户：</p>
              <p>{{message.companyName}}</p>
            </div>
            <div class="text">
              <p>预计销售金额：</p>
              <p>{{message.expectPrice}}</p>
            </div>
            <div class="text">
              <p>预计签单日期：</p>
              <p>{{message.expectDate}}</p>
            </div>
            <div class="text">
              <p>销售阶段：</p>
              <p>{{message.stageStr}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text">其他信息</div>
            <div class="text">
              <p>机会类型：</p>
              <p>{{message.typeStr}}</p>
            </div>
            <div class="text">
              <p>机会获取日期：</p>
              <p>{{message.expectDate}}</p>
            </div>
            <div class="text">
              <p>机会来源：</p>
              <p>{{message.sourceName}}</p>
            </div>
            <div class="text">
              <p>下次跟进时间：</p>
              <p>{{message.nextTime}}</p>
            </div>
            <div class="text">
              <p>备注：</p>
              <p>{{message.remarks}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text">系统信息</div>
            <div class="text">
              <p>负责人：</p>
              <p>{{message.userName}}</p>
            </div>
            <div class="text">
              <p>所属部门：</p>
              <p>{{message.departName}}</p>
            </div>
            <div class="text">
              <p>创建人：</p>
              <p>{{message.createUser}}</p>
            </div>
            <!-- <div>
              <p>前负责人：</p>
              <p>{{message.oldFollowUser}}</p>
              </div>
            <div>
              <p>前所属部门：</p>
              <p>{{message.oldFollowDepart}}</p>
            </div>-->
            <div class="text">
              <p>创建时间：</p>
              <p>{{message.createTime}}</p>
            </div>
            <div class="text">
              <p>更新于：</p>
              <p>{{message.updateTime}}</p>
            </div>
          </div>
        </el-card>
        <follow-up-history
          class="genjin box-card"
          @writeFollow="isShowFollow= true"
          :customerId="customerId"
          :type="1"
          ref="followUpHistory"
        ></follow-up-history>
      </el-tab-pane>
      <el-tab-pane label="任务">
        <tab-task ref="tabTask" :customerId="customerId"></tab-task>
      </el-tab-pane>
      <el-tab-pane label="附件">
        <tab-annex ref="tabAnnex" :customerId="customerId"></tab-annex>
      </el-tab-pane>

      <!-- <el-tab-pane label="流转履历">
        <el-table
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%;margin-top: 20px;"
          border
          :fit="true"
        >
          <el-table-column prop="name" label="操作时间" width align="center"></el-table-column>
          <el-table-column prop="name" label="操作人员" width align="center"></el-table-column>
          <el-table-column prop="name" label="操作类型" width align="center"></el-table-column>
          <el-table-column prop="name" label="备注" width align="center"></el-table-column>
        </el-table>
      </el-tab-pane>-->
    </el-tabs>
    <edit-chance
      :isShowChanceEdit="isShowChanceEdit"
      :customerId="customerId"
      :opportunityId="opportunityId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-chance>
    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>
    <Followup
      :isShowFollow="isShowFollow"
      :customerId="customerId"
      @sureDialogFather="$refs.followUpHistory.getFollowData()"
      @closeDialogFather="getSonCancel"
    ></Followup>
  </div>
</template>

<script>
import API from "@/api/chance";
import EditChance from "@/components/element/EditChance"; // 编辑
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 跟进历史
import NewSend from "@/components/element/NewSend"; // 发送短信
import TabAnnex from "@/components/element/TabAnnex";
import TabTask from "@/components/element/TabTask";
import Followup from "@/components/element/Followup"; // 写更进
export default {
  name: "opportunitydetails",
  components: {
    EditChance,
    NewSend,
    FollowUpHistory,
    TabTask,
    TabAnnex,
    Followup
  },
  data() {
    return {
      isShowChanceEdit: false,
      isShowSend: false,
      isShowFollow: false,
      opportunityId: "", // 当前详情页id
      customerId: null, // 客户id
      message: {}, // 基本信息
      tableData: [],
      followList: [] // 跟进记录
    };
  },
  methods: {
    handleClick(tab, event) {
      if (tab.label == "基本信息") {
        this.getDetailData();
      } else if (tab.label == "任务") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "附件") {
        this.$refs.tabAnnex.getAnnexTable();
      } else if (tab.label == "流转履历") {
        // console.log('流转履历')
      }
    },
    // 删除当前的机会
    deleteChance() {
      this.$confirm("是否删除此机会？", "确认删除", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        API.postdeleteChance({ ids: this.opportunityId }).then(res => {
          this.$message({
            message: "删除成功",
            type: "success"
          });
          this.$router.go(-1);
        });
      });
    },
    // 发送短信
    showSend() {
      this.isShowSend = true;
    },
    // 写跟进
    showFollow() {
      this.isShowFollow = true;
    },
    // 保存Dialog
    getSonSure() {
      this.isShowChanceEdit = false;
      this.isShowSend = false;
      this.isShowFollow = false;
      this.getDetailData();
    },
    // 关闭Dialog
    getSonCancel() {
      this.isShowChanceEdit = false;
      this.isShowSend = false;
      this.isShowFollow = false;
    },
    getDetailData() {
      API.getChanceDetails(this.opportunityId).then(res => {
        console.log(res.data);
        this.message = res.data;
        this.customerId = res.data.customerId;
        // this.followList = res.data.follows;
      });
    }
  },
  mounted() {
    this.opportunityId = this.$route.query.id;
    this.getDetailData();
  }
};
</script>
<style lang='scss' scoped>
.el-dropdown-link {
  cursor: pointer;
  // color: #409EFF;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-top: 20px;
  margin-bottom: 20px;
}

.text {
  font-size: 14px;
  line-height: 40px;
  display: flex;
  & > :first-child {
    width: 100px;
  }
  & > :last-child {
    flex: 1;
    word-break: break-all;
    word-wrap: break-word;
  }
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  width: 48%;
  margin-top: 20px;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;
}
.quanjing {
  position: relative;
}
span {
  font-size: 18px;
}
</style>