<template>
  <div>
    <!-- 编辑回款记录 -->
    <el-dialog title="编辑回款记录" :visible.sync="outerVisible" width="40%" @close="closeDialog">
      <el-form :model="form" label-width="120px" ref="form" :rules="rules">
        <el-form-item label="实际回款日期" prop="opDate">
          <el-date-picker
            v-model="form.opeDate"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss "
            placeholder="选择日期"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="对应客户" prop="customerId">
          <el-select v-model="form.customerId" placeholder="请选择" disabled>
            <el-option
              v-for="(item, index) in clientList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="合同名称" prop="contractId">
          <el-select
            v-model="form.contractId"
            :no-data-text="form.customerId?'此用户暂无合同':'请选择用户'"
            placeholder="请选择"
            disabled
            @change="contractChange"
          >
            <el-option
              v-for="(item, index) in contractList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="回款类型" prop="payType">
          <el-select v-model="form.payType" placeholder="请选择" clearable>
            <el-option
              v-for="(item, i) in payTypeList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属计划期次" v-if="form.payType==3" prop="planpayId">
          <el-select
            v-model="form.planpayId"
            :no-data-text="form.contractId?'此合同暂无回款计划':'请选择合同'"
            placeholder="请选择"
            clearable
          >
            <el-option
              v-for="(item, index) in periodList"
              :key="index"
              :label="item.opeDate.slice(0,10)"
              :value="item.id"
            >
              <span style="float: right; color: #8492a6; font-size: 13px">￥{{ item.price }}</span>
              <span style="float: left;">{{ item.opeDate.slice(0,10) }}</span>
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="回款金额" prop="price">
          <el-input v-model="form.price" disabled placeholder="请输入内容"></el-input>
        </el-form-item>

        <el-form-item label="本期是否完成" v-if="form.payType!==1" prop="isOver">
          <el-select v-model="form.isOver" placeholder="请选择" clearable>
            <el-option label="是" :value="1"></el-option>
            <el-option label="否" :value="0"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="付款方式" prop="payNo">
          <el-select v-model="form.payNo" placeholder="请选择">
            <el-option
              v-for="(item, i) in payaccountList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="备注">
          <el-input
            style="width:60%"
            v-model="form.remarks"
            placeholder="请输入内容"
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 10}"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success">保存</el-button>
          <el-button @click="closeDialog">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
// 编辑回款记录
import API from "@/api/payment";
export default {
  name: "EditReceivablerecord",
  props: ["isEditReceivablerecord", "editRecordData"],
  data() {
    return {
      outerVisible: this.isEditReceivablerecord,
      departmentsList: [], // 部门列表
      clientList: [], // 对应客户列表
      contractList: [], //合同名称
      periodList: [], // 所属回款计划期次
      payaccountList: [], // 支付方式
      payTypeList: [], // 回款类型
      userList: [], // 收款人列表
      form: {
        opeDate: "",
        price: "",
        customerId: "",
        contractId: "",
        planpayId: "",
        isOver: "",
        payNo: "",
        payType: "",
        departId: "",
        userId: "",
        remarks: ""
      },
      rules: {
        opeDate: [
          { required: true, message: "请选回款日期", trigger: "change" }
        ],
        customerId: [
          { required: true, message: "请选择客户", trigger: "change" }
        ],
        contractId: [
          { required: true, message: "请选择合同", trigger: "change" }
        ],
        payType: [
          { required: true, message: "请选择回款类型", trigger: "change" }
        ],
        price: [{ required: true, message: "请输入回款金额", trigger: "blur" }],
        isOver: [
          { required: true, message: "请选择是否完成", trigger: "change" }
        ],
        planpayId: [
          { required: true, message: "请选择回款期次", trigger: "change" }
        ],
        payNo: [
          { required: true, message: "请选择付款方式", trigger: "change" }
        ],
        departId: [
          { required: true, message: "请选择收款部门", trigger: "change" }
        ],
        userId: [{ required: true, message: "请选择收款人", trigger: "change" }]
      },
      contractNameId: "" // 合同名称id
    };
  },
  methods: {
    // 所属负责人
    userChange(item) {
      if (item) {
        this.form.departId = item.departId;
      }
    },
    // 清除负责人
    clearFollowUser() {
      if (this.form.departId) {
        this.departChange();
      }
    },
    departChange() {
      let val = this.form.departId;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.userList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.userList = res.data;
        });
      }
      this.form.userId = null;
    },
    // 对应客户
    clientSearch(queryString, cb) {
      var restaurants = this.clientList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    customerSelect(e) {
      // console.log(item.id)
      API.getContractByCustomerId(e.id)
        .then(res => {
          this.$nextTick(() => {
            this.contractList = res.data;
          });
        })
        .catch(err => {
          console.error("获取用户合同失败");
        });
    },
    contractChange(e) {
      console.log(e);
      API.getPaymentListByContractId({ contractId: e }).then(res => {
        console.log(res);
        this.periodList = res.data;
      });
    },
    // 保存
    sureDialog() {
      let form = this.form;
      // let userId = this.userList.find(ele => ele.value === form.userId).id;
      // let payNo = this.payaccountList.find(ele => ele.val === form.payNo).code;
      // let payType = this.payTypeList.find(ele => ele.val === form.payType).code;
      let params = {
        opeDate: form.opeDate,
        price: form.price,
        customerId: form.customerId,
        departId: form.departId,
        id: form.id,
        contractId: form.contractId,
        planpayId: form.payType == 1 ? null : form.planpayId,
        isOver: form.payType == 1 ? 1 : form.isOver,
        payNo: form.payNo,
        payType: form.payType,
        userId: form.userId,
        remarks: form.remarks
      };
      console.log(params);

      API.updateContractPaymentPlan(params)
        .then(res => {
          console.log(res);
          this.$message({
            message: "保存成功",
            type: "success"
          });
          this.$emit("sureDialogFather");
          this.closeDialog();
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });
    },
    userSearch(queryString, cb) {
      var restaurants = this.userList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      cb(results);
    },
    clearCustormer() {
      this.form.contractId = "";
      this.contractList = [];
    },
    // 关闭
    closeDialog() {
      this.$refs.form.resetFields();
      this.outerVisible = false;
      this.$emit("closeDialogFather");
    }
  },
  watch: {
    isEditReceivablerecord(val) {
      this.outerVisible = this.isEditReceivablerecord;
      if (!val) return;
      // let detail = this.editRecordData;
      let form = Object.assign({}, this.editRecordData);
      form.userId = form.departId;
      form.payNo = form.payNoCode || form.payNo;
      form.payType = form.payTypeCode || form.payType;
      this.form = form;

      API.getPaymentListByContractId({ contractId: form.contractId }).then(
        res => {
          this.periodList = res.data;
        }
      );
      API.getContractByCustomerId(this.editRecordData.customerId)
        .then(res => {
          this.$nextTick(() => {
            this.contractList = res.data;
          });
        })
        .catch(err => {
          if (err.indexOf("权限") === -1) this.$message.warning(err);
        });

      // 对应客户
      API.getCustomerByCompanyName().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.clientList = res.data;
      });
      // 回款类型
      API.getfindPaymentType().then(res => {
        this.payTypeList = res.data;
      });
      // 支付账号
      API.getContractPayNo().then(res => {
        this.payaccountList = res.data;
      });
      // 收款人
      API.getAllUser().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.userList = res.data;
      });
      // 所有部门列表
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input {
    width: auto;
  }
  // .el-input:not(.el-input--suffix) {
  //   width: 1%;
  // }
}
</style>