// 合同管理
import fetch from '@/utils/fetch';
import Qs from 'qs'
export default {
	// get 带参请求
	// 合同状态
	getContractStatus(data) {
		return fetch({
			url: `contract/findContractStatus`,
			method: 'get',
			params: data
		})
	},
	// 回款状态
	getPaymentStatus(data) {
		return fetch({
			url: `payment/findPaymentStatus`,
			method: 'get',
			params: data
		})
	},
	// 转移负责人
	changeFollowUser(data) {
		return fetch({
			url: `/contract/changeFollowUser`,
			method: 'post',
			params: data
		})
	},
	// 导出合同
	exportContractExc(data) {
		return fetch({
			url: `/excel/getContract`,
			method: 'post',
			params: data,
			responseType: 'arraybuffer'

		})
	},
	// 写批注
	replyFollow(data) {
		return fetch({
			url: `/follow/replyFollow`,
			method: 'post',
			params: data
		})
	},
	// 预计签单日期
	getCustomerTimeCode(data) {
		return fetch({
			url: `customer/findCustomerTimeCode`,
			method: 'get',
			params: data
		})
	},
	// 所属部门的api
	getDepartments(data) {
		return fetch({
			url: `customer/findAllDepart`,
			method: 'get',
			params: data
		})
	},
	// 根据选择的所属部门的id来获取负责人
	getHead(data) {
		return fetch({
			url: `customer/findAllDepartUser/${data}`,
			method: 'get'
		})
	},
	// 根据选择的所属负责人的id来获取部门
	getDepartByUserId(data) {
		return fetch({
			url: `customer/findDepartByUserId/${data}`,
			method: 'get'
		})
	},
	// 所属负责人
	getAllUser(data) {
		return fetch({
			url: `customer/findAllUser`,
			method: 'get',
			params: data
		})
	},
	// 所属客户
	getCustomerByCompanyName(data) {
		return fetch({
			url: `customer/findCustomerByCompanyName`,
			method: 'get',
			params: data
		})
	},
	// 业务类型
	getType(data) {
		return fetch({
			url: `customer/service/type`,
			method: 'get',
			params: data
		})
	},
	//查看跟进
	getFollowList(data) {
		return fetch({
			url: `/follow/getFollowList`,
			method: 'get',
			params: data
		})
	},
	// 所属套餐
	getPackageTypes(data) {
		return fetch({
			url: `contract/findContractPackageTypes`,
			method: 'get',
			params: data
		})
	},
	// 客户来源
	getchannel(data) {
		return fetch({
			url: `customer/channel`,
			method: 'get',
			params: data
		})
	},
	// 支付账号
	getContractPayNo(data) {
		return fetch({
			url: `contract/findContractPayNo`,
			method: 'get',
			params: data
		})
	},
	// 意向度
	getContractIntention(data) {
		return fetch({
			url: `contract/findContractIntention`,
			method: 'get',
			params: data
		})
	},
	// 进账类型
	getContractIncomeType(data) {
		return fetch({
			url: `contract/findContractIncomeType`,
			method: 'get',
			params: data
		})
	},
	// 跟进id查询编辑的回显
	getfindContractById(data) {
		return fetch({
			url: `contract/findContractById/${data}`,
			method: 'get'
		})
	},
	// 根据id查询合同的详细
	getContractDetails(data) {
		return fetch({
			url: `contract/findContractDetails/${data}`,
			method: 'get'
		})
	},
	// 根据表格id查询附件
	getContractFileById(data) {
		return fetch({
			url: `contract/findContractFileById/${data}`,
			method: 'get'
		})
	},
	// 客户类型/状态
	getCustomerType(data) {
		return fetch({
			url: `customer/status`,
			method: 'get',
			params: data
		})
	},
	// 根据合同的id查询合同的状态
	getfindContractStatusById(data) {
		return fetch({
			url: `contract/findContractStatusById/${data}`,
			method: 'get'
		})
	},
	// 根据所属客户id查询合同名称
	getpaymentfindContractById(data) {
		return fetch({
			url: `payment/findContractById/${data}`,
			method: 'get'
		})
	},
	// 回款类型
	getfindPaymentType(data) {
		return fetch({
			url: `payment/findPaymentType`,
			method: 'get',
			params: data
		})
	},
	// 查询所有合同
	getfindAllContractIdAndName(data) {
		return fetch({
			url: `payment/findAllContractIdAndName`,
			method: 'get',
			params: data
		})
	},
	// 根据合同的名称模糊查询客户
	getfindCustomerByContractId(data) {
		return fetch({
			url: `payment/findCustomerByContractId/${data}`,
			method: 'get'
		})
	},


	// 获取客户来源
	customerFrom(data) {
		return fetch({
			url: `clue/customerFrom`,
			method: 'get',
			params: data
		})
	},
	// 根据合同的id查询合同有多少计划回款期次
	getfindContractPeriods(data) {
		return fetch({
			url: `payment/findContractPeriods/${data}`,
			method: 'get'
		})
	},
	// 查询合同管理页面回款金额的查询(顶部金额展示)
	getfindContractPaymentPrice(data) {
		return fetch({
			url: `contract/findContractPaymentPrice/${data}`,
			method: 'get'
		})
	},
	// 查询合同的回款详情无计划回款
	getfindContractPayment(data) {
		return fetch({
			url: `contract/findContractPayment/${data}`,
			method: 'get'
		})
	},
	// 查询合同的回款详情有计划回款
	getfindContractPaymentPlans(data) {
		return fetch({
			url: `contract/findContractPaymentPlans/${data}`,
			method: 'get'
		})
	},

	//查看线索跟进
	getFollowListByClueId(data) {
		return fetch({
			url: `/follow/getFollowListByClueId`,
			method: 'get',
			params: data
		})
	},
	// 回款记录的回显
	getfindPayHistoryById(data) {
		return fetch({
			url: `payment/findPayHistoryById/${data}`,
			method: 'get'
		})
	},
	// post 带参请求
	// 表格数据api
	postList(data) {
		let data1 = Qs.stringify(data)
		return fetch({
			url: `contract/findContract?${data1}`,
			method: 'post'
		})
	},
	// 添加合同
	postaddContract(data) {
		let data1 = Qs.stringify(data)
		return fetch({
			url: `contract/add?${data1}`,
			method: 'post'
		})
	},
	// 更新合同
	postupdateContract(data) {
		let data1 = Qs.stringify(data)
		return fetch({
			url: `contract/updateContract?${data1}`,
			method: 'post'
		})
	},
	// 删除合同
	postdeleteContract(data) {
		let data1 = Qs.stringify(data)
		return fetch({
			url: `contract/deleteContract?${data1}`,
			method: 'post'
		})
	},
	// 转移负责人
	postFollowUser(data) {
		let data1 = Qs.stringify(data)
		return fetch({
			url: `contract/changeFollowUser?${data1}`,
			method: 'post'
		})
	},
	// 写跟进
	postaddCustomerFollow(data) {
		let data1 = Qs.stringify(data)
		return fetch({
			url: `customer/addCustomerFollow?${data1}`,
			method: 'post',
		})
	},
	// 根据合同得id变更合同的状态
	postchangeContractStatus(data) {
		let data1 = Qs.stringify(data)
		return fetch({
			url: `contract/changeContractStatus?${data1}`,
			method: 'post',
		})
	},

	// 回款计划的批量删除
	postdeletePaymentPlans(data) {
		let data1 = Qs.stringify(data)
		return fetch({
			url: `payment/deletePaymentPlans?${data1}`,
			method: 'post',
		})
	},
	// 回款记录的编辑
	postupdatePaymentHistory(data) {
		let data1 = Qs.stringify(data)
		return fetch({
			url: `payment/updatePaymentHistory?${data1}`,
			method: 'post',
		})
	},

}