import fetch from '@/utils/fetch';
export default {
	// 线索来源api
	getCustomerSource(data) {
		return fetch({
			url: `customer/channel`,
			method: 'get',
			params: data
		})
	},
	// 查询渠道
	getChannelList(data) {
		return fetch({
			url: `channel/getChannelList`,
			method: 'get',
			params: data
		})
	},
	// 添加渠道
	addChannel(data) {
		return fetch({
			url: `channel/addChannel`,
			method: 'post',
			params: data
		})
	},
	// 删除渠道
	delChannel(data) {
		return fetch({
			url: "channel/delChannel",
			method: "get",
			params: data
		})
	},
	// 修改渠道
	updateChannel(data) {
		return fetch({
			url: "channel/updateChannel",
			method: "get",
			params: data
		})
	},
	// 新增子销售渠道
	addChildrenChannel(data) {
		return fetch({
			url: "channel/addChildrenChannel",
			method: "post",
			params: data
		})
	},
	// 新增支付渠道
	addPayAccount(data) {
		return fetch({
			url: "payAccount/addPayAccount",
			method: "post",
			params: data
		})
	},
	// 查询支付渠道列表
	getPayAccountList(data) {
		return fetch({
			url: "payAccount/getPayAccountList",
			method: "post",
			params: data
		})
	},

}