<template>
    <div>
        <el-dialog title="发送短信" :visible.sync="outerVisible" width="40%" @close="closeDialog">
            <el-form :model="form">
                <el-form-item label="收信人" :label-width="formLabelWidth">
                    <el-checkbox v-model="form.checked">王小二</el-checkbox>
                </el-form-item>
                <el-form-item label="选择短信模版" :label-width="formLabelWidth">
                    <el-select v-model="form.value" placeholder="请选择">
                        <el-option label="不使用模板" value="1"></el-option>
                        <el-option label="使用模板" value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="短信内容" :label-width="formLabelWidth">
                    <el-input type="textarea" placeholder="请输入内容" v-model="form.textarea" maxlength="280" show-word-limit :autosize="{ minRows: 4, maxRows: 12}" ></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="sureDialog" type="success">发送</el-button>
                <el-button @click="closeDialog">取消</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
// 发送短信
export default {
    name:'newsend',
    props: {
        isShowSend:{
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            outerVisible: this.isShowSend,
            formLabelWidth: '110px',
            form: {
                checked: true,
                value: '',
                textarea: ''
            },
        };
    },
    methods: {
        // 关闭
        closeDialog(){
            this.outerVisible = false
            this.$emit('closeDialogFather',this.outerVisible);
        },
        // 保存
        sureDialog(){
            this.$message({
                message: '保存成功',
                type: 'success'
            },1000);
            this.$emit('sureDialogFather');
        },
    },
    mounted() {
    },
    watch: {
        isShowSend () {
            this.outerVisible = this.isShowSend;
        }
    }
}
</script>
<style lang='scss' scoped>

</style>