<template>
  <!-- 回款计划详情 -->
  <div>
    <el-row>
      <el-col :span="24">
        <h1>{{detailData.contractName}}</h1>
      </el-col>
    </el-row>
    <el-row class="el-dro">
      <!-- <el-button
        icon="el-icon-s-operation"
        v-permi="'/payment/updateContractPaymentPlan'"
        @click="isEditReceivableplan=true"
        :disabled="detailData.isOver==='已完成'"
      >编辑</el-button>-->
      <el-button
        type="primary"
        v-permi="'/payment/addContractPaymentPlan'"
        @click="isNewReceivablerecord=true"
      >新增回款记录</el-button>
    </el-row>

    <el-tabs type="card" @tab-click="handleClick">
      <el-tab-pane label="基本信息">
        <el-card class="box-card">
          <div class="text item">
            <div>基本信息</div>
            <div>
              <p>计划回款日期：</p>
              <p>{{detailData.opeDate&&detailData.opeDate.slice(0,11)}}</p>
            </div>
            <div>
              <p>计划回款金额：</p>
              <p>{{detailData.price}}</p>
            </div>
            <div>
              <p>合同名称：</p>
              <p>{{detailData.contractName}}</p>
            </div>
            <div>
              <p>对应客户：</p>
              <p>{{detailData.customerName}}</p>
            </div>
            <div>
              <p>备注：</p>
              <p>{{detailData.remarks}}</p>
            </div>
            <el-divider></el-divider>
            <div>其他信息</div>
            <div>
              <p>本期回款状态：</p>
              <p>{{detailData.isOver}}</p>
            </div>
            <div>
              <p>逾期天数：</p>
              <p>{{detailData.overDays}}</p>
            </div>
            <div>
              <p>已回款金额：</p>
              <p>{{detailData.price}}</p>
            </div>
            <div>
              <p>未回款金额：</p>
              <p>{{detailData.notPrice}}</p>
            </div>
            <el-divider></el-divider>
            <div>系统信息</div>
            <div>
              <p>创建时间：</p>
              <p>{{detailData.createTime}}</p>
            </div>
            <div>
              <p>更新于：</p>
              <p>{{detailData.updateTime}}</p>
            </div>
            <div>
              <p>创建人：</p>
              <p>{{detailData.createUserName}}</p>
            </div>
          </div>
        </el-card>
        <follow-up-history
          class="genjin"
          ref="followUpHistory"
          @writeFollow="isShowFollow=true"
          :customerId="detailData.customerId"
          :type="1"
        ></follow-up-history>
      </el-tab-pane>
      <el-tab-pane label="任务">
        <tab-task ref="tabTask" :customerId="detailData.customerId"></tab-task>
      </el-tab-pane>
      <el-tab-pane label="附件">
        <tab-annex ref="tabAnnex" :customerId="detailData.customerId"></tab-annex>
      </el-tab-pane>
    </el-tabs>

    <edit-receivableplan
      :isEditReceivableplan="isEditReceivableplan"
      @sureDialogFather="getSonSure"
      :editPlanData="detailData"
      @closeDialogFather="getSonCancel"
    ></edit-receivableplan>
    <new-receivablerecord
      :isNewReceivablerecord="isNewReceivablerecord"
      :detailData="detailData"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-receivablerecord>

    <Followup
      :isShowFollow="isShowFollow"
      :customerId="detailData.customerId"
      @sureDialogFather="$refs.followUpHistory.getFollowData()"
      @closeDialogFather="getSonCancel"
    ></Followup>
  </div>
</template>

<script>
// 回款计划详情
import API from "@/api/payment";
import * as filters from "@/filters/index";
import EditReceivableplan from "../components/EditReceivableplan"; // 新增回款计划
import Followup from "@/components/element/Followup"; // 写更进
import TabAnnex from "@/components/element/TabAnnex";
import TabTask from "@/components/element/TabTask";
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 跟进历史
import NewReceivablerecord from "../components/NewReceivablerecord"; // 新增回款记录
export default {
  name: "receivablePlandetails",
  components: {
    EditReceivableplan,
    NewReceivablerecord,
    FollowUpHistory,
    Followup,
    TabTask,
    TabAnnex
  },
  data() {
    return {
      isShowFollow: false,
      followList: [],
      isEditReceivableplan: false,
      isNewReceivablerecord: false,
      detailData: {} // 基本信息
    };
  },
  methods: {
    // 保存Dialog
    getSonSure() {
      this.getSonCancel();
      this.getDetailData();
    },
    // tab 被选中时触发
    handleClick(tab, event) {
      this.getDetailData();
      if (tab.label == "基本信息") {
      } else if (tab.label == "任务") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "附件") {
        this.$refs.tabAnnex.getAnnexTable();
      }
    },

    // 关闭Dialog 弹框点击关闭的值
    getSonCancel() {
      this.isShowFollow = false;
      this.isEditReceivableplan = false;
      this.isNewReceivablerecord = false;
    },
    getDetailData() {
      API.getPaymentPlanById({ id: this.$route.query.id }).then(res => {
        let data = res.data;
        data.isOver = data.isOver == 1 ? "已完成" : "未完成";
        data.overDays = data.overDays || "未逾期";
        // this.followList = data.follows
        this.detailData = data;
        console.log(this.detailData);
      });
    }
  },
  mounted() {
    this.getDetailData();
  }
};
</script>
<style lang='scss' scoped>
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-top: 20px;
  margin-bottom: 20px;
}

.text {
  font-size: 14px;
  div {
    margin-bottom: 18px;
    display: flex;
    & > :first-child {
      width: 100px;
      & > :last-child {
        flex: 1;
        word-break: break-all;
        word-wrap: break-word;
      }
    }
  }
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  width: 48%;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;
  width: 48%;
}
</style>