<template>
  <div>
    <el-dialog
      title="新增业务类型"
      :close-on-click-modal="false"
      :visible.sync="outerVisible"
      @close="closeDialog"
    >
      <el-form :model="form" label-width="100px" :rules="rules" ref="form">
        <el-form-item label="业务名称" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="服务周期" prop="cycle">
          <el-select v-model="form.cycle" placeholder="请选择">
            <el-option label="一次性" value="0"></el-option>
            <el-option label="周期性" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success">保存</el-button>
          <el-button @click="closeDialog">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/business";

export default {
  name: "NewBusiness",
  props: ["showAddDialog"],
  data() {
    return {
      outerVisible: this.showAddDialog,
      // 表单数据
      form: {
        name: "",
        cycle: ""
      },
      rules: {
        name: [{ required: true, message: "请输入业务名称", trigger: "blur" }],
        cycle: [
          { required: true, message: "请选择服务周期", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    // 确认提交
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let params = this.form;
        console.log(params);

        API.addType(params)
          .then(res => {
            this.$emit("sureDialogFather");
            this.$emit("closeDialog");
            this.$message({
              type: "success",
              message: "添加成功"
            });
          })
          .catch(err => {
            console.error(err);
            if (err.indexOf("权限") === -1) this.$message.warning(err);
          });
      });
    },
    // 关闭弹窗
    closeDialog() {
      this.$emit("closeDialog");
    }
  },
  watch: {
    showAddDialog(newVal) {
      this.outerVisible = newVal;
      if (!newVal) return;
      this.$nextTick(() => {
        this.$refs.form.resetFields();
      });
    }
  }
};
</script>

<style lang="scss" scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>

