<template>
  <div>
    <el-dialog
      title="发送短信"
      :visible.sync="outerVisible"
      :modal-append-to-body="false"
      class="lodings3"
			@close="closeDialog"
    >
      <el-form ref="form" :model="form" label-width="120px">
        <el-form-item label="收信人：" v-model="form.arrlists">
          <el-checkbox-group v-model="form.checked">
            <el-checkbox
              :label="item.guest"
              name="type"
              v-for="(item,i) in multipleSelection"
              :key="i"
              :value="item.telnum"
              @change="multipleSelections(item)"
            ></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="选择短信模版：">
          <el-select v-model="form.customerSource" placeholder="不使用模板" clearable class="item-css">
            <el-option label="一" value="1"></el-option>
            <el-option label="二" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="短信内容：">
          <el-input type="textarea" v-model="form.desc"></el-input>
        </el-form-item>
        <div style="text-align:right">
          <el-button type="primary" @click="sureDialog">保存</el-button>
          <el-button type="info" @click="closeDialog">取消</el-button>
        </div>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
// 发送短信
export default {
  name: "SendMessage",
  props: ['multipleSelection','isShowSend'],
  data() {
    return {
      outerVisible: this.isShowSend,
      formLabelWidth: "110px",
      form: {
        checked: [],
        customerSource: "",
        desc: ""
      }
    };
  },
  methods: {
    // 关闭
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather");
    },
    // 保存
    sureDialog() {
      this.$message(
        {
          message: "保存成功",
          type: "success"
        },
        1000
      );
			this.$emit("sureDialogFather");
			
    }
  },
  mounted() {},
  watch: {
    isShowSend() {
      this.outerVisible = this.isShowSend;
    }
  }
};
</script>
<style lang='scss' scoped>
</style>