<template>
  <el-card class="follow-card">
    <div slot="header" class="follow-header">
      <div>
        <p>
          <span>跟进记录</span>
          &nbsp;&nbsp;&nbsp;
        </p>
        <el-button type="warning" v-permi="'/customer/addCustomerFollow'" @click="$emit('writeFollow')">写跟进</el-button>
      </div>
      <slot></slot>
    </div>
    <div class="text">
      <div class="timeline-block">
        <el-timeline>
          <el-timeline-item
            v-for="(objItem, index) in followObj"
            :key="index"
            :timestamp="index"
            placement="top"
          >
            <el-card
              style="width: 100%;margin: 10px 0;"
              v-for="(item, index) in objItem"
              :key="index"
            >
              <div class="timeline-contents">{{item.contents}}</div>
              <div class="timeline-item">
                <span>创建人: {{item.createUserName||'未知用户'}}</span>
                <span>创建时间: {{item.createTime}}</span>
              </div>
            </el-card>
          </el-timeline-item>
        </el-timeline>
        <div class="pager">
          <el-pagination
            small
            @current-change="currentChange"
            layout="prev, pager, next"
            :total="pageObj.total"
            :page-size="pageObj.pageSize"
          ></el-pagination>
        </div>
      </div>
    </div>
  </el-card>
</template>


<script>
import API from "@/api/contract";
export default {
  props: {
    customerId: { type: Number, default: null },
    type: { type: Number, default: null }
  },
  data() {
    return {
      followList: [],
      pageObj: {
        pageNum: 1,
        pageSize: 5,
        total: 0
      }
    };
  },
  methods: {
    currentChange(a) {
      this.pageObj.pageNum = a;
      this.getFollowData();
    },
    getFollowData() {
      let params = {
        linkId: this.customerId,
        type: this.type,
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize
      };

      API.getFollowList(params).then(res => {
        let data = res.data;
        this.followList = data.list;
        this.pageObj.total = data.total;
      });
    }
  },

  watch: {
    customerId(val) {
      if (val) {
        this.$nextTick(() => {
          this.getFollowData();
        });
      }
    }
  },
  computed: {
    followObj() {
      if (!this.followList.length) return {};
      let followList = JSON.parse(JSON.stringify(this.followList));
      let followObj = {};
      for (const item of followList) {
        let followData = item.createTime && item.createTime.slice(0, 10);
        if (followObj[followData]) {
          followObj[followData].push(item);
        } else {
          followObj[followData] = [item];
        }
      }
      console.log(followObj);

      return followObj;
    }
  }
};
</script>

<style lang="scss" scoped>
.follow-card {
  .follow-header {
    font-size: 18px;
    height: 100%;
    padding: 0 20px;
    div {
      align-items: center;
      display: flex;
      justify-content: space-between;
    }
  }
  .timeline-item {
    display: flex;
    justify-content: space-between;
    margin: 20px 0 10px;
    font-size: 14px;
  }
  .timeline-contents {
    font-size: 15px;
  }
}
.pager {
  text-align: right;
}
</style>
